<template>
  <div class="alarmHistoryData mb-2">
    <div class="col-12 row text-center mx-0 table-responsive">
      <b-table
        :items="alarmHistoryDataItem"
        :fields="alarmHistoryDataFields"
        sticky-header="240px"
        :no-border-collapse="true"
        :class="{
          'alarmHistoryData-table-detail': alarmHistoryTableStyle == '',
          'machStatus-components-table':
            alarmHistoryTableStyle == 'machStatus-components-table',
        }"
        class="alarmHistoryData-table-detail-select customize_scrollbar"
      >
        <template #cell(alarmVideo)="row">
          <template v-if="row.item['alarmVideo'] == ''">無</template>
          <template v-else>
            <b-icon
              icon="cloud-download"
              aria-hidden="true"
              class="alarmHistoryData-icon"
              @click="JSdownloadfile(row.item['alarmVideo'])"
            ></b-icon>
          </template>
        </template>
      </b-table>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "alarmHistoryData",
  data() {
    return {
      noBorderCollapse: true,
    };
  },
  computed: {
    ...mapGetters({
      alarmHistoryDataFields: "mesAlarmHistoryData/get_alarmHistoryDataFields",
      alarmHistoryDataItem: "mesAlarmHistoryData/get_alarmHistoryDataItem",
      alarmHistoryTableStyle: "mesAlarmHistoryData/get_alarmHistoryTableStyle",
      deviceName: "mesAlarmHistoryData/get_deviceName",
    }),
  },
  methods: {
    ...mapActions({}),

    _jsDownloadFile(file) {
      let vm = this;
      console.log(file);
      vm.axios({
        url: `/ofco/site2/${vm.deviceName}/${file}`,
        method: "GET",
        responseType: "blob",
        onDownloadProgress: function (progressEvent) {
          let percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
        },
      })
        .then((response) => {
          console.log(response);
          var fileURL = window.URL.createObjectURL(new Blob([response.data]));
          var fileLink = document.createElement("a");
          fileLink.href = fileURL;
          fileLink.setAttribute("download", file);
          document.body.appendChild(fileLink);
          fileLink.click();
          document.body.removeChild(fileLink);
        })
        .catch(function (err) {
          console.log(err);
          console.log(err.response.status);
        });
    },
  },
};
</script>

