<template>
  <div class="commonQueryIoT">
    <div class="col-12 row text-left">
      <div class style="">
        <p class style="margin-bottom: 0.625rem" v-if="selectTag.startTime">
          日期
        </p>
        <datepicker
          v-if="selectTag.startTime"
          :date="startTime"
          :option="startoption"
        ></datepicker>
        <span v-if="selectTag.endTime">~</span>
        <datepicker
          v-if="selectTag.endTime"
          :date="endTime"
          :option="endoption"
          :limit="endoption.limit"
        ></datepicker>
      </div>
      <!-- <div class="mx-2" style="margin-top:10px;width: 100px;">
        <p class="mb-2">廠區</p>
        <b-form-select
          style="max-width: 100px;height: 2.375rem;line-height: 1.375rem;font-size: 0.875rem;border: 2px solid rgb(255, 255, 255);box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;color: rgb(95, 95, 95);"
        ></b-form-select>
      </div>
      <div class="mx-2" style="margin-top:10px;width: 100px;">
        <p class="mb-2">組別</p>
        <b-form-select
          style="max-width: 100px;height: 2.375rem;line-height: 1.375rem;font-size: 0.875rem;border: 2px solid rgb(255, 255, 255);box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;color: rgb(95, 95, 95);"
        ></b-form-select>
      </div>
      <div
        v-if="selectPage!=='workorderhistory'"
        class="mx-2"
        style="margin-top:10px;width: 100px;"
      >
        <p class="mb-2">班別</p>
        <b-form-select
          style="max-width: 100px;height: 2.375rem;line-height: 1.375rem;font-size: 0.875rem;border: 2px solid rgb(255, 255, 255);box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;color: rgb(95, 95, 95);"
        ></b-form-select>
      </div>-->
      <div v-if="selectTag.model" class="mx-2" style="width: 9rem">
        <p class style="margin-bottom: 0.625rem">型號</p>
        <b-form-select
          v-model="modelSelected"
          :options="modelOptions"
          class=""
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
        >
          <!-- This slot appears above the options from 'options' prop -->
          <template #first>
            <b-form-select-option :value="null" disabled
              >-- 選擇型號 --</b-form-select-option
            >
          </template>
        </b-form-select>
        <!-- <b-form-input
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
          v-model="deviceID"
        ></b-form-input> -->
      </div>
      <div v-if="selectTag.deviceCode" class="mx-2" style="width: 9rem">
        <p class style="margin-bottom: 0.625rem">機台</p>
        <b-form-select
          v-model="deviceSelected"
          :options="deviceOptions"
          class=""
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
        >
          <!-- This slot appears above the options from 'options' prop -->
          <template #first>
            <b-form-select-option :value="null" disabled
              >-- 選擇機台 --</b-form-select-option
            >
          </template>
        </b-form-select>
        <!-- <b-form-input
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
          v-model="deviceID"
        ></b-form-input> -->
      </div>
      <div v-if="selectTag.productCode" class="mx-2" style="width: 9rem">
        <p class style="margin-bottom: 0.625rem">產品料號</p>
        <b-form-input
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
          v-model="itemNumber"
        ></b-form-input>
      </div>
      <div v-if="selectTag.workCode" class="mx-2" style="width: 9rem">
        <p class style="margin-bottom: 0.625rem">派工單號</p>
        <b-form-input
          style="
            max-width: 9rem;
            height: 2.375rem;
            line-height: 1.375rem;
            font-size: 0.875rem;
            border: 2px solid rgb(255, 255, 255);
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
            color: rgb(95, 95, 95);
          "
          v-model="workID"
        ></b-form-input>
      </div>
      <div class="mx-2" style="width: 4.25rem">
        <b-button
          class="p-0"
          @click="_queryData"
          style="
            margin-top: 38px;
            background-color: #536172;
            border-color: #536172;
            width: 4.25rem;
            height: 2rem;
            font-size: 1.125rem;
          "
          >查詢</b-button
        >
        <div
          v-if="click_btn"
          style="
            width: 4.25rem;
            height: 2.1rem;
            background-color: transparent;
            cursor: progress;
            position: absolute;
            bottom: 0px;
          "
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import datepicker from "vue-datepicker/vue-datepicker-es6.vue";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "commonQueryIoT",
  data() {
    return {
      startTime: {
        time: "",
      },
      endTime: {
        time: "",
      },
      itemNumber: "",
      deviceID: "",
      modelSelected: "CBP136L",
      modelOptions: [],
      deviceSelected: 1,
      deviceOptions: [],
      workID: "",
      startoption: {
        type: "day",
        week: ["Mo", "Tu", "We", "Th", "Fr", "Sa", "Su"],
        month: [
          "January",
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December",
        ],
        format: "YYYY-MM-DD",
        placeholder: "",
        inputStyle: {
          display: "inline-block",
          padding: "6px",
          height: "2.375rem",
          "line-height": "2.375rem",
          "font-size": "0.875rem",
          border: "2px solid #fff",
          "box-shadow": "0 1px 3px 0 rgba(0, 0, 0, 0.2)",
          "border-radius": "2px",
          color: "#5F5F5F",
          width: "6.5rem",
        },
        color: {
          header: "#ccc",
          headerText: "#f00",
        },
        overlayOpacity: 0.5, // 0.5 as default
        dismissible: true, // as true as default
      },
      endoption: {
        type: "day",
        week: ["Mo", "Tu", "We", "Th", "Fr", "Sa", "Su"],
        month: [
          "January",
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December",
        ],
        format: "YYYY-MM-DD",
        placeholder: "",
        inputStyle: {
          display: "inline-block",
          padding: "6px",
          height: "2.375rem",
          "line-height": "2.375rem",
          "font-size": "0.875rem",
          border: "2px solid #fff",
          "box-shadow": "0 1px 3px 0 rgba(0, 0, 0, 0.2)",
          "border-radius": "2px",
          color: "#5F5F5F",
          width: "6.5rem",
        },
        color: {
          header: "#ccc",
          headerText: "#f00",
        },
        overlayOpacity: 0.5, // 0.5 as default
        dismissible: true, // as true as default
        limit: [
          {
            type: "fromto",
            from: "1995-01-01",
            to: "9999-01-01",
          },
        ],
      },
      inputtext: "",
      selected: "",
      selectedCH: {
        depID: "部門編號",
        depName: "部門名稱",
        uID: "帳號",
        uName: "姓名",
        setName: "設定名稱",
      },
      settingtime: true,
      click_btn: false,
    };
  },
  created: function () {
    let vm = this;
    vm._dateFormat("Default");
    vm._setModelOption();
    if (vm.receivequeryAgain) {
      vm._queryData();
      vm.doQueryAgain();
    }
  },
  components: {
    datepicker,
  },
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",
      queryResponse: "mesCommonQueryIoT/get_queryResponse",
      receivequeryAgain: "mesCommonQueryIoT/receive_queryAgain",
      selectCustomer: "mesData/get_selectedCustomer",
      phpfunction: "mesCommonQueryIoT/get_phpfunction",
      selectTag: "mesCommonQueryIoT/get_selectTag",
      otherData: "mesCommonQueryIoT/get_otherData",
      modelOption: "mesCommonQueryIoT/get_modelOption",
      deviceOption: "mesCommonQueryIoT/get_deviceOption",
      selectPage: "mesGetLogin/get_selectPage",
      selectPageName: "mesGetLogin/get_selectPageName",
      setTimeOutAlertMsg: "alertmodal/get_setTimeOutAlertMsg",
    }),
  },
  watch: {
    "startTime.time": {
      handler(value) {
        let vm = this;
        if (Date.parse(value).valueOf() < Date.parse("2021").valueOf()) {
          value = "2021-01-01";
          vm.startTime.time = value;
          vm.startTime.placeholder = value;
        }
        if (
          Date.parse(vm.endTime.time).valueOf() < Date.parse(value).valueOf()
        ) {
          vm.endTime.time = value;
          vm.endoption.placeholder = value;
        }
        vm.endoption.limit[0].from = value;
        vm.endoption.limit[0].to = "";
      },
      // deep: true
    },
    "endTime.time": {
      handler(value) {
        let vm = this;
        if (
          Date.parse(value).valueOf() < Date.parse(vm.startTime.time).valueOf()
        ) {
          vm.endTime.time = vm.startTime.time;
          vm.endTime.placeholder = vm.startTime.placeholder;
        }
      },
      // deep: true
    },
    modelOption: {
      handler() {
        this._setModelOption();
      },
    },
    modelSelected: {
      handler() {
        this._setDeviceOption();
      },
    },
    receivequeryAgain: {
      handler() {
        let vm = this;
        if (vm.receivequeryAgain) {
          vm._queryData();
          vm.do_queryAgain();
        }
      },
    },
  },
  methods: {
    ...mapActions({
      mesAxiosAction: "mescommonaxios/axiosAction",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
      setQueryResponse: "mesCommonQueryIoT/set_queryResponse",
      changeTableBusy: "mesCommonQueryIoT/change_tableBusy",
      doQueryAgain: "mesCommonQueryIoT/do_queryAgain",
      showLoadScreenModal: "mesLoadScreenModal/show_loadScreenModal",
      hideLoadScreenModal: "mesLoadScreenModal/hide_loadScreenModal",
      setExportFileName: "mesExportCsv/set_exportFileName",
      setQueryResultCount: "mesQueryResultCount/set_queryResultCount",
      clearQueryResultCount: "mesQueryResultCount/clear_queryResultCount",
      setSelectValue: "mesCommonQueryIoT/set_selectValue",
    }),

    _setModelOption() {
      let vm = this;
      vm.modelOptions = vm.modelOption;
    },

    _setDeviceOption() {
      let vm = this;
      vm.deviceSelected = 1;
      vm.deviceOptions = vm.deviceOption[vm.modelSelected];
    },

    //查詢API
    _queryDataFunction() {
      let vm = this;
      vm.click_btn = true;
      var params = {};
      var selectCustomer = vm.selectCustomer;
      var startTime = vm.startTime.time;
      var endTime = vm.endTime.time;
      let itemNumber = vm.itemNumber;
      let model = vm.modelSelected;
      let deviceID = vm.deviceSelected;
      let workID = vm.workID;
      let selectValue = {
        startTime: startTime + " 00:00:00",
        endTime: endTime + " 23:59:59",
        model: vm.modelSelected,
        deviceCode: vm.deviceSelected,
      };
      vm.setSelectValue(selectValue);
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = vm.phpfunction;
      params["otherData"] = vm.otherData;
      // startTime != "" ? (params["startTime"] = startTime + " 00:00:00") : "";
      endTime != "" ? (params["endTime"] = endTime + " 23:59:59") : "";
      itemNumber != "" ? (params["itemNumber"] = itemNumber) : "";
      model != "" ? (params["model"] = model) : "";
      deviceID != "" ? (params["deviceID"] = deviceID) : "";
      workID != "" ? (params["workID"] = workID) : "";
      //預設今天
      params["startTime"] = startTime + " 00:00:00";
      params["cusID"] = selectCustomer;
      // params["endTime"] = endTime + " 23:59:59";
      console.log(params);
      vm.mesAxiosAction(params).then(() => {
        vm.hideLoadScreenModal();
        vm.click_btn = false;
        var result = vm.axiosResult;
        console.log(result);
        vm.changeTableBusy();
        if (result["Response"] == "ok") {
          result["QueryTableData"] = result["QueryTableData"].sort(function (
            a,
            b
          ) {
            return a.lastUpdateTime < b.lastUpdateTime ? 1 : -1;
          });
          vm.setQueryResponse(result["QueryTableData"]);
          let exportFileName =
            vm.selectPageName +
            "_" +
            (deviceID != "" ? deviceID : "ALL") +
            "_" +
            new Date().getFullYear() +
            ("0" + (new Date().getMonth() + 1)).slice(-2) +
            ("0" + new Date().getDate()).slice(-2);
          vm.setExportFileName(exportFileName);
          // vm.setQueryResultCount(result["QueryTableData"].length);
        } else if (result["Response"] == "no data") {
          vm.setSetTimeOutAlertMsg("查無資料");
          vm.toggleSetTimeOutAlertModal();
          vm.setQueryResponse("查無資料");
        } else {
          console.log(result["Response"]);
          vm.setSetTimeOutAlertMsg(result["Response"]);
          console.log(vm.setTimeOutAlertMsg);
          vm.toggleSetTimeOutAlertModal();
        }
      });
    },

    //時間格式化
    _dateFormat(Default) {
      let vm = this;
      var now = new Date();
      if (Default === "Default") {
        var todayDate_start =
          now.getFullYear() +
          "-" +
          ("0" + (now.getMonth() + 1)).slice(-2) +
          "-" +
          ("0" + now.getDate()).slice(-2);
        var todayDate_end =
          now.getFullYear() +
          "-" +
          ("0" + (now.getMonth() + 1)).slice(-2) +
          "-" +
          ("0" + now.getDate()).slice(-2);
        vm.startoption.placeholder = todayDate_start;
        vm.startTime.time = todayDate_start;
        vm.endoption.placeholder = todayDate_end;
        vm.endTime.time = todayDate_end;
      } else {
        vm._reset([
          "startTime",
          "endTime",
          "selected",
          "inputtext",
          "settingtime",
          "itemNumber",
          "deviceID",
          "workID",
          // "modelSelected",
          "modelOptions",
          // "deviceSelected",
          "deviceOptions",
        ]);
        vm.changeTableBusy();
        vm._queryDataFunction();
      }
    },

    //查詢按鈕
    _queryData() {
      let vm = this;
      vm.showLoadScreenModal();
      if (!vm.settingtime) {
        if (vm.startTime.time == "" || vm.endTime.time == "") {
          if (vm.queryResponse == "時間尚未選擇") {
            vm.setSetTimeOutAlertMsg("時間尚未選擇");
            vm.toggleSetTimeOutAlertModal();
          }
          vm.setQueryResponse("時間尚未選擇");
          vm.hideLoadScreenModal();
          vm.click_btn = false;
          return;
        }
      }
      let selectTagKey = Object.keys(vm.selectTag);

      let tag = {
        model: {
          value: "modelSelected",
          text: "請選擇型號",
          check: true,
        },
        deviceCode: {
          value: "deviceSelected",
          text: "請選擇機台",
          check: true,
        },
        productCode: { value: "itemNumber", text: "", check: false },
        workCode: { value: "workID", text: "", check: false },
      };

      for (let i = 0; i < selectTagKey.length; i++) {
        if (selectTagKey[i] != "startTime" && selectTagKey[i] != "endTime") {
          if (vm.selectTag[selectTagKey[i]]) {
            if (tag[selectTagKey[i]].check) {
              if (
                vm[tag[selectTagKey[i]].value] == "" ||
                vm[tag[selectTagKey[i]].value] == null ||
                vm[tag[selectTagKey[i]].value] == undefined
              ) {
                vm.setSetTimeOutAlertMsg(tag[selectTagKey[i]].text);
                vm.toggleSetTimeOutAlertModal();
                vm.hideLoadScreenModal();
                vm.click_btn = false;
                return;
              }
            }
          }
        }
      }
      vm._dateFormat(vm.selected);
    },

    //data reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.custom-select {
  width: 200px !important;
}
.card-title {
  font-size: 25px;
}
.input-title {
  width: 200px !important;
  display: inline-block;
  margin-top: 5px;
}
.commonQueryIoT div,
.commonQueryIoT div p {
  font-family: dfyuanstd-w7;
}
</style>