<template>
    <!-- <div id="app" style="height: 100%; min-width: 650px"> -->
    <div id="app" style="height: 100%; min-width: 580px">
        <router-view />
        <alertModal />
    </div>
</template>

<script>
import "jquery";
import "bootstrap";
import { mapGetters, mapActions } from "vuex";
import { childRouter } from "@/router/childRouter";
import alertModal from "@/components/alertModal.vue";
import modal from "@/components/modal.vue";
export default {
    name: "App",
    computed: {
        ...mapGetters({
            loginData: "getlogin/get_loginData",
            DEFAULT_pageAccess: "getlogin/get_DEFAULT_pageAccess",
            apiDetail: "apidetail/get_apiDetail",
            sandboxApiMap: "apidetail/get_sandboxApiMap",
            apiMap: "apidetail/get_apiMap",
            clickChild: "featurelistbar/get_clickChild",
            collapseIDList: "featurelistbar/get_collapseIDList",
        }),
    },
    components: {
        modal,
        alertModal,
    },
    methods: {
        ...mapActions({
            changeLoginData: "getlogin/change_loginData",
            changePageAccess: "getlogin/change_pageAccess",
            setApiDetail: "apidetail/set_apiDetail",
            setSandboxApiMap: "apidetail/set_sandboxApiMap",
            setApiMap: "apidetail/set_apiMap",
            setClickChild: "featurelistbar/set_clickChild",
            setCollapseIDList: "featurelistbar/set_collapseIDList",
            // setSystem: "apidetail/set_system",
        }),
    },
    created: function () {
        console.log(sessionStorage.getItem("loginStatus"));
        var vm = this;
        console.log(vm.$router.currentRoute.path);
        if (sessionStorage.getItem("api_map") != null) {
            vm.setApiMap(JSON.parse(sessionStorage.getItem("api_map")));
            sessionStorage.removeItem("api_map");
        }
        // if (sessionStorage.getItem("system") != null) {
        //     console.log("^^^^^^^^^^^^^");
        //     vm.setSystem(sessionStorage.getItem("system"));
        //     sessionStorage.removeItem("system");
        // }

        if (sessionStorage.getItem("apiDetail") != null) {
            vm.setApiDetail(JSON.parse(sessionStorage.getItem("apiDetail")));
            sessionStorage.removeItem("apiDetail");
        }

        if (sessionStorage.getItem("clickChild") != null) {
            this.setClickChild(
                JSON.parse(sessionStorage.getItem("clickChild"))
            );
            sessionStorage.removeItem("clickChild");
        }

        if (sessionStorage.getItem("collapseIDList") != null) {
            this.setCollapseIDList(
                JSON.parse(sessionStorage.getItem("collapseIDList"))
            );
            sessionStorage.removeItem("collapseIDList");
        }

        console.log(this.$router.currentRoute.path);

        if (vm.$router.currentRoute.path === "/sandbox") {
            vm.setSandboxApiMap(
                JSON.parse(sessionStorage.getItem("sandboxApiMap"))
            );
            sessionStorage.removeItem("sandboxApiMap");
        } else {
            sessionStorage.removeItem("sandboxApiMap");
        }

        if (
            vm.$router.currentRoute.path != "/login" &&
            vm.$router.currentRoute.path != "/index"
        ) {
            console.log("###########");
            //在頁面載入時讀取sessionStorage裡的狀態資訊並更新vuex資料，更新完成後remove sessionStorage
            if (
                sessionStorage.getItem("loginStatus") != null &&
                sessionStorage.getItem("loginStatus") != "undefined"
            ) {
                var obj = {};
                obj.account = sessionStorage.getItem("loginAccount");
                obj.status = Boolean(sessionStorage.getItem("loginStatus"));
                obj.username = sessionStorage.getItem("loginUsername");
                obj.customerId = sessionStorage.getItem("loginCustomerId");
                obj.accesslist = JSON.parse(
                    sessionStorage.getItem("loginAccessList")
                );
                var access = JSON.parse(
                    sessionStorage.getItem("loginAccessList")
                );
                console.log(access);
                vm.changeLoginData(obj);
                let trueList = [];
                Object.entries(access).forEach((element) => {
                    if (element[1]["authority"]) {
                        if (element[1].hasOwnProperty("children")) {
                            Object.keys(element[1].children).map((item) => {
                                if (element[1].children[item].authority) {
                                    trueList.push(element[0] + "/" + item);
                                }
                            });
                        } else {
                            trueList.push(element[0]);
                            if (element[0] === "apilist") {
                                trueList.push("apilist/:endpoint");
                            }
                        }
                    }
                });
                console.log(trueList);
                trueList.push("home");
                childRouter[0].children = childRouter[0].children.filter(
                    (item) => trueList.includes(item.path)
                );
                vm.changePageAccess(access);
                console.log(childRouter);
                vm.$router.addRoutes(childRouter);
                sessionStorage.removeItem("loginStatus");
                sessionStorage.removeItem("loginUsername");
                sessionStorage.removeItem("loginAccount");
                sessionStorage.removeItem("loginCustomerId");
                sessionStorage.removeItem("loginAccessList");
            } else {
                vm.$router.push("/login");
            }
        }
        console.log(vm.loginData);
        console.log(vm.loginData.status);
        //在頁面重新整理時將vuex裡的資訊儲存到localStorage裡
        window.addEventListener("beforeunload", () => {
            if (vm.$router.currentRoute.path != "/index") {
                if (vm.loginData.status === true) {
                    sessionStorage.setItem(
                        "loginStatus",
                        Boolean(vm.loginData.status)
                    );
                    sessionStorage.setItem(
                        "loginUsername",
                        vm.loginData.username
                    );
                    sessionStorage.setItem(
                        "loginAccount",
                        vm.loginData.account
                    );
                    sessionStorage.setItem(
                        "loginCustomerId",
                        vm.loginData.customerId
                    );
                    sessionStorage.setItem(
                        "loginAccessList",
                        JSON.stringify(vm.loginData.accesslist)
                    );

                    if (Object.keys(vm.apiDetail).length != 0) {
                        sessionStorage.setItem(
                            "apiDetail",
                            JSON.stringify(vm.apiDetail)
                        );
                    }
                    sessionStorage.setItem(
                        "api_map",
                        JSON.stringify(vm.apiMap)
                    );
                } else {
                    console.log("@@@@@@@@");
                    var obj = {};
                    obj.account = null;
                    obj.status = false;
                    obj.username = null;
                    obj.accesslist = null;
                    var obj2 = {};
                    //Deep copy
                    var pageAccessobj = Object.assign(
                        {},
                        vm.DEFAULT_pageAccess
                    );
                    vm.changePageAccess(pageAccessobj);
                    vm.changeLoginData(obj);
                    vm.$router.push("/login");
                }
            }

            if (vm.$router.currentRoute.path === "/sandbox") {
                sessionStorage.setItem(
                    "sandboxApiMap",
                    JSON.stringify(vm.sandboxApiMap)
                );
            }

            sessionStorage.setItem(
                "clickChild",
                JSON.stringify(this.clickChild)
            );
            sessionStorage.setItem(
                "collapseIDList",
                JSON.stringify(this.collapseIDList)
            );
        });
    },
};
</script>

<style lang="scss">
@import "~bootstrap/scss/bootstrap";
@import "~bootstrap-vue/src/index.scss";
* {
    font-family: "微軟正黑體";
    outline: none; //去除點擊的藍邊框
}
