<template>
    <div id="Edoc" ref="Edoc">
        <b-row
            class="h-100 mh-100"
            :class="{ publicMinWidth550: this_route != 'edoc' }"
        >
            <template v-if="this_route != 'edoc'">
                <featureListBar />
                <div class="featureContent customize_scrollbar h-100 mh-100">
                    <!-- <b-row
                        class="info"
                        v-if="
                            this_route != 'apidoc' &&
                            featureMainData.body[
                                $router.currentRoute.path.split('/')[
                                    $router.currentRoute.path.split('/')
                                        .length - 1
                                ]
                            ].showtitlebanner
                        "
                    >
                        <b-container>
                            <h2 class="publicText pt-5 pb-5">
                                {{
                                    featureMainData.body[
                                        $router.currentRoute.path.split("/")[
                                            $router.currentRoute.path.split("/")
                                                .length - 1
                                        ]
                                    ].title
                                }}
                            </h2>
                            <h5 class="publicText pb-5">
                                {{
                                    featureMainData.body[
                                        $router.currentRoute.path.split("/")[
                                            $router.currentRoute.path.split("/")
                                                .length - 1
                                        ]
                                    ].content
                                }}
                            </h5>
                        </b-container>
                    </b-row> -->
                    <b-container fluid>
                        <router-view
                            class="edoc_router pt-2"
                            style="height: calc(100vh - 76px)"
                        >
                        </router-view>
                    </b-container>
                </div>
            </template>
            <!-- <template v-else>
                <b-container fluid class="h-100 mh-100 publicMinWidth550">
                    <b-row
                        align-h="center"
                        align-v="center"
                        class="h-100 mh-100"
                        style="overflow: auto"
                    >
                        <featureMain></featureMain>
                    </b-row>
                </b-container>
            </template> -->
        </b-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import featureMain from "@/components/featureMain.vue";
import featureListBar from "@/components/featureListBar.vue";
export default {
    name: "Edoc",
    data() {
        let vm = this;
        return {
            this_route: "edoc",
            api_group: { my: [], ms: [], myps: [], ps: [], other: [] },
            api_version: {},
            api_group_config: {
                my: ["管理性資料庫", "MariaDB"],
                ms: ["管理性資料庫", "MSSQL"],
                myps: ["跨資料庫", "Mix"],
                ps: ["串流資料庫", "Postgres"],
                other: ["其他", "Other"],
            },
            collapse_apilist: false,
            collapse_info: false,
            api_group_display_type: 0,
            featureMainData: {
                title: "電子文件",
                cols: {
                    xl: 3,
                    lg: 6,
                    md: 6,
                    sm: 12,
                },
                body: {
                    apilist: {
                        title: "API文件",
                        showtitlebanner: true,
                        content: "提供api執行資料庫各種服務的說明文件",
                        img: require("@/assets/main/apidoc.png"),
                    },
                    apiinfo: {
                        title: "規格說明",
                        showtitlebanner: true,
                        content:
                            "使用者能透過此頁面查看api的規格定義、名稱等簡易教學的文字說明。",
                        img: require("@/assets/main/apiinfo.png"),
                    },
                    apisandbox: {
                        title: "測試沙盒",
                        showtitlebanner: true,
                        content:
                            "用來提供對於不熟悉api架構與使用方式等方法的測試空間，提供有效的資料庫做查詢。",
                        img: require("@/assets/main/apisandbox.png"),
                    },
                    apitableschema: {
                        title: "資料表結構",
                        showtitlebanner: true,
                        content:
                            "提供使用者透過輸入資料表名稱，就能查詢屬性、主鍵、空值等欄位。",
                        img: require("@/assets/main/apitableschema.png"),
                    },
                },
            },
            featureListBarData: {
                body: {
                    apilist: {
                        title: "API文件",
                        collapse: {
                            childitem: {
                                "v-for": "",
                                click: {
                                    data: "",
                                },
                                content: {
                                    condition: "",
                                    conversiontable: "",
                                },
                            },
                        },
                    },
                    apiinfo: {
                        title: "規格說明",
                        collapse: {
                            childitem: {
                                "v-for": [
                                    "簡介",
                                    "名詞定義",
                                    "APIS規格",
                                    "APIS定義",
                                ],
                                click: {
                                    data: {},
                                },
                                content: {},
                            },
                        },
                    },
                    apisandbox: {
                        title: "測試沙盒",
                    },
                    apitableschema: {
                        title: "資料表結構",
                    },
                },
            },

            searchBarFixed: false,
            leftblockShow: true,
            monitorScrollTop: 0,
        };
    },
    created() {
        console.log(this.$router.currentRoute.path);
        console.log(/(\/edoc)$/.test(this.$router.currentRoute.path));
        console.log(this.clickRoute);
        if (/(\/edoc)$/.test(this.$router.currentRoute.path)) {
            this.setClickRoute("apilist");
            // this.setFeatureMainData(this.featureMainData);
        } else {
            this.this_route = this.$router.currentRoute.path.split("/")[
                this.$router.currentRoute.path.split("/").length - 1
            ];
        }
        console.log(this.$router.currentRoute.path);

        if (/(\/apilist)/.test(this.$router.currentRoute.path))
            this.toggleCollapseID("apilist");
        if (/(\/apiinfo)/.test(this.$router.currentRoute.path))
            this.toggleCollapseID("apiinfo");
        this._renderingApiList();
        console.log(this.$route);
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            apiMap: "apidetail/get_apiMap",
            clickRoute: "featuremain/get_clickRoute",
            clickChild: "featurelistbar/get_clickChild",
        }),
    },
    components: {
        featureMain,
        featureListBar,
    },
    watch: {
        clickRoute: {
            handler(value) {
                console.log(value);
                console.log(this.$router.currentRoute.path);
                if (value != "") {
                    this.$router.push({
                        name: value,
                        params: {
                            SYSTEM: this.$route.params.SYSTEM,
                        },
                    });
                    this.this_route = value;
                    console.log(this.this_route);
                    console.log(this.$router.currentRoute.path);
                    console.log(
                        /(\/edoc)$/.test(this.$router.currentRoute.path)
                    );
                    if (value === "apilist") {
                        console.log(Object.entries(this.api_group));
                        let thisclick = Object.entries(this.api_group);
                        console.log(thisclick);
                        this.setClickChild({
                            child_name: value,
                            data: this.api_version,
                        });
                    } else {
                        this.setClickChild({
                            child_name: value,
                            data: {},
                        });
                    }
                    this.setClickRoute("");
                }
            },
        },
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
                if (value.split("_")[0] === "apilist") {
                    this.setClickChild({
                        child_name: value,
                        data: this.api_version,
                    });
                }
            },
        },
    },
    mounted() {},
    methods: {
        ...mapActions({
            setFeatureMainData: "featuremain/set_featureMainData",
            setFeatureListBarData: "featurelistbar/set_featureListBarData",
            setClickRoute: "featuremain/set_clickRoute",
            setClickChild: "featurelistbar/set_clickChild",
            setCollapseIDList: "featurelistbar/set_collapseIDList",
            toggleCollapseID: "featurelistbar/toggle_collapseID",
        }),

        _renderingApiList() {
            let vm = this;
            console.log(vm.apiMap);
            vm.apiMap.forEach((element) => {
                if (
                    ["my", "ms", "myps", "ps"].includes(
                        element[0].split("/")[3]
                    )
                ) {
                    vm.api_group[element[0].split("/")[3]].push(element);
                } else {
                    vm.api_group["other"].push(element);
                }
            });
            console.log(vm.api_group);
            Object.entries(vm.api_group).forEach((element) => {
                if (element[1].length === 0) {
                    console.log(element[0]);
                    delete vm.api_group[element[0]];
                    // if (element[0] == "my" || element[0] == "ms") {
                    //     vm.api_group_display_type = "join.-";
                    //     console.log(vm.api_group_display_type);
                    // }
                } else {
                    console.log(element);
                    vm.api_version[element[0]] = {};
                    if (element[0] == "my" || element[0] == "ms") {
                        vm.api_group_display_type += 1;
                    }
                    element[1].map((element2) => {
                        if (
                            typeof vm.api_version[element[0]][
                                element2[3].split("/")[1]
                            ] === "undefined"
                        ) {
                            vm.api_version[element[0]][
                                element2[3].split("/")[1]
                            ] = {};
                        }

                        if (
                            typeof vm.api_version[element[0]][
                                element2[3].split("/")[1]
                            ][element2[2]] === "undefined"
                        ) {
                            vm.api_version[element[0]][
                                element2[3].split("/")[1]
                            ][element2[2]] = [element2];
                        } else {
                            vm.api_version[element[0]][
                                element2[3].split("/")[1]
                            ][element2[2]].push(element2);
                        }
                    });
                }
                console.log(vm.api_version);
            });
            //my跟ms都存在
            if (vm.api_group_display_type == 2) {
                vm.api_group_display_type = "join.-";
            } else {
                vm.api_group_display_type = 0;
            }
            vm.featureListBarData.body.apilist.collapse["childitem"] = {
                "v-for": Object.keys(vm.api_group),
                click: {
                    data: vm.api_version,
                },
                content: {
                    condition: vm.api_group_display_type,
                    conversiontable: vm.api_group_config,
                },
            };
            console.log(vm.featureListBarData);
            vm.setCollapseIDList({ apilist: false, apiinfo: false });
            vm.setFeatureListBarData(vm.featureListBarData);
        },

        _onResize() {
            let vm = this;
            console.log(window.innerWidth);
            if (window.innerWidth < 1200) {
                // vm.resize_style["margin-top"] =
                //     vm.$refs.leftblock.clientHeight + "px";
                vm.searchBarFixed = true;
            } else {
                vm.searchBarFixed = false;
                // vm.resize_style["margin-top"] = "0px";
            }
        },

        _handleScroll() {
            let scrollTop =
                window.pageYOffset ||
                document.documentElement.scrollTop ||
                document.body.scrollTop;
            console.log(scrollTop);

            let offsetTop = document.querySelector("#featureListBar").offsetTop;
            console.log(offsetTop);
            if (window.innerWidth < 1200) {
                if (scrollTop > offsetTop) {
                    if (!this.searchBarFixed) {
                        setTimeout(() => {
                            this.searchBarFixed = true;
                        }, 50);
                    } else {
                        this.searchBarFixed = true;
                    }
                } else {
                    this.searchBarFixed = false;
                }
                this.monitorScrollTop = scrollTop;
            }

            this.$nextTick(() => {
                if (this.monitorScrollTop > scrollTop) {
                    console.log("~~~~");
                    this.leftblockShow = true;
                } else {
                    console.log("@@@@");
                    this.leftblockShow = false;
                }
            });
            this.monitorScrollTop = scrollTop;
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
    beforeRouteUpdate(to, from, next) {
        console.warn(to);
        console.warn(from);
        console.warn(next);
        this._reset(["api_group", "api_version", "api_group_display_type"]);
        this.this_route = to.name;
        if (to.name === "edoc") {
            this.setClickRoute("apilist");
            // this.setFeatureMainData(this.featureMainData);
        } else {
            this.toggleCollapseID(to.name);
            if (to.name != "apilist" || to.name != "apiinfo")
                this.setClickChild({
                    child_name: to.name,
                    data: {},
                });
            console.log(this.this_route);
            this.$nextTick(() => {
                window.addEventListener("resize", this._onResize);
                window.addEventListener("scroll", this._handleScroll);
                console.log("####");
            });
        }
        next();
    },
    //調用router守衛，在離開時reset
    beforeRouteLeave(to, from, next) {
        this._reset([]);
        next();
    },
    beforeDestroy() {
        let vm = this;
        window.removeEventListener("resize", vm._onResize);
        window.removeEventListener("scroll", vm._handleScroll);
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.card,
.card-header {
    border: 0;
}
.apipath {
    color: #007bff;
    cursor: pointer;
}
.apimethod {
    font-weight: bold;
    min-width: 60px;
    /* color: green; */
}
.bcardtext {
    display: flex;
    flex-direction: row;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    align-items: center;
    height: 36px;
    min-height: 36px;
    margin: 5px;
    padding: 5px;
}
</style>

