<template>
  <div id="Bigdata" ref="Bigdata">
    <b-row
      class="h-100 mh-100"
      style="overflow-x: auto"
      :class="{ publicMinWidth550: this_route != 'bigdata' }"
    >
      <template v-if="this_route != 'bigdata'">
        <featureListBar />
        <div class="featureContent customize_scrollbar h-100 mh-100">
          <router-view class="pt-2" style="height: calc(100vh - 76px)">
          </router-view>
        </div>
      </template>
    </b-row>
    <loadScreenModal />
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import featureMain from "@/components/featureMain.vue";
import featureListBar from "@/components/featureListBar.vue";
import loadScreenModal from "@/components/MES/loadScreenModal.vue";
export default {
  name: "Bigdata",
  data() {
    return {
      this_route: "bigdata",
      collapse_apilist: false,
      collapse_info: false,
      featureMainData: {
        title: "AI診斷履歷",
        cols: {
          xl: 3,
          lg: 6,
          md: 6,
          sm: 12,
        },
        body: {
          aicontrol: {
            title: "AI診斷履歷",
            showtitlebanner: false,
            content: "AI診斷履歷",
            img: require("@/assets/main/apidoc.png"),
          },
        },
      },
      featureListBarData: {
        body: {
          aicontrol: {
            title: "AI診斷履歷",
            showtitlebanner: false,
            content: "AI診斷履歷",
            img: require("@/assets/main/apidoc.png"),
          },
        },
      },
    };
  },
  created() {
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("ai_selectdCustomer", this.selectCustomer);
    });
    if (!sessionStorage.getItem("ai_selectdCustomer")) {
      this.setSelectedCustomer(0);
    } else {
      this.setSelectedCustomer(sessionStorage.getItem("ai_selectdCustomer"));
    }
    sessionStorage.removeItem("ai_selectdCustomer");

    // if (
    //   this.pageAccess.logging.authority == false &&
    //   this.pageAccess.setting.authority == false &&
    //   this.pageAccess.operation.authority == false
    // ) {
    //   if (/(\/bigdata)$/.test(this.$router.currentRoute.path)) {
    //     this.setClickRoute("diagnosis");
    //   } else {
    //     this.this_route = this.$router.currentRoute.path.split("/")[
    //       this.$router.currentRoute.path.split("/").length - 1
    //     ];
    //   }
    //   this._setListBar(this.loginData.customerId);
    //   this.setSelectedCustomer(this.loginData.customerId);
    // } else {
    //   this._setListBar(this.loginData.customerId);
    //   this.setFeatureMainData(this.featureMainData);
    //   this.setFeatureListBarData(this.featureListBarData);
    //   if (/(\/bigdata)$/.test(this.$router.currentRoute.path)) {
    //     this.setClickRoute("aicontrol");
    //   } else {
    //     this.this_route = this.$router.currentRoute.path.split("/")[
    //       this.$router.currentRoute.path.split("/").length - 1
    //     ];
    //   }
    // }

    if (/(\/bigdata)$/.test(this.$router.currentRoute.path)) {
        this.setClickRoute("diagnosis");
      } else {
        this.this_route = this.$router.currentRoute.path.split("/")[
          this.$router.currentRoute.path.split("/").length - 1
        ];
      }
      this._setListBar(1);
      this.setSelectedCustomer(1);
  },
  computed: {
    ...mapGetters({
      axiosResult: "commonaxios/get_axiosResult",
      clickRoute: "featuremain/get_clickRoute",
      clickChild: "featurelistbar/get_clickChild",
      pageAccess: "getlogin/get_pageAccess",
      loginData: "getlogin/get_loginData",
      selectCustomer: "mesData/get_selectedCustomer",
    }),
  },
  components: {
    featureMain,
    featureListBar,
    loadScreenModal
  },
  watch: {
    selectCustomer: {
      handler(value) {
        this._setListBar(value);
      },
    },
    clickRoute: {
      handler(value) {
        console.log(value);
        console.log(this.$router.currentRoute.path);
        if (value != "") {
          this.$router.push({
            name: value,
            params: {
              SYSTEM: this.$route.params.SYSTEM,
            },
          });
          this.this_route = value;
          console.log(this.this_route);
          console.log(this.$router.currentRoute.path);
          console.log(/(\/bigdata)$/.test(this.$router.currentRoute.path));

          this.setClickChild({
            child_name: value,
            data: {},
          });

          this.setClickRoute("");
        }
      },
    },
    "clickChild.child_name": {
      handler(value) {
        let vm = this;
        let child_name = value.split("_");
        if (child_name.length >= 2) {
          if (child_name[0] == "elecBoard") {
            sessionStorage.setItem("mes_elecboard_process", child_name[1]);
          }
        }
        if (child_name.length >= 2) {
          if (child_name[0] == "deviceSetting") {
            sessionStorage.setItem("mes_deviceSetting_process", child_name[1]);
          }
        }
        console.log("@@@@@@@");
        console.log(vm.clickChild);
        console.log(value);
      },
    },
  },
  methods: {
    ...mapActions({
      setFeatureMainData: "featuremain/set_featureMainData",
      setFeatureListBarData: "featurelistbar/set_featureListBarData",
      setClickRoute: "featuremain/set_clickRoute",
      setClickChild: "featurelistbar/set_clickChild",
      setCollapseIDList: "featurelistbar/set_collapseIDList",
      toggleCollapseID: "featurelistbar/toggle_collapseID",
      setSelectedCustomer: "mesData/set_selectedCustomer",
    }),
    _onResize() {
      let vm = this;
      console.log(window.innerWidth);
      if (window.innerWidth < 1200) {
        // vm.resize_style["margin-top"] =
        //     vm.$refs.leftblock.clientHeight + "px";
        vm.searchBarFixed = true;
      } else {
        vm.searchBarFixed = false;
        // vm.resize_style["margin-top"] = "0px";
      }
    },
    _handleScroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      console.log(scrollTop);

      let offsetTop = document.querySelector("#featureListBar").offsetTop;
      console.log(offsetTop);
      if (window.innerWidth < 1200) {
        if (scrollTop > offsetTop) {
          if (!this.searchBarFixed) {
            setTimeout(() => {
              this.searchBarFixed = true;
            }, 50);
          } else {
            this.searchBarFixed = true;
          }
        } else {
          this.searchBarFixed = false;
        }
        this.monitorScrollTop = scrollTop;
      }

      this.$nextTick(() => {
        if (this.monitorScrollTop > scrollTop) {
          console.log("~~~~");
          this.leftblockShow = true;
        } else {
          console.log("@@@@");
          this.leftblockShow = false;
        }
      });
      this.monitorScrollTop = scrollTop;
    },
    _setListBar(customer) {
      let vm = this;
      if (customer == "0") {
        vm.featureMainData.body = {
          aicontrol: {
            title: "AI診斷履歷",
            showtitlebanner: false,
            content: "",
            img: require("@/assets/main/apidoc.png"),
          },
        };
        vm.featureListBarData.body = {
          aicontrol: {
            title: "AI診斷履歷",
          },
        };
      } else {
        vm.featureMainData.body = {
          diagnosis: {
            title: "AI診斷履歷",
            showtitlebanner: false,
            content: "",
            img: require("@/assets/main/apidoc.png"),
          },
        };
        vm.featureListBarData.body = {
          diagnosis: {
            title: "AI診斷履歷",
          },
        };
      }
      vm.setFeatureListBarData(vm.featureListBarData);
    },
    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
  beforeRouteUpdate(to, from, next) {
    console.warn(to);
    console.warn(from);
    console.warn(next);
    // this._reset(["api_group", "api_version", "api_group_display_type"]);
    this.this_route = to.name;
    if (to.name === "bigdata") {
      this.setClickRoute("aicontrol");
      //   this.setFeatureMainData(this.featureMainData);
    } else {
      // if (to.name != "elecboard") this.toggleCollapseID(to.name);
      this.setClickChild({
        child_name: to.name,
        data: {},
      });
      this.$nextTick(() => {
        window.addEventListener("resize", this._onResize);
        window.addEventListener("scroll", this._handleScroll);
        console.log("####");
      });
    }
    next();
  },
  //調用router守衛，在離開時reset
  beforeRouteLeave(to, from, next) {
    this._reset([]);
    next();
  },
  beforeDestroy() {
    let vm = this;
    // vm.setSelectedCustomer(0);
    window.removeEventListener("resize", vm._onResize);
    window.removeEventListener("scroll", vm._handleScroll);
  },
};
</script>

