<template>
    <div id="apiinfo" class="customize_scrollbar">
        <b-row class="apilist-block" id="apiinfo_簡介" ref="apiinfo_簡介">
            <b-col cols="12">
                <h2>{{ title[0] }}</h2>
            </b-col>
            <b-col sm="4" class="pl-4 pr-4">
                <h5 class="text-center">注意事項</h5>
                <b-card-text>All Rights Reserved.</b-card-text>
                <b-card-text class="textindent"
                    >This document, may not, in whole or partial, be copied,
                    photocopied, translated or reproduced to any electronic
                    media or any other form without prior written consent from
                    Sensing and Transmitting Technology Corp. Ltd.
                    (SAPIDO).</b-card-text
                >
                <b-card-text
                    >In no circumstance will SAPIDO be liable for direct,
                    indirect, special, incidental, or consequential damages
                    arising out of the use or inability to use of this product
                    or documentation even if is advised of the possibility of
                    such damages. SAPIDO shall not be liable for any hardware
                    used with the product, including the costs of repairing,
                    replacing, or recovering such hardware.</b-card-text
                >
            </b-col>
            <b-col sm="4" class="rightdiv pl-4 pr-4">
                <h5 class="text-center">目的</h5>
                <b-card-text class="textindent"
                    >本規格書定義本公司研發之RESTful
                    API服務架構設計風格，發行之服務提供客戶於企業內部環境以全球資訊網架構為基礎，進行物聯網元件資料通訊的環境。主要考量企業客戶對來自於物聯網大量資料所需的存取與資料分析需求，訂定資料服務平台(DPaaS)
                    API服務介面規格，提供企業內部系統與物聯網元件的通訊平台。</b-card-text
                >
                <b-card-text class="textindent"
                    >對於使用本公司資料服務平台之企業客戶可參考本規格以通用的RESTful
                    API服務建置物聯設備端資料存取，及應用程式端資訊產生與快速地導入業界規範的巨量資料分析方法：如Hadoop/Spark，進行快速而有效率的建立與存取企業轄下各單位基本資料及所屬物聯設備所產生的巨量資料；並可針對巨量數據分析提供擴充功能，有效提高企業產能，提升企業具備競爭的實力。</b-card-text
                >
            </b-col>
            <b-col sm="4" class="rightdiv pl-4 pr-4">
                <h5 class="text-center">應用範圍</h5>
                <b-card-text class="textindent"
                    >本設計規格之RESTful API介面服務
                    (以下簡稱APIS)定義範圍包含用於物聯感測端與資料服務平台(STTC
                    DPaaS以下簡稱STTC-DPaaS)做自動化介接、即時作業監控、提供資料存取與導入巨量資料分析程式等功能。</b-card-text
                >
                <b-card-text
                    >企業客戶可根據本規格提供的APIS定義，進行相關感測端/應用程式介面開發，以提供資料使用者或開發者一致性APIS，進行取得、搜尋或分析所需的資料集資料，取得有益信息反饋物聯設備進行產能修正，有效提升產能。</b-card-text
                >
                <b-card-text
                    >本規格制定之資料資源內容APIS適用於一般結構化資料內容(如JSON)，其他相關特定資料格式(如docx、pdf)，則不在此應用範圍內。</b-card-text
                >
                <b-card-text
                    >資料存取應用程式APIS介面主要提供一般結構化資料內容存取，不包括資料內容應用層面解析，如複雜數學邏輯運算篩選、應用領域所代表之意義判斷、欄位之間相關性等。</b-card-text
                ></b-col
            >
        </b-row>
        <b-row
            class="apilist-block"
            id="apiinfo_名詞定義"
            ref="apiinfo_名詞定義"
        >
            <b-col sm="12">
                <h2>{{ title[1] }}</h2>
            </b-col>
            <b-col sm="12">
                <b-table
                    sticky-header="500px"
                    :items="items"
                    :fields="fields"
                    :fixed="false"
                    responsive
                    :head-variant="'light'"
                    class="customize_scrollbar"
                ></b-table>
            </b-col>
        </b-row>
        <b-row
            class="apilist-block"
            id="apiinfo_APIS規格"
            ref="apiinfo_APIS規格"
        >
            <b-col sm="12">
                <h2>{{ title[2] }}</h2>
            </b-col>
            <b-col sm="6" class="pl-5 pr-5">
                <h4 class="text-center">語法規則</h4>
                <b-card-text class="textindent"
                    >RESTful API的設計，主要目標在於提供企業客戶以HTTP協定之
                    GET/POST/DELETE方法，存取物聯網感測元件上傳的原始資料，主要運作平台為資料服務平台(DPaaS)，提供客戶私有運作平台，保有企業運作資料的保密性與完整性。</b-card-text
                >
                <b-card-text
                    >APIS呼叫/回傳內容以JSON格式為主，APIS
                    輸出內容格式為JSON，HTTP 標頭 Content-Type為
                    “application/json”。</b-card-text
                >
                <b-card-text
                    >APIS路徑採用URL通用語法組成:
                    分為服務網址(Scheme+host)、資源路徑(APIS
                    path)和服務參數(APIS Query parameters)</b-card-text
                >
                <img
                    src="../../../public/static/img/apipath.png"
                    alt="apipath"
                />
                <b-card-text class="small"
                    >-
                    服務：平台上提供該類別APIS之協定。(目前僅支援HTTP)</b-card-text
                >
                <b-card-text class="small"
                    >- 網址：主機網址/網域名稱。</b-card-text
                >
                <b-card-text class="small"
                    >-
                    資源路徑：接續於服務網址後，指定某一APIS項目路徑名稱。</b-card-text
                >
                <b-card-text class="small"
                    >-
                    服務參數：接續於APIS項目路徑後，提供一特定APIS所需之參數。</b-card-text
                >
            </b-col>
            <b-col sm="6" class="rightdiv pl-5 pr-5">
                <h4 class="text-center">命名規則</h4>
                <b-card-text class="textindent"
                    >本規則定義APIS介面的命名原則，提供企業客戶於開發資料服務平台(DPaaS)應用程式可依循之APIS呼叫，命名規則依據下列格式定義提供APIS。</b-card-text
                >
                <b-card-text
                    ><ul>
                        <li>APIs命名格式</li>
                    </ul></b-card-text
                >
                <b-card-text class="textindent colorblue"
                    >[{/}{COMPONENT}]{/}{OBJECT}[{/}{SUBOBJ} … ]</b-card-text
                >
                <b-card-text class="textindent"
                    ><span>{} : </span>必要項目</b-card-text
                >
                <b-card-text class="textindent"
                    ><span v-html="'&lt&gt : '"></span>選擇項目</b-card-text
                >
                <b-card-text class="textindent"
                    ><span>... : </span>允許多重項目</b-card-text
                >
                <b-card-text class="textindent">範例:</b-card-text>
                <b-card-text class="textindent"
                    >"/session": 索取作業會期所需使用之辨識碼。</b-card-text
                >
                <b-card-text class="textindent"
                    >"/factory/registerDep": 註冊一"部門"元件。</b-card-text
                >
            </b-col>
        </b-row>
        <b-row
            class="apilist-block"
            id="apiinfo_APIS定義"
            ref="apiinfo_APIS定義"
        >
            <b-col sm="12">
                <h2>{{ title[3] }}</h2>
            </b-col>
            <b-col sm="6">
                <h4 class="text-center">APIS類型</h4>
                <ul>
                    <li>
                        <b-card-text>資料異動</b-card-text>
                        <b-card-text class="textindent"
                            >POST:用於增/刪/修/查
                            部門/廠區/場所/裝置/感測元件的基本管理資料及感測資料。</b-card-text
                        >
                        <b-card-text class="textindent"
                            >DELETE: 用於刪除資料，目前暫不提供對streaming
                            data的刪除作業。</b-card-text
                        >
                    </li>
                    <li>
                        <b-card-text>資料取出</b-card-text>
                        <b-card-text class="textindent"
                            >GET:
                            用於取得資料庫的資料，包括會期識別碼、元件(部門/廠區/場所/裝置/感測)列表的管理資料、感測器的感測資料。</b-card-text
                        >
                    </li>
                </ul>
            </b-col>
            <b-col sm="6" class="rightdiv">
                <h4 class="text-center">APIS定義</h4>
                <b-card-text class="textindent"
                    >APIS介面格式主要以URI
                    為參考依據，格式定義如下:</b-card-text
                >
                <b-card-text class="colorblue textindent">
                    https://&lt;IP/Host:Port_No&gt;/api/system/1.0/[my]/serviceType
                    /API_service[/sub_service]?PARAMs
                </b-card-text>
                <ul type="circle">
                    <li>
                        <b-card-text
                            ><span v-html="'&ltIP/Host:Port_No&gt: '"></span
                            >資料服務平台(DPaaS) 伺服器IP
                            位址/網域名稱。</b-card-text
                        >
                    </li>
                    <li>
                        <b-card-text><span>api:</span>統一APIS URI</b-card-text>
                    </li>
                    <li>
                        <b-card-text
                            ><span>system:</span
                            >服務系統，不同系統分類。</b-card-text
                        >
                    </li>
                    <li>
                        <b-card-text><span>1.0:</span>APIS版本聲明</b-card-text>
                    </li>
                    <li>
                        <b-card-text
                            ><span>[my]:</span>資料庫服務分類</b-card-text
                        >
                    </li>
                    <li>
                        <b-card-text
                            ><span>serviceType:</span
                            >服務類型，主要以元件為主作服務分類。</b-card-text
                        >
                    </li>
                    <li>
                        <b-card-text
                            ><span>API_service:</span>APIS
                            伺服器所提供服務名稱，目前包含以下服務:</b-card-text
                        >
                        <b-card-text
                            >註:CRUD資料處理用英文符號代表定義如下:</b-card-text
                        >
                        <b-card-text
                            class="colorblue small textindent"
                            v-html="'C : 新增-&gtcreate'"
                        ></b-card-text>
                        <b-card-text
                            class="colorblue small textindent"
                            v-html="'R : 查詢-&gtread'"
                        ></b-card-text>
                        <b-card-text
                            class="colorblue small textindent"
                            v-html="'U : 修改-&gtupdate'"
                        ></b-card-text>
                        <b-card-text
                            class="colorblue small textindent"
                            v-html="'D : 刪除-&gtdelete'"
                        ></b-card-text>
                    </li>
                    <li>
                        <b-card-text
                            ><span>?PARAMs:</span
                            >提供執行特定API服務所需的參數值，例如:</b-card-text
                        >
                        <b-card-text class="small"
                            >DATA:
                            傳送資料(以JSON格式表示)，為選項項目</b-card-text
                        >
                        <b-card-text class="small"
                            >method: HTTP傳送方法</b-card-text
                        >
                        <b-card-text class="small"
                            >附註:
                            以下HTTP相關資訊，請依據所選用開發的程式語言，於適當的位置加入後再進行傳送。</b-card-text
                        >
                    </li>
                </ul>
            </b-col>
        </b-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "apiinfo",
    data() {
        let vm = this;
        return {
            title: ["簡介", "名詞定義", "APIS規格", "APIS定義"],
            //table
            fields: [
                {
                    key: "en",
                    label: "英文名稱",
                    thClass: "publicTH",
                },
                {
                    key: "cn",
                    label: "全文名稱",
                    thClass: "publicTH",
                },
                {
                    key: "definition",
                    label: "定義",
                    thClass: "publicTH",
                },
            ],
            items: [
                {
                    en: "APIS Path",
                    cn: "API服務路徑",
                    definition:
                        "接續於服務根網址後，指定某一資料集之資料資源路徑",
                },
                {
                    en: "Base64",
                    cn: "Base64編碼",
                    definition:
                        "一種基於64個可列印字元來表示二進位資料的表示方法。常用於在通常處理文字資料的場合，表示、傳輸、儲存一些二進位資料。包括MIME的email、在XML中儲存複雜資料。",
                },
                {
                    en: "Data resource",
                    cn: "資料資源",
                    definition: "提供資料和服務之來源",
                },
                { en: "Dataset", cn: "資料集", definition: "可辨識的資料集合" },
                {
                    en: "HMAC",
                    cn:
                        "雜湊訊息鑑別碼(Hash-based message authentication code)",
                    definition:
                        "是一種通過特別計算方式之後產生的訊息鑑別碼(MAC)，使用密碼雜湊函式，同時結合一個加密金鑰。可以用來保證資料的完整性，同時可以用來作某個訊息的身分驗證。",
                },
                {
                    en: "HTTP",
                    cn: "超文字傳輸協定(HyperText Transfer Protocol)",
                    definition:
                        "是網際網路上應用最為廣泛的一種網路協議。設計HTTP最初的目的是為了提供一種發布和接收HTML頁面的方法。通過HTTP或者HTTPS協定請求的資源由 URI 來標識。",
                },
                {
                    en: "HTTPS",
                    cn:
                        "超文字傳輸安全協定(Hypertext Transfer Protocol Secure)",
                    definition:
                        "是一種網路安全傳輸協議，經由超文字傳輸協定進行通訊，但利用SSL/TLS來對封包進行加密。",
                },
                {
                    en: "Metadata",
                    cn: "資料資源",
                    definition: "描述資料的資料",
                },
                {
                    en: "PGP",
                    cn: "良好隱私密碼法(Pretty Good Privacy)",
                    definition:
                        "一套用於訊息加密、驗證的應用程序，採用IDEA的散列演算法作為加密與驗證之用。開源並具有同類功能的工具名為GPG（GnuPG）",
                },
                {
                    en: "Scheme + host",
                    cn: "服務網址",
                    definition:
                        "平臺上提供該類別API應用服務之協定(目前僅支援HTTP)及主機網址。",
                },
                {
                    en: "Sha1",
                    cn: "安全雜湊演算法(Secure Hash Algorithm)",
                    definition:
                        "是一個能計算出一個數位訊息所對應到的長度固定的字串(又稱訊息摘要)的演算法。SHA1可將一個最大264位元的訊息，轉換成一串160位元的訊息摘要；其設計原理相似於MIT教授Ronald L. Rivest所設計的密碼學雜湊演算法MD4和MD5。",
                },
                {
                    en: "RESTful",
                    cn: "表述狀態傳輸的網際網路服務",
                    definition:
                        "是一個使用HTTP並遵循REST原則，以 URL 定位資源，根據 HTTP 內容指示操作動作與回應訊息。",
                },
                {
                    en: "URI",
                    cn: "統一資源識別元（Uniform Resource Identifier)",
                    definition:
                        "是一個用於標識某一網際網路資源名稱的字串。該種標識允許使用者對網路中（一般指全球資訊網）的資源通過特定的協定進行互動操作。",
                },
                {
                    en: "UTF-8",
                    cn: "UTF-8編碼(8-bit Unicode Transformation Format)",
                    definition:
                        "針對Unicode的可變長度字元編碼，也是一種字首碼。用來表示Unicode標準中的任何字元，且其編碼中的第一個位元組與ASCII相容，這使得原來處理ASCII字元的軟體無須或只須做少部份修改，即可繼續使用。",
                },
            ],
        };
    },
    watch: {
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
                if (value.split("_")[0] === "apiinfo") {
                    //定位至選擇點
                    this.$el.querySelector("#" + value).scrollIntoView({
                        behavior: "smooth", // 平滑过渡
                        block: "start", // 上边框与视窗顶部平齐。默认值
                    });
                }
            },
        },
    },
    computed: {
        ...mapGetters({
            clickChild: "featurelistbar/get_clickChild",
        }),
    },
    created() {
        let vm = this;
        console.log(vm.clickChild);
        console.log(this.$router.currentRoute.path);
        console.log(this.$route);
    },
    mounted() {
        let vm = this;
        console.log(vm.clickChild.child_name);
        if (vm.clickChild.child_name.split("_").length > 1) {
            //定位至選擇點
            vm.$nextTick(() => {
                vm.$el
                    .querySelector("#" + vm.clickChild.child_name)
                    .scrollIntoView({
                        behavior: "auto",
                        block: "start",
                    });
            });
        }
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
