const state = {
    workorderHistoryDataFields: [
        {
            key: "workID",
            label: "工單",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "processSchedule",
            label: "製程進度",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "itemName",
            label: "料名",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "qty",
            label: "派工數量",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "time",
            label: "完工日期",
            sortable: true,
            thClass: "publicTH"
        }
    ],
    workorderHistoryDataItem: [],
    rowClickData: {}
}
const getters = {
    get_workorderHistoryDataFields: state => {
        return state.workorderHistoryDataFields
    },
    get_workorderHistoryDataItem: state => {
        return state.workorderHistoryDataItem
    },
    get_rowClickData: state => {
        return state.rowClickData
    },
}
const actions = {
    set_workorderHistoryDataFields(context, array) {
        context.commit('SET_WORKORDERHISTORYDATAFIELDS', array);
    },
    set_workorderHistoryDataItem(context, array) {
        context.commit('SET_WORKORDERHISTORYDATAITEM', array);
    },
    set_rowClickData(context, obj) {
        context.commit('SET_ROWCLICKDATA', obj);
    },
    clear_workorderHistoryDataItem(context) {
        context.commit('CLEAR_WORKORDERHISTORYDATAITEM');
    },
    clear_rowClickData(context) {
        context.commit('CLEAR_ROWCLICKDATA');
    }
}
const mutations = {
    SET_WORKORDERHISTORYDATAFIELDS(state, array) {
        state.workorderHistoryDataItem = array;
    },
    SET_WORKORDERHISTORYDATAITEM(state, array) {
        state.workorderHistoryDataItem = array;
    },
    SET_ROWCLICKDATA(state, obj) {
        state.rowClickData = obj;
    },
    CLEAR_WORKORDERHISTORYDATAITEM(state) {
        state.workorderHistoryDataItem = [];
    },
    CLEAR_ROWCLICKDATA(state) {
        state.rowClickData = {};
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
