const state = {
    produceWeightFields: [
        {
            key: "code",
            label: "製程",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "totalBucketWeight",
            label: "生產總重(KG)",
            sortable: false,
            thClass: "publicTH"
        }
    ],
    produceWeightItem: [],
}
const getters = {
    get_produceWeightFields: state => {
        return state.produceWeightFields
    },
    get_produceWeightItem: state => {
        return state.produceWeightItem
    },
}
const actions = {
    set_produceWeightFields(context, array) {
        context.commit('SET_PRODUCEWEIGHTFIELDS', array);
    },
    set_produceWeightItem(context, array) {
        context.commit('SET_PRODUCEWEIGHTITEM', array);
    },
}
const mutations = {
    SET_PRODUCEWEIGHTFIELDS(state, array) {
        state.produceWeightItem = array;
    },
    SET_PRODUCEWEIGHTITEM(state, array) {
        state.produceWeightItem = array;
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
