        _getSelectDeviceName() {
            let vm = this;
            let device_name = vm.device_name;
            console.log(device_name);
            vm.setSingleChartName(vm.alarmHistoryChart.name);
            vm.setSingleChartSelected(vm.alarmHistoryChart.options[0].value);
            vm.setSingleChartOptions(vm.alarmHistoryChart.options);
            if (device_name == "") {
                vm.changeSelectPage("elecboard");
                // vm.changeSelectPageName(vm.pageName.elecboard);
                vm._clearTimer();
                vm.$router.push("/" + $route.params.SYSTEM + "/mes/elecboard");
            } else {
                vm._selectDevice(device_name);
                vm._queryCunter(device_name);
                vm.setOtherData(device_name);
            }
        },

        _selectDevice(device) {
            let vm = this;
            if (vm.selectPage != "machstatus") {
                return;
            }
            vm._clearTimer(vm.timer_selectDevice);
            if (!vm.first_load) {
                vm.showLoadScreenModal();
            }
            var params = {};
            params["vuePage"] = vm.selectPage;
            params["methods"] = "GET";
            params["whichFunction"] = "SelectMachineQuery";
            params["device_name"] = device;
            params["model"] = vm.sideBarSelect.model;
            vm._machStatusAxiosAction(params, "_selectDevice");
        },

        _queryCunter(device) {
            let vm = this;
            if (vm.selectPage != "machstatus") {
                return;
            }
            vm._clearTimer(vm.timer_queryCunter);
            var params = {};
            params["vuePage"] = vm.selectPage;
            params["methods"] = "GET";
            params["whichFunction"] = "DemoCounter";
            params["deviceID"] = device;
            params["model"] = vm.sideBarSelect.model;
            vm._machStatusAxiosAction(params, "_queryCunter");
        },

        _queryWorkorderHistory() {
            let vm = this;
            let endTime = new Date();
            let startTime = new Date(endTime.getTime() - 172800000);
            var params = {};
            params["vuePage"] = vm.selectPage;
            params["methods"] = "POST";
            params["whichFunction"] = "WorkorderHistory";
            params["startTime"] = `${startTime.getFullYear()}-${
                startTime.getMonth() + 1
            }-${startTime.getDate()} ${startTime.getHours()}:${startTime.getMinutes()}:${startTime.getSeconds()}`;
            params["endTime"] = `${endTime.getFullYear()}-${
                endTime.getMonth() + 1
            }-${endTime.getDate()} ${endTime.getHours()}:${endTime.getMinutes()}:${endTime.getSeconds()}`;
            params["deviceID"] = vm.device_name;
            console.log(params);
            vm.showLoadScreenModal();
            vm.axiosAction(params).then(() => {
                vm.hideLoadScreenModal();
                var result = vm.axiosResult;
                console.log(result);
                if (result["Response"] == "ok") {
                    let result_Data = result["QueryTableData"];

                    let workorderHistoryArray = [];
                    for (let i = 0; i < result_Data.length; i++) {
                        let workorderHistoryObj = {};
                        workorderHistoryObj["workID"] =
                            result_Data[i]["workID"];
                        workorderHistoryObj["modelSchedule"] =
                            result_Data[i]["modelSchedule"];
                        workorderHistoryObj["itemName"] =
                            result_Data[i]["product_name_chinese"];
                        workorderHistoryObj["qty"] = result_Data[i]["work_qty"];
                        workorderHistoryObj["time"] =
                            result_Data[i]["completion_date"];
                        workorderHistoryArray.push(workorderHistoryObj);
                    }
                    console.log(workorderHistoryArray);
                    vm.setWorkorderHistoryDataItem(workorderHistoryArray);
                    vm.setExportData(workorderHistoryArray);
                } else if (result["Response"] == "no data") {
                    vm.setSetTimeOutAlertMsg("查無資料");
                    vm.toggleSetTimeOutAlertModal();
                    vm.setWorkorderHistoryDataItem([]);
                    vm.setExportData([]);
                } else {
                    vm.setSetTimeOutAlertMsg(result["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                    vm.setWorkorderHistoryDataItem([]);
                    vm.setExportData([]);
                }
            });
        },

        _getNowTime() {
            let vm = this;
            let Today = new Date();
            vm.nowtime =
                Today.getFullYear() +
                "-" +
                String(Today.getMonth() + 1).padStart(2, "0") +
                "-" +
                String(Today.getDate()).padStart(2, "0") +
                " " +
                String(Today.getHours()).padStart(2, "0") +
                ":" +
                String(Today.getMinutes()).padStart(2, "0") +
                ":" +
                String(Today.getSeconds()).padStart(2, "0");
            setTimeout(function () {
                vm._getNowTime();
            }, 1000);
        },

