        _machStatusImportAxios() {
            const axios = require("axios");
            this.machStatusAxios = axios;
        },

        _machStatusAxiosCancel() {
            const CancelToken = this.machStatusAxios.CancelToken;
            const source = CancelToken.source();
            this.machStatusAxiosSource = source;
        },

        _machStatusAxiosAction(params, functionName) {
            let vm = this;
            vm.machStatusAxios
                .post("/static/php/modules.php", params, {
                    cancelToken: vm.machStatusAxiosSource.token,
                })
                .then(
                    function (response) {
                        const result = response.data;
                        vm._machStatusAxiosResult(result, functionName);
                    }.bind(this)
                )
                .catch(function (err) {
                    console.log(err);
                });
        },

        _machStatusAxiosResult(result, functionName) {
            let vm = this;
            console.log(functionName);
            console.log(result);
            if (functionName == "_selectDevice") {
                if (!vm.first_load) {
                    vm.hideLoadScreenModal();
                    vm.first_load = true;
                }
                if (
                    result["Response"] == "ok" &&
                    result["page"] == "machstatus"
                ) {
                    vm.focusDeviceSelect = 1;
                    // vm.nowDeviceNumber = device;

                    let result_Data = result["QueryTableData"];
                    //機台資料
                    // vm.device_detail = result_Data[0].device_detail;
                    vm.device_detail.device_activation =
                        result_Data[0].device_detail["device_activation"];
                    vm.device_detail.device_capacity =
                        result_Data[0].device_detail["device_capacity"];
                    vm.device_detail.device_model =
                        result_Data[0].device_detail["device_model"];
                    vm.device_detail.device_name =
                        result_Data[0].device_detail["device_name"];
                    vm.device_detail.device_run_time =
                        result_Data[0].device_detail["device_run_time"];
                    vm.device_detail.device_status =
                        result_Data[0].device_detail["device_status"];
                    vm.device_detail.product_line =
                        result_Data[0].device_detail["product_line"];
                    vm.device_image =
                        vm[result_Data[0].device_detail.device_image];

                    //工單基本資料
                    // vm.work_order_detail = result_Data[0].work_order_detail;
                    vm.work_order_detail;

                    vm.work_order_detail.actual_weight =
                        result_Data[0].work_order_detail["actual_weight"];
                    vm.work_order_detail.material_total_weight =
                        result_Data[0].work_order_detail[
                            "material_total_weight"
                        ];
                    vm.work_order_detail.material_weight =
                        result_Data[0].work_order_detail["material_weight"];
                    vm.work_order_detail.product_chinese_name =
                        result_Data[0].work_order_detail[
                            "product_chinese_name"
                        ];
                    vm.work_order_detail.productCode =
                        result_Data[0].work_order_detail["productCode"];
                    vm.work_order_detail.standard_total_weight =
                        result_Data[0].work_order_detail[
                            "standard_total_weight"
                        ];
                    vm.work_order_detail.standard_weight =
                        result_Data[0].work_order_detail["standard_weight"];
                    vm.work_order_detail.wire_chinese_name =
                        result_Data[0].work_order_detail["wire_chinese_name"];
                    vm.work_order_detail.wire_code =
                        result_Data[0].work_order_detail["wire_code"];
                    vm.work_order_detail.wire_consumption =
                        result_Data[0].work_order_detail["wire_consumption"];
                    vm.work_order_detail.wire_now_weight =
                        result_Data[0].work_order_detail["wire_now_weight"];
                    vm.work_order_detail.wire_start_weight =
                        result_Data[0].work_order_detail["wire_start_weight"];
                    vm.work_order_detail.workCode =
                        result_Data[0].work_order_detail["workCode"];
                    vm.work_order_detail.workOrgCode =
                        result_Data[0].work_order_detail["workOrgCode"];
                    vm.work_order_detail.work_control_card =
                        result_Data[0].work_order_detail["work_control_card"];
                    vm.work_order_detail.work_qty =
                        result_Data[0].work_order_detail["work_qty"];
                    vm.work_order_detail.work_status =
                        result_Data[0].work_order_detail["work_status"];

                    //堆疊圖
                    vm.setStacksData(result_Data[0].stack_bar);

                    let wire_use_information_item = [];
                    let mold_use_information_item = [];
                    let thread_use_information_item = [];
                    if (vm.sideBarSelect.model == "打頭") {
                        //使用線材
                        let wire_use_information =
                            result_Data[0].wire_use_information;
                        if (
                            wire_use_information.wire_use_information.length > 0
                        ) {
                            wire_use_information.wire_use_information.forEach(
                                (data) => {
                                    let wire_use_information_obj = {};
                                    wire_use_information_obj["code"] =
                                        data["scroll_no"];
                                    wire_use_information_obj["furnaceCode"] =
                                        data["furnaceCode"];
                                    wire_use_information_obj["consumption"] =
                                        data["consumption"];
                                    wire_use_information_obj["attrition"] = "-";
                                    wire_use_information_item.push(
                                        wire_use_information_obj
                                    );
                                }
                            );
                            let wire_use_information_obj = {};
                            wire_use_information_obj["code"] = "";
                            wire_use_information_obj["furnaceCode"] = "總計";
                            wire_use_information_obj["consumption"] =
                                wire_use_information.wire_total_weight;
                            wire_use_information_obj["attrition"] =
                                wire_use_information.wire_loss_rate;
                            wire_use_information_item.push(
                                wire_use_information_obj
                            );
                            vm.wire_use_information_item = wire_use_information_item;
                        }

                        //使用模具
                        let mold_use_information =
                            result_Data[0].mold_use_information;
                        if (
                            mold_use_information.mold_use_information.length > 0
                        ) {
                            mold_use_information.mold_use_information.forEach(
                                (data) => {
                                    let mold_use_information_obj = {};
                                    mold_use_information_obj["code"] =
                                        data["code"];
                                    mold_use_information_obj["seriesNo"] =
                                        data["seriesNo"];
                                    mold_use_information_obj["name"] =
                                        data["name"];
                                    mold_use_information_item.push(
                                        mold_use_information_obj
                                    );
                                }
                            );
                            vm.mold_use_information_item = mold_use_information_item;
                        }
                    } else if (vm.sideBarSelect.model == "輾牙") {
                        //使用牙板
                        let thread_use_information =
                            result_Data[0].thread_use_information;
                        if (
                            thread_use_information.thread_use_information
                                .length > 0
                        ) {
                            thread_use_information.thread_use_information.forEach(
                                (data) => {
                                    let thread_use_information_obj = {};
                                    thread_use_information_obj["code"] =
                                        data["code"];
                                    thread_use_information_obj["seriesNo"] =
                                        data["seriesNo"];
                                    thread_use_information_obj["name"] =
                                        data["name"];
                                    thread_use_information_item.push(
                                        thread_use_information_obj
                                    );
                                }
                            );
                            vm.thread_use_information_item = thread_use_information_item;
                        }
                    }

                    //每桶產出
                    let bucket_use_information =
                        result_Data[0].bucket_use_information;
                    let bucket_use_information_item = [];
                    if (
                        bucket_use_information.bucket_use_information.length > 0
                    ) {
                        bucket_use_information.bucket_use_information.forEach(
                            (data) => {
                                let bucket_use_information_obj = {};
                                bucket_use_information_obj["code"] =
                                    data["code"];
                                bucket_use_information_obj["furnaceCode"] =
                                    data["furnaceCode"];
                                bucket_use_information_obj["screwWeight"] =
                                    data["screwWeight"];
                                bucket_use_information_item.push(
                                    bucket_use_information_obj
                                );
                            }
                        );
                        vm.bucket_use_information_item = bucket_use_information_item;
                    }

                    //檢驗資料
                    let inspection_use_information =
                        result_Data[0].inspection_use_information
                            .inspection_use_information;
                    vm.inspection_use_information = inspection_use_information;

                    //總生產時間
                    let totalProduceTime = result_Data[0].totalProduceTime;
                    let totalProduceTime_item = [];
                    if (totalProduceTime.totalProduceTime.length > 0) {
                        totalProduceTime.totalProduceTime.forEach((data) => {
                            let totalProduceTime_obj = {};
                            totalProduceTime_obj["code"] = data["code"];
                            totalProduceTime_obj["device_name"] =
                                data["device_name"];
                            totalProduceTime_obj["startTime"] =
                                data["startTime"];
                            totalProduceTime_obj["endTime"] = data["endTime"];
                            totalProduceTime_obj["totalTime"] =
                                data["totalTime"];
                            totalProduceTime_item.push(totalProduceTime_obj);
                        });
                        vm.totalProduceTime_item = totalProduceTime_item;
                    }

                    let total_work_time =
                        result_Data[0].totalProduceTime.total_work_time;
                    vm.total_work_time = total_work_time;

                    //運轉日誌
                    let operatelog = result_Data[0].operatelog;
                    let operatelog_item = [];
                    if (operatelog.length > 0) {
                        operatelog.forEach((data) => {
                            let operatelog_obj = {};
                            operatelog_obj["device_name"] = data["device_name"];
                            operatelog_obj["place_item"] = data["place_item"];
                            operatelog_obj["group_item"] = data["group_item"];
                            operatelog_obj["class_item"] = data["class_item"];
                            operatelog_obj["status"] = data["status"];
                            operatelog_obj["alarmCode"] = data["alarmCode"];
                            operatelog_obj["alarmDetail"] = data["alarmDetail"];
                            operatelog_obj["startTime"] = data["startTime"];
                            operatelog_obj["endTime"] = data["endTime"];
                            operatelog_obj["durationTime"] =
                                data["durationTime"];
                            operatelog_item.push(operatelog_obj);
                        });
                        vm.setOperateLogDataItem(operatelog_item);
                    }

                    //機台燈號
                    let machine_light =
                        result_Data[0].machine_light_information.machine_light;
                    vm.machineItem =
                        result_Data[0].machine_light_information.machine_item;
                    let machineItemName = Object.keys(vm.machineItem);
                    machineItemName.forEach((itemName) => {
                        let thisColor = "white";
                        if (
                            machine_light[itemName] == 1 ||
                            machine_light[itemName] == 0
                        ) {
                            thisColor = "gray";
                            if (
                                machine_light[itemName] ==
                                vm.machineItem[itemName].value
                            ) {
                                thisColor = vm.machineItem[itemName]["color"];
                            }
                        }
                        if (vm.machineItem_select[itemName] === undefined) {
                            vm.machineItem_select[itemName] = {
                                color: thisColor,
                                name: vm.machineItem[itemName].name,
                            };
                        } else {
                            vm.machineItem_select[itemName][
                                "color"
                            ] = thisColor;
                        }
                    });

                    if (!vm.workorderHistory_row_click) {
                        vm.setWireUseInformationItem(wire_use_information_item);
                        if (vm.sideBarSelect.model == "打頭") {
                            vm.setMoldUseInformationItem(
                                mold_use_information_item
                            );
                        } else if (vm.sideBarSelect.model == "輾牙") {
                            vm.setThreadUseInformationItem(
                                thread_use_information_item
                            );
                        }
                        vm.setBucketUseInformationItem(
                            bucket_use_information_item
                        );
                        vm.setInspectionUseInformationItemData(
                            inspection_use_information
                        );
                        vm.setTotalProduceTimeItem(totalProduceTime_item);
                        vm.setTotalProduceTiming(total_work_time);
                    }
                } else if (result["Response"] == "no data") {
                    vm.setSetTimeOutAlertMsg("查無資料");
                } else {
                    vm.setSetTimeOutAlertMsg(result["Response"]);
                }
                vm.timer_selectDevice = setTimeout(function () {
                    // vm._selectDevice(vm.device_name);
                }, 4000);
            } else if (functionName == "_queryCunter") {
                if (
                    result["Response"] == "ok" &&
                    result["page"] == "machstatus"
                ) {
                    let result_Data = result["QueryTableData"];
                    vm.work_order_detail.device_count =
                        result_Data[0]["device_count"];
                    vm.device_detail.device_cumulative_production =
                        result_Data[0]["device_cumulative_production"];
                }
                vm.timer_queryCunter = setTimeout(function () {
                    // vm._queryCunter(vm.device_name);
                }, 1000);
            }
        },

        _clearTimer(timerName) {
            if (timerName === "undefined") {
                var highestIntervalId = setInterval(";");
                for (var i = 0; i < highestIntervalId; i++) {
                    //清除所有計時資料
                    clearInterval(i);
                }
                if (this.selectPage == "machstatus") {
                    this._getNowTime();
                }
            } else {
                clearTimeout(timerName);
            }
        },
    },
    destroyed() {
        this.machStatusAxiosSource.cancel("back");
        this._clearTimer(this.timer_selectDevice);
        this._clearTimer(this.timer_queryCunter);
        this._clearTimer();
        this.clearStacksData();
        this.clearMachineHealthData();
        this.clearWireUseInformationItem();
        this.clearMoldUseInformationItem();
        this.clearThreadUseInformationItem();
        this.clearBucketUseInformation();
        this.clearInspectionUseInformation();
        this.clearOperateLogDataItem();
        this.clearWorkorderHistoryDataItem();
        this.clearAlarmHistoryDataItem();
    },
};
</script>

