                            <b-icon
                                class="arrowdown"
                                icon="caret-down-fill"
                            ></b-icon>
                        </template>
                    </p>
                </router-link>
                <template v-if="item.hasOwnProperty('collapse')">
                    <b-collapse
                        :id="'collapse-' + key"
                        :ref="'collapse-' + key"
                        class="mt-2"
                        v-model="collapseIDList[key]"
                    >
                        <router-link
                            :to="{
                                name: key,
                                params: {
                                    SYSTEM: $route.params.SYSTEM,
                                },
                            }"
                        >
                            <b-button-group vertical>
                                <div
                                    v-for="(item2, index2) in item.collapse[
                                        'childitem'
                                    ]['v-for']"
                                    :key="index2"
                                    @click="
                                        setClickChild({
                                            child_name: key + '_' + item2,
                                            data:
                                                item.collapse['childitem']
                                                    .click.data,
                                        })
                                    "
                                    class="insidebtn"
                                    :class="{
                                        inside_click:
                                            clickChild['child_name'] ==
                                            key + '_' + item2,
                                    }"
                                >
                                    <template
                                        v-if="
                                            item.collapse[
                                                'childitem'
                                            ].content.hasOwnProperty(
                                                'condition'
                                            )
                                        "
                                    >
                                        <template
                                            v-if="
                                                Number.isInteger(
                                                    item.collapse[
                                                        'childitem'
