                                                    item2
                                                ][
                                                    item.collapse[
                                                        "childitem"
                                                    ].content.condition
                                                ]
                                            }}
                                        </template>
                                        <template v-else>
                                            {{
                                                item.collapse[
                                                    "childitem"
                                                ].content.conversiontable[
                                                    item2
                                                ].join(
                                                    item.collapse[
                                                        "childitem"
                                                    ].content.condition.split(
                                                        "."
                                                    )[1]
