                                            }}
                                        </template>
                                    </template>
                                    <template v-else>{{ item2 }}</template>
                                </div>
                            </b-button-group>
                        </router-link>
                    </b-collapse>
                </template>
            </b-col> -->

            <!-- ul li版 -->
            <ul
                class="dropdown publicDarkBG"
                v-for="(item, key, index) in featureListBarData.body"
                :key="index"
            >
                <router-link
                    :to="{
                        name: key,
                        params: {
                            SYSTEM: $route.params.SYSTEM,
                        },
                    }"
                >
                    <li>
                        {{ item.title }}
                        <template v-if="item.hasOwnProperty('collapse')">
                            <ul>
                                <li
                                    v-for="(item2, index2) in item.collapse[
                                        'childitem'
                                    ]['v-for']"
                                    :key="index2"
                                    @click="
                                        setClickChild({
                                            child_name: key + '_' + item2,
                                            data:
                                                item.collapse['childitem'].click
                                                    .data,
                                        })
                                    "
                                    :class="{
                                        inside_click:
                                            clickChild['child_name'] ==
                                            key + '_' + item2,
                                    }"
                                >
                                    <router-link
                                        :to="{
                                            name: key,
                                            params: {
                                                SYSTEM: $route.params.SYSTEM,
                                            },
                                        }"
                                    >
                                        <template
                                            v-if="
                                                item.collapse[
                                                    'childitem'
                                                ].content.hasOwnProperty(
                                                    'condition'
                                                )
                                            "
                                        >
                                            <template
                                                v-if="
                                                    Number.isInteger(
                                                        item.collapse[
                                                            'childitem'
                                                        ].content.condition
                                                    )
                                                "
                                            >
                                                {{
                                                    item.collapse["childitem"]
                                                        .content
                                                        .conversiontable[item2][
                                                        item.collapse[
                                                            "childitem"
                                                        ].content.condition
                                                    ]
                                                }}
                                            </template>
                                            <template v-else>
                                                {{
                                                    item.collapse[
                                                        "childitem"
                                                    ].content.conversiontable[
                                                        item2
                                                    ].join(
                                                        item.collapse[
                                                            "childitem"
                                                        ].content.condition.split(
                                                            "."
                                                        )[1]
                                                    )
                                                }}
                                            </template>
                                        </template>
                                        <template v-else>{{ item2 }}</template>
                                    </router-link>
                                </li>
                            </ul>
                        </template>
                    </li>
                </router-link>
            </ul>
        </b-row>
    </b-col>
