<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "modal",
    data() {
        return {};
    },
    computed: {
        ...mapGetters({
            featureMainData: "featuremain/get_featureMainData",
            featureListBarData: "featurelistbar/get_featureListBarData",
            clickChild: "featurelistbar/get_clickChild",
            toggleCollapseID: "featurelistbar/get_toggleCollapseID",
            collapseIDList: "featurelistbar/get_collapseIDList",
            clickRoute: "featuremain/get_clickRoute",
        }),
    },
    mounted() {
        console.log(this.featureListBarData);
        this.$root.$emit(
            "bv::toggle::collapse",
            "collapse-" + this.toggleCollapseID
        );
    },
    methods: {
        ...mapActions({
            setClickChild: "featurelistbar/set_clickChild",
        }),

        _cardBodyTextStyle(element) {
            if (
                element.hasOwnProperty("style") &&
                element["style"].hasOwnProperty("card-body-text")
            ) {
                return element["style"]["card-body-text"];
            }
        },

        _cardBodyImgStyle(element) {},
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.featureListBar {
    width: 200px;
    padding: 0;
    // overflow-x: hidden;
    // overflow-y: scroll;
    .publicText {
        font-size: 20px;
    }
    .insidebtn {
        padding: 5px 2px;
        background-color: transparent;
        border-color: transparent;
        color: white;
        font-size: 15px;
        &:hover {
            color: #0566ca !important;
            // background-color: #eeebeb !important;
        }
        &:focus {
            box-shadow: unset !important;
        }
    }
    .titlechoose {
        color: #0566ca !important;
        &:hover {
        }
    }
    .inside_click {
        color: #0566ca !important;
        // border: 2px solid #aab4d1 !important;
        // border-radius: 8px !important;
        /* color: black !important; */
    }
    .edoc-feature-list {
        margin-top: 2rem !important;
        p {
            color: white;
            &:hover {
                color: #0566ca;
            }
            .arrowup,
            .arrowdown {
                display: none;
            }
            &.collapsed {
                .arrowdown {
                    display: inline-block !important;
                }
            }
            &.not-collapsed {
                .arrowup {
                    display: inline-block !important;
                }
            }
        }
    }
    &.isFixed {
        position: fixed;
        top: 0;
        z-index: 999;
        opacity: 0;
        // display: none;
    }
    // ul {
    //     width: 100%;
    //     height: 40px;
    //     line-height: 40px;
    //     display: flex;
    //     li {
    //         font-size: 0.8rem;
    //         text-align: center;
    //         flex: 1;
    //         i {
    //             font-size: 0.9rem;
    //             padding-left: 5px;
    //             color: #ccc;
    //         }
    //     }
    //     border-bottom: 1px solid #ddd;
    // }
    .feature-list-icon {
        cursor: pointer;
    }

    .dropdown {
        margin: 0;
        padding: 0;
        list-style: none;
        width: 100%;
        li {
            position: relative;

            ul {
                position: absolute;
                top: 0;
                left: 100%;
                margin: 0;
                padding: 0;
                list-style: none;
                display: none;
                line-height: normal;
                // background-color: #333;

                li {
                    ul {
                        left: 100%;
                        top: 0;
                    }
                }
            }
        }
    }

    ul li:hover {
        background-color: #65646a;
        color: #ffffff !important;
        cursor: pointer;
    }
    ul li {
        height: 60px;
        line-height: 60px;
        text-align: center;
        font-size: 18px;
        font-weight: bolder;
        width: 200px;
        z-index: 3;
        background-color: $final-color-1;
    }

    ul li:hover > ul {
        display: block;
    }

    a {
        color: #ffffff;
        text-decoration: none;
        // display: block;
        white-space: nowrap;
    }
}

