const getters = {
    get_sideBarIcon: state => {
        return state.sideBarIcon;
    },
    get_sideBarSelect: state => {
        return state.sideBarSelect;
    }
};
const actions = {
    set_sideBarSelect(context, array) {
        context.commit("SET_SIDEBARSELECT", array);
    }
};
const mutations = {
    SET_SIDEBARSELECT(state, array) {
        let page = array[0];
        if (page == 'elecboard') {
            let process = array[1];
            let group = array[2];
            let data = state.sideBar.elecboard.other.process;
            state.sideBarSelect = {
                page: page,
                process: data[process].name,
                group: {
                    name: data[process].group[group].name,
                    device: data[process].group[group].device,
                    user: data[process].group[group].user
                },
                change: Math.random()
            };
        }
        console.log(state.sideBarSelect);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
