<template>
  <div class="selectCustomer">
    <div class="col-12 row text-left">
      <div class="mx-2">
        <div>
          <p class style="margin-bottom: 0.625rem">客戶編號</p>
          <b-form-select
            v-model="customerSelected"
            :options="customerOption"
            class=""
            style="
              max-width: 9rem;
              min-width: 20rem;
              height: 2.375rem;
              line-height: 1.375rem;
              font-size: 0.875rem;
              border: 2px solid rgb(255, 255, 255);
              box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
              color: rgb(95, 95, 95);
            "
          >
            <template #first>
              <b-form-select-option :value="null" disabled
                >-- 選擇客戶 --</b-form-select-option
              >
            </template>
          </b-form-select>
        </div>
      </div>
      <div class="mx-2" style="width: 4.25rem">
        <b-button
          class="p-0"
          @click="_queryData"
          style="
            margin-top: 38px;
            background-color: #536172;
            border-color: #536172;
            width: 4.25rem;
            height: 2rem;
            font-size: 1.125rem;
          "
          >查閱</b-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "selectCustomer",
  data() {
    return {
      customerSelected: null,
    };
  },
  created: function () {},
  components: {},
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",
      selectPage: "mesGetLogin/get_selectPage",
      selectPageName: "mesGetLogin/get_selectPageName",
      setTimeOutAlertMsg: "alertmodal/get_setTimeOutAlertMsg",
      customerOption: "mesSelectCustomer/get_customerOptions",
    }),
  },
  watch: {},
  methods: {
    ...mapActions({
      mesAxiosAction: "mescommonaxios/axiosAction",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
      showLoadScreenModal: "mesLoadScreenModal/show_loadScreenModal",
      hideLoadScreenModal: "mesLoadScreenModal/hide_loadScreenModal",
      setExportFileName: "mesExportCsv/set_exportFileName",
      setQueryResultCount: "mesQueryResultCount/set_queryResultCount",
      clearQueryResultCount: "mesQueryResultCount/clear_queryResultCount",
      setSelectedCustomer: "mesData/set_selectedCustomer",
    }),
    //查詢按鈕
    _queryData() {
      let vm = this;
      if (!vm.customerSelected) {
        vm.setSetTimeOutAlertMsg("客戶編號尚未選擇");
        vm.toggleSetTimeOutAlertModal();
      } else {
        vm.setSelectedCustomer(vm.customerSelected);
        if (vm.selectPage == "deviceManage") {
          this.$router.push("/" + this.$route.params.SYSTEM + "/mes/overView");
        } else if(vm.selectPage == "aicontrol"){
          this.$router.push("/" + this.$route.params.SYSTEM + "/bigdata/diagnosis");
        }
      }
      // vm._dateFormat(vm.selected);
    },

    //data reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.custom-select {
  width: 200px !important;
}
.card-title {
  font-size: 25px;
}
.input-title {
  width: 200px !important;
  display: inline-block;
  margin-top: 5px;
}
.selectCustomer div,
.selectCustomer div p {
  font-family: dfyuanstd-w7;
}
</style>