const state = {
   dataCount:{
       total:0,
       view:false
   }
}
const getters = {
    get_queryResultCount: state => {
        return state.dataCount;
    },
 
}
const actions = {
    set_queryResultCount(context, num) {
        context.commit('SET_QUERYRESULTCOUNT', num);
    },
    clear_queryResultCount(context){
        context.commit('CLEAR_QUERYRESULTCOUNT');
    }
}
const mutations = {
    SET_QUERYRESULTCOUNT(state, num) {
        state.dataCount.total = num;
        state.dataCount.view = true;
    },
    CLEAR_QUERYRESULTCOUNT(state){
        state.dataCount.total = 0;
        state.dataCount.view = false;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}