<template>
  <div id="InspectionRecord" class="inspectionRecord">
    <b-row class="mx-0 mb-3">
      <b-col cols="1"> </b-col>

      <b-col
        cols="10"
        class="p-0 m-0 mt-4 text-center"
        style="font-size: 1.75rem; color: #000000; margin-top: 3rem"
      >
        <span class="m-auto">檢測履歷</span>
      </b-col>
      <b-col cols="1"> </b-col>
      <b-col cols="1"> </b-col>
      <b-col cols="10" class="mx-0 px-0" style="margin-top: 2rem">
        <b-row>
          <commonQueryIoT />
          <b-button
            style="
              font-family: 'dfyuanstd-w7';
              margin-top: 38px;
              background-color: #536172;
              border-color: #536172;
              width: 4.25rem;
              height: 2rem;
              font-size: 1.125rem;
              padding: 0px;
            "
            @click="exportFile()"
            >匯出</b-button
          >
        </b-row>
        <template v-if="inspectionSensor.length > 0">
          <div
            style="
              font-family: dfyuanstd-w7;
              margin-top: 20px;
              font-size: 1.5rem;
            "
          >
            選擇感測器項目:
          </div>
          <b-row>
            <b-col
              cols="4"
              v-for="(item, index) in inspectionSensor"
              :key="index"
            >
              <input
                type="checkbox"
                :id="index"
                :disabled="item.disable"
                :checked="item.checked"
                v-model="item.checked"
              /><label :for="index">{{ item.sensor_class_name }}</label>
            </b-col>
          </b-row>
        </template>
      </b-col>
      <b-col cols="1"> </b-col>
      <b-col cols="1"> </b-col>
    </b-row>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
import queryResult from "@/components/MES/queryResult.vue";
import exportCsv from "@/components/MES/exportCsv.vue";
export default {
  name: "InspectionRecord",
  data() {
    return {
      inspectionSensor: [],
      deviceId: "",
    };
  },
  created() {
    let vm = this;
    this.changeSelectPage("inspectionrecord");
    // this.changeSelectPageName(this.pageName.devicesetting);

    this._setCommonQueryData();
    this._setQueryInput();
    this._getSelectOption();
  },
  mounted: function () {
    let vm = this;
  },
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",
      selectPage: "mesGetLogin/get_selectPage",
      selectPageName: "mesGetLogin/get_selectPageName",
      pageName: "mesGetLogin/get_pageName",
      selectCustomer: "mesData/get_selectedCustomer",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
      inputData: "mesCommonQueryIoT/get_inputData",
      clickChild: "featurelistbar/get_clickChild",
      selectValue: "mesCommonQueryIoT/get_selectValue",
    }),
  },
  components: {
    alertModal,
    queryResult,
    commonQueryIoT,
    exportCsv,
  },
  watch: {
    mainQueryResponse: {
      handler() {
        let vm = this;
        let sensorObj = [];
        vm._reset([]);
        if (vm.mainQueryResponse.length > 0) {
          vm.mainQueryResponse.forEach((item) => {
            sensorObj.push({
              sensor_class_code: item.sensor_class_code,
              sensor_class_name: item.sensor_class_name,
              checked: false,
              disable: false,
            });
          });
          vm.inspectionSensor = sensorObj;
          vm.deviceId = vm.mainQueryResponse[0]["device_id"];
        }
      },
    },
  },
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      changeSelectPage: "mesGetLogin/change_selectPage",
      changeSelectPageName: "mesGetLogin/change_selectPageName",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      clearQueryResultValue: "mesQueryResult/clear_queryResultItem",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
      trigger_loadScreenModal: "mesLoadScreenModal/trigger_loadScreenModal",
      show_loadScreenModal: "mesLoadScreenModal/show_loadScreenModal",
      hide_loadScreenModal: "mesLoadScreenModal/hide_loadScreenModal",
    }),
    _setCommonQueryData() {
      let vm = this;
      vm.setPhpFunction("MainQuery");
    },
    _setQueryInput() {
      let vm = this;
      vm.setSelectTag({
        model: false,
        deviceCode: false,
      });
    },

    _getSelectOption() {
      let vm = this;
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "GET";
      params["whichFunction"] = "SelectOption";
      params["cusID"] = vm.selectCustomer;
      console.log(params);
      vm.axiosAction(params).then(() => {
        var result = vm.axiosResult;
        console.log(result);
        if (result["Response"] == "ok") {
          let backData = result["QueryTableData"];
          let modelOption = backData[0]["model"];
          let deviceOption = backData[0]["device"];
          console.log(backData);
          vm.setModelOption(modelOption);
          vm.setDeviceOption(deviceOption);
          console.log(modelOption);
        } else {
          vm.setModelOption([]);
          vm.setDeviceOption([]);
        }
      });
    },

    exportFile() {
      let vm = this;
      if (vm.inspectionSensor.length == 0) {
        vm.setSetTimeOutAlertMsg("無匯出資料");
        vm.toggleSetTimeOutAlertModal();
        return;
      }

      var params = {};
      params["vuePage"] = vm.selectPage;
      console.log(vm.selectPage);
      params["methods"] = "POST";
      params["whichFunction"] = "download_inspection_file";
      params["cusID"] = vm.selectCustomer; //取客戶編號
      params["deviceID"] = vm.deviceId; //取機台編號

      let sensorCode = [];
      vm.inspectionSensor.forEach((item) => {
        if (item.checked == true) {
          sensorCode.push(item.sensor_class_code);
        }
      });
      if (sensorCode.length == 0) {
        vm.setSetTimeOutAlertMsg("選擇感測器項目");
        vm.toggleSetTimeOutAlertModal();
        return;
      }
      params["selected_sensor"] = sensorCode;
      params["selected_date"] = {
        startTime: vm.selectValue.startTime,
        endTime: vm.selectValue.endTime,
      };
      console.log(params);
      vm.show_loadScreenModal();
      vm.axiosAction(params).then(() => {
        var result = vm.axiosResult;
        vm.hide_loadScreenModal();
        console.log(result);
        if (result["Response"] == "ok") {
          let fileNameArray = result["QueryTableData"];
          fileNameArray.forEach((item) => {
            vm.axios({
              method: "get",
              url: "/dashboard/csvfile/CHUNZU/" + item + ".csv",
              responseType: "blob",
            })
              .then((res) => {
                let url = window.URL.createObjectURL(new Blob([res.data]));
                let link = document.createElement("a");
                link.style.display = "none";
                link.href = url;
                link.setAttribute("download", item + ".csv");
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
              })
              .catch((err) => {
                console.log(err);
              });
          });
        }
      });
    },
    testDownload() {
      this.axios({
        method: "get",
        url: "/csvfile/CHUNZU/test.txt",
        responseType: "blob",
      })
        .then((res) => {
          let url = window.URL.createObjectURL(new Blob([res.data]));
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute("download", "test");
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
};
</script>

