                    <b-form-input
                        class="text"
                        v-model="post_data.devID"
                    ></b-form-input>
                </b-col>
                <b-col cols="auto">
                    <span class="title">實際生產時間/hr: </span>
                    <b-form-input
                        class="text"
                        type="number"
                        v-model="post_data.production_time"
                    ></b-form-input>
                </b-col>
                <b-col cols="auto">
                    <button
                        type="button"
                        class="btn submit publicBlueBG-level2"
                        :disabled="
                            post_data.devID == '' ||
                            post_data.serialID == '' ||
                            post_data.production_time == ''
