<template>
  <div class="stackBar">
    <!-- <div
      class="col-12 row text-left mx-0 mt-3 mb-1 px-1"
      style="background-color: #f2f2f2"
    >
      <h5 class="my-auto text-left">堆疊圖</h5>
    </div> -->
    <div v-for="(data, device_name) in stacksData" :key="device_name">
      <div class="row mx-0" style="padding: 0px 15px">
        <!-- <div class="col-1 px-0 m-auto">{{ device_name }}:</div> -->
        <div class="col-12 px-0">
          <div class="px-2">
            <b-progress
              :max="stacksBarMaxValue"
              class="mb-1"
              :style="{ height: stacksSize == 'small' ? '1.2rem' : '3rem' }"
            >
              <b-progress-bar
                v-for="(item, key) in data"
                :key="key"
                class="stacked-bar"
                :class="{
                  'progress-bar-danger': item.status == '警報',
                  'progress-bar-success': item.status == '生產',
                  'progress-bar-warning': item.status == '待機',
                  'progress-bar-secondary': item.status == '關機',
                  'progress-bar-nodata': item.status == null,
                }"
                :value="item.duration_number"
                v-b-tooltip="{
                  trigger: 'hover',
                  title:
                    item.status != null
                      ? `狀態:${item.status}<br>開始時間:${item.startTime}<br>結束時間:${item.endTime}`
                      : 'no data',
                  html: true,
                  placement: 'bottom',
                  variant:
                    item.status == '警報'
                      ? 'danger'
                      : item.status == '生產'
                      ? 'success'
                      : item.status == '待機'
                      ? 'warning'
                      : item.status == '關機'
                      ? 'secondary'
                      : 'dark',
                }"
              ></b-progress-bar>
            </b-progress>
          </div>
          <div
            class="d-flex justify-content-between"
            :style="{ 'font-size': stacksSize == 'small' ? '12px' : '18px' }"
          >
            <template v-if="data[0] != undefined">
              <template v-if="stacksSize == 'normal'">
                <div
                  v-for="(item, key) in data[0].stacksBarNumber"
                  :class="stacksBarClass[key]"
                  :key="key"
                >
                  {{ item }}
                </div>
              </template>
              <template v-if="stacksSize == 'small'">
                <div
                  v-for="(item, key) in data[0].stacksBarNumber"
                  :class="stacksBarClass[key]"
                  :key="key"
                  :style="{
                    display:
                      stacksBarClass[key] == 'stacked-bar-time-24'
                        ? 'none'
                        : '',
                  }"
                >
                  {{ item }}
                </div>
              </template>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "stackBar",
  props: {
    stacksBarClass: Array,
    stacksBarMaxValue: Number,
    stacksData: Object,
    stacksSize: String,
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      // stacksBarClass: "mesStackBar/get_stacksBarClass",
      // stacksBarMaxValue: "mesStackBar/get_stacksBarMaxValue",
      // stacksData: "mesStackBar/get_stacksData",
    }),
  },
};
</script>

<style>
@media (max-width: 1350px) {
  .stacked-bar-time-24 {
    display: none;
  }
}
@media (max-width: 950px) {
  .stacked-bar-time-12 {
    display: none;
  }
}
@media (max-width: 650px) {
  .stacked-bar-time-12.stacked-bar-time-6 {
    display: block;
  }
  .stacked-bar-time-6 {
    display: none;
  }
}
@media (max-width: 350px) {
  .stacked-bar-time-12.stacked-bar-time-6 {
    display: none;
  }
}
@media (max-width: 200px) {
  .stacked-bar-time-3 {
    display: none;
  }
}
.progress-bar-danger {
  background-color: #dc3545 !important;
}
.progress-bar-success {
  background-color: #28a745 !important;
}
.progress-bar-warning {
  background-color: #ffc107 !important;
}
.progress-bar-secondary {
  background-color: #6c757d !important;
}
.progress-bar-nodata {
  background-color: #e9ecef !important;
}
.progress-bar-danger:hover {
  background-color: #c82333 !important;
}
.progress-bar-success:hover {
  background-color: #218838 !important;
}
.progress-bar-warning:hover {
  background-color: #e0a800 !important;
}
.progress-bar-secondary:hover {
  background-color: #5a6268 !important;
}
</style>
