<template>
    <div
        id="devicetype"
        ref="devicetype"
        class="customize_scrollbar"
        style="position: relative"
    >
        <b-row class="devicetype-block">
            <b-col cols="12" class="text-center">
                <h3>機型配套管理</h3>
            </b-col>
            <b-col cols="12">
                <b-row>
                    <b-col cols="12">
                        <span>機台型號:</span>
                    </b-col>
                </b-row>
                <b-row align-h="start">
                    <b-col cols="2">
                        <b-form-select
                            v-model="devicetype_selected"
                            :options="devicetype_options"
                            required
                        ></b-form-select>
                    </b-col>
                    <b-col cols="4">
                        <b-button
                            class="publicButton"
                            :disabled="
                                devicetype_options.length == 0 ||
                                devicetype_selected == ''
                            "
                            @click.prevent="_queryModelSensorListSensorList"
                            >送出</b-button
                        >
                    </b-col>
                </b-row>
                <b-form @submit="_onSubmit" v-if="cud_items.length > 0">
                    <b-row align-h="center">
                        <b-table
                            sticky-header="600px"
                            :items="cud_items"
                            :fields="cud_fields"
                            :fixed="false"
                            responsive
                            :head-variant="'light'"
                            class="customize_scrollbar text-center mt-3"
                            v-if="cud_items.length != 0"
                            :tbody-tr-attr="_rowClass"
                        >
                            <template v-slot:cell(type)="row">
                                {{ type_config[row.item.type] }}
                            </template>
                            <template v-slot:cell(enable)="row">
                                <b-form-checkbox
                                    v-model="row.item.enable"
                                    value="Y"
                                    unchecked-value="N"
                                >
                                </b-form-checkbox>
                            </template>
                            <template v-slot:cell(range)="row">
                                <b-form-select
                                    v-model="row.item.range"
                                    :options="range_options"
                                    v-if="row.item.type == 'sensor'"
                                ></b-form-select>
                                <span v-else>-</span>
                            </template>
                            <template v-slot:cell(error_min)="row">
                                <template v-if="row.item.range === null">
                                    <span>-</span>
                                </template>
                                <b-form-input
                                    v-else-if="
                                        ['min', 'between'].indexOf(
                                            row.item.range
                                        ) != -1
                                    "
                                    type="number"
                                    v-model="row.item.error_min"
                                    step="0.1"
                                    :required="row.item.range == 'between'"
                                ></b-form-input>
                                <span v-else>-</span>
                            </template>
                            <template v-slot:cell(warn_min)="row">
                                <template v-if="row.item.range === null">
                                    <span>-</span>
                                </template>
                                <b-form-input
                                    type="number"
                                    v-model="row.item.warn_min"
                                    v-else-if="
                                        ['min', 'between'].indexOf(
                                            row.item.range
                                        ) != -1
                                    "
                                    step="0.1"
                                    :required="row.item.range == 'between'"
                                ></b-form-input>
                                <span v-else>-</span>
                            </template>
                            <template v-slot:cell(warn_max)="row">
                                <template v-if="row.item.range === null">
                                    <span>-</span>
                                </template>
                                <b-form-input
                                    type="number"
                                    v-model="row.item.warn_max"
                                    v-else-if="
                                        ['max', 'between'].indexOf(
                                            row.item.range
                                        ) != -1
                                    "
                                    step="0.1"
                                    :required="row.item.range == 'between'"
                                ></b-form-input>
                                <span v-else>-</span>
                            </template>
                            <template v-slot:cell(error_max)="row">
                                <template v-if="row.item.range === null">
                                    <span>-</span>
                                </template>
                                <b-form-input
                                    type="number"
                                    v-model="row.item.error_max"
                                    v-else-if="
                                        ['max', 'between'].indexOf(
                                            row.item.range
                                        ) != -1
                                    "
                                    step="0.1"
                                    :required="row.item.range == 'between'"
                                ></b-form-input>
                                <span v-else>-</span>
                            </template>
                        </b-table>
                    </b-row>
                    <b-row align-h="center">
                        <b-button type="submit" class="ml-2 mt-3 publicButton"
                            >修改</b-button
                        >
                    </b-row>
                </b-form>
            </b-col>
        </b-row>
    </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import Temp from "../Temp.vue";
export default {
    components: { Temp },
    name: "devicetype",
    data() {
        let vm = this;
        return {
            devicetype_selected: "",
            devicetype_options: [],
            deviceID: "",
            cud_fields: [
                {
                    key: "type",
                    label: "類別",
                    thClass: "publicTH",
                },
                {
                    key: "name",
                    label: "感測點名稱",
                    thClass: "publicTH",
                },
                {
                    key: "enable",
                    label: "啟用",
                    thClass: "publicTH",
                },
                {
                    key: "range",
                    label: "條件",
                    thClass: "publicTH",
                },
                {
                    key: "error_min",
                    label: "異常下限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
                {
                    key: "warn_min",
                    label: "注意下限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
                {
                    key: "warn_max",
                    label: "注意上限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
                {
                    key: "error_max",
                    label: "異常上限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
            ],
            cud_items: [],
            range_options: [
                { text: "最大值", value: "max" },
                { text: "最小值", value: "min" },
                { text: "範圍", value: "between" },
                { text: "Null", value: null },
            ],
            type_config: {
                light: "燈號點",
                sensor: "感測點",
            },
        };
    },
    computed: {
        ...mapGetters({
            loginData: "getlogin/get_loginData",
            axiosResult: "commonaxios/get_axiosResult",
            // system: "apidetail/get_system",
            DEFAULT_commonModalConfig: "usemodal/get_DEFAULT_commonModalConfig",
            clickChild: "featurelistbar/get_clickChild",
        }),
    },
    watch: {
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
                vm._reset([]);
                this._queryMachineModel(); //抓取機台機型
            },
        },
    },
    created() {
        // this._checkApiDetailExist();
        // this.apipath_prefix += this.$route.params.SYSTEM;
        console.log(this.$router);
        console.log(this.$router.currentRoute.matched);
        console.log(this.endpoint);
        console.log(this.$route.params.SYSTEM);
        console.log(this.clickChild);
        this._queryMachineModel(); //抓取機台機型
    },
    mounted() {
        // var VueScrollTo = require("vue-scrollto");
        // var options = {
        //     container: "#devicetype",
        //     easing: "ease-in",
        //     offset: -60,
        //     force: true,
        //     cancelable: true,
        //     onStart: function (element) {
        //         // scrolling started
        //     },
        //     onDone: function (element) {
        //         // scrolling is done
        //     },
        //     onCancel: function () {
        //         // scrolling has been interrupted
        //     },
        //     x: false,
        //     y: true,
        // };
        // var cancelScroll = VueScrollTo.scrollTo("#devicetype", options);
        // // or alternatively inside your components you can use
        // // cancelScroll = this.$scrollTo(element, duration, options);
        // // to cancel scrolling you can call the returned function
        // cancelScroll();
        // // this.$scrollTo("#devicetype");
    },
    methods: {
        ...mapActions({
            axiosAction: "commonaxios/axiosAction",
            axiosGetAction: "commonaxios/axiosGetAction",
            axiosPostAction: "commonaxios/axiosPostAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleCommonModal: "usemodal/toggle_commonModal",
            setCommonModalConfig: "usemodal/set_commonModalConfig",
        }),

        _rowClass(item, type) {
            if (!item || type !== "row") return;
            if (item.enable == "N")
                return { style: "background-color:rgba(0, 0, 0, 0.144)" };
        },

        _onSubmit(event) {
            event.preventDefault();
            event.stopPropagation();
            let vm = this;
            console.log(vm.cud_items);
            let postdatas = {
                model_sensor_list: [],
                model_light_list: [],
            };

            let isok = true;
            for (let i = 0; i < vm.cud_items.length; i++) {
                const element = vm.cud_items[i];
                let sensor_warn = null;
                let sensor_error = null;
                if (
                    ["min", "max"].indexOf(element.range) != -1 &&
                    element.type == "sensor"
                ) {
                    if (
                        (element["warn_" + element.range] === null ||
                            element["warn_" + element.range] == "") &&
                        (element["error_" + element.range] === null ||
                            element["error_" + element.range] == "") &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg("資料尚未輸入完整");
                        break;
                    }

                    if (
                        element["warn_" + element.range] != null &&
                        element["warn_" + element.range] != "" &&
                        element["error_" + element.range] != null &&
                        element["error_" + element.range] != "" &&
                        element.enable == "Y"
                    ) {
                        if (
                            element.range == "max" &&
                            element.error_max < element.warn_max
                        ) {
                            console.log(element["warn_" + element.range]);
                            console.log(element["error_" + element.range]);
                            console.log(element);

                            isok = false;
                            vm.setSetTimeOutAlertMsg(
                                "「異常上限」需大於等於「注意上限」"
                            );
                            break;
                        }

                        if (
                            element.range == "min" &&
                            element.warn_min < element.error_min
                        ) {
                            isok = false;
                            vm.setSetTimeOutAlertMsg(
                                "「注意下限」需大於等於「異常下限」"
                            );
                            break;
                        }
                    }

                    if (element["warn_" + element.range] != null) {
                        sensor_warn = String(element["warn_" + element.range]);
                    }
                    if (element["error_" + element.range] != null) {
                        sensor_error = String(
                            element["error_" + element.range]
                        );
                    }
                } else if (
                    element.range == "between" &&
                    element.type == "sensor"
                ) {
                    if (
                        element.error_max < element.warn_max &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg(
                            "「異常上限」需大於等於「注意上限」"
                        );
                        break;
                    }

                    if (
                        element.warn_min < element.error_min &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg(
                            "「注意下限」需大於等於「異常下限」"
                        );
                        break;
                    }

                    if (
                        element.warn_max <= element.warn_min &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg(
                            "「注意上限」需大於「注意上限」"
                        );
                        break;
                    }

                    sensor_warn =
                        String(element.warn_min) +
                        "," +
                        String(element.warn_max);
                    sensor_error =
                        String(element.error_min) +
                        "," +
                        String(element.error_max);
                }

                if (element.type == "sensor") {
                    postdatas.model_sensor_list.push({
                        id: element.id,
                        model: element.model,
                        sensor_id: element.sensor_id,
                        sensor_range: element.range,
                        sensor_warn: sensor_warn,
                        sensor_error: sensor_error,
                        sensor_unit: element.sensor_unit,
                        enable: element.enable,
                        creator: element.creator,
                        modifier: vm.loginData.account,
                        created_at: element.created_at,
                    });
                } else {
                    postdatas.model_light_list.push({
                        id: element.id,
                        model: element.model,
                        light_id: element.light_id,
                        enable: element.enable,
                        creator: element.creator,
                        modifier: vm.loginData.account,
                        created_at: element.created_at,
                    });
                }
            }
            if (!isok) {
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            console.log(postdatas.model_sensor_list);
            console.log(postdatas.model_light_list);

            console.log("go update");
            let params = {};
            params["methods"] = "PATCH";
            params["whichFunction"] = "deviceTypeUpdate";
            params["postdata"] = postdatas;
            console.log(params);
            let success = false;
            vm.axiosAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result["Response"] == "ok") {
                        vm.setSetTimeOutAlertMsg("修改成功");
                        success = true;
                    } else {
                        vm.setSetTimeOutAlertMsg(result["Response"]);
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    vm.toggleSetTimeOutAlertModal();
                    if (success) {
                        vm._queryModelSensorListSensorList();
                    }
                });
        },

        _queryMachineModel() {
            let vm = this;
            let params = {};
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/TableData";
            params["urlparams"] = {
                table: "machine_model",
            };
            console.log(params);
            let showmsg = true;
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    let options = [];
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length > 0) {
                            result.data["QueryTableData"].map((ele) => {
                                options.push({
                                    text: ele.model,
                                    value: ele.model,
                                });
                                showmsg = false;
                            });
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                    console.log(options);
                    vm.devicetype_options = options;
                    if (vm.devicetype_options.length == 0) {
                        vm.setSetTimeOutAlertMsg("查無機型資料");
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    if (showmsg) {
                        vm.toggleSetTimeOutAlertModal();
                    }
                });
        },

        _queryModelSensorListSensorList() {
            let vm = this;
            vm._reset(["devicetype_selected", "devicetype_options"]);
            console.log(vm.devicetype_selected);
            let params = {};
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/2.0/my/CommonUse/SqlSyntax/JoinMultiTable";
            params["urlparams"] = {
                getSqlSyntax: "no",
            };
            params["postdata"] = {
                condition_1: {
                    fields: {
                        model_sensor_list: "",
                        sensor_list: ["sensor_name"],
                    },
                    join: {
                        model_sensor_list: [
                            {
                                sensor_list: {
                                    sensor_id: "id",
                                },
                            },
                        ],
                    },
                    jointype: { model_sensor_list_sensor_list: "left" },
                    tables: ["model_sensor_list", "sensor_list"],
                    limit: ["0", "9999999"],
                    where: {
                        model_sensor_list: {
                            model: [vm.devicetype_selected],
                        },
                    },
                    symbols: {
                        model_sensor_list: {
                            model: ["equal"],
                        },
                    },
                    orderby: "",
                    subquery: "",
                },
            };
            console.log(params);
            let showmsg = true;
            vm.axiosPostAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length == 0) {
                            vm.setSetTimeOutAlertMsg(
                                "查無此機型預測感測點資料"
                            );
                        } else {
                            result.data["QueryTableData"].map((ele) => {
                                let warn = {
                                    min: null,
                                    max: null,
                                };
                                let error = {
                                    min: null,
                                    max: null,
                                };
                                if (
                                    ele["model_sensor_list$sensor_range"] ==
                                    "between"
                                ) {
                                    if (
                                        ele["model_sensor_list$sensor_warn"] !=
                                        null
                                    ) {
                                        warn.min = ele[
                                            "model_sensor_list$sensor_warn"
                                        ].split(",")[0];
                                        warn.max = ele[
                                            "model_sensor_list$sensor_warn"
                                        ].split(",")[1];
                                    }
                                    if (
                                        ele["model_sensor_list$sensor_error"] !=
                                        null
                                    ) {
                                        error.min = ele[
                                            "model_sensor_list$sensor_error"
                                        ].split(",")[0];
                                        error.max = ele[
                                            "model_sensor_list$sensor_error"
                                        ].split(",")[1];
                                    }
                                } else if (
                                    ele["model_sensor_list$sensor_range"] ==
                                        "max" ||
                                    ele["model_sensor_list$sensor_range"] ==
                                        "min"
                                ) {
                                    if (
                                        ele["model_sensor_list$sensor_warn"] !=
                                        null
                                    ) {
                                        warn[
                                            ele[
                                                "model_sensor_list$sensor_range"
                                            ]
                                        ] =
                                            ele[
                                                "model_sensor_list$sensor_warn"
                                            ];
                                    }
                                    if (
                                        ele["model_sensor_list$sensor_error"] !=
                                        null
                                    ) {
                                        error[
                                            ele[
                                                "model_sensor_list$sensor_range"
                                            ]
                                        ] =
                                            ele[
                                                "model_sensor_list$sensor_error"
                                            ];
                                    }
                                }
                                vm.cud_items.push({
                                    id: ele["model_sensor_list$id"],
                                    model: ele["model_sensor_list$model"],
                                    sensor_id:
                                        ele["model_sensor_list$sensor_id"],
                                    type: "sensor",
                                    name: ele["sensor_list$sensor_name"],
                                    range:
                                        ele["model_sensor_list$sensor_range"],
                                    error_min: error.min,
                                    warn_min: warn.min,
                                    warn_max: warn.max,
                                    error_max: error.max,
                                    sensor_unit:
                                        ele["model_sensor_list$sensor_unit"],
                                    enable: ele["model_sensor_list$enable"],
                                    creator: ele["model_sensor_list$creator"],
                                    created_at:
                                        ele["model_sensor_list$created_at"],
                                });
                                showmsg = false;
                            });
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    console.log("done");
                    //抓取該機型的預設燈號點
                    vm._queryModelLightListLightList();
                    if (showmsg) vm.toggleSetTimeOutAlertModal();
                });
        },

        _queryModelLightListLightList() {
            let vm = this;
            let params = {};
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/2.0/my/CommonUse/SqlSyntax/JoinMultiTable";
            params["urlparams"] = {
                getSqlSyntax: "no",
            };
            params["postdata"] = {
                condition_1: {
                    fields: {
                        model_light_list: "",
                        light_list: ["light_name"],
                    },
                    join: {
                        model_light_list: [
                            {
                                light_list: {
                                    light_id: "id",
                                },
                            },
                        ],
                    },
                    jointype: { model_light_list_light_list: "left" },
                    tables: ["model_light_list", "light_list"],
                    limit: ["0", "9999999"],
                    where: {
                        model_light_list: {
                            model: [vm.devicetype_selected],
                        },
                    },
                    symbols: {
                        model_light_list: {
                            model: ["equal"],
                        },
                    },
                    orderby: "",
                    subquery: "",
                },
            };
            console.log(params);
            let showmsg = true;
            vm.axiosPostAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length == 0) {
                            vm.setSetTimeOutAlertMsg(
                                "查無此機型預測燈號點資料"
                            );
                        } else {
                            result.data["QueryTableData"].map((ele) => {
                                vm.cud_items.push({
                                    id: ele["model_light_list$id"],
                                    model: ele["model_light_list$model"],
                                    light_id: ele["model_light_list$light_id"],
                                    enable: ele["model_light_list$enable"],
                                    type: "light",
                                    name: ele["light_list$light_name"],
                                    creator: ele["model_light_list$creator"],
                                    created_at:
                                        ele["model_light_list$created_at"],
                                    range: null,
                                    error_min: null,
                                    warn_min: null,
                                    warn_max: null,
                                    error_max: null,
                                });
                                showmsg = false;
                            });
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    vm.toggleAlertModal(false);
                    console.log("done");
                    if (showmsg) vm.toggleSetTimeOutAlertModal();
                    console.log(vm.cud_items);
                });
        },

        //data Reset
        _reset(keep) {
            console.log(keep);
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="../../../public/static/css/api_docs.css"></style>
<style lang="scss" scoped>
.devicetype-block {
    // position: absolute;
    // width: 100%;
    // top: 40%;
    // transform: translateY(-45%);

    .input-block {
        border: 2px solid $final-color-1;
        border-radius: 5px;
        .input-text {
            // width: 80% !important;
            border: unset;
            border-bottom: 1px solid;
            border-radius: unset;
        }
        .pwdeye {
            float: right;
            margin-left: -25px;
            margin-top: -25px;
            position: relative;
            z-index: 2;
        }
    }
}
.delbtn {
