const state = {
    threadUseInformationFields: [
        {
            key: "code",
            label: "牙板編號",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "seriesNo",
            label: "牙板序號",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "name",
            label: "牙板名稱",
            sortable: false,
            thClass: "publicTH"
        }
    ],
    threadUseInformationItem: [],
}
const getters = {
    get_threadUseInformationFields: state => {
        return state.threadUseInformationFields
    },
    get_threadUseInformationItem: state => {
        return state.threadUseInformationItem
    },
}
const actions = {
    set_threadUseInformationFields(context, array) {
        context.commit('SET_THREADUSEINFORMATIONFIELDS', array);
    },
    set_threadUseInformationItem(context, array) {
        context.commit('SET_THREADUSEINFORMATIONITEM', array);
    },
    clear_threadUseInformationItem(context) {
        context.commit('CLEAR_THREADUSEINFORMATIONITEM');
    },
}
const mutations = {
    SET_THREADUSEINFORMATIONFIELDS(state, array) {
        state.threadUseInformationItem = array;
    },
    SET_THREADUSEINFORMATIONITEM(state, array) {
        state.threadUseInformationItem = array;
    },
    CLEAR_THREADUSEINFORMATIONITEM(state) {
        state.threadUseInformationItem = [];
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
