const state = {
  queryResponse: null,
  tableBusy: false,
  do_queryAgain: false,
  inputData: {
    options: [],
    selected: "",
    inputtext: "",
    table: "",
  },
  phpfunction: "",
  selectTag_default: {
    startTime: true,
    endTime: true,
    model: false,
    deviceCode: false,
    productCode: false,
    workCode: false,
  },
  selectTag: {
    startTime: true,
    endTime: true,
    model: false,
    deviceCode: false,
    productCode: false,
    workCode: false,
  },
  otherData: {},
  modelOption: [],
  deviceOption: {},
  selectValue: {
    startTime: "",
    endTime: "",
    model: "",
    deviceCode: "",
  }
};
const getters = {
  get_selectValue: state =>{
    return state.selectValue;
  },
  get_queryResponse: state => {
    return state.queryResponse;
  },
  get_tableBusy: state => {
    return state.tableBusy;
  },
  receive_queryAgain: state => {
    return state.do_queryAgain;
  },
  get_inputData: state => {
    var inputData = {};
    inputData.options = state.inputData.options;
    inputData.selected = state.inputData.selected;
    inputData.inputtext = state.inputData.inputtext;
    inputData.table = state.inputData.table;
    return inputData;
  },
  get_phpfunction: state => {
    return state.phpfunction;
  },
  get_selectTag: state => {
    return state.selectTag;
  },
  get_modelOption: state => {
    return state.modelOption;
  },
  get_deviceOption: state => {
    return state.deviceOption;
  },
  get_otherData: state => {
    return state.otherData;
  }
};
const actions = {
  set_selectValue(context,data){
    context.commit("SET_SELECTVALUE",data);
  },
  set_queryResponse(context, data) {
    context.commit("SET_QUERYRESPONSE", data);
  },
  change_tableBusy(context) {
    context.commit("CHANGE_TABLEBUSY");
  },
  do_queryAgain(context) {
    context.commit("DO_QUERYAGAIN");
  },
  set_inputData(context, obj) {
    context.commit("SET_INPUTDATA", obj);
  },
  set_phpfunction(context, data) {
    context.commit("SET_PHPFUNCTION", data);
  },
  set_selectTag(context, data) {
    context.commit("SET_SELECTTAG", data);
  },
  set_modelOption(context, data) {
    context.commit("SET_MODELOPTION", data);
  },
  set_deviceOption(context, data) {
    context.commit("SET_DEVICEOPTION", data);
  },
  set_otherData(context, data) {
    context.commit("SET_OTHERDATA", data);
  }
};
const mutations = {
  SET_SELECTVALUE(state,data){
    state.selectValue.startTime = data.startTime;
    state.selectValue.endTime = data.endTime;
    state.selectValue.model = data.model;
    state.selectValue.deviceCode = data.deviceCode;
  },
  SET_QUERYRESPONSE(state, data) {
    state.queryResponse = data;
  },
  CHANGE_TABLEBUSY(state) {
    state.tableBusy = !state.tableBusy;
  },
  DO_QUERYAGAIN(state) {
    state.do_queryAgain = !state.do_queryAgain;
  },
  SET_INPUTDATA(state, obj) {
    state.inputData.options = obj.options;
    state.inputData.selected = obj.selected;
    state.inputData.inputtext = obj.inputtext;
    state.inputData.table = obj.table;
  },
  SET_PHPFUNCTION(state, data) {
    state.phpfunction = data;
  },
  SET_SELECTTAG(state, obj) {
    let obj_key = Object.keys(obj);
    let selectTag_default = state.selectTag_default;
    let selectTag_default_key = Object.keys(selectTag_default);
    let selectTag = {};
    for (let i = 0; i < selectTag_default_key.length; i++) {
      if (obj_key.indexOf(selectTag_default_key[i]) != -1) {
        selectTag[selectTag_default_key[i]] = obj[selectTag_default_key[i]]
      } else {
        selectTag[selectTag_default_key[i]] = selectTag_default[selectTag_default_key[i]]
      }
    }
    state.selectTag = selectTag;
  },
  SET_MODELOPTION(state, array) {
    state.modelOption = array;
  },
  SET_DEVICEOPTION(state, obj) {
    state.deviceOption = obj;
  },
  SET_OTHERDATA(state, data) {
    state.otherData = data;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
