<template>
  <div id="abnormaladd" class="abnormaladd">
    <b-row>
      <b-col
        cols="12"
        class="p-0 m-0 mt-4 text-center"
        style="font-size: 1.75rem; color: #000000; margin-top: 3rem"
      >
        <div class="m-auto">異常組合新增</div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="12">
        <b-row>
          <div class="mx-2" style="width: 9rem">
            <p class style="margin-bottom: 0.625rem">機台型號</p>
            <b-form-select
              v-model="modelSelected"
              :options="modelOptions"
              class=""
              style="
                max-width: 9rem;
                height: 2.375rem;
                line-height: 1.375rem;
                font-size: 0.875rem;
                border: 2px solid rgb(255, 255, 255);
                box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
                color: rgb(95, 95, 95);
              "
            >
              <template #first>
                <b-form-select-option :value="null" disabled
                  >-- 選擇機型 --</b-form-select-option
                >
              </template>
            </b-form-select>
          </div>
        </b-row>

        <b-table
          :items="abnormalCombine"
          :fields="fields"
          responsive
          class="text-center abnormal-table mt-4"
          @row-clicked="_choiceCombine"
        >
          <template #cell(choice)="data">
            <div
              style="width: 8px; height: 8px; border-radius: 100%"
              :style="{
                'background-color':
                  data.item.choice == true ? '#536172' : 'transparent',
              }"
            ></div>
          </template>
          <template #cell(status)="data">
            <div style="width: 100%">
              <div
                v-for="(item, index) in data.item.status"
                :key="index"
                style="border-radius: 5px"
                :style="{
                  border: item.choice ? 'solid 1px #536172' : 'none',
                }"
                @click="
                  _choiceSensor(data.index, index),
                    _choiceCombine(data.item, data.index)
                "
              >
                <div style="display: inline-block; vertical-align: middle">
                  <div style="display: inline-block; margin: 5px">
                    <b-select
                      v-model="data.item.status[index].sensorCode"
                      :options="data.item.status[index].sensorOptions"
                      @change="_removeRepeatOption(data.index)"
                    >
                    </b-select>
                  </div>
                  <div style="display: inline-block;margin: 5px" >
                    <b-select
                      v-model="data.item.status[index].sensorSetting"
                      :options="data.item.status[index].statusOptions"
                    ></b-select>
                  </div>
                </div>
              </div>
              <div
                style="color: #c10000; font-size: 12px"
                v-if="data.item.repeatPrompt"
              >
                組合重複，請重新修改
              </div>
            </div>
          </template>
          <template #cell(adujstbtn)="data">
            <div
              class="adjust-value-btn"
              @click="_adjustOptions('reduce', data.index)"
            >
              -
            </div>
            <div
              class="adjust-value-btn"
              @click="_adjustOptions('add', data.index)"
            >
              +
            </div>
          </template>
          <template #cell(reason)="data">
            <div>
              <div style="display: inline-block">
                <b-form-textarea
                  v-model="data.item.reason"   
                  rows="5"
                  cols="30"
                  :class="{
                    'warning-border':
                      abnormalCombine[data.index].warnClass.reason,
                  }"
                  @click="_choiceCombine(data.item, data.index)"
                ></b-form-textarea>
              </div>
              <div
                style="
                  display: inline-block;
                  color: #c10000;
                  font-size: 14px;
                  margin-left: 5px;
                "
              >
                *必填
              </div>
            </div>
          </template>
          <template #cell(solution)="data">
            <div>
              <div style="display: inline-block">
                <b-form-textarea
                  v-model="data.item.solution"
                  cols="60"
                  rows="5"
                  :class="{
                    'warning-border':
                      abnormalCombine[data.index].warnClass.solution,
                  }"
                  @click="_choiceCombine(data.item, data.index)"
                ></b-form-textarea>
              </div>
              <div
                style="
                  display: inline-block;
                  color: #c10000;
                  font-size: 14px;
                  margin-left: 5px;
                "
              >
                *必填
              </div>
            </div>
          </template>
        </b-table>

        <b-row style="text-align: right">
          <b-col cols="12">
            <div
              class="adjust-value-btn"
              style="border-radius: 3px"
              @click="_removeCombine()"
            >
              -
            </div>
            <div
              class="adjust-value-btn"
              style="border-radius: 3px"
              @click="_addCombine()"
            >
              +
            </div>
          </b-col>
        </b-row>

        <div class="mx-2 mb-2" style="width: 100%; text-align: center">
          <b-button
            class="p-0"
            style="
              margin-top: 38px;
              background-color: #536172;
              border-color: #536172;
              width: 6rem;
              height: 2rem;
              font-size: 1.125rem;
            "
            @click="_confirmSetting()"
            >資料送出</b-button
          >
        </div>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
export default {
  name: "abnormalAdd",
  data() {
    return {
      modelOptions: [],
      modelSelected: null,
      sensorOptions: [],
      fields: [
        {
          key: "choice",
          label: "",
          thClass: "abnormal-table-th",
        },
        {
          key: "status",
          label: "感測狀態",
          thClass: "abnormal-table-th",
        },
        {
          key: "adujstbtn",
          label: "",
          thClass: "abnormal-table-th",
        },
        {
          key: "reason",
          label: "異常原因",
          thClass: "abnormal-table-th",
        },
        {
          key: "solution",
          label: "建議排除方式",
          thClass: "abnormal-table-th",
        },
      ],
      abnormalCombine: [], //格式在function _addCombine裡
      choiceRow: "no choice",
    };
  },
  created() {
    let vm = this;
    vm._queryMouldOption();
  },
  mounted: function () {},
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",
      selectPage: "mesGetLogin/get_selectPage",
      pageName: "mesGetLogin/get_pageName",
      clickChild: "featurelistbar/get_clickChild",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
      inputData: "mesCommonQueryIoT/get_inputData",
    }),
  },
  components: {
    commonQueryIoT,
    alertModal,
  },
  watch: {
    abnormalCombine: {
      handler(value) {},
    },
    modelSelected: {
      handler(value) {
        let vm = this;
        if (vm.modelSelected != null) {
          vm._querySensorOptions(value);
        }
      },
    },
  },
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      changeSelectPage: "mesGetLogin/change_selectPage",
      changeSelectPageName: "mesGetLogin/change_selectPageName",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      clearQueryResultValue: "mesQueryResult/clear_queryResultItem",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
    }),
    _queryMouldOption() {
      let vm = this;
      let params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = "query_cus_mould";
      params["cus_id"] = "1";
      vm.axiosAction(params).then(() => {
        let result = vm.axiosResult;
        if (result.Response == "ok") {
          vm.modelOptions = result.QueryTableData;
        }
      });
    },
    _querySensorOptions($model) {
      let vm = this;
      let params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = "query_model_light_list";
      params["model"] = $model;
      vm.axiosAction(params).then(() => {
        let result = vm.axiosResult;
        if (result.Response == "ok") {
          vm.sensorOptions = result["QueryTableData"];
        }
      });
    },
    _adjustOptions(mode, index) {
      //增減感測狀態選項
      let vm = this;
      if (mode == "add") {
        //判斷sensorOptions無重複選項
        let notSelectedOption = [];
        let selectedOption = [];
        vm.abnormalCombine[index].status.forEach((item) => {
          selectedOption.push(item.sensorCode);
        });
        if (selectedOption.includes("")) {
          return;
        }
        vm.sensorOptions.forEach((item) => {
          if (!selectedOption.includes(item.value)) {
            notSelectedOption.push(item);
          }
        });
        vm.abnormalCombine[index].status.push({
          sensorOptions: notSelectedOption,
          statusOptions: [
            {
              value: "1",
              text: "正常",
            },
            {
              value: "0",
              text: "異常",
            },
          ],
          sensorCode: notSelectedOption[0].value,
          sensorSetting: "1",
          choice: false,
        });
        vm._ctrlOptionRepeat(index);
      } else if (mode == "reduce") {
        if (vm.abnormalCombine[index].status.length > 2) {
          //把移除的感測點選項加回其他感測點的選項中
          if (vm.abnormalCombine[index].choiceSensor != "no choice") {
            vm.abnormalCombine[index].status.splice(
              vm.abnormalCombine[index].choiceSensor,
              1
            );
            vm.abnormalCombine[index].choiceSensor = "no choice";
            vm._ctrlOptionRepeat(index);
          }
        }
      }
    },
    _removeRepeatOption(combineIndex) {
      //感測點選擇變動時，移除其他感測點的重複選項
      let vm = this;
      vm._ctrlOptionRepeat(combineIndex);
    },
    _ctrlOptionRepeat(combineIndex) {
      //控制已選擇選項不重複
      let vm = this;
      let selectedOption = [];
      vm.abnormalCombine[combineIndex].status.forEach((item) => {
        selectedOption.push(item.sensorCode);
      });
      vm.abnormalCombine[combineIndex].status.forEach((item, index) => {
        let repeatOption = [];
        selectedOption.forEach((option) => {
          if (option != item.sensorCode) {
            repeatOption.push(option);
          }
        });
        let newOption = [];
        vm.sensorOptions.forEach((option) => {
          if (!repeatOption.includes(option.value)) {
            newOption.push(option);
          }
        });
        vm.abnormalCombine[combineIndex].status[
          index
        ].sensorOptions = newOption;
      });
    },
    _addCombine() {
      //增減異常組合組數
      let vm = this;
      if (vm.modelSelected == null) {
        vm.setSetTimeOutAlertMsg("請選擇機台型號");
        vm.toggleSetTimeOutAlertModal();
        return;
      }
      vm.abnormalCombine.push({
        status: [
          {
            sensorOptions: vm.sensorOptions,
            statusOptions: [
              {
                value: "1",
                text: "正常",
              },
              {
                value: "0",
                text: "異常",
              },
            ],
            sensorCode: vm.sensorOptions[0].value,
            sensorSetting: "1",
            choice: false,
          },
          {
            sensorOptions: vm.sensorOptions,
            statusOptions: [
              {
                value: "1",
                text: "正常",
              },
              {
                value: "0",
                text: "異常",
              },
            ],
            sensorCode: vm.sensorOptions[1].value,
            sensorSetting: "1",
            choice: false,
          },
        ],
        reason: "",
        solution: "",
        warnClass: {
          reason: false,
          solution: false,
        },
        choice: false,
        choiceSensor: "no choice",
        repeatPrompt: false,
      });
      vm._ctrlOptionRepeat(vm.abnormalCombine.length - 1);
    },
    _removeCombine() {
      let vm = this;
      if (vm.choiceRow != "no choice") {
        vm.abnormalCombine.splice(vm.choiceRow, 1);
        vm.choiceRow = "no choice";
      }
    },
    _choiceCombine(record, index) {
      //選擇組合->選擇後按下"-"按鈕刪除該組合
      let vm = this;
      vm.choiceRow = index;
      vm.abnormalCombine.forEach((item, combineIndex) => {
        vm.abnormalCombine[combineIndex].choice = false;
      });
      vm.abnormalCombine[index].choice = true;
    },
    _choiceSensor(dataIndex, sensorIndex) {
      //選擇感應器->選擇後按下"-"按鈕刪除該感應器
      let vm = this;
      vm.abnormalCombine[dataIndex].status.forEach((item, index) => {
        vm.abnormalCombine[dataIndex].status[index].choice = false;
      });
      vm.abnormalCombine[dataIndex].status[sensorIndex].choice = true;
      vm.abnormalCombine[dataIndex].choiceSensor = sensorIndex;
    },
    _confirmSetting() {
      let vm = this;
      if (vm.abnormalCombine.length == 0) {
        vm.setSetTimeOutAlertMsg("尚未新增組合");
        vm.toggleSetTimeOutAlertModal();
        return;
      }
      let errorText = [];
      vm.abnormalCombine.forEach((item, index) => {
        if (item.reason == "" && item.solution == "") {
          vm.abnormalCombine[index].warnClass.reason = true;
          vm.abnormalCombine[index].warnClass.solution = true;
          if (!errorText.includes("異常原因")) {
            errorText.push("異常原因");
          }
          if (!errorText.includes("建議排除方式")) {
            errorText.push("建議排除方式");
          }
        } else if (item.reason == "" || item.solution == "") {
          if (item.reason == "") {
            vm.abnormalCombine[index].warnClass.reason = true;
            vm.abnormalCombine[index].warnClass.solution = false;
            if (!errorText.includes("異常原因")) {
              errorText.push("異常原因");
            }
          } else if (item.solution == "") {
            vm.abnormalCombine[index].warnClass.reason = false;
            vm.abnormalCombine[index].warnClass.solution = true;
            if (!errorText.includes("建議排除方式")) {
              errorText.push("建議排除方式");
            }
          }
        } else {
          vm.abnormalCombine[index].warnClass.reason = false;
          vm.abnormalCombine[index].warnClass.solution = false;
        }
      });
      if (errorText.length > 0) {
        let exportText = errorText.join("、");
        vm.setSetTimeOutAlertMsg("請輸入" + exportText);
        vm.toggleSetTimeOutAlertModal();
      } else {
        //送出資料
        let params = {};
        let sendData = [];
        vm.abnormalCombine.forEach((item, index) => {
          vm.abnormalCombine[index].repeatPrompt = false;
          sendData.push({
            status: item.status,
            reason: item.reason,
            solution: item.solution,
          });
        });
        params["combineData"] = sendData;
        params["vuePage"] = vm.selectPage;
        params["methods"] = "POST";
        params["whichFunction"] = "add_new_combine";
        params["model"] = vm.modelSelected;
        params["cus_id"] = "1";
        vm.axiosAction(params).then(() => {
          let result = vm.axiosResult;
          if (result["Response"] == "add_new_combine_success") {
            vm.setSetTimeOutAlertMsg("新增成功");
            vm.toggleSetTimeOutAlertModal();
            vm._resetCombine();
          } else if (result["Response"] == "add_new_combine_repeat") {
            vm.setSetTimeOutAlertMsg("組合重複，請重新修改");
            vm.toggleSetTimeOutAlertModal();
            console.log(result["repeat_data"]);
            result["repeat_data"].forEach((item) => {
              console.log(vm.abnormalCombine[item].repeatPrompt);
              vm.abnormalCombine[item].repeatPrompt = true;
            });
          }
        });
      }
    },
    _resetCombine() {
      let vm = this;
      vm.abnormalCombine = [];
      vm.choiceRow = "no choice";
    },
  },
  destroyed() {},
};
</script>
<style scoped>
div,
p,
span {
  font-family: dfyuanstd-w7;
}
</style>

<style lang="scss">
.adjust-value-btn {
  color: #ffffff !important;
  background-color: #536172 !important;
  border-radius: 100%;
  text-align: center;
  width: 20px;
  height: 20px;
  line-height: 16px;
  display: inline-block;
  cursor: pointer;
  margin: 5px;
  vertical-align: middle;
}
.abnormal-table td {
  white-space: pre-line;
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
}
.abnormal-table td:nth-child(1) {
  width: 2%;
}
.abnormal-table td:nth-child(2) {
  width: 32%;
}
.abnormal-table td:nth-child(3) {
  width: 8%;
}
.abnormal-table td:nth-child(4) {
  width: 22.5%;
}
.abnormal-table td:nth-child(5) {
  width: 35.5%;
}
.abnormal-table tr {
  border-left: 1px solid #65646a;
  border-right: 1px solid #65646a;
}

.abnormal-table tr:nth-child(even) {
  background-color: #cccccc;
}
.abnormal-table-th {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
}
.table > tbody > tr > td {
  vertical-align: middle;
}
.warning-border {
  border: 1px solid #c10000 !important;
}
</style>


