<template>
    <div id="Setting" ref="Setting">
        <b-row
            class="h-100 mh-100"
            :class="{ publicMinWidth550: this_route != 'setting' }"
        >
            <template v-if="this_route != 'setting'">
                <featureListBar />
                <div class="featureContent customize_scrollbar h-100 mh-100">
                    <b-container>
                        <router-view
                            class="setting_router pt-2"
                            style="height: calc(100vh - 76px)"
                        >
                        </router-view>
                    </b-container>
                </div>
            </template>
        </b-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import featureMain from "@/components/featureMain.vue";
import featureListBar from "@/components/featureListBar.vue";
export default {
    name: "Setting",
    data() {
        let vm = this;
        return {
            this_route: "setting",
            featureListBarData: {
                body: {
                    account: {
                        title: "客戶帳號管理",
                        collapse: {
                            childitem: {
                                "v-for": ["基本資料設定", "基本資料查詢"],
                                click: {
                                    data: "",
                                },
                                content: {},
                            },
                        },
                    },
                    device: {
                        title: "客戶機台管理",
                        collapse: {
                            childitem: {
                                "v-for": ["客戶機台新增", "客戶機台查詢"],
                                click: {
                                    data: {},
                                },
                                content: {},
                            },
                        },
                    },
                    devicetype: {
                        title: "機型配套管理",
                    },
                    abnormal: {
                        title: "異常診斷管理",
                        collapse:{
                            childitem:{
                                "v-for":["異常組合新增","異常組合查詢"],
                                click:{
                                    data:{},
                                },
                                content:{}
                            }
                        }
                    },
                },
            },
            searchBarFixed: false,
            leftblockShow: true,
            monitorScrollTop: 0,
        };
    },
    created() {
        console.log(this.$router.currentRoute.path);
        console.log(/(\/setting)$/.test(this.$router.currentRoute.path));
        console.log(this.clickRoute);
        if (/(\/setting)$/.test(this.$router.currentRoute.path)) {
            this.setClickRoute("account");
            // this.setFeatureMainData(this.featureMainData);
        } else {
            this.this_route = this.$router.currentRoute.path.split("/")[
                this.$router.currentRoute.path.split("/").length - 1
            ];
        }
        console.log(this.$router.currentRoute.path);
        console.log(this.$route);
        console.log(this.featureListBarData);
        this.setFeatureListBarData(this.featureListBarData);
        console.log(this.this_route);
        console.log(this.clickChild);
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            apiMap: "apidetail/get_apiMap",
            clickRoute: "featuremain/get_clickRoute",
            clickChild: "featurelistbar/get_clickChild",
        }),
    },
    components: {
        featureMain,
        featureListBar,
    },
    watch: {
        clickRoute: {
            handler(value) {
                console.log(value);
                console.log(this.$router.currentRoute.path);
                if (value != "") {
                    this.$router.push({
                        name: value,
                        params: {
                            SYSTEM: this.$route.params.SYSTEM,
                        },
                    });
                    this.this_route = value;
                    console.log(this.this_route);
                    console.log(this.$router.currentRoute.path);
                    console.log(
                        /(\/setting)$/.test(this.$router.currentRoute.path)
                    );
                    this.setClickChild({
                        child_name: value,
                        data: {},
                    });
                    this.setClickRoute("");
                }
            },
        },
    },
    mounted() {},
    methods: {
        ...mapActions({
            setFeatureMainData: "featuremain/set_featureMainData",
            setFeatureListBarData: "featurelistbar/set_featureListBarData",
            setClickRoute: "featuremain/set_clickRoute",
            setClickChild: "featurelistbar/set_clickChild",
            setCollapseIDList: "featurelistbar/set_collapseIDList",
        }),

        _onResize() {
            let vm = this;
            console.log(window.innerWidth);
            if (window.innerWidth < 1200) {
                // vm.resize_style["margin-top"] =
                //     vm.$refs.leftblock.clientHeight + "px";
                vm.searchBarFixed = true;
            } else {
                vm.searchBarFixed = false;
                // vm.resize_style["margin-top"] = "0px";
            }
        },

        _handleScroll() {
            let scrollTop =
                window.pageYOffset ||
                document.documentElement.scrollTop ||
                document.body.scrollTop;
            console.log(scrollTop);

            let offsetTop = document.querySelector("#featureListBar").offsetTop;
            console.log(offsetTop);
            if (window.innerWidth < 1200) {
                if (scrollTop > offsetTop) {
                    if (!this.searchBarFixed) {
                        setTimeout(() => {
                            this.searchBarFixed = true;
                        }, 50);
                    } else {
                        this.searchBarFixed = true;
                    }
                } else {
                    this.searchBarFixed = false;
                }
                this.monitorScrollTop = scrollTop;
            }

            this.$nextTick(() => {
                if (this.monitorScrollTop > scrollTop) {
                    console.log("~~~~");
                    this.leftblockShow = true;
                } else {
                    console.log("@@@@");
                    this.leftblockShow = false;
                }
            });
            this.monitorScrollTop = scrollTop;
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
    beforeRouteUpdate(to, from, next) {
        console.warn(to);
        console.warn(from);
        console.warn(next);
        this._reset([]);
        this.this_route = to.name;
        if (to.name === "setting") {
            this.setClickRoute("account");
            // this.setFeatureMainData(this.featureMainData);
        } else {
            this.setClickChild({
                child_name: to.name,
                data: {},
            });
            console.log(this.this_route);
            this.$nextTick(() => {
                window.addEventListener("resize", this._onResize);
                window.addEventListener("scroll", this._handleScroll);
                console.log("####");
            });
        }
        next();
    },
    //調用router守衛，在離開時reset
    beforeRouteLeave(to, from, next) {
        this._reset([]);
        next();
    },
    beforeDestroy() {
        let vm = this;
        window.removeEventListener("resize", vm._onResize);
        window.removeEventListener("scroll", vm._handleScroll);
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.card,
.card-header {
    border: 0;
}
.apipath {
    color: #007bff;
    cursor: pointer;
}
.apimethod {
    font-weight: bold;
    min-width: 60px;
    /* color: green; */
}
.bcardtext {
    display: flex;
    flex-direction: row;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    align-items: center;
    height: 36px;
    min-height: 36px;
    margin: 5px;
    padding: 5px;
}
</style>

