<template>
    <div id="apilist">
        <b-row
            class="apilist-block"
            v-for="(item0, index0) in Object.entries(clickChild.data)"
            :key="index0"
            :id="'apilist_' + item0[0]"
            :ref="'apilist_' + item0[0]"
        >
            <!-- :class="{ 'publicLightBlueBG': index0 % 2 == 0 }" -->
            <b-col cols="12">
                <template v-if="!transaction_DB_is_duplicate">
                    <h2>{{ api_group_config[item0[0]][0] }}</h2>
                </template>
                <template v-else>
                    <h2>
                        {{
                            api_group_config[item0[0]][0] +
                            "-" +
                            api_group_config[item0[0]][1]
                        }}
                    </h2>
                </template>
            </b-col>
            <b-col sm="12">
                <b-tabs
                    v-if="Object.keys(item0[1]).length != 0"
                    class="customizeTab"
                    style="min-width: 700px"
                    v-model="api_tabIndex[item0[0]]"
                >
                    <b-tab
                        v-for="(item1, index1) in Object.keys(item0[1])"
                        :key="index1"
                        :title="'版本' + item1"
                    >
                        <b-row style="margin-top: 0.25rem !important">
                            <b-col
                                v-for="(item2, index2, keys2) in _sortKeys(
                                    item0[1][item1]
                                )"
                                :key="keys2"
                            >
                                <b-button
                                    variant="light"
                                    @click.prevent="
                                        api_method[item0[0]] = index2
                                    "
                                    :class="{
                                        publicButton:
                                            api_method[item0[0]] == index2,
                                    }"
                                    block
                                    >{{ index2 }}</b-button
                                >
                            </b-col>
                        </b-row>
                        <div class="customizeTab_div customize_scrollbar">
                            <template
                                v-if="item0[1][item1][api_method[item0[0]]]"
                            >
                                <div
                                    v-for="(item3, index3) in item0[1][item1][
                                        api_method[item0[0]]
                                    ]"
                                    :key="index3"
                                    class="bcardtext"
                                >
                                    <span class="apimethod"
                                        >{{ item3[2] }}&nbsp;</span
                                    >
                                    <b-button
                                        variant="light"
                                        class="apipath"
                                        @click="_getApiDoc(item3[1])"
                                        :disabled="/(Invalid)/g.test(item3[4])"
                                    >
                                        <!-- :disabled="!item3[4].search(/Invalid/g)" -->
                                        {{ item3[4] }}
                                    </b-button>
                                </div>
                            </template>
                            <template v-else>
                                <div
                                    v-for="(item3, index3) in item0[1][item1]"
                                    :key="index3"
                                    class="bcardtext"
                                >
                                    <span class="apimethod"
                                        >{{ item3[2] }}&nbsp;</span
                                    >
                                    <b-button
                                        variant="light"
                                        class="apipath"
                                        @click="_getApiDoc(item3[1])"
                                        :disabled="/(Invalid)/g.test(item3[4])"
                                    >
                                        <!-- :disabled="!item3[4].search(/Invalid/g)" -->
                                        {{ item3[4] }}
                                    </b-button>
                                </div>
                            </template>
                        </div>
                    </b-tab>
                </b-tabs>
            </b-col>
        </b-row>
    </div>
</template>

