                }
            });
        },

        _new_method(key, value) {
            // console.log(key);
            // console.log(value);
            this.api_method[key] = Object.keys(
                this._sortKeys(
                    this.clickChild.data[key][
                        Object.keys(this.clickChild.data[key])[value]
                    ]
                )
            )[0];
        },

        _sortKeys(data) {
            const ordered = {};
            Object.keys(data)
                .sort((a, b) => {
                    if (a == "GET") return -1;
                    if (b == "GET") return 1;
                    return a - b;
                })
                .forEach(function (key) {
                    ordered[key] = data[key];
                });
            // console.log(ordered);
            return ordered;
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
