<template>
  <div id="ElecBoard">
    <template v-if="nowPage == 'elecboard'">
      <b-row class="mx-0 mb-3">
        <b-col cols="12" class="text-right my-3" style="font-size: 1rem">
          <span class="my-0" id="elecboard-update-time">更新時間:</span>
          <span class="my-0" for="elecboard-update-time">{{ nowtime }}</span>
        </b-col>
        <b-col :cols="window_innerWidth > 992 ? 1 : 0" class="px-0"></b-col>
        <b-col :cols="window_innerWidth > 992 ? 11 : 12" class="px-0">
          <b-row class="mx-0">
            <b-col
              cols="12"
              class="text-left pb-2"
              :class="{ 'px-0': window_innerWidth > 992 }"
              style="
                color: #536172;
                font-size: 1.75rem;
                font-family: dfyuanstd-w8, dfyuanstd-w7;
              "
              >即時看板-{{ device_category }}</b-col
            >
            <b-col
              :cols="window_innerWidth > 672 ? 11 : 10"
              class="mb-3"
              :class="{ 'px-0': window_innerWidth > 992 }"
            >
              <b-row class="mx-0">
                <b-col cols="12" class="px-0 elecboard-device-col">
                  <b-row class="mx-0" ref="elecboard_detail">
                    <b-col
                      v-for="(item, key) in show_device_list"
                      :key="key"
                      cols="12"
                      xl="6"
                      lg="6"
                      md="12"
                      sm="12"
                      class="px-0 elecboard-device-col"
                    >
                      <b-row
                        v-if="result_Data[item] != undefined"
                        class="mx-0 py-1"
                        style="
                          color: #000000;
                          font-size: 1.125rem;
                          border: 1px solid #cccccc;
                        "
                      >
                        <b-col
                          cols="4"
                          xl="3"
                          lg="3"
                          md="3"
                          sm="4"
                          class="px-3 text-center"
                          style="align-self: center"
                        >
                          <div class="mx-auto my-2">
                            <p
                              class="m-0"
                              style="
                                background-color: #cccccc;
                                color: #ffffff;
                                height: 40.5px;
                                line-height: 40.5px;
                              "
                              :style="{
                                'background-color':
                                  result_Data[item].machine_status == '運作'
                                    ? '#00b152'
                                    : result_Data[item].machine_status == '待機'
                                    ? '#ffc100'
                                    : result_Data[item].machine_status == '異常'
                                    ? '#c10000'
                                    : '#cccccc',
                              }"
                            >
                              {{ item }}
                            </p>
                            <p
                              class="m-0"
                              style="
                                background-color: #f2f2f2;
                                height: 40.5px;
                                line-height: 40.5px;
                              "
                              :style="{
                                color:
                                  result_Data[item].machine_status == '運作'
                                    ? '#00b152'
                                    : result_Data[item].machine_status == '待機'
                                    ? '#ffc100'
                                    : result_Data[item].machine_status == '異常'
                                    ? '#c10000'
                                    : '#cccccc',
                              }"
                            >
                              {{
                                result_Data[item].machine_status == "運作"
                                  ? "運作"
                                  : result_Data[item].machine_status == "待機"
                                  ? "待機"
                                  : result_Data[item].machine_status == "異常"
                                  ? "異常"
                                  : "-"
                              }}
                            </p>
                          </div>
                        </b-col>
                        <b-col
                          cols="8"
                          xl="9"
                          lg="9"
                          md="9"
                          sm="8"
                          class="pl-0 pr-3 my-2 text-left"
                        >
                          <b-row class="">
                            <b-col cols="12" class="px-0">
                              <span>速度(支/分):</span>
                              <span>{{ result_Data[item].rpm }}</span>
                            </b-col>
                          </b-row>
                          <b-row class="mx-0">
                            <b-col
                              cols="12"
                              class="px-0 d-flex justify-content-between"
                            >
                              <div
                                style="overflow-x: hidden; white-space: nowrap"
                              >
                                <span>即時狀態:</span>
                                <span>
                                  {{ result_Data[item].work_status }}
                                </span>
                              </div>
                              <div>
                                <button
                                  type="button"
                                  class="btn publicDarkBG"
                                  style="
                                    width: 53px;
                                    height: 22px;
                                    line-height: 22px;
                                    padding: 0px;
                                    color: #ffffff;
                                    border: 0px;
                                  "
                                  @click="
                                    (deviceName = item),
                                      (nowPage = 'machstatus'),
                                      (deviceId = result_Data[item].device_id),
                                      _getSingleMachineData()
                                  "
                                >
                                  明細
                                </button>
                              </div>
                            </b-col>
                          </b-row>
                          <b-row class="">
                            <b-col cols="12" class="px-0">
                              <stackBar
                                :stacksBarClass="stacksBarClass"
                                :stacksBarMaxValue="stacksBarMaxValue"
                                :stacksData="result_Data[item].device_stackBar"
                                :stacksSize="'small'"
                              />
                            </b-col>
                          </b-row>
                        </b-col>
                      </b-row>
                      <b-row
                        v-else
                        class="mx-0 py-1"
                        style="
                          color: #000000;
                          font-size: 1.125rem;
                          border: 1px solid #cccccc;
                        "
                      >
                        <b-col
                          cols="4"
                          xl="3"
                          lg="3"
                          md="3"
                          sm="4"
                          class="px-3 text-center"
                          style="align-self: center"
                        >
                          <div class="mx-auto my-2">
                            <p
                              class="m-0"
                              style="
                                background-color: #cccccc;
                                color: #ffffff;
                                height: 40.5px;
                                line-height: 40.5px;
                              "
                            >
                              -
                            </p>
                            <p
                              class="m-0"
                              style="
                                background-color: #f2f2f2;
                                color: #cccccc;
                                height: 40.5px;
                                line-height: 40.5px;
                              "
                            >
                              -
                            </p>
                          </div>
                        </b-col>
                        <b-col
                          cols="8"
                          xl="9"
                          lg="9"
                          md="9"
                          sm="8"
                          class="pl-0 pr-3 my-2 text-left"
                        >
                          <b-row class="">
                            <b-col cols="12" class="px-0">
                              <span>速度(支/分):</span>
                              <span>-</span>
                            </b-col>
                          </b-row>
                          <b-row class="mx-0">
                            <b-col
                              cols="12"
                              class="px-0 d-flex justify-content-between"
                            >
                              <div
                                style="overflow-x: hidden; white-space: nowrap"
                              >
                                <span>即時狀態:</span>
                                <span> - </span>
                              </div>
                              <div>
                                <button
                                  type="button"
                                  class="btn"
                                  style="
                                    width: 53px;
                                    height: 22px;
                                    line-height: 22px;
                                    padding: 0px;
                                    background-color: #cccccc;
                                    color: #ffffff;
                                    border: 0px;
                                    cursor: default;
                                  "
                                >
                                  明細
                                </button>
                              </div>
                            </b-col>
                          </b-row>
                          <b-row class="">
                            <b-col cols="12" class="px-0">
                              <stackBar
                                :stacksBarClass="stacksBarClass"
                                :stacksBarMaxValue="stacksBarMaxValue"
                                :stacksData="stacksBarDefault"
                                :stacksSize="'small'"
                              />
                            </b-col>
                          </b-row>
                        </b-col>
                      </b-row>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
            </b-col>
            <b-col :cols="window_innerWidth > 672 ? 1 : 2">
              <b-col
                cols="12"
                class="text-center mb-3"
                ref="elecboard_status_bar"
              >
                即時狀態
              </b-col>
              <b-col
                cols="12"
                class="px-0 text-center"
                :style="{
                  height: elecboard_detail_status_height + 'px',
                }"
              >
                <div class="mx-auto" style="width: 100%; height: 100%">
                  <div
                    v-for="(item, key) in totalDeviceStatus"
                    :key="key"
                    style="width: 100%"
                    :style="{
                      height:
                        item > 0
                          ? (elecboard_detail_status_height /
                              device_array.length) *
                              item <
                            50
                            ? 50 + 'px'
                            : (elecboard_detail_status_height /
                                device_array.length) *
                                item +
                              'px'
                          : 0,
                    }"
                    class="d-flex justify-content-center px-3"
                  >
                    <b-row
                      align-v="center"
                      style="
                        width: 50%;
                        height: 100%;
                        min-width: 3rem;
                        position: relative;
                        color: #ffffff;
                      "
                      :style="{
                        'background-color':
                          key == '運作'
                            ? '#00b152'
                            : key == '待機'
                            ? '#ffc100'
                            : key == '異常'
                            ? '#c10000'
                            : '#cccccc',
                      }"
                    >
                      <template v-if="item != 0">
                        <b-col cols="12" class="px-0">
                          <div>{{ key }}</div>
                          <div>{{ item }}</div>
                        </b-col>
                      </template>
                    </b-row>
                  </div>
                </div>
              </b-col>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </template>
    <template v-else-if="nowPage == 'machstatus'">
      <b-row class="mx-0 mb-3">
        <b-col cols="12" class="text-right my-3" style="font-size: 1rem">
          <span class="my-0" id="elecboard-update-time">更新時間:</span>
          <span class="my-0" for="elecboard-update-time">{{ nowtime }}</span>
        </b-col>
        <b-col cols="12" class="px-5">
          <!-- <b-container class="px-0"> -->
          <b-row class="mx-0">
            <b-col cols="12" sm="12" md="12" lg="12" xl="12" class="px-0">
              <b-row class="mx-0">
                <b-col cols="12" class="p-0 m-0 mt-3">
                  <b-row class="mx-0">
                    <b-col cols="12" class="p-0 m-0 mb-2">
                      <b-row class="mx-0">
                        <b-col
                          cols="12"
                          sm="12"
                          md="12"
                          lg="4"
                          xl="3"
                          class="p-0 m-0 text-center"
                        >
                          <img
                            :src="device_image"
                            class="mx-auto"
                            alt="Center image"
                            style="max-height: 8rem"
                            @click.right.ctrl.exact="_click_right()"
                          />
                        </b-col>

                        <b-col
                          cols="12"
                          sm="12"
                          md="12"
                          lg="8"
                          xl="9"
                          class="p-0 m-0"
                        >
                          <b-row class="mx-0">
                            <b-col
                              cols="12"
                              class="p-0 mx-0"
                              style="margin-bottom: 2.3rem"
                            >
                              <b-row class="mx-0">
                                <b-col
                                  cols="4"
                                  sm="3"
                                  md="3"
                                  lg="3"
                                  xl="2"
                                  class="p-0 mb-1 text-left"
                                  style="line-height: 2rem"
                                >
                                  <span class="m-0" style>機號:</span>
                                  <span class="m-0" style>
                                    {{
                                      result_Data[0].device_name != undefined
                                        ? result_Data[0].device_name
                                        : ""
                                    }}
                                  </span>
                                </b-col>
                                <b-col
                                  cols="4"
                                  sm="3"
                                  md="3"
                                  lg="3"
                                  xl="2"
                                  class="p-0 mb-1 text-left"
                                  style="line-height: 2rem"
                                >
                                  <span class="m-0" style>型號:</span>
                                  <span class="m-0" style>{{
                                    result_Data[0].device_model
                                  }}</span>
                                </b-col>
                                <b-col
                                  cols="12"
                                  sm="12"
                                  md="6"
                                  lg="6"
                                  xl="8"
                                  :class="{
                                    'text-right': window_innerWidth > 768,
                                    'text-left': window_innerWidth < 768,
                                  }"
                                  class="p-0 mb-1"
                                  style="line-height: 2rem"
                                >
                                  <span class="m-0" style>狀態:</span>
                                  <span class="m-0" style>
                                    <div
                                      style="
                                        display: inline-block;
                                        color: #ffffff;
                                        padding: 0px 13px;
                                        width: 4.25rem;
                                        height: 2rem;
                                        line-height: 2rem;
                                        font-size: 1.125rem;
                                        font-family: dfyuanstd-w7, dfyuanstd-w5;
                                        text-align: center;
                                        margin: 0 0 0 0.5rem;
                                        font-weight: normal;
                                      "
                                      :style="{
                                        'background-color':
                                          result_Data[0].machine_status == 'R'
                                            ? '#00b152'
                                            : '#cccccc',
                                      }"
                                    >
                                      運轉
                                    </div>
                                    <div
                                      style="
                                        display: inline-block;
                                        color: #ffffff;
                                        padding: 0px 13px;
                                        width: 4.25rem;
                                        height: 2rem;
                                        line-height: 2rem;
                                        font-size: 1.125rem;
                                        font-family: dfyuanstd-w7, dfyuanstd-w5;
                                        text-align: center;
                                        margin: 0 0 0 0.5rem;
                                        font-weight: normal;
                                      "
                                      :style="{
                                        'background-color':
                                          result_Data[0].machine_status == 'Q'
                                            ? '#ffc100'
                                            : '#cccccc',
                                      }"
                                    >
                                      待機
                                    </div>
                                    <div
                                      style="
                                        display: inline-block;
                                        background-color: #cccccc;
                                        color: #ffffff;
                                        padding: 0px 13px;
                                        width: 4.25rem;
                                        height: 2rem;
                                        line-height: 2rem;
                                        font-size: 1.125rem;
                                        font-family: dfyuanstd-w7, dfyuanstd-w5;
                                        text-align: center;
                                        margin: 0 0 0 0.5rem;
                                        font-weight: normal;
                                      "
                                      :style="{
                                        'background-color':
                                          result_Data[0].machine_status == 'H'
                                            ? '#c10000'
                                            : '#cccccc',
                                      }"
                                    >
                                      異常
                                    </div>
                                  </span>
                                </b-col>
                              </b-row>
                            </b-col>
                          </b-row>

                          <b-row class="mx-0">
                            <b-col cols="12" class="p-0 m-0 mb-2">
                              <b-input-group prepend="機台狀態" class="mb-2">
                                <b-form-input
                                  aria-label="First name"
                                  disabled
                                  :value="
                                    '運轉時間：' +
                                    result_Data[0].device_run_time
                                  "
                                  style="min-width: 210px"
                                ></b-form-input>
                                <b-form-input
                                  aria-label="Last name"
                                  disabled
                                  :value="
                                    '開機稼動率：' +
                                    result_Data[0].device_activation
                                  "
                                  style="min-width: 165px"
                                ></b-form-input>
                                <b-form-input
                                  aria-label="Last name"
                                  disabled
                                  :value="'目前RPM：' + result_Data[0].rpm"
                                  style="min-width: 135px"
                                ></b-form-input>
                                <b-form-input
                                  aria-label="Last name"
                                  disabled
                                  :value="
                                    '日累計產量(支):' +
                                    result_Data[0].device_day_count
                                  "
                                  style="min-width: 215px"
                                ></b-form-input>
                                <b-form-input
                                  aria-label="Last name"
                                  disabled
                                  :value="
                                    '目前產量(支):' +
                                    result_Data[0].device_now_count
                                  "
                                  style="min-width: 215px"
                                ></b-form-input>
                                <b-form-input
                                  aria-label="Last name"
                                  disabled
                                  :value="
                                    '剩餘料重(kg):' + result_Data[0].wire_weight
                                  "
                                  style="min-width: 215px"
                                ></b-form-input>
                              </b-input-group>
                            </b-col>
                          </b-row>
                        </b-col>
                      </b-row>

                      <stackBar
                        :stacksBarClass="stacksBarClass"
                        :stacksBarMaxValue="stacksBarMaxValue"
                        :stacksData="result_Data[0].device_stackBar"
                        :stacksSize="'normal'"
                      />
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>

              <b-row class="mx-0">
                <b-col cols="12" class="p-0 m-0 mt-3">
                  <b-tabs class="customize-tab" v-model="tabIndex">
                    <b-tab title="機台影像" class="mt-3" lazy>
                      <!-- @click="_query_machineCamera()" -->
                      <machineCamera />
                    </b-tab>
                    <b-tab title="運轉日誌" class="mt-3" lazy>
                      <b-table
                        :items="operateLogDataItem"
                        :fields="operateLogDataFields"
                        sticky-header="400px"
                        :no-border-collapse="noBorderCollapse"
                        style="white-space: pre-line"
                        class="customize_scrollbar mt-3"
                      >
                        <template #cell(status)="row">
                          <template v-if="row.item['status'] == '警報'">
                            <b style="color: red">{{ row.item["status"] }}</b>
                          </template>
                          <template v-else>{{ row.item["status"] }}</template>
                        </template>
                        <template #cell(alarmCode)="row">
                          <template v-if="row.item['status'] == '警報'">
                            <b style="color: red">{{
                              row.item["alarmCode"]
                            }}</b>
                          </template>
                          <template v-else>{{
                            row.item["alarmCode"]
                          }}</template>
                        </template>
                        <template #cell(alarmDetail)="row">
                          <template v-if="row.item['status'] == '警報'">
                            <b style="color: red">{{
                              row.item["alarmDetail"]
                            }}</b>
                          </template>
                          <template v-else>{{
                            row.item["alarmDetail"]
                          }}</template>
                        </template>
                        <template #cell(startTime)="row">
                          <template v-if="row.item['status'] == '警報'">
                            <b style="color: red">{{
                              row.item["startTime"]
                            }}</b>
                          </template>
                          <template v-else>{{
                            row.item["startTime"]
                          }}</template>
                        </template>
                        <template #cell(endTime)="row">
                          <template v-if="row.item['status'] == '警報'">
                            <b style="color: red">{{ row.item["endTime"] }}</b>
                          </template>
                          <template v-else>{{ row.item["endTime"] }}</template>
                        </template>
                        <template #cell(durationTime)="row">
                          <template v-if="row.item['status'] == '警報'">
                            <b style="color: red">{{
                              row.item["durationTime"]
                            }}</b>
                          </template>
                          <template v-else>{{
                            row.item["durationTime"]
                          }}</template>
                        </template>
                      </b-table>
                    </b-tab>
                    <b-tab title="警報履歷" class="mt-3" lazy>
                      <b-table
                        :items="alarmHistoryDataItem"
                        :fields="alarmHistoryDataFields"
                        sticky-header="240px"
                        :no-border-collapse="noBorderCollapse"
                        style="white-space: pre-line"
                        class="customize_scrollbar mt-3"
                      >
                      </b-table>
                    </b-tab>
                    <b-tab title="機台指示燈號" class="mt-3" lazy>
                      <b-row class="mx-0">
                        <b-col cols="12" class="text-left">
                          <b-row
                            class="justify-content-start"
                            style="padding: 15px"
                          >
                            <b-col
                              v-for="(item, key) in machineItem_select"
                              :key="key"
                              cols="2"
                              xl="2"
                              lg="3"
                              md="4"
                              sm="6"
                              class="mt-2 ml-0 justify-content-center"
                            >
                              <b-card
                                no-body
                                class="overflow-hidden border-dark"
                                style="max-width: 250px"
                              >
                                <b-row
                                  no-gutters
                                  class="align-items-center text-center"
                                >
                                  <b-col md="5">
                                    <b-card-img
                                      v-if="item.color == 'red'"
                                      :src="redlight"
                                      class="rounded-0"
                                      style="max-width: 85px"
                                    ></b-card-img>
                                    <b-card-img
                                      v-else-if="item.color == 'orange'"
                                      :src="orangelight"
                                      class="rounded-0"
                                      style="max-width: 85px"
                                    ></b-card-img>
                                    <b-card-img
                                      v-else-if="item.color == 'green'"
                                      :src="greenlight"
                                      class="rounded-0"
                                      style="max-width: 85px"
                                    ></b-card-img>
                                    <b-card-img
                                      v-else-if="item.color == 'white'"
                                      :src="whitelight"
                                      class="rounded-0"
                                      style="max-width: 85px"
                                    ></b-card-img>
                                    <b-card-img
                                      v-else-if="item.color == 'gray'"
                                      :src="graylight"
                                      class="rounded-0"
                                      style="max-width: 85px"
                                    ></b-card-img>
                                  </b-col>
                                  <b-col md="7" class>
                                    <b-card-text>{{ item.name }}</b-card-text>
                                  </b-col>
                                </b-row>
                              </b-card>
                            </b-col>
                          </b-row>
                        </b-col>
                        <!-- <b-col
                            cols="12"
                            class="text-left"
                            style="margin: 0 15px"
                          >
                            未亮燈: 正常 / 紅燈: 異常
                          </b-col> -->
                      </b-row>
                    </b-tab>
                    <b-tab title="健康狀態" class="mt-3" lazy>
                      <b-row class="mx-0">
                        <b-col cols="12" class="m-0">
                          <b-tabs
                            class="customize-tab"
                            v-model="health_tabIndex"
                          >
                            <b-tab title="總覽" class="mt-3" lazy>
                              <transition name="fade" mode="out-in">
                                <div v-if="viewSensorDetail" key="1">
                                  <b-row class="mx-0">
                                    <b-col cols="12">
                                      <div
                                        style="
                                          border-radius: 5px;
                                          background-color: #536172;
                                          color: #ffffff;
                                          padding: 0px 13px;
                                          width: 4.25rem;
                                          height: 2rem;
                                          line-height: 2rem;
                                          font-size: 1.125rem;
                                          font-family: dfyuanstd-w7;
                                          text-align: center;
                                          margin: 0.5rem;
                                          cursor: pointer;
                                        "
                                        @click="viewSensor()"
                                      >
