    _getTotalDeviceStatus() {
      let vm = this;
      let totalDeviceStatus = {};
      vm.device_array.forEach((device_name) => {
        if (vm.result_Data[device_name] != undefined) {
          let machine_status = vm.result_Data[device_name].machine_status;
          if (machine_status == "-") {
            if (totalDeviceStatus["離線"] == undefined) {
              totalDeviceStatus["離線"] = 0;
            }
            totalDeviceStatus["離線"]++;
          } else if (machine_status == "異常") {
            if (totalDeviceStatus["異常"] == undefined) {
              totalDeviceStatus["異常"] = 0;
            }
            totalDeviceStatus["異常"]++;
          } else if (machine_status == "待機") {
            if (totalDeviceStatus["待機"] == undefined) {
              totalDeviceStatus["待機"] = 0;
            }
            totalDeviceStatus["待機"]++;
          } else if (machine_status == "運作") {
            if (totalDeviceStatus["運作"] == undefined) {
              totalDeviceStatus["運作"] = 0;
            }
            totalDeviceStatus["運作"]++;
          }
        } else {
          if (totalDeviceStatus["離線"] == undefined) {
            totalDeviceStatus["離線"] = 0;
          }
          totalDeviceStatus["離線"]++;
        }
      });
      console.log(totalDeviceStatus);
      vm.totalDeviceStatus = totalDeviceStatus;
    },

    _getMachineData() {
      let vm = this;
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["cusID"] = vm.selectCustomer; //取客戶編號
      params["category"] = vm.device_category; //取機型類別
      params["methods"] = "GET";
      params["whichFunction"] = "MainQuery";
      vm._machineAxiosAction(params, "_getMachineData");
    },

    _getSingleMachineData() {
      let vm = this;
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["cusID"] = vm.selectCustomer; //取客戶編號
      params["deviceID"] = vm.deviceId; //取機台編號
      params["methods"] = "GET";
      params["whichFunction"] = "SingleQuery";
      vm._machineAxiosAction(params, "_getSingleMachineData");
    },

    _machineImportAxios() {
      const axios = require("axios");
      this.machineAxios = axios;
    },
    _machineAxiosCancel() {
      const CancelToken = this.machineAxios.CancelToken;
      const source = CancelToken.source();
      this.machineAxiosSource = source;
    },
    _machineAxiosAction(params, functionName) {
      let vm = this;
      vm.machineAxios
        .post("/static/php/modules.php", params, {
          cancelToken: vm.machineAxiosSource.token,
        })
        .then(
          function (response) {
            const result = response.data;
            vm._machineAxiosResult(result, functionName);
          }.bind(this)
        )
        .catch(function (err) {
          console.log(err);
          // console.log(JSON.stringify(err));
          if (vm.selectPage == "elecboard") {
            if (vm.nowPage != "elecboard" && vm.nowPage != "machstatus") {
              vm.timer_machineData = setTimeout(function () {
                vm._getMachineData();
              }, 1000);
            } else if (vm.nowPage == "machstatus") {
              vm.timer_machineData = setTimeout(function () {
                vm._getSingleMachineData();
              }, 1000);
            } else if (vm.nowPage == "elecboard") {
              vm.timer_machineData = setTimeout(function () {
                vm._getMachineData();
              }, 1000);
            }
          }
        });
    },
    _machineAxiosResult(result, functionName) {
      let vm = this;
      console.log(functionName);
      console.log(result);
      if (functionName == "_getMachineData") {
        let device_array = [];
        if (result.Response == "ok") {
          let result_query_data = result["QueryTableData"];
          let result_Data = {};
          result_query_data.forEach((data) => {
            result_Data[data["device_name"]] = data;
            device_array.push(data["device_name"]);
          });
          vm.result_Data = result_Data;
          vm._getTotalDeviceStatus();
        }

        if (device_array.length < 5) {
          let device_max = 5;
          let device_for = device_max - device_array.length;
          for (let i = 0; i < device_for; i++) {
            device_array.push("-");
          }
        }
        vm.device_array = device_array;
        vm._onResize_elecboard();
        vm._getNowTime();
        if (vm.nowPage == "machstatus") {
          return;
        }
        vm.timer_machineData = setTimeout(function () {
          vm._clearTimer("timer_singleMachineData");
          vm._getMachineData();
        }, 1000);
      } else if (functionName == "_getSingleMachineData") {
        if (result.Response == "ok") {
          let result_query_data = result["QueryTableData"];
          let result_Data = [];
          result_query_data.forEach((data) => {
            result_Data.push(data);
          });
          if (result_Data[0].machine_image != "") {
            vm.device_image = require(`../../assets/MES/${result_Data[0].machine_image}`);
          }
          vm.result_Data = result_Data;
          vm.machineItem_select = result_Data[0]["machine_light_value"];
          if (result_Data[0].machine_camera.length != 0) {
            vm.set_machineCameraData(result_Data[0].machine_camera);
          } else {
            vm.set_machineCameraData([]);
          }
          vm.operateLogDataItem = result_Data[0]["operatelog_information"];
          vm.alarmHistoryDataItem = result_Data[0]["alarmHistory_information"];

          let health_status = {};
          let sensor_group = result_Data[0]["sensor_group"];
          let sensor_group_array = Object.keys(sensor_group);
          let machineHealthData = {};
          let machineHealthDetailData = {};
          let emeterData = {
            electricity_RELEC: {
              table: {
                fields: [
                  {
                    key: "interval",
                    label: "",
                    thClass: "emeter-table-th",
                  },
                  {
                    key: "powerUsed",
                    label: "用電量(度)",
                    thClass: "emeter-table-th",
                  },
                  {
                    key: "estimatedCost",
                    label: "預估電費(NT)",
                    thClass: "emeter-table-th",
                  },
                ],
              },
            },
          };

          emeterData["electricity_RELEC"]["table"]["items"] =
            result_Data[0]["electricity_RELEC"];
          emeterData["single"] = {};

          sensor_group_array.forEach((group_code) => {
            health_status[group_code] = {
              name: sensor_group[group_code].name,
              status: sensor_group[group_code].status,
            };
            let sensor_unit = "";
            if (sensor_group[group_code].data.length != 0) {
              if (sensor_group[group_code].data[0].unit != undefined) {
                sensor_unit = sensor_group[group_code].data[0].unit;
              }
            }
            let sensor_data = [];
            let sensor_fields = [];
            if (group_code == "serverDriver" || group_code == "oilLevel") {
              sensor_fields = [
                {
                  key: "value",
                  label: "實際值",
                  unit: sensor_unit,
                },
              ];
              sensor_group[group_code].data.forEach((data) => {
                sensor_data.push({
                  isActive: true,
                  value: data.value,
                  status: data.status,
                });
              });
            } else {
              sensor_fields = [
                {
                  key: "name",
                  label: "名稱",
                },
                {
                  key: "stand",
                  label: "標準值",
                  unit: sensor_unit,
                },
                {
                  key: "value",
                  label: "實際值",
                  unit: sensor_unit,
                },
              ];
              sensor_group[group_code].data.forEach((data) => {
                sensor_data.push({
                  isActive: true,
                  name: data.name,
                  value: data.value,
                  stand: data.stand,
                  status: data.status,
                  unit: data.unit,
                });
              });
            }
            machineHealthData[group_code] = {
              name: sensor_group[group_code].name,
              table: {
                fields: sensor_fields,
                items: sensor_data,
              },
            };
            if (group_code != "electricity") {
              if (sensor_data.length > 5) {
                let count = sensor_data.length;
                let new_sensor_data = [];
                while (count > 5) {
                  count = Math.ceil(sensor_data.length / 2);
                }
                for (let i = 0; i < sensor_data.length; i += count) {
                  new_sensor_data.push(sensor_data.slice(i, i + count));
                }
                new_sensor_data.forEach((data, index) => {
                  machineHealthDetailData[group_code + "_" + index] = {
                    name: sensor_group[group_code].name,
                    table: {
                      fields: sensor_fields,
                      items: data,
                    },
                    item_length: count,
                  };
                });
              } else {
                machineHealthDetailData[group_code] = {
                  name: sensor_group[group_code].name,
                  table: {
                    fields: sensor_fields,
                    items: sensor_data,
                  },
                  item_length: sensor_data.length,
                };
              }
            }
            if (group_code == "serverDriver" || group_code == "oilLevel") {
              machineHealthDetailData[group_code].item_length -= 0.5;
            }
          });
          const sort_machineHealthDetailData = Object.fromEntries(
            Object.entries(machineHealthDetailData).sort(
              ([, a], [, b]) => b.item_length - a.item_length
            )
          );
          vm.health_status = health_status;
          vm.machineHealthData = machineHealthData;
          vm.machineHealthDetailData = sort_machineHealthDetailData;

          let error_message_length = 0;
          let machine_abn_text = result_Data[0]["machine_abn_text"];
          vm.machine_abn_id = result_Data[0]["machine_abn_id"];
          vm.machine_abn_text = machine_abn_text;

          let diagnosis_message = result_Data[0]["diagnosis_message"];
          vm.diagnosis_message = diagnosis_message;

          if (diagnosis_message.length > 0 || machine_abn_text.length > 0) {
            error_message_length = 2;
          }
          if (error_message_length == 0) {
            error_message_length++;
          }
          vm.error_message_length = error_message_length;

          let current = [];
          let voltage = [];
          let volt_vubr = [];
          if (sensor_group["electricity"].data.length > 0) {
            sensor_group["electricity"].data.forEach((data) => {
              if (
                data.name == "功率" ||
                data.name == "功率峰值" ||
                data.name == "功率需量峰值" ||
                data.name == "頻率" ||
                data.name == "功因調整比例" ||
                data.name == "功率因數"
              ) {
                emeterData["single"][data.name] = {
                  name: data.name,
                  table: {
                    fields: [
                      {
                        key: "value",
                        label: "實際值",
                        unit: data.unit,
                        thClass: "hide-table-title",
                      },
                    ],
                    items: [{ value: data.value }],
                  },
                };
              } else if (data.name == "當前電流值") {
                current.push({ value: data.value, unit: data.unit });
              } else if (data.name == "當前電壓值") {
                voltage.push({ value: data.value, unit: data.unit });
              } else if (data.name == "電壓不平衡率") {
                volt_vubr.push({ value: data.value, unit: data.unit });
              }
            });
          }
          vm.emeterData = emeterData;
          if (current.length != 0) {
            vm.smartMeterChart.options[0].value.data.emeter_data[0].value =
              current[0]["value"];
            vm.smartMeterChart.options[0].value.data.emeter_data[0].unit =
              current[0]["unit"];
            vm.smartMeterChart.options[0].value.data.emeter_data[0].name =
              "當前電流值";
          } else {
            vm.smartMeterChart.options[0].value.data.emeter_data[0].value =
              null;
            vm.smartMeterChart.options[0].value.data.emeter_data[0].unit = "";
            vm.smartMeterChart.options[0].value.data.emeter_data[0].name =
              "當前電流值";
          }
          if (voltage.length != 0) {
            vm.smartMeterChart.options[0].value.data.emeter_data[1].value =
              voltage[0]["value"];
            vm.smartMeterChart.options[0].value.data.emeter_data[1].unit =
              voltage[0]["unit"];
            vm.smartMeterChart.options[0].value.data.emeter_data[1].name =
              "當前電壓值";
          } else {
            vm.smartMeterChart.options[0].value.data.emeter_data[1].value =
              null;
            vm.smartMeterChart.options[0].value.data.emeter_data[1].unit = "";
            vm.smartMeterChart.options[0].value.data.emeter_data[1].name =
              "當前電壓值";
          }
          if (volt_vubr.length != 0) {
            vm.smartMeterChart.options[0].value.data.emeter_data[2].value =
              volt_vubr[0]["value"];
            vm.smartMeterChart.options[0].value.data.emeter_data[2].unit =
              volt_vubr[0]["unit"];
            vm.smartMeterChart.options[0].value.data.emeter_data[2].name =
              "電壓不平衡率";
          } else {
            vm.smartMeterChart.options[0].value.data.emeter_data[2].value =
              null;
            vm.smartMeterChart.options[0].value.data.emeter_data[2].unit = "";
            vm.smartMeterChart.options[0].value.data.emeter_data[2].name =
              "電壓不平衡率";
          }
        }

        vm.query_counter++;

        vm._getNowTime();
        if (vm.nowPage != "elecboard" && vm.nowPage != "machstatus") {
          return;
        } else if (vm.nowPage == "elecboard") {
          return;
        }
        vm.timer_singleMachineData = setTimeout(function () {
          vm._clearTimer("timer_machineData");
          vm._getSingleMachineData();
        }, 1000);
      }
    },

