    _clearTimer(timerName) {
      if (timerName === "undefined") {
        var highestIntervalId = setInterval(";");
        for (var i = 0; i < highestIntervalId; i++) {
          //清除所有計時資料
          clearInterval(i);
        }
        if (this.selectPage == "elecboard") {
          this._getNowTime();
        }
      } else {
        clearTimeout(timerName);
      }
    },

    _getNowTime() {
      let vm = this;
      let Today = new Date();
      vm.nowtime =
        Today.getFullYear() +
        "-" +
        String(Today.getMonth() + 1).padStart(2, "0") +
        "-" +
        String(Today.getDate()).padStart(2, "0") +
        " " +
        String(Today.getHours()).padStart(2, "0") +
        ":" +
        String(Today.getMinutes()).padStart(2, "0") +
        ":" +
        String(Today.getSeconds()).padStart(2, "0");
      console.log(vm.nowtime);

      //   setTimeout(function () {
      //     vm._getNowTime();
      //   }, 1000);
    },

    _onResize_elecboard() {
      let vm = this;
      vm.window_innerWidth = window.innerWidth;
      vm._onResize_elecboard_detail_list();
      setTimeout(() => {
        vm._onResize_elecboard_status_bar();
      }, 0);
    },

    _onResize_elecboard_detail_list() {
      let vm = this;
      if (vm.nowPage == "elecboard") {
        let show_device_list = [];
        if (vm.window_innerWidth > 992) {
          let divisor = Math.round(vm.device_array.length / 2);
          vm.device_array.forEach((data, index) => {
            if (index < divisor) {
              show_device_list[index * 2] = data;
            } else {
              show_device_list[(index - divisor) * 2 + 1] = data;
            }
          });
        } else {
          show_device_list = vm.device_array;
        }
        vm.show_device_list = show_device_list;
      }
    },

    _onResize_elecboard_status_bar() {
      let vm = this;
      if (vm.nowPage == "elecboard") {
        vm.elecboard_detail_status_height =
          vm.$refs.elecboard_detail.clientHeight -
          vm.$refs.elecboard_status_bar.clientHeight -
          16;
      }
    },
    viewSensorValue(sensorName) {
      let vm = this;
      vm.viewSensorDetail = 1;
      vm.selectSensor = sensorName;
    },
    viewSensor() {
      let vm = this;
      vm.viewSensorDetail = 0;
    },
    setWarningText() {
      let vm = this;
      let health_array = Object.keys(vm.health_status);
      let status = "offline";
      health_array.forEach((item) => {
        if (status == "error") {
          return;
        }
        if (vm.health_status[item].status == "warning") {
          status = "warning";
        } else if (vm.health_status[item].status == "error") {
          status = "error";
        }
      });
      vm.sensorStutas.status = status;

      // let machine_abn_text = vm.machine_abn_text;
      // let warningTextArray = [];
      // machine_abn_text.forEach((data) => {
      //   warningTextArray.push(
      //     `${data.machine_abn_description}:\n${data.machine_abn_solution}\n`
      //   );
      // });
      // vm.sensorStutas.warningText = warningTextArray.join("\n");
    },
    _ipModal() {
      let vm = this;
      vm._getMachineCamera();
      vm.ipModalCtrl.openModal = true;
      vm.ipModalCtrl.oldIpTemp = vm.ipData;
    },
    _getMachineCamera() {
      let vm = this;
      let deviceId = vm.deviceId;
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = "QueryMachineCamera";
      params["deviceID"] = deviceId;
      console.log(params);
      vm.axiosAction(params).then(() => {
        var result = vm.axiosResult;
        console.log(result);
        if (result["Response"] == "ok") {
          console.log(result["QueryTableData"]);
          let ipCamArray = [];
          result["QueryTableData"].forEach((item) => {
            let ipCamObj = {};
            ipCamObj["camera_name"] = item.camera_name;
            let urlSplit = item.url.split("/");
            let ipSplit = urlSplit[2].split(":");
            ipCamObj["ip"] = ipSplit[0];
            ipCamObj["port"] = ipSplit[1];
            ipCamArray.push(ipCamObj);
          });
          vm.ipData = ipCamArray;
        }
      });
    },
    _okBtnEvent(bvModalEvt) {
      let vm = this;
      bvModalEvt.preventDefault();

      let emptyInput = false;
      vm.ipData.forEach((item) => {
        if (item.ip === "" || item.port === "") {
          emptyInput = true;
        }
      });
      if (emptyInput) {
        vm.setSetTimeOutAlertMsg("請輸入IP及Port");
        vm.toggleSetTimeOutAlertModal();
      } else {
        console.log(vm.ipData);
        vm._updatetMachineCamera();
        vm.ipModalCtrl.openModal = false;
        vm.click_right_count = 0;
      }
    },
    _updatetMachineCamera() {
      let vm = this;
      let ipData = vm.ipData;
      let deviceId = vm.deviceId;
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = "updateMachineCamera";
      params["cusID"] = vm.selectCustomer; //取客戶編號
      params["ipData"] = ipData;
      params["deviceID"] = deviceId;
      params["modifier"] = vm.loginData.account;
      console.log(params);
      vm.axiosAction(params)
        .then(() => {
          var result = vm.axiosResult;
          if (result["Response"] == "ok") {
            vm.setSetTimeOutAlertMsg("已更新IP CAM");
            vm.toggleSetTimeOutAlertModal();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    _cancelBtnEvent() {
      let vm = this;
      vm.ipModalCtrl.openModal = false;
      vm.ipData = vm.ipModalCtrl.oldIpTemp;
      vm.click_right_count = 0;
    },

    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
  destroyed() {
    this.machineAxiosSource.cancel("back");
    this._reset();
    this._clearTimer();
  },
};
</script>

