<template>
  <div id="diagnosis" class="diagnosis">
    <b-row>
      <b-col cols="1"></b-col>
      <b-col
        cols="10"
        class="p-0 m-0 mt-4 text-center"
        style="font-size: 1.75rem; color: #000000; margin-top: 3rem"
      >
        <div class="m-auto">AI診斷履歷</div>
      </b-col>
      <b-col cols="1"></b-col>
    </b-row>
    <b-row>
      <b-col cols="1"></b-col>
      <b-col cols="10">
        <b-row>
          <commonQueryIoT />
        </b-row>
        <b-row>
          <b-table
            :fields="fields"
            :items="diagnosisData"
            responsive
            class="text-center diagnosis-table mt-4"
            style="white-space: pre-line"
          >
            <template #cell(solution)="data">
              <span v-html="data.value"></span>
            </template>
          </b-table>
        </b-row>
      </b-col>
      <b-col cols="1"></b-col>
    </b-row>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import queryResult from "@/components/MES/queryResult.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
export default {
  name: "diagnosis",
  data() {
    return {
      fields: [
        {
          key: "reason",
          label: "異常原因",
          thClass: "diagnosis-table-th",
        },
        {
          key: "solution",
          label: "建議排除方式",
          thClass: "diagnosis-table-th",
        },
        {
          key: "startTime",
          label: "開始時間",
          thClass: "diagnosis-table-th",
        },
        {
          key: "endTime",
          label: "結束時間",
          thClass: "diagnosis-table-th",
        },
        {
          key: "duration",
          label: "持續時間",
          thClass: "diagnosis-table-th",
        },
      ],
      diagnosisData: [],
    };
  },
  created() {
    let vm = this;
    vm.changeSelectPage("diagnosis");
    vm._setQueryInput();
    vm._getSelectOption();
    vm._setCommonQueryData();
  },
  mounted: function () {},
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",
      selectPage: "mesGetLogin/get_selectPage",
      pageName: "mesGetLogin/get_pageName",
      clickChild: "featurelistbar/get_clickChild",
      selectCustomer: "mesData/get_selectedCustomer",
      inputData: "mesCommonQueryIoT/get_inputData",
      selectValue: "mesCommonQueryIoT/get_selectValue",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
    }),
  },
  components: {
    commonQueryIoT,
  },
  watch: {
    mainQueryResponse: {
      handler() {
        let vm = this;
        vm._reset([]);
        if (
          vm.mainQueryResponse == "查無資料" ||
          vm.mainQueryResponse == "時間尚未選擇"
        ) {
          vm.selected = vm.options[0].value;
          return;
        }

        var itemsArray = [];
        if (vm.mainQueryResponse[0].device_detail_data.length > 0) {
          for (
            var i = 0;
            i < vm.mainQueryResponse[0].device_detail_data.length;
            i++
          ) {
            var itemsObj = {};
            itemsObj["reason"] =
              vm.mainQueryResponse[0].device_detail_data[i]["reason"];
            itemsObj["solution"] =
              vm.mainQueryResponse[0].device_detail_data[i]["solution"];
            itemsObj["startTime"] =
              vm.mainQueryResponse[0].device_detail_data[i]["startTime"];
            itemsObj["endTime"] =
              vm.mainQueryResponse[0].device_detail_data[i]["endTime"];
            itemsObj["duration"] =
              vm.mainQueryResponse[0].device_detail_data[i]["duration"];
            itemsArray.push(itemsObj);
          }
        }
        vm.diagnosisData = itemsArray;
      },
    },
  },
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      changeSelectPage: "mesGetLogin/change_selectPage",
      changeSelectPageName: "mesGetLogin/change_selectPageName",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      clearQueryResultValue: "mesQueryResult/clear_queryResultItem",
      setcustomerOptions: "mesSelectCustomer/set_customerOptions",
      setSelectedCustomer: "mesData/set_selectedCustomer",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
    }),
    _setCommonQueryData() {
      let vm = this;
      vm.setPhpFunction("MainQuery");
    },
    _setQueryInput() {
      let vm = this;
      vm.setSelectTag({
        model: false,
        deviceCode: false,
      });
    },
    _getSelectOption() {
      let vm = this;
      var params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "GET";
      params["whichFunction"] = "SelectOption";
      params["cusID"] = vm.selectCustomer; 
      console.log(params);
      vm.axiosAction(params).then(() => {
        var result = vm.axiosResult;
        console.log(result);
        if (result["Response"] == "ok") {
          let backData = result["QueryTableData"];
          let modelOption = backData[0]["model"];
          let deviceOption = backData[0]["device"];
          vm.setModelOption(modelOption);
          vm.setDeviceOption(deviceOption);
          console.log(modelOption);
        } else {
          vm.setModelOption([]);
          vm.setDeviceOption([]);
        }
      });
    },
    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
  destroyed() {},
};
</script>
<style scoped>
div,
p,
span {
  font-family: dfyuanstd-w7;
}
</style>

<style lang="scss">
.diagnosis-table td {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  font-family: dfyuanstd-w7;
}
.diagnosis-table td:nth-child(1) {
  width: 15%;
}
.diagnosis-table td:nth-child(2) {
  width: 40%;
}
.diagnosis-table td:nth-child(3) {
  width: 17.5%;
}
.diagnosis-table td:nth-child(4) {
  width: 17.5%;
}
.diagnosis-table td:nth-child(5) {
  width: 10%;
}
.diagnosis-table tr {
  border-left: 1px solid #65646a;
  border-right: 1px solid #65646a;
}

.diagnosis-table tr:nth-child(even) {
  background-color: #cccccc;
}
.diagnosis-table-th {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
}
.table > tbody > tr > td {
  vertical-align: middle;
}
.warning-border {
  border: 1px solid #c10000 !important;
}
</style>



