<template>
    <div
        id="account"
        ref="account"
        class="customize_scrollbar"
        style="position: relative"
    >
        <b-row :class="accountblock">
            <b-col cols="12" class="text-center">
                <h3 v-if="is_add_update == 'add'">
                    {{ action_config[action] }}
                </h3>
                <h3 v-else>基本資料查詢</h3>
            </b-col>
            <template v-if="action == 'cud'">
                <b-col cols="12" class="input-block p-3">
                    <b-form @submit="_onSubmit">
                        <b-row align-h="between">
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="客戶編號:"
                                    label-for="cus_id"
                                >
                                    <b-form-input
                                        id="cus_id"
                                        class="input-text"
                                        v-model.trim="form.cus_id"
                                        disabled
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="統一編號:"
                                    label-for="tax_id"
                                >
                                    <b-form-input
                                        id="tax_id"
                                        class="input-text"
                                        v-model.trim="form.tax_id"
                                        ref="tax_id"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row align-h="start">
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    label="帳號:"
                                    label-for="user_id"
                                >
                                    <b-form-input
                                        id="user_id"
                                        class="input-text col"
                                        v-model.trim="form.user_id"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                            <div
                                v-if="typeof id_existed === 'boolean'"
                                style="display: flex; align-items: center"
                            >
                                <b-icon
                                    v-if="!id_existed"
                                    icon="check-circle-fill"
                                    scale="1.5"
                                    variant="success"
                                ></b-icon>
                                <template v-else>
                                    <b-icon
                                        icon="exclamation-circle-fill"
                                        scale="1.5"
                                        variant="danger"
                                    ></b-icon>
                                    <span class="ml-2 text-danger"
                                        >此帳號已被註冊使用
                                    </span>
                                </template>
                            </div>
                        </b-row>
                        <b-row align-h="start">
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    label="密碼:"
                                    label-for="pwd"
                                >
                                    <b-form-input
                                        id="pwd"
                                        :type="pwd_type"
                                        class="input-text col"
                                        v-model.trim="form.pwd"
                                    ></b-form-input>
                                    <b-icon
                                        v-if="!pwd_show"
                                        icon="eye-fill"
                                        class="pwdeye"
                                        @click="_showHidePwd(true)"
                                    ></b-icon>
                                    <b-icon
                                        v-else
                                        icon="eye-slash-fill"
                                        class="pwdeye"
                                        @click="_showHidePwd(true)"
                                    ></b-icon>
                                </b-form-group>
                            </b-col>
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="確認密碼:"
                                    label-for="confirmpwd"
                                >
                                    <b-form-input
                                        id="confirmpwd"
                                        :type="confirmpwd_type"
                                        class="input-text"
                                        v-model.trim="form.confirmpwd"
                                    ></b-form-input>
                                    <b-icon
                                        v-if="!confirmpwd_show"
                                        icon="eye-fill"
                                        class="pwdeye"
                                        @click="_showHidePwd(false)"
                                    ></b-icon>
                                    <b-icon
                                        v-else
                                        icon="eye-slash-fill"
                                        class="pwdeye"
                                        @click="_showHidePwd(false)"
                                    ></b-icon>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row align-h="start">
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="客戶名稱:"
                                    label-for="cus_name"
                                >
                                    <b-form-input
                                        id="cus_name"
                                        class="input-text"
                                        v-model.trim="form.cus_name"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="負責人:"
                                    label-for="owner"
                                >
                                    <b-form-input
                                        id="owner"
                                        class="input-text"
                                        v-model.trim="form.owner"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row align-h="start">
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="聯絡人:"
                                    label-for="contact_person"
                                >
                                    <b-form-input
                                        id="contact_person"
                                        class="input-text"
                                        v-model.trim="form.contact_person"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="聯絡電話:"
                                    label-for="telphone"
                                >
                                    <b-form-input
                                        id="telphone"
                                        class="input-text"
                                        v-model.trim="form.telphone"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row align-h="start">
                            <b-col cols="8">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="2"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="公司地址:"
                                    label-for="address"
                                >
                                    <b-form-input
                                        id="address"
                                        class="input-text"
                                        v-model.trim="form.address"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                            <b-col cols="4">
                                <b-form-group
                                    label-cols-sm="4"
                                    label-cols-lg="3"
                                    content-cols-sm
                                    content-cols-lg="7"
                                    label="備註:"
                                    label-for="remarks"
                                >
                                    <b-form-input
                                        id="remarks"
                                        class="input-text"
                                        v-model.trim="form.remarks"
                                    ></b-form-input>
                                </b-form-group>
                            </b-col>
                        </b-row>
                        <b-row align-h="center">
                            <template v-if="is_add_update == 'add'">
                                <b-button
                                    type="submit"
                                    class="mt-3 publicButton"
                                    >送出</b-button
                                >
                                <b-button
                                    type="reset"
                                    class="ml-2 mt-3 publicButton"
                                    >清除</b-button
                                >
                            </template>
                            <template v-else>
                                <b-button
                                    class="mt-3 publicButton"
                                    @click.prevent="
                                        _reset(['query_select', 'cus_items']);
                                        action = 'query';
                                    "
                                    >返回</b-button
                                >
                                <b-button
                                    type="submit"
                                    class="ml-2 mt-3 publicButton"
                                    >修改</b-button
                                >
                            </template>
                        </b-row>
                    </b-form>
                </b-col>
            </template>
            <template v-else>
                <b-col cols="12">
                    <b-row>
                        <b-col cols="12">
                            <span>客戶編號:</span>
                        </b-col>
                    </b-row>
                    <b-row align-h="start">
                        <b-col cols="4">
                            <b-form-select
                                id="cus_id"
                                v-model="query_select.selected"
                                :options="query_select.options"
                                required
                            ></b-form-select>
                        </b-col>
                        <b-col cols="4">
                            <b-button
                                class="publicButton"
                                :disabled="
                                    query_select.options.length == 0 ||
                                    query_select.selected == ''
                                "
                                @click.prevent="_queryCustomer"
                                >送出</b-button
                            >
                        </b-col>
                    </b-row>
                    <br />
                    <b-row align-h="center">
                        <b-table
                            sticky-header="500px"
                            :items="cus_items"
                            :fields="cus_fields"
                            :fixed="false"
                            responsive
                            :head-variant="'light'"
                            class="customize_scrollbar text-center"
                            v-if="cus_items.length != 0"
                            :tbody-tr-attr="_rowClass"
                        >
                            <template v-slot:cell(detail)="row">
                                <b-button
                                    size="sm"
                                    class="publicButton"
                                    @click="_checkDetail(row.item)"
                                >
                                    內容
                                </b-button>
                            </template>
                            <template v-slot:cell(del)="row">
                                <b-icon
                                    icon="x-circle-fill"
                                    scale="1.5"
                                    class="mt-2 delbtn"
                                    @click="_deleteCustomer(row.item.id)"
                                ></b-icon>
                            </template>
                        </b-table>
                    </b-row>
                </b-col>
            </template>
        </b-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Temp from "../Temp.vue";
export default {
    components: { Temp },
    name: "account",
    data() {
        let vm = this;
        return {
            action: "cud",
            action_config: {
                基本資料設定: "cud",
                基本資料查詢: "query",
                cud: "基本資料設定",
                query: "基本資料查詢",
            },
            form: {
                id: "",
                cus_id: "系統自動產生",
                tax_id: "",
                old_user_id: "",
                user_id: "",
                pwd: "",
                confirmpwd: "",
                cus_name: "",
                owner: "",
                contact_person: "",
                telphone: "",
                address: "",
                remarks: "",
            },
            access_list: {
                web: {
                    bigdata: { authority: true },
                    logging: { authority: false },
                    edoc: { authority: true },
                    mes: { authority: true },
                    operation: { authority: false },
                    setting: { authority: false },
                },
            },
            query_select: {
                selected: "",
                options: [],
            },
            cus_fields: [
                {
                    key: "id",
                    label: "客戶編號",
                    thClass: "publicTH",
                },
                {
                    key: "cus_name",
                    label: "客戶名稱",
                    thClass: "publicTH",
                },
                {
                    key: "contact_person",
                    label: "聯絡人",
                    thClass: "publicTH",
                },
                {
                    key: "telphone",
                    label: "聯絡電話",
                    thClass: "publicTH",
                },
                {
                    key: "detail",
                    label: "詳細資料",
                    thClass: "publicTH",
                },
                {
                    key: "del",
                    label: "刪除",
                    thClass: "publicTH",
                },
            ],
            cus_items: [],
            id_existed: "",
            pwd_show: false,
            pwd_type: "password",
            confirmpwd_show: false,
            confirmpwd_type: "password",
            is_add_update: "add",
        };
    },
    computed: {
        ...mapGetters({
            loginData: "getlogin/get_loginData",
            axiosResult: "commonaxios/get_axiosResult",
            // system: "apidetail/get_system",
            DEFAULT_commonModalConfig: "usemodal/get_DEFAULT_commonModalConfig",
            clickChild: "featurelistbar/get_clickChild",
        }),
        accountblock: function () {
            if (this.action == "cud") {
                return {
                    "account-block": true,
                };
            } else {
                return {
                    "account-block": false,
                };
            }
        },
    },
    watch: {
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
                vm.action = vm.action_config[value.split("_")[1]];
                if (vm.action == "query") {
                    vm._queryCusID();
                }
            },
        },
    },
    created() {
        // this._checkApiDetailExist();
        // this.apipath_prefix += this.$route.params.SYSTEM;
        console.log(this.$router);
        console.log(this.$router.currentRoute.matched);
        console.log(this.endpoint);
        console.log(this.$route.params.SYSTEM);
        console.log(this.clickChild);
        if (
            this.action_config[this.clickChild.child_name.split("_")[1]] ==
            "query"
        ) {
            this.action = "query";
            this._queryCusID();
        }
    },
    mounted() {
        // var VueScrollTo = require("vue-scrollto");
        // var options = {
        //     container: "#account",
        //     easing: "ease-in",
        //     offset: -60,
        //     force: true,
        //     cancelable: true,
        //     onStart: function (element) {
        //         // scrolling started
        //     },
        //     onDone: function (element) {
        //         // scrolling is done
        //     },
        //     onCancel: function () {
        //         // scrolling has been interrupted
        //     },
        //     x: false,
        //     y: true,
        // };
        // var cancelScroll = VueScrollTo.scrollTo("#account", options);
        // // or alternatively inside your components you can use
        // // cancelScroll = this.$scrollTo(element, duration, options);
        // // to cancel scrolling you can call the returned function
        // cancelScroll();
        // // this.$scrollTo("#account");
    },
    methods: {
        ...mapActions({
            axiosAction: "commonaxios/axiosAction",
            axiosGetAction: "commonaxios/axiosGetAction",
            axiosPostAction: "commonaxios/axiosPostAction",
            axiosDeleteAction: "commonaxios/axiosDeleteAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleCommonModal: "usemodal/toggle_commonModal",
            setCommonModalConfig: "usemodal/set_commonModalConfig",
        }),

        _rowClass(item, type) {
            if (!item || type !== "row") return;
        },

        _onSubmit(event) {
            event.preventDefault();
            event.stopPropagation();
            let vm = this;
            console.log(vm.form);
            let isok = true;
            Object.entries(vm.form).map((ele) => {
                if (
                    ele[0] != "remarks" &&
                    ele[0] != "tax_id" &&
                    ele[0] != "id" &&
                    ele[0] != "old_user_id" &&
                    ele[1] == ""
                ) {
                    isok = false;
                }
            });
            if (!isok) {
                vm.setSetTimeOutAlertMsg("資料尚未輸入完整");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            if (vm.form.pwd != vm.form.confirmpwd) {
                vm.setSetTimeOutAlertMsg("確認密碼與密碼不相同");
                vm.toggleSetTimeOutAlertModal();
                vm.form.pwd = "";
                vm.form.confirmpwd = "";
                return;
            }

            if (vm.is_add_update == "add") {
                const addUser = async () => {
                    await vm._checkTaxNumber(); //檢查統編
                    await vm._checkUserID(); //檢查userID是否存在
                    await vm._addCustomer(); //新增客戶資料
                };
                addUser()
                    .then(() => {
                        let vm = this;
                        vm.toggleAlertModal(true);
                        console.log("go add");
                        console.log(vm.form);
                        let params = {};
                        params["url"] =
                            "/api/" +
                            vm.$route.params.SYSTEM +
                            "/1.0/my/user/reg_User";
                        params["postdata"] = {
                            user_id: vm.form.user_id,
                            cus_id: vm.form.cus_id,
                            pwd: vm.form.pwd,
                            name: vm.form.cus_name,
                            email: "",
                            access_list: vm.access_list,
                            // delete_enable: [null],
                            delete_at: [null],
                            creator: vm.loginData.account,
                            modifier: vm.loginData.account,
                        };
                        console.log(params);

                        vm.axiosPostAction(params)
                            .then(() => {
                                var result = vm.axiosResult;
                                console.log(result);
                                if (result.status != 200) {
                                    vm.setSetTimeOutAlertMsg(result.data);
                                    return;
                                }
                                if (result.data["Response"] == "ok") {
                                    vm.setSetTimeOutAlertMsg("新增成功");
                                    vm._reset([]);
                                } else {
                                    vm.setSetTimeOutAlertMsg(
                                        result.data["Response"]
                                    );
                                }
                            })
                            .catch((response) => {
                                console.log(response);
                                vm.setSetTimeOutAlertMsg(response);
                            })
                            .finally(() => {
                                console.log("done");
                                vm.toggleAlertModal(false);
                                vm.toggleSetTimeOutAlertModal();
                            });
                    })
                    .catch((response) => {
                        console.log(response);
                        vm.setSetTimeOutAlertMsg(response);
                        vm.toggleSetTimeOutAlertModal();
                    });
            } else {
                const updateUser = async () => {
                    await vm._checkTaxNumber(); //檢查統編
                    if (vm.form.old_user_id != vm.form.user_id) {
                        await vm._checkUserID(); //檢查userID是否存在
                    }
                    await vm._updateCustomer(); //修改客戶
                };
                updateUser()
                    .then(() => {
                        let params = {};
                        params["methods"] = "PATCH";
                        params["whichFunction"] = "userUpdate";
                        params["postdata"] = {
                            old_user_id: vm.form.old_user_id,
                            new_user_id: vm.form.user_id,
                            cus_id: vm.form.id,
                            pwd: vm.form.pwd,
                            name: vm.form.cus_name,
                            email: "",
                            access_list: vm.form.access_list,
                            modifier: vm.loginData.account,
                        };
                        console.log(params);
                        vm.axiosAction(params)
                            .then(() => {
                                var result = vm.axiosResult;
                                console.log(result);
                                if (result["Response"] == "ok") {
                                    vm.setSetTimeOutAlertMsg("修改成功");
                                } else {
                                    vm.setSetTimeOutAlertMsg(
                                        result["Response"]
                                    );
                                }
                            })
                            .catch((response) => {
                                console.log(response);
                                vm.setSetTimeOutAlertMsg(response);
                            })
                            .finally(() => {
                                vm.toggleSetTimeOutAlertModal();
                                vm._reset(["query_select"]);
                                vm.action = "query";
                                vm._queryCustomer();
                            });
                    })
                    .catch((response) => {
                        console.log(response);
                        vm.setSetTimeOutAlertMsg(response);
                        vm.toggleSetTimeOutAlertModal();
                    });
            }
        },

        _showHidePwd(which) {
            let vm = this;
            if (which) {
                vm.pwd_show = !vm.pwd_show;
                if (vm.pwd_show) {
                    vm.pwd_type = "text";
                } else {
                    vm.pwd_type = "password";
                }
            } else {
                vm.confirmpwd_show = !vm.confirmpwd_show;
                if (vm.confirmpwd_show) {
                    vm.confirmpwd_type = "text";
                } else {
                    vm.confirmpwd_type = "password";
                }
            }
        },

        _checkTaxNumber() {
            return new Promise((resolve, reject) => {
                const vm = this;
                if (vm.form.tax_id == "") {
                    resolve("ok");
                }
                var validateOperator = [1, 2, 1, 2, 1, 2, 4, 1];
                if (vm.form.tax_id.length !== 8) {
                    reject("統編格式錯誤，要有8個數字");
                }
                let sum = 0;
                const calculate = function (product) {
                    // 個位數 + 十位數
                    var ones = product % 10,
                        tens = (product - ones) / 10;
                    return ones + tens;
                };
                for (var i = 0; i < validateOperator.length; i++) {
                    sum += calculate(vm.form.tax_id[i] * validateOperator[i]);
                }

                if (
                    sum % 10 == 0 ||
                    (vm.form.tax_id[6] == "7" && (sum + 1) % 10 == 0)
                ) {
                    resolve("ok");
                } else {
                    reject("統一編號錯誤");
                }
            });
        },

        _checkUserID() {
            return new Promise((resolve, reject) => {
                let vm = this;
                let params = {};
                //檢查帳號是否存在
                params["url"] =
                    "/api/" +
                    vm.$route.params.SYSTEM +
                    "/1.0/my/CommonUse/Interval/user";
                params["urlparams"] = {
                    attr: "user_id",
                    valueStart: vm.form.user_id,
                    valueEnd: vm.form.user_id,
                };
                console.log(params);
                vm.axiosGetAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result.status != 200) {
                            reject(result.data);
                        }
                        if (result.data["Response"] == "ok") {
                            if (result.data["QueryTableData"].length > 0) {
                                vm.id_existed = true;
                                reject("此帳號已被註冊使用");
                            } else {
                                vm.id_existed = false;
                                resolve("ok");
                            }
                        } else {
                            reject(result.data["Response"]);
                        }
                    })
                    .catch((response) => {
                        console.log(response);
                        reject(response);
                    });
            });
        },

        _addCustomer() {
            return new Promise((resolve, reject) => {
                let params = {};
                let vm = this;
                params["url"] =
                    "/api/" +
                    vm.$route.params.SYSTEM +
                    "/1.5/my/CommonUse/customer";
                params["postdata"] = {
                    id: [""],
                    cus_name: [vm.form.cus_name],
                    tax_id: [vm.form.tax_id],
                    owner: [vm.form.owner],
                    contact_person: [vm.form.contact_person],
                    telphone: [vm.form.telphone],
                    address: [vm.form.address],
                    remarks: [vm.form.remarks],
                    // delete_enable: [null],
                    delete_at: [null],
                    creator: [vm.loginData.account],
                    modifier: [vm.loginData.account],
                };
                console.log(params);

                vm.axiosPostAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result.status != 200) {
                            reject(result.data);
                        }
                        if (result.data["Response"] == "ok") {
                            vm.form.cus_id = result.data["nextPRIid"];
                            resolve("ok");
                        } else {
                            reject(result.data["Response"]);
                        }
                    })
                    .catch((response) => {
                        console.log(response);
                        reject(response);
                    });
            });
        },

        _updateCustomer() {
            return new Promise((resolve, reject) => {
                let vm = this;
                let params = {};
                params["methods"] = "PATCH";
                params["whichFunction"] = "update";
                params["database"] = "MySQL";
                params["table"] = "customer";
                params["postdata"] = {
                    old_id: [vm.form.id],
                    cus_name: [vm.form.cus_name],
                    tax_id: [vm.form.tax_id],
                    owner: [vm.form.owner],
                    contact_person: [vm.form.contact_person],
                    telphone: [vm.form.telphone],
                    address: [vm.form.address],
                    remarks: [vm.form.remarks],
                    modifier: [vm.loginData.account],
                };
                console.log(params);

                vm.axiosAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result["Response"] == "ok") {
                            resolve("修改成功");
                        } else {
                            reject(result["Response"]);
                        }
                    })
                    .catch((response) => {
                        console.log(response);
                        reject(response);
                    });
            });
        },

        _queryCusID() {
            let vm = this;
            let params = {};
            vm._reset(["action"]);
            //檢查帳號是否存在
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/TableData";
            params["urlparams"] = {
                table: "customer",
            };
            console.log(params);
            let showmsg = true;
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    let options = [];
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length > 0) {
                            result.data["QueryTableData"].map((ele) => {
                                if (ele.delete_enable == "N") {
                                    options.push({
                                        text: ele.id + " " + ele.cus_name,
                                        value: ele.id,
                                    });
                                    showmsg = false;
                                }
                            });
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                    console.log(options);
                    vm.query_select.options = options;
                    if (vm.query_select.options.length == 0) {
                        vm.setSetTimeOutAlertMsg("查無資料");
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    if (showmsg) {
                        vm.toggleSetTimeOutAlertModal();
                    }
                });
        },

        _queryCustomer() {
            let vm = this;
            let params = {};
            vm._reset(["query_select", "action"]);
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/Interval/customer";
            params["urlparams"] = {
                uid: "@sapido@PaaS",
                attr: "id",
                valueStart: vm.query_select.selected,
                valueEnd: vm.query_select.selected,
            };
            console.log(params);
            let showmsg = true;
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length > 0) {
                            result.data["QueryTableData"].map((ele) => {
                                if (ele.delete_enable == "N") {
                                    vm.cus_items.push({
                                        id: ele.id,
                                        cus_name: ele.cus_name,
                                        tax_id: ele.tax_id,
                                        owner: ele.owner,
                                        contact_person: ele.contact_person,
                                        telphone: ele.telphone,
                                        address: ele.address,
                                        remarks: ele.remarks,
                                    });
                                    showmsg = false;
                                }
                            });
                        }
                        if (vm.cus_items.length == 0) {
                            vm.setSetTimeOutAlertMsg("查無資料");
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    if (showmsg) {
                        vm.toggleSetTimeOutAlertModal();
                    }
                });
        },

        _checkDetail(item) {
            console.log(item);
            let vm = this;
            let params = {};
            //抓取對應帳號
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/Interval/user";
            params["urlparams"] = {
                uid: "@sapido@PaaS",
                attr: "cus_id",
                valueStart: item.id,
                valueEnd: item.id,
            };
            console.log(params);
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        vm.toggleSetTimeOutAlertModal();
                        return;
                    }
                    if (
                        result.data["Response"] == "ok" &&
                        result.data["QueryTableData"].length > 0
                    ) {
                        vm.form = {
                            id: item.id,
                            cus_id: item.id,
                            tax_id: item.tax_id,
                            old_user_id:
                                result.data["QueryTableData"][0]["user_id"],
                            user_id:
                                result.data["QueryTableData"][0]["user_id"],
                            pwd: result.data["QueryTableData"][0]["pwd"],
                            confirmpwd: result.data["QueryTableData"][0]["pwd"],
                            cus_name: item.cus_name,
                            owner: item.owner,
                            contact_person: item.contact_person,
                            telphone: item.telphone,
                            address: item.address,
                            remarks: item.remarks,
                            access_list: JSON.parse(
                                result.data["QueryTableData"][0]["access_list"]
                            ),
                        };
                    }
                })
                .catch((response) => {
                    console.log(response);
                })
                .finally(() => {
                    vm.action = "cud";
                    vm.is_add_update = "update";
                });
        },

        _deleteCustomer(cus_id) {
            console.log(cus_id);
            let vm = this;
            let params = {};
            //抓取對應帳號
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/Customized/Customer/Macro";
            params["postdata"] = {
                cus_id: cus_id,
            };
            console.log(params);
            vm.axiosDeleteAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    if (result.data["Response"] == "ok") {
                        vm.setSetTimeOutAlertMsg("刪除成功");
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    vm.toggleSetTimeOutAlertModal();
                    setTimeout(function () {
                        vm._queryCusID();
                    }, 1000);
                });
        },

        //data Reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
};
</script>

