<template>
    <div id="apitableschema">
        <b-row>
            <b-col md="12" class="text-right">
                <b-form-select
                    v-model="selected"
                    :options="options"
                    style="width: 200px; border-color: #06122a"
                    size="md"
                    class="mr-3 publicText"
                >
                    <template v-slot:first>
                        <b-form-select-option :value="null" disabled
                            >-- 請選擇一個選項 --</b-form-select-option
                        >
                    </template>
                </b-form-select>
                <label for="table" style="font-weight: bold">資料表名稱:</label>
                <b-form-input
                    size="sm"
                    id="table"
                    style="width: 200px; display: inline-block; margin: 10px"
                    v-model="inputtable"
                ></b-form-input>
                <button
                    type="button"
                    class="btn publicButton"
                    @click.prevent="_submit"
                >
                    送出
                </button>
            </b-col>
        </b-row>
        <b-row class="mt-4">
                <!-- sticky-header="500px" -->
            <b-table
                v-if="items.length != 0"
                :items="items"
                :fields="fields"
                responsive
                style="text-align: center"
                class="customize_scrollbar featureContentInside"
            >
                <template v-slot:thead-top>
                    <b-tr>
                        <b-th class="publicTH">資料表名稱</b-th>
                        <b-th>{{ tablename }}</b-th>
                        <b-th class="publicTH">資料表描述</b-th>
                        <b-th colspan="2">{{ tablecomment }}</b-th>
                    </b-tr>
                </template>
            </b-table>
        </b-row>
    </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import modal from "@/components/modal.vue";
// import { childRouter } from "@/router/childRouter";
export default {
    name: "apitableschema",
    data() {
        let vm = this;
        return {
            options: [],
            selected: null,
            inputtable: null,
            tablename: "",
            tablecomment: "",
            //table
            fields: [
                {
                    key: "name",
                    label: "名稱",
                    thClass: "publicTH",
                },
                {
                    key: "type",
                    label: "屬性",
                    thClass: "publicTH",
                },
                {
                    key: "pri",
                    label: "主鍵",
                    thClass: "publicTH",
                },
                {
                    key: "nullable",
                    label: "允許空值",
                    thClass: "publicTH",
                },
                {
                    key: "comment",
                    label: "備註",
                    thClass: "publicTH",
                },
            ],
            items: [],
            db_config: {
                MYSQL: "管理性資料庫-MariaDB",
                MSSQL: "管理性資料庫-MSSQL",
                POSTGRESQL: "串流資料庫",
            },
        };
    },
    created() {
        this._getDBDetail();
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            // system: "apidetail/get_system",
        }),
    },
    components: {},
    methods: {
        ...mapActions({
            axiosGetAction: "commonaxios/axiosGetAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
        }),

        _getDBDetail() {
            let vm = this;
            var params = {};
            params["url"] = "/" + vm.$route.params.SYSTEM + "/dbDetail";

            // let temp = {};
            // Object.entries({
            //     MYSQL: "/api/sapidoSystem/1.0/my/CommonUse/Schema"
            // }).forEach(element => {
            //     temp["value"] = element[1];
            //     temp["text"] = element[0];
            // });
            // vm.options.push(temp);
            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    Object.entries(result.data["dbDetail"]).forEach((element) => {
                        console.log(element);
                        vm.options.push({
                            value: element[1],
                            text: vm.db_config[element[0]],
                        });
                    });
                } else {
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
        },

        _submit() {
            let vm = this;
            console.log(vm.selected);
            console.log(vm.inputtable);
            if (!vm.selected || !vm.inputtable) {
                vm.setSetTimeOutAlertMsg("尚未輸入或選擇");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            vm._reset(["options", "selected", "inputtable"]);
            vm.toggleAlertModal(true);
            let tempurl = vm.selected;
            let re = new RegExp("<tableName>", "g");
            let re2 = new RegExp("<SYSTEM>", "g");
            tempurl = tempurl.replace(new RegExp(re, "g"), vm.inputtable);
            console.log(vm.$route.params.SYSTEM);
            tempurl = tempurl.replace(
                new RegExp(re2, "g"),
                vm.$route.params.SYSTEM
            );
            var params = {};
            params["url"] = tempurl + "?uid=@sapido@PaaS";
            console.log(params);
            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                vm.toggleAlertModal(false);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    vm.tablename = result.data["schemaList"][0]["tablename"];
                    vm.tablecomment = result.data["schemaList"][0]["tablecomment"];
                    result.data["schemaList"].forEach((element) => {
                        vm.items.push({
                            name: element.name,
                            type: element.type,
                            pri: element.primary_key,
                            nullable: element.nullable,
                            comment: element.comment,
                        });
                    });
                } else {
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
};
</script>

