const state = {
    exportData: [],
    exportDataTitle: [],
    exportFileName: ""
};
const getters = {
    get_exportData: state => {
        return state.exportData;
    },
    get_exportDataTitle: state => {
        return state.exportDataTitle;
    },
    get_exportFileName: state => {
        return state.exportFileName;
    }
};
const actions = {
    set_exportData(context, data) {
        context.commit("SET_EXPORTDATA", data);
    },
    set_exportDataTitle(context, data) {
        context.commit("SET_EXPORTDATATITLE", data);
    },
    set_exportFileName(context, data) {
        context.commit("SET_EXPORTFILENAME", data);
    }
};
const mutations = {
    SET_EXPORTDATA(state, data) {
        state.exportData = data;
    },
    SET_EXPORTDATATITLE(state, data) {
        state.exportDataTitle = data;
    },
    SET_EXPORTFILENAME(state, data) {
        state.exportFileName = data;
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
