<script>
import { mapGetters, mapActions } from "vuex";
import { resetRouter } from "@/router/index.js"; //import自定義export function
export default {
    name: "home",
    data() {
        return {
            slidebar: true,
            col_width: 100,
            layout_config: {
                col: {
                    element: [],
                },
            },
            img: {
                operation: require("@/assets/home/operation.png"),
                edoc: require("@/assets/home/edoc.png"),
                logging: require("@/assets/home/logging.png"),
                sensor: require("@/assets/home/sensor.png"),
                mes: require("@/assets/home/mes.png"),
                setting: require("@/assets/home/setting.png"),
                bigdata: require("@/assets/home/bigdata.png"),
            },
            //https://medium.com/@franklion1227/vue-js-%E5%9C%96%E7%89%87%E8%B7%AF%E5%BE%91%E5%95%8F%E9%A1%8C-907d6af6fe88
        };
    },
    components: {},
    computed: {
        ...mapGetters({
            pageAccess: "getlogin/get_pageAccess",
            loginData: "getlogin/get_loginData",
            pageList: "getlogin/get_pageList",
            isLoginMoment: "getlogin/get_isLoginMoment",
            isRoot: "getlogin/get_isRoot",
            DEFAULT_pageList: "getlogin/get_DEFAULT_pageList",
            sideBarIcon: "sidebartree/get_sideBarIcon",
            sideBarStatus: "featuresidebar/get_sideBarStatus",
            // system: "apidetail/get_system",
        }),
    },
    created: function () {
        this._getNow();
        this._handlePageAccess();
        console.log(this.$route.params.SYSTEM);
        console.log(this.pageList);
        console.log(this.pageList["name"].length);
        this.this_page_list_length_flag = this.pageList["name"].length;
        this.this_page_list_length_flag = "other";
        let cols_auto = { cols: "auto", xl: "4", lg: "", md: "4", class: "" };
        let temp = 0;
        for (let i = 0; i < this.pageList["name"].length; i++) {
            console.log(this.pageList["name"][i]);
            this.layout_config.col.element.push(cols_auto);
        }
        console.log(this.layout_config);
        console.log(this.isLoginMoment);
        console.log(this.isRoot);
        console.log(!this.isRoot && this.isLoginMoment);
        if (!this.isRoot && this.isLoginMoment) {
            this.changeIsLoginMoment(false);
            this.$router.push("/" + this.$route.params.SYSTEM + "/mes");
        }
    },
    mounted: function () {
        // this.col_width = this.$refs.sidenav.clientWidth;
        // console.log(this.col_width);
    },
    methods: {
        ...mapActions({
            changeLoginData: "getlogin/change_loginData",
            changeIsRoot: "getlogin/change_isRoot",
            changeIsLoginMoment: "getlogin/change_isLoginMoment",
            setPageList: "getlogin/set_pageList",
            toggleSideBarStatus: "featuresidebar/toggle_sideBarStatus",
            setDate: "getdate/set_Date",
        }),

        _getNow() {
            let vm = this;
            let weekdaysCountConfig = [
                [Number(-6), Number(0)],
                [Number(0), Number(6)],
                [Number(-1), Number(5)],
                [Number(-2), Number(4)],
                [Number(-3), Number(3)],
                [Number(-4), Number(2)],
                [Number(-5), Number(1)],
            ];
            let nowDate = new Date();
            let returnobj = {};
            returnobj = vm._dateFormat(nowDate);
            console.log(returnobj);
            let obj = {};
            obj.now =
                returnobj.weekday +
                ", " +
                returnobj.month +
                "月 " +
                returnobj.day +
                ", " +
                returnobj.year;

            obj.nowFormat =
                returnobj.year + "-" + returnobj.month + "-" + returnobj.day;

            Date.prototype.addDays = function (days) {
                this.setDate(this.getDate() + days);
                return this;
            };

            let thisweekdaystart = new Date();
            thisweekdaystart.addDays(
                weekdaysCountConfig[thisweekdaystart.getDay()][0]
            );
            let thisweekdaystartreturnobj = {};
            thisweekdaystartreturnobj = vm._dateFormat(thisweekdaystart);
            let thisweekdayend = new Date();
            thisweekdayend.addDays(
                weekdaysCountConfig[thisweekdayend.getDay()][1]
            );
            let thisweekdayendreturnobj = {};
            thisweekdayendreturnobj = vm._dateFormat(thisweekdayend);
            obj.thisweekday = [
                thisweekdaystartreturnobj.year +
                    "-" +
                    thisweekdaystartreturnobj.month +
                    "-" +
                    thisweekdaystartreturnobj.day,
                thisweekdayendreturnobj.year +
                    "-" +
                    thisweekdayendreturnobj.month +
                    "-" +
                    thisweekdayendreturnobj.day,
            ];
            vm.setDate(obj);
        },

        _dateFormat(time) {
            let vm = this;
            let weekdays = [
                "星期日",
                "星期一",
                "星期二",
                "星期三",
                "星期四",
                "星期五",
                "星期六",
            ];
            let thisDay = time.getDate();
            if (thisDay < 10) thisDay = "0" + thisDay;
            let thisMonth = time.getMonth() + 1;
            if (thisMonth < 10) thisMonth = "0" + thisMonth;
            return {
                year: time.getFullYear(),
                month: thisMonth,
                day: thisDay,
                weekday: weekdays[time.getDay()],
            };
        },

        _handlePageAccess() {
            let vm = this;
            console.log(vm.pageAccess);
            console.log(vm.DEFAULT_pageList);
            let this_pagelist = JSON.parse(JSON.stringify(vm.DEFAULT_pageList));
            console.log(this_pagelist);
            Object.entries(vm.pageAccess).map((element) => {
                if (!element[1]["authority"]) {
                    let thisindex = this_pagelist["name"].indexOf(element[0]);
                    this_pagelist["name"].splice(thisindex, 1);
                    this_pagelist["trans"].splice(thisindex, 1);
                }
            });
            console.log(this_pagelist);
            vm.setPageList(this_pagelist);
        },

        _checkRouteIsActive: function (item) {
            console.log(item);
            console.log(this.$router.currentRoute.path);
            const pattern = new RegExp("(/" + item + ")", "g");
            return {
                isactive: pattern.test(this.$router.currentRoute.path),
            };
        },

        _logout() {
            var vm = this;
            var obj = {};
            obj.account = null;
            obj.status = false;
            obj.username = null;
            obj.accesslist = null;
            vm.changeLoginData(obj);
            vm.changeIsRoot(false);
            vm.changeIsLoginMoment(false);
            resetRouter();
            vm.$router.push("/index");
        },
    },
    beforeRouteUpdate(to, from, next) {
        if (this.sideBarStatus) {
            this.toggleSideBarStatus();
        }
        next();
    },
};
</script>

