const state = {
    device_name: ""
}
const getters = {
    get_deviceName: state => {
        return state.device_name
    }
}
const actions = {
    set_deviceName(context, String) {
        context.commit('SET_DEVICENAME', String);
    }
}
const mutations = {
    SET_DEVICENAME(state, String) {
        state.device_name = String;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}