<template>
  <div class="machineCamera table-responsive col-12 mb-2">
    <div class="mb-3 text-left">
      <template v-if="machineCameraData.length == 0">
        本機無掛載影像攝相機
      </template>
      <template v-else-if="machineCameraData.length > 1">
        <b-button
          v-for="(item, key) in machineCameraData"
          :key="key"
          style="
            background-color: #474747;
            color: #ffffff;
            border-color: #474747;
          "
          class="mr-2 mt-1"
          @click="(select_tab = key), _changeCamera(item.camera_name)"
          >{{ item.camera_name }}</b-button
        >
        <b-button
          style="
            background-color: #474747;
            color: #ffffff;
            border-color: #474747;
          "
          class="mr-2 mt-1"
          @click="_changeCamera('ALL')"
          >ALL</b-button
        >
      </template>
    </div>
    <div v-if="machineCameraData.length != 0" class="col-12 row p-0 m-0 mb-2">
      <template v-if="allCamera">
        <img
          v-for="(item, key) in machineCameraData"
          :key="key"
          :id="item.camera_name"
          :src="item.url"
          alt="機台關機中"
          class="mr-3 mb-3"
          style="max-width: 100%; border-radius: 0.625rem"
        />
      </template>
      <template v-else>
        <img
          :id="camera_name"
          :src="camera_url"
          alt="機台關機中"
          style="max-width: 100%; border-radius: 0.625rem"
        />
      </template>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "machineCamera",
  data() {
    return {
      allCamera: false,
      camera_name: "",
      camera_url: "",
      select_tab: 0,
      machineCameraDetail: {},
    };
  },
  created: function () {
    let vm = this;
    vm._setMachineCameraData();
  },
  mounted: function () {},
  watch: {
    machineCameraData: {
      handler() {
        let vm = this;
        vm._setMachineCameraData();
      },
    },
  },
  computed: {
    ...mapGetters({
      machineCameraData: "mesMachineCamera/get_machineCameraData",
    }),
  },
  methods: {
    _setMachineCameraData() {
      let vm = this;
      if (vm.machineCameraData.length > 0) {
        vm.machineCameraData.forEach((data) => {
          vm.machineCameraDetail[data.camera_name] = data.url;
        });
        vm.camera_name = vm.machineCameraData[vm.select_tab]["camera_name"];
        vm.camera_url = vm.machineCameraData[vm.select_tab]["url"];
      } else {
        vm.machineCameraDetail = null;
      }
    },

    _changeCamera(data) {
      let vm = this;
      if (data == "ALL") {
        vm.allCamera = true;
      } else {
        vm.allCamera = false;
        vm.camera_name = data;
        vm.camera_url = vm.machineCameraDetail[data];
      }
    },
  },
};
</script>

