<template>
    <div id="apidoc" ref="apidoc" v-if="apiDetail" class="customize_scrollbar">
        <b-row class="apidoc-block">
            <b-col cols="12">
                <div class="text-right mt-1">
                    <button
                        class="btn btn-secondary mb-1 mt-0"
                        type="button"
                        @click="_checkSampleCode()"
                    >
                        範例程式碼
                    </button>
                </div>
            </b-col>
            <b-col cols="12">
                <table class="table">
                    <tr style="background-color: #fcf8e3">
                        <th
                            colspan="5"
                            style="text-align: center; border: 1px solid #000"
                        >
                            HTTP 服務要求
                        </th>
                    </tr>
                    <tr>
                        <td
                            class="tdred"
                            rowspan="2"
                            style="width: 10%; vertical-align: middle"
                        >
                            API服務
                        </td>
                        <td class="tdblue" style="width: 5%">名稱</td>
                        <td style="width: 75%" colspan="3">
                            {{ apiDetail.methods }} {{ apipath_prefix
                            }}{{ apiDetail.url }}
                        </td>
                    </tr>
                    <tr>
                        <td class="tdblue">用途</td>
                        <td colspan="3">
                            {{ apiDetail.doc.API_application }}
                        </td>
                    </tr>
                    <tr>
                        <td
                            class="tdred"
                            rowspan="4"
                            style="width: 10%; vertical-align: middle"
                        >
                            URI資源及參數定義
                        </td>
                        <td style="width: 75%" colspan="4">
                            URI: https://&lt;IP/Host:Port_No&gt;{{
                                apipath_prefix
                            }}{{ apiDetail.url }}?Parameters
                        </td>
                    </tr>
                    <tr>
                        <td colspan="4">
                            "&lt;IP/Host:Port_No&gt;" : 資料服務平台(DPaaS) IP
                            位址/網域名稱
                        </td>
                    </tr>
                    <tr>
                        <td colspan="4" id="uri_parameters">
                            "{{ apipath_prefix }}{{ apiDetail.url }}" :
                            API路徑，目前僅允許superUser ID 可進行此作業
                            <ol v-if="apiDetail.doc.API_path_parameters">
                                <li
                                    v-for="(item, index, key) in apiDetail.doc
                                        .API_path_parameters"
                                    :key="key"
                                >
                                    &lt;{{ index }}&gt;: {{ item }}
                                </li>
                            </ol>
                        </td>
                    </tr>
                    <tr>
                        <td colspan="4" id="parameters">
                            "Parameters"(參數):
                            <ol v-if="apiDetail.doc.API_parameters">
                                <li
                                    v-for="(item, index, key) in apiDetail.doc
                                        .API_parameters"
                                    :key="key"
                                >
                                    {{ index }}: {{ item }}
                                </li>
                            </ol>
                        </td>
                    </tr>
                    <tr id="postdata_tr" v-if="apiDetail.doc.API_postData">
                        <td class="tdred">
                            Data
                            <br />(請參照各種程式語言對http JSON data的包裝)
                        </td>
                        <td colspan="4" id="postdata_td">
                            <ol style="padding-left: 20px">
                                <li>以JSON格式傳送資料。</li>
                                <li>Request Body：</li>
                                <b-tabs content-class="mt-3 mb-2">
                                    <b-tab title="schema" active>
                                        <!-- <div
                                    class="col-12 row text-center mx-0 mt-3 px-0 d-flex justify-content-between table-responsive"
                                    style="font-size: 20px;line-height: 34px;border-top: 1px #ffffff solid;border-bottom: 1px #ffffff solid;"
                                >
                                    <b-table
                                        :items="items"
                                        :fields="fields"
                                        class="operateLog-table-detail operateLog-table-detail-select"
                                    ></b-table>
                                </div>-->
                                        <span class="requestbodytype">{{
                                            apiDetail.doc.API_postData.bodytype
                                        }}</span>
                                        <span class="requestbodyarrow">→</span>
                                        <span class="requestbodyschema">{{
                                            apiDetail.doc.API_postData
                                                .bodyschema
                                        }}</span>
                                        <b-table
                                            hover
                                            borderless
                                            outlined
                                            head-variant="light"
                                            responsive="xl"
                                            :items="items"
                                            :fields="fields"
                                            class="requestbodytable"
                                            :tbody-tr-class="_rowClass"
                                        >
                                            <template
                                                v-slot:cell(show_details)="row"
                                            >
                                                <b-button
                                                    size="sm"
                                                    @click="row.toggleDetails"
                                                    class="mr-2"
                                                    >{{
                                                        row.detailsShowing
                                                            ? "Hide"
                                                            : "Show"
                                                    }}
                                                    Details</b-button
                                                >
                                            </template>

                                            <template v-slot:row-details="row">
                                                <b-card>
                                                    <b-row class="mb-2">
                                                        <b-col
                                                            class="text-sm-left"
                                                        >
                                                            <jsonView
                                                                :data="
                                                                    row.item
                                                                        .detail
                                                                "
                                                                :theme="''"
                                                                :deep="3"
                                                                :iconStyle="'triangle'"
                                                                :fontSize="15"
                                                                :lineHeight="24"
                                                                :closed="false"
                                                                :iconColor="[]"
                                                            />
                                                        </b-col>
                                                    </b-row>
                                                    <b-button
                                                        size="sm"
                                                        @click="
                                                            row.toggleDetails
                                                        "
                                                        >Hide Details</b-button
                                                    >
                                                </b-card>
                                            </template>
                                        </b-table>
                                    </b-tab>
                                    <b-tab
                                        :title="'example' + (index + 1)"
                                        v-for="(item, index) in apiDetail.doc
                                            .API_postData.example"
                                        :key="index"
                                        style
                                    >
                                        <jsonView
                                            :data="item"
                                            :theme="''"
                                            :deep="3"
                                            :iconStyle="'triangle'"
                                            :fontSize="15"
                                            :lineHeight="24"
                                            :closed="false"
                                            :iconColor="[]"
                                        />
                                    </b-tab>
                                </b-tabs>
                                <template
                                    v-if="
                                        apiDetail.doc.API_postData.precautions
                                    "
                                >
                                    <li>Description：</li>
                                    <jsonView
                                        :data="
                                            apiDetail.doc.API_postData
                                                .precautions
                                        "
                                        :theme="''"
                                        :deep="3"
                                        :iconStyle="'triangle'"
                                        :fontSize="15"
                                        :lineHeight="24"
                                        :closed="false"
                                        :iconColor="[]"
                                    />
                                </template>
                            </ol>
                        </td>
                    </tr>
                    <tr class="tdgreen">
                        <th
                            colspan="5"
                            style="text-align: center; border: 1px solid #000"
                        >
                            HTTP 服務回覆訊息
                        </th>
                    </tr>
                    <tr>
                        <td class="tdred">格式型態</td>
                        <td colspan="4">JSON</td>
                    </tr>
                    <tr id="msgParameters">
                        <td
                            class="tdred"
                            :rowspan="
                                msgParametersrowspan +
                                Object.keys(
                                    apiDetail.doc.API_message_parameters
                                ).length
                            "
                            style="width: 10%; vertical-align: middle"
                        >
                            訊息參數
                        </td>
                        <td class="tdblue tdcolspan2" colspan="2">參數名稱</td>
                        <td class="tdblue">資料型態</td>
                        <td class="tdblue">備註</td>
                    </tr>
                    <tr>
                        <td colspan="2" class="tdcolspan2">APIS</td>
                        <td>string</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td colspan="2" class="tdcolspan2">OperationTime</td>
                        <td>string</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td colspan="2" class="tdcolspan2">BytesTransferred</td>
                        <td>int</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td colspan="2" class="tdcolspan2">System</td>
                        <td>string</td>
                        <td></td>
                    </tr>
                    <tr id="msgParameters_response">
                        <td colspan="2" class="tdcolspan2">Response</td>
                        <td>string</td>
                        <td></td>
                    </tr>
                    <tr
                        v-for="(item, index, key) in apiDetail.doc
                            .API_message_parameters"
                        :key="key"
                    >
                        <td colspan="2" class="tdcolspan2">{{ index }}</td>
                        <template v-if="item.split('+').length > 1">
                            <td>{{ item.split("+")[0] }}</td>
                            <td>{{ item.split("+")[1] }}</td>
                        </template>
                        <template v-else>
                            <td>{{ item.split("+")[0] }}</td>
                            <td></td>
                        </template>
                    </tr>
                    <tr>
                        <td class="tdred">範例</td>
                        <td colspan="4" id="API_example">
                            <jsonView
                                :data="apiDetail.doc.API_example"
                                :theme="''"
                                :deep="3"
                                :iconStyle="'triangle'"
                                :fontSize="15"
                                :lineHeight="24"
                                :closed="false"
                                :iconColor="[]"
                            />
                        </td>
                    </tr>
                </table>
            </b-col>
            <modal>
                <template v-slot:modalheader>
                    <h5>範例程式碼</h5>
                </template>
                <template v-slot:default>
                    <div>
                        <b-tabs content-class="mt-3">
                            <b-tab title="PYTHON" active>
                                <pre
                                    class="line-numbers"
                                ><code class="language-python"># -*- coding: utf-8 -*-
import requests
import json

headers = {'Content-type': 'application/json'}
params = {'uid':'@sapido@PaaS'}

#GET
response = requests.get("https://IP:23687/APIURL", params=params, verify=False, headers=headers)

testdata = {"power_light":0,"in_lube":0,"overload":0,"fnt_sf_pin":0,"sf_door":0}
data_json = json.dumps({"old_device_name":["test"],"machine_detail":[json.dumps(testdata)]})
#PATCH
response = requests.patch("https://IP:23687/APIURL", params=params, verify=False, data=data_json, headers=headers)

#POST
response = requests.post("https://IP:23687/APIURL", params=params, verify=False, data=data_json, headers=headers)

#DELETE
response = requests.delete("https://IP:23687/APIURL", params=params, verify=False, data=data_json, headers=headers)

response_dic = response.json()
print(response_dic["Response"])</code></pre>
                            </b-tab>
                            <b-tab title="PHP">
                                <pre
                                    class="line-numbers"
                                ><code class="language-php">$url = "https://IP:23687/APIURL";
$data = array(
    "timeflag" => array(
        "updated_at" => ['1980-01-01 08:00:00', $delete_end_time]
    )   
);  

$options = array(
    'http' => array(
        'method' => 'DELETE',
        'content' => json_encode($data),
        'header' => "Content-Type: application/json\r\n" .
        "Accept: application/json\r\n"
    ),  
    //忽略認證
    "ssl"=>array(
        "verify_peer"=>false,
        "verify_peer_name"=>false,
    ),  
);  
$context = stream_context_create($options);
$result = file_get_contents($url, false, $context);
$Arr = json_decode($result, true);</code></pre>
                            </b-tab>
                            <b-tab title="JAVA">
                                <pre
                                    class="line-numbers"
                                ><code class="language-java">import java.io.BufferedReader;
import java.io.InputStreamReader;
import java.io.Reader;
import java.net.URL;
import java.net.URLConnection;
import java.security.cert.X509Certificate;
import javax.net.ssl.HostnameVerifier;
import javax.net.ssl.HttpsURLConnection;
import javax.net.ssl.SSLContext;
import javax.net.ssl.SSLSession;
import javax.net.ssl.TrustManager;
import javax.net.ssl.X509TrustManager;
public class HelloWorld{
    public static void main(String []args){
        System.out.println("Hello World");
        
        // Create a trust manager that does not validate certificate chains
        TrustManager[] trustAllCerts = new TrustManager[] { 
            new X509TrustManager() {
                public java.security.cert.X509Certificate[] getAcceptedIssuers() {
                    return null;
                }//end of java.security.cert.X509Certificate[] getAcceptedIssuers
                public void checkClientTrusted(X509Certificate[] certs, String authType) {
                }//end of checkClientTrusted()
                public void checkServerTrusted(X509Certificate[] certs, String authType) {
                }//end of checkServerTrusted()
            }//end of  X509TrustManager()
            
        };//end of rustManager
        
        // Install the all-trusting trust manager
        try{
            SSLContext sc = SSLContext.getInstance("SSL");
            sc.init(null, trustAllCerts, new java.security.SecureRandom());
            HttpsURLConnection.setDefaultSSLSocketFactory(sc.getSocketFactory());
            // Create all-trusting host name verifier
            HostnameVerifier allHostsValid = new HostnameVerifier() {
                public boolean verify(String hostname, SSLSession session) {
                    return true;
                }
            };
    
            // Install the all-trusting host verifier
            HttpsURLConnection.setDefaultHostnameVerifier(allHostsValid);
            
            URL url = new URL("https://IP:23687/APIURL");
            URLConnection con = url.openConnection();
            final Reader reader = new InputStreamReader(con.getInputStream());
            final BufferedReader br = new BufferedReader(reader);        
            String line = "";
            while ((line = br.readLine()) != null) {
                System.out.println(line);
            }        
            br.close();
        } catch (Exception exc) {
            
        }    
    }//end of  main
}</code></pre>
                            </b-tab>
                        </b-tabs>
                    </div>
                </template>
                <template v-slot:modalfooter>
                    <div class="w-100">
                        <b-button
                            variant="success"
                            size="sm"
                            class="float-right"
                            style="margin-right: 10px"
                            @click="toggleCommonModal(false)"
                            >close</b-button
                        >
                    </div>
                </template>
            </modal>
        </b-row>
    </div>
</template>

<script>
import jsonView from "@/components/json-view/index.vue";
import modal from "@/components/modal.vue";
import Prism from "../../../public/static/js/prism";
import Clipboard from "clipboard";
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
export default {
    name: "apidoc",
    data() {
        let vm = this;
        return {
            endpoint: vm.$route.params.endpoint, //抓取動態路由
            msgParametersrowspan: 6,
            apipath_prefix: "/api/",
            exclude_postdata: [
                "bodytype",
                "bodyschema",
                "example",
                "precautions",
            ],
            fields: [
                {
                    key: "Name",
                    sortable: true,
                },
                {
                    key: "Type",
                    sortable: true,
                },
                {
                    key: "Directions",
                    sortable: false,
                },
                {
                    key: "Example",
                    sortable: false,
                },
                {
                    key: "Requirement",
                    sortable: true,
                },
            ],
            items: [],
            need_show_detail: {},
        };
    },
    computed: {
        ...mapGetters({
            apiDetail: "apidetail/get_apiDetail",
            axiosResult: "commonaxios/get_axiosResult",
            // system: "apidetail/get_system",
            DEFAULT_commonModalConfig: "usemodal/get_DEFAULT_commonModalConfig",
        }),
    },
    components: {
        jsonView,
        modal,
    },
    created() {
        this._checkApiDetailExist();
        // this.apipath_prefix += this.$route.params.SYSTEM;
        console.log(this.$router);
        console.log(this.$router.currentRoute.matched);
        console.log(this.endpoint);
    },
    mounted() {
        // var VueScrollTo = require("vue-scrollto");
        // var options = {
        //     container: "#apidoc",
        //     easing: "ease-in",
        //     offset: -60,
        //     force: true,
        //     cancelable: true,
        //     onStart: function (element) {
        //         // scrolling started
        //     },
        //     onDone: function (element) {
        //         // scrolling is done
        //     },
        //     onCancel: function () {
        //         // scrolling has been interrupted
        //     },
        //     x: false,
        //     y: true,
        // };
        // var cancelScroll = VueScrollTo.scrollTo("#apidoc", options);
        // // or alternatively inside your components you can use
        // // cancelScroll = this.$scrollTo(element, duration, options);
        // // to cancel scrolling you can call the returned function
        // cancelScroll();
        // // this.$scrollTo("#apidoc");
    },
    methods: {
        ...mapActions({
            axiosGetAction: "commonaxios/axiosGetAction",
            setApiDetail: "apidetail/set_apiDetail",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleCommonModal: "usemodal/toggle_commonModal",
            setCommonModalConfig: "usemodal/set_commonModalConfig",
        }),

        _checkApiDetailExist() {
            let vm = this;
            vm.toggleAlertModal(true);
            console.log(vm.apiDetail);
            if (Object.keys(vm.apiDetail).length === 0) {
                console.log(vm.$route.params.SYSTEM);
                console.log(vm.endpoint);

                var params = {};
                params["url"] = "/edoc/" + vm.endpoint;
                console.log(params);

                vm.axiosGetAction(params).then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        vm.toggleSetTimeOutAlertModal();
                        return;
                    }
                    if (result.data["Response"] == "ok") {
                        vm.setApiDetail(result.data["api"]);
                        vm._exclude();
                        vm.toggleAlertModal(false);
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                        vm.toggleSetTimeOutAlertModal();
                    }
                });
            } else {
                vm._exclude();
                vm.toggleAlertModal(false);
            }
        },

        _exclude() {
            let vm = this;
            console.log(vm.fields);
            if (vm.apiDetail.doc.hasOwnProperty("API_postData")) {
                let thisdata = JSON.parse(
                    JSON.stringify(vm.apiDetail.doc.API_postData)
                );
                console.log(thisdata);
                Object.keys(thisdata).forEach((element) => {
                    console.log(element);
                    if (this.exclude_postdata.includes(element)) {
                        delete thisdata[element];
                    }
                });
                let check_parameters = {};
                if (thisdata.hasOwnProperty("parameters")) {
                    check_parameters = thisdata["parameters"];
                } else {
                    check_parameters = thisdata;
                }
                Object.entries(check_parameters).forEach((item) => {
                    console.log(item);
                    let temp = {};
                    //判斷directions是否有Detail
                    if (typeof item[1].directions === "string") {
                        temp = {
                            Name: item[0],
                            Type: item[1].type,
                            Directions: item[1].directions,
                            Example: item[1].example,
                            Requirement: item[1].requirement,
                            status: false,
                        };
                    } else {
                        if (vm.fields[3].key === "Example") {
                            vm.fields.splice(3, 0, { key: "show_details" });
                        }
                        temp = {
                            Name: item[0],
                            Type: item[1].type,
                            Directions: item[1].directions[0],
                            Example: item[1].example,
                            Requirement: item[1].requirement,
                            status: true,
                            detail: item[1].directions[1],
                        };
                    }
                    console.log(temp);
                    vm.items.push(temp);
                });
            }
        },

        _rowClass(item, type) {
            console.log(item);
            console.log(type);
            if (!item || type !== "row") return;
            console.log(item.status);
            if (!item.status) return "hide_showdetail";
        },

        _checkSampleCode() {
            let vm = this;
            vm.toggleCommonModal(true);
            let commonModalConfig = JSON.parse(
                JSON.stringify(vm.DEFAULT_commonModalConfig)
            );
            console.log(commonModalConfig);
            commonModalConfig.size = "xl";
            commonModalConfig.modalClassFull = true;
            vm.setCommonModalConfig(commonModalConfig);
            setTimeout(() => {
                Prism.highlightAll(); // 这里加定时器让它后执行，不然没效果
                vm._setCopyBtn(); //增加tempcopybtn以讓chrome可以執行複製功能
            }, 0);
        },

        _setCopyBtn() {
            //https://codepen.io/wilbo/pen/xRVLOj?editors=1111
            //
            // al pre tags on the page
            const pres = document.getElementsByTagName("pre");
            // //
            console.log(pres);
            // // reformat html of pre tags
            if (pres !== null) {
                for (let i = 0; i < pres.length; i++) {
                    console.log(pres[i]);
                    let node = document.createElement("div");
                    let textnode = document.createTextNode("tempcopy");
                    node.className = "tempcopy";
                    node.style.cssText = "display:none";
                    node.appendChild(textnode);
                    pres[i].appendChild(node);
                }
            }
            //
            // create clipboard for every copy element
            const clipboard = new Clipboard(".copy", {
                target: (trigger) => {
                    return trigger.parentElement.parentElement
                        .previousElementSibling.children[1]
                        .previousElementSibling;
                },
            });
            console.log(clipboard);
            //
            // do stuff when copy is clicked
            clipboard.on("success", (event) => {
                console.log(event);
                event.trigger.textContent = "copied!";
                setTimeout(() => {
                    event.clearSelection();
                    event.trigger.textContent = "copy";
                }, 2000);
            });
        },
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="../../../public/static/css/api_docs.css"></style>
<style scoped>
.page-header {
    padding-bottom: 9px;
    margin: 25px 0 20px;
    border-bottom: 1px solid #eee;
}
.tdred {
    background-color: #f2dede;
}
.tdblue {
    background-color: #d9edf7;
}
.tdgreen {
    background-color: #dff0d8;
}
td ol {
    margin-bottom: 0;
}
.tdcolspan2 {
    width: 30%;
}
.requestbodyarrow {
    color: rgb(230, 146, 188);
    font-weight: bold;
}
.requestbodytype,
.requestbodyschema {
    color: rgb(248, 66, 157);
    font-weight: bold;
    font-size: 18px;
}
.requestbodyarrow,
.requestbodyschema {
    margin-left: 10px;
}
.customize_scrollbar::-webkit-scrollbar-track {
    background-color: white !important;
}
</style>