<template>
    <div id="Operation">
        <b-row
            class="h-100 mh-100 customize_scrollbar"
            style="overflow-y: auto"
        >
            <b-container fluid class="h-100 mh-100 publicMinWidth550">
                <b-row>
                    <b-col xl="3" lg="3" md="12" sm="12" class="title"
                        >API用量
                        <chart
                            :chartId="apiChart.name.chartId"
                            :chartName="apiChart.name.chartName"
                            :styleGroup="apiChart.styleGroup"
                            :chartData="apiChart.options[0].value.data"
                        ></chart>
                    </b-col>
                    <b-col xl="9" lg="9" md="12" sm="12" class="title"
                        >機台狀況
                        <chart
                            :chartId="MachineChart.name.chartId"
                            :chartName="MachineChart.name.chartName"
                            :styleGroup="MachineChart.styleGroup"
                            :chartData="MachineChart.options[0].value.data"
                        ></chart>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col xl="4" lg="4" md="12" sm="12" class="title"
                        >合約時間
                        <chart
                            :chartId="'Time'"
                            :chartName="TimeChart.name.chartName"
                            :styleGroup="TimeChart.styleGroup"
                            :chartData="TimeChart.options[0].value.data"
                        ></chart>
                    </b-col>
                    <b-col xl="4" lg="4" md="12" sm="12" class="title">
                        邏輯CPU使用率
                        <chart
                            :chartId="CPUChart.name.chartId"
                            :chartName="CPUChart.name.chartName"
                            :styleGroup="CPUChart.styleGroup"
                            :chartData="CPUChart.options[0].value.data"
                        ></chart>
                    </b-col>
                    <b-col xl="4" lg="4" md="12" sm="12" class="title">
                        實體RAM使用概況
                        <chart
                            :chartId="'EntityRAM'"
                            :chartName="EntityRAM.name.chartName"
                            :styleGroup="EntityRAM.styleGroup"
                            :chartData="EntityRAM.options[0].value.data"
                        ></chart>
                        虛擬RAM使用概況
                        <chart
                            :chartId="'SwapRAM'"
                            :chartName="SwapRAM.name.chartName"
                            :styleGroup="SwapRAM.styleGroup"
                            :chartData="SwapRAM.options[0].value.data"
                        ></chart>
                        DISK容量
                        <chart
                            :chartId="'DISK'"
                            :chartName="DISKChart.name.chartName"
                            :styleGroup="DISKChart.styleGroup"
                            :chartData="DISKChart.options[0].value.data"
                        ></chart>
                    </b-col>
                </b-row>
            </b-container>
        </b-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import chart from "@/components/MES/chart.vue";
export default {
    name: "Operation",
    data() {
        let vm = this;
        return {
            table_notfound: true,
            options: [],
            selected: null,
            frequency_fields: [
                {
                    key: "fail_counts",
                    label: "失敗呼叫次數",
                    thClass: "publicTH",
                },
                {
                    key: "success_counts",
                    label: "成功呼叫次數",
                    thClass: "publicTH",
                },
                {
                    key: "success_averagetime",
                    label: "成功呼叫的平均回覆時間(s)",
                    thClass: "publicTH",
                },
                {
                    key: "upload_time",
                    label: "最新一次呼叫時間",
                    thClass: "publicTH",
                },
            ],
            frequency_items: [],
            detail_fields: [
                {
                    key: "upload_time",
                    label: "記錄時間",
                    thClass: "publicTH",
                    sortable: true,
                },
                {
                    key: "method",
                    label: "HTTP請求方法",
                    thClass: "publicTH",
                    sortable: true,
                },
                {
                    key: "apipath",
                    label: "API路徑",
                    thClass: "publicTH",
                    sortable: false,
                },
                {
                    key: "operation_time",
                    label: "API執行時間",
                    thClass: "publicTH",
                    sortable: false,
                },
                {
                    key: "status",
                    label: "狀態",
                    thClass: "publicTH",
                    sortable: true,
                },
            ],
            apiChart: {
                name: {
                    chartId: "api", //英文
                    chartName: "api用量", //中文
                },
                options: [
                    {
                        text: "api用量", //中文
                        value: {
                            name: "api", //英文
                            data: {
                                senTime: [],
                                senData: {},
                                senType: "API", //圖表類型
                            },
                        },
                    },
                ],
                styleGroup: { width: "100%", height: "450px" },
            },
            MachineChart: {
                name: {
                    chartId: "Machine", //英文
                    chartName: "Machine狀態", //中文
                },
                options: [
                    {
                        text: "Machine用量", //中文
                        value: {
                            name: "Machine", //英文
                            data: {
                                senTime: [],
                                senData: {},
                                senType: "Machine", //圖表類型
                            },
                        },
                    },
                ],
                styleGroup: { width: "100%", height: "450px" },
            },
            CPUChart: {
                name: {
                    chartId: "cpu_usage", //英文
                    chartName: "CPU使用率", //中文
                },
                options: [
                    {
                        text: "CPU使用率", //中文
                        value: {
                            name: "CPU", //英文
                            data: {
                                yAxisData: [],
                                seriesData: [],
                                senType: "CPU", //圖表類型
                            },
                        },
                    },
                ],
                styleGroup: { width: "100%", height: "450px" },
            },
            DISKChart: {
                name: {
                    chartId: "DISK", //英文
                    chartName: "DISK用量", //中文
                },
                options: [
                    {
                        text: "DISK用量", //中文
                        value: {
                            name: "DISK", //英文
                            data: {
                                senTime: [],
                                senData: {},
                                senType: "DISK", //圖表類型
                            },
                        },
                    },
                ],
                styleGroup: { width: "100%", height: "225px" },
            },
            EntityRAM: {
                name: {
                    chartId: "EntityRAM", //英文
                    chartName: "實體RAM使用概況", //中文
                },
                options: [
                    {
                        text: "實體RAM使用概況", //中文
                        value: {
                            name: "EntityRAM", //英文
                            data: {
                                yAxisData: ["Mem"],
                                legendData: [
                                    "used",
                                    "buffers",
                                    "cached",
                                    "shared",
                                    "available",
                                ],
                                seriesData: [],
                                senType: "RAM", //圖表類型
                            },
                        },
                    },
                ],
                styleGroup: { width: "100%", height: "150px" },
            },
            SwapRAM: {
                name: {
                    chartId: "SwapRAM", //英文
                    chartName: "虛擬RAM使用概況", //中文
                },
                options: [
                    {
                        text: "虛擬RAM使用概況", //中文
                        value: {
                            name: "SwapRAM", //英文
                            data: {
                                yAxisData: ["Swap"],
                                legendData: ["used", "free"],
                                seriesData: [],
                                senType: "RAM", //圖表類型
                            },
                        },
                    },
                ],
                styleGroup: { width: "100%", height: "150px" },
            },
            TimeChart: {
                name: {
                    chartId: "Time", //英文
                    chartName: "Time用量", //中文
                },
                options: [
                    {
                        text: "Time用量", //中文
                        value: {
                            name: "Time", //英文
                            data: {
                                senTime: [],
                                senData: {},
                                senType: "Time", //圖表類型
                            },
                        },
                    },
                ],
                styleGroup: { width: "100%", height: "450px" },
            },
            detail_items: [],
            detail_height: "",
            timer_get_cpu: null,
        };
    },
    created() {
        console.log(this.$router.currentRoute.path);
        console.log(this.$route);
        this._getServerProcessesUsage();
    },
    mounted() {
        this.timer_get_cpu = setInterval(() => {
            this._getServerProcessesUsage();
        }, 3000);
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            // system: "apidetail/get_system",
        }),
    },
    components: {
        chart,
    },
    methods: {
        ...mapActions({
            axiosGetAction: "commonaxios/axiosGetAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
        }),

        _getServerProcessesUsage() {
            let vm = this;
            var params = {};
            params["url"] = "/api/PaaS/1.0/sys/Monitor/Server/Processes/Usage";

            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    console.log(result.data["Information"]["cpu_usage"]);
                    console.log(
                        Array.from(
                            Array(
                                result.data["Information"]["cpu_usage"].length
                            ).keys(),
                            (x) => x + 1
                        )
                    );
                    //https://stackoverflow.com/questions/36947847/how-to-generate-range-of-numbers-from-0-to-n-in-es2015-only

                    //CPU
                    vm.CPUChart.options[0].value.data.yAxisData = Array.from(
                        Array(result.data["Information"]["cpu_usage"].length).keys(),
                        (x) => x + 1
                    );
                    vm.CPUChart.options[0].value.data.seriesData =
                        result.data["Information"]["cpu_usage"];

                    //RAM
                    vm.EntityRAM.options[0].value.data.seriesData = [];
                    vm.EntityRAM.options[0].value.data.legendData.map(
                        (element) => {
                            vm.EntityRAM.options[0].value.data.seriesData.push({
                                name: element,
                                type: "bar",
                                stack: "total",
                                // color: "#4E84BA",
                                label: {
                                    show: true,
                                },
                                emphasis: {
                                    focus: "series",
                                },
                                data: [
                                    result.data["Information"]["memory_virtual"][
                                        element
                                    ],
                                ],
                            });
                        }
                    );
                    vm.SwapRAM.options[0].value.data.seriesData = [];
                    vm.SwapRAM.options[0].value.data.legendData.map(
                        (element) => {
                            vm.SwapRAM.options[0].value.data.seriesData.push({
                                name: element,
                                type: "bar",
                                stack: "total",
                                // color: "#4E84BA",
                                label: {
                                    show: true,
                                },
                                emphasis: {
                                    focus: "series",
                                },
                                data: [
                                    result.data["Information"]["memory_swap"][
                                        element
                                    ],
                                ],
                            });
                        }
                    );
                } else {
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
        },

        _getApiCountedTable() {
            let vm = this;
            var params = {};
            console.log(vm.$route.params.SYSTEM);
            params["url"] =
                "/api/PaaS/1.0/ps/ApiCountedTable/" + vm.$route.params.SYSTEM;

            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    result.data["ApiCountedTable"].map((element) => {
                        if (element.split("_").length == 3) {
                            vm.options.push(element.split("_")[2]);
                        }
                    });
                    vm.options.sort(function (a, b) {
                        return b - a;
                    });
                    if (vm.options.length != 0) {
                        vm.table_notfound = false;
                    }
                } else {
                    vm.table_notfound = true;
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
        },

        _getApiCountedTableData() {
            let vm = this;
            vm.frequency_items = [];
            vm.detail_items = [];
            console.log(vm.selected);
            let tablename =
                "api_" + vm.$route.params.SYSTEM + "_" + vm.selected;
            if (vm.selected === null) {
                vm.setSetTimeOutAlertMsg("必須選擇一個選項");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            vm.toggleAlertModal(true);
            console.log(tablename);

            var params = {};
            console.log(vm.$route.params.SYSTEM);
            params["url"] = "/api/PaaS/1.0/ps/ApiCountedTableData/" + tablename;

            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                vm.toggleAlertModal(false);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    result.data["ApiCountedTableData"].map((element) => {
                        vm.frequency_items.push({
                            fail_counts: element.fail_counts,
                            upload_time: element.upload_time,
                            success_averagetime: element.success_averagetime,
                            success_counts: element.success_counts,
                        });
                    });
                } else {
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
        },

        _onRowClicked(items, index, event) {
            var vm = this;
            console.log(vm.selected);

            vm.detail_items = [];
            let tablename =
                "api_" +
                vm.$route.params.SYSTEM +
                "_" +
                vm.selected +
                "_record";
            if (vm.selected === null) {
                vm.setSetTimeOutAlertMsg("必須選擇一個選項");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            vm.toggleAlertModal(true);
            console.log(tablename);

            var params = {};
            console.log(vm.$route.params.SYSTEM);
            params["url"] = "/api/PaaS/1.0/ps/ApiCountedTableData/" + tablename;

            let _status = "成功";
            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                vm.toggleAlertModal(false);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    result.data["ApiCountedTableData"].map((element) => {
                        if (!element.status) _status = "失敗";
                        vm.detail_items.push({
                            method: element.method,
                            apipath: element.apipath,
                            status: _status,
                            operation_time: element.operation_time.toFixed(6),
                            upload_time: element.upload_time,
                        });
                    });
                } else {
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
    //調用router守衛，在離開時將this_click reset
    beforeRouteLeave(to, from, next) {
        clearInterval(this.timer_get_cpu);
        this._reset([]);
        next();
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rightblock {
    max-height: calc(100vh - 150px);
    overflow-y: auto;
    padding-top: 10px;
}
.title {
    color: #40576e;
    font-weight: bold;
    font-size: 24px;
    box-shadow: 0 2px 15px rgb(0 0 0 / 5%), 0 1px 6px rgb(0 0 0 / 5%);
    /* padding-right: 1px !important; */
    padding: 1rem 0.5rem 0 0.5rem;
}
.detail_table {
    max-height: calc(100vh - 400px);
}
.card,
.card-header {
    border: 0;
}
.apipath {
    color: #007bff;
    cursor: pointer;
}
.apimethod {
    font-weight: bold;
    min-width: 60px;
    /* color: green; */
}
.bcardtext {
    display: flex;
    flex-direction: row;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    align-items: center;
    height: 36px;
    min-height: 36px;
    margin: 5px;
    padding: 5px;
}
.leftblock {
    background-color: #f4f4f4;
}
.leftblock .publicText {
    font-size: 20px;
}
.leftblock p:hover,
.insidebtn:hover {
    background-color: #eeebeb !important;
}
.titlechoose {
    color: #aab4d1 !important;
}
.insidebtn:focus {
    box-shadow: unset !important;
}
.inside_click {
    border: 2px solid #aab4d1 !important;
    border-radius: 8px !important;
    /* color: black !important; */
}
@media (max-width: 1200px) {
    .leftblock .row {
        margin-top: 0 !important;
    }
    .leftblock {
        height: fit-content !important;
    }
}
</style>
