const state = {
    bucketUseInformationFields: [
        {
            key: "code",
            label: "流程卡號",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "furnaceCode",
            label: "爐號",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "screwWeight",
            label: "重量",
            sortable: false,
            thClass: "publicTH"
        }
    ],
    bucketUseInformationItem: [],
    bucketUseInformationTotalWeight: 0
}
const getters = {
    get_bucketUseInformationFields: state => {
        return state.bucketUseInformationFields
    },
    get_bucketUseInformationItem: state => {
        return state.bucketUseInformationItem
    },
    get_bucketUseInformationTotalWeight: state => {
        return state.bucketUseInformationTotalWeight
    },
}
const actions = {
    set_bucketUseInformationFields(context, array) {
        context.commit('SET_BUCKETUSEINFORMATIONFIELDS', array);
    },
    set_bucketUseInformationItem(context, array) {
        context.commit('SET_BUCKETUSEINFORMATIONITEM', array);
    },
    clear_bucketUseInformation(context) {
        context.commit('CLEAR_BUCKETUSEINFORMATION');
    },
}
const mutations = {
    SET_BUCKETUSEINFORMATIONFIELDS(state, array) {
        state.bucketUseInformationItem = array;
    },
    SET_BUCKETUSEINFORMATIONITEM(state, array) {
        state.bucketUseInformationItem = array;
        let bucketUseInformationTotalWeight = 0;
        for (let i = 0; i < array.length; i++) {
            bucketUseInformationTotalWeight += Number(array[i]['screwWeight']);
        }
        state.bucketUseInformationTotalWeight = bucketUseInformationTotalWeight.toFixed(2);
    },
    CLEAR_BUCKETUSEINFORMATION(state) {
        state.bucketUseInformationItem = [];
        state.bucketUseInformationTotalWeight = 0;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
