<template>
  <div id="abnormalquery" class="abnormalquery">
    <b-row>
      <b-col
        cols="12"
        class="p-0 m-0 mt-4 text-center"
        style="font-size: 1.75rem; color: #000000; margin-top: 3rem"
      >
        <div class="m-auto">異常組合查詢</div>
      </b-col>
    </b-row>
    <b-row>
      <b-col cols="12">
        <b-row>
          <div class="mx-2" style="width: 9rem">
            <p class style="margin-bottom: 0.625rem">機台型號</p>
            <b-form-select
              v-model="modelSelected"
              :options="modelOptions"
              class=""
              style="
                max-width: 9rem;
                height: 2.375rem;
                line-height: 1.375rem;
                font-size: 0.875rem;
                border: 2px solid rgb(255, 255, 255);
                box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 3px 0px;
                color: rgb(95, 95, 95);
              "
            >
              <template #first>
                <b-form-select-option :value="null" disabled
                  >-- 選擇機型 --</b-form-select-option
                >
              </template>
            </b-form-select>
          </div>
          <div class="mx-2" style="width: 4.25rem">
            <b-button
              v-if="!modifyMod"
              class="p-0"
              style="
                margin-top: 38px;
                background-color: #536172;
                border-color: #536172;
                width: 4.25rem;
                height: 2rem;
                font-size: 1.125rem;
              "
              @click="_queryAbnormal()"
              >查詢</b-button
            >
          </div>
        </b-row>
        <b-row>
          <b-table
            :items="abnormalCombine"
            :fields="fields"
            class="text-center abnormal-query-table mt-4"
          >
            <template #cell(status)="data">
              <div v-if="data.index === modifyItem">
                <div
                  v-for="(item, index) in modifyStatus"
                  :key="index"
                  style="border-radius: 5px"
                  :style="{
                    border: item.choice ? 'solid 1px #536172' : 'none',
                  }"
                  @click="_choiceSensor(index)"
                >
                  <div style="display: inline-block; vertical-align: middle">
                    <div style="display: inline-block; margin: 5px">
                      <b-select
                        :options="item.sensorOptions"
                        v-model="modifyStatus[index].sensorCode"
                        @change="_removeRepeatOption()"
                      >
                      </b-select>
                    </div>
                    <div style="display: inline-block;margin: 5px;">
                      <b-select
                        :options="item.statusOptions"
                        v-model="modifyStatus[index].sensorSetting"
                      ></b-select>
                    </div>
                  </div>
                </div>
                <div
                  style="color: #c10000; font-size: 12px"
                  v-if="repeatPrompt"
                >
                  組合重複，請重新修改
                </div>
              </div>
              <div v-else>
                <span
                  v-for="(item, index) in abnormalCombine[data.index].status"
                  :key="index"
                  style="margin: 5px"
                >
                  {{ item }}
                </span>
              </div>
            </template>
            <template #cell(adjustbtn)="data">
              <div
                v-if="data.index == modifyItem"
                class="adjust-value-btn"
                @click="_adjustOptions('reduce')"
              >
                -
              </div>
              <div
                v-if="data.index == modifyItem"
                class="adjust-value-btn"
                @click="_adjustOptions('add')"
              >
                +
              </div>
            </template>
            <template #cell(reason)="data" v-if="modifyMod">
              <div v-if="data.index == modifyItem">
                <b-form-textarea
                  v-model="modifyReasonText"
                  rows="5"
                  :class="{
                    'warning-border': warnClass.reason,
                  }"
                ></b-form-textarea>
              </div>
              <div v-else>
                {{ data.item.reason }}
              </div>
            </template>
            <template #cell(solution)="data" v-if="modifyMod">
              <div v-if="data.index == modifyItem">
                <b-form-textarea
                  v-model="modifySolutionText"
                  rows="5"
                  :class="{
                    'warning-border': warnClass.solution,
                  }"
                ></b-form-textarea>
              </div>
              <div v-else>
                {{ data.item.solution }}
              </div>
            </template>
            <template #cell(modify)="data" v-if="!modifyMod">
              <b-icon
                icon="pencil-square"
                style="color: #536172; cursor: pointer; font-size: 18px"
                @click="_modifyItem(data.index, data.item.id)"
              ></b-icon>
            </template>
            <template #cell(delete)="data" v-if="!modifyMod">
              <b-icon
                icon="x-circle-fill"
                style="color: #536172; cursor: pointer; font-size: 18px"
                @click="_deleteItem(data.item.id)"
              ></b-icon>
            </template>
          </b-table>
        </b-row>
        <b-row>
          <div style="width: 100%; text-align: center">
            <b-button
              v-if="modifyMod"
              class="p-0"
              style="
                display: inline-block;
                margin: 20px;
                margin-top: 30px;
                background-color: #536172;
                border-color: #536172;
                width: 4.25rem;
                height: 2rem;
                font-size: 1.125rem;
              "
              @click="_modifyItemClose()"
              >返回</b-button
            ><b-button
              v-if="modifyMod"
              class="p-0"
              style="
                display: inline-block;
                margin: 20px;
                margin-top: 30px;
                background-color: #536172;
                border-color: #536172;
                width: 4.25rem;
                height: 2rem;
                font-size: 1.125rem;
              "
              @click="_saveModify()"
              >儲存</b-button
            >
          </div>
        </b-row>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import alertModal from "@/components/MES/alertModal.vue";
import commonQueryIoT from "@/components/MES/commonQueryIoT.vue";
export default {
  name: "abnormalQuery",
  data() {
    return {
      modelOptions: [],
      modelSelected: null,
      sensorOptions: [],
      fields: [
        {
          key: "status",
          label: "感測狀態",
          thClass: "abnormal-query-status",
        },
        {
          key: "reason",
          label: "異常原因",
          thClass: "abnormal-query-reason",
        },
        {
          key: "solution",
          label: "建議排除方式",
          thClass: "abnormal-query-solution",
        },
        {
          key: "modify",
          label: "修改",
          thClass: "abnormal-query-modify ",
        },
        {
          key: "delete",
          label: "刪除",
          thClass: "abnormal-query-delete",
        },
      ],
      abnormalCombine: {},
      modifyStatusOption: [
        {
          value: "1",
          text: "正常",
        },
        {
          value: "0",
          text: "異常",
        },
      ],
      modifyMod: false,
      modifyItem: "",
      modifyReasonText: "",
      modifySolutionText: "",
      modifyStatus: [],
      choiceSensor: "no choice",
      choiceId: "",
      repeatPrompt: false,
      warnClass: {
        solution: false,
        reason: false,
      },
    };
  },
  created() {
    let vm = this;
    vm._queryMouldOption();
  },
  mounted: function () {},
  computed: {
    ...mapGetters({
      axiosResult: "mescommonaxios/get_axiosResult",
      selectPage: "mesGetLogin/get_selectPage",
      pageName: "mesGetLogin/get_pageName",
      clickChild: "featurelistbar/get_clickChild",
      mainQueryResponse: "mesCommonQueryIoT/get_queryResponse",
      inputData: "mesCommonQueryIoT/get_inputData",
    }),
  },
  components: {
    commonQueryIoT,
    alertModal,
  },
  watch: {
    abnormalCombine: {
      handler(value) {},
    },
    modelSelected: {
      handler(value) {
        let vm = this;
        if (vm.modelSelected != null) {
          vm._querySensorOptions(value);
        }
      },
    },
  },
  methods: {
    ...mapActions({
      axiosAction: "mescommonaxios/axiosAction",
      changeSelectPage: "mesGetLogin/change_selectPage",
      changeSelectPageName: "mesGetLogin/change_selectPageName",
      setQueryResultValue: "mesQueryResult/set_queryResultItem",
      clearQueryResultValue: "mesQueryResult/clear_queryResultItem",
      setPhpFunction: "mesCommonQueryIoT/set_phpfunction",
      setSelectTag: "mesCommonQueryIoT/set_selectTag",
      setModelOption: "mesCommonQueryIoT/set_modelOption",
      setDeviceOption: "mesCommonQueryIoT/set_deviceOption",
      setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
      toggleSetTimeOutAlertModal: "alertmodal/toggle_settimeout_alertModal",
    }),
    _queryMouldOption() {
      let vm = this;
      let params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = "query_cus_mould";
      params["cus_id"] = "1";
      vm.axiosAction(params).then(() => {
        let result = vm.axiosResult;
        if (result.Response == "ok") {
          vm.modelOptions = result.QueryTableData;
        }
      });
    },
    _querySensorOptions($model) {
      let vm = this;
      let params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = "query_model_light_list";
      params["model"] = $model;
      vm.axiosAction(params).then(() => {
        let result = vm.axiosResult;
        if (result.Response == "ok") {
          vm.sensorOptions = result["QueryTableData"];
        }
      });
    },
    _adjustOptions(mode, index) {
      //增減感測狀態選項
      let vm = this;
      if (mode == "add") {
        //判斷sensorOptions無重複選項
        let notSelectedOption = [];
        let selectedOption = [];
        vm.modifyStatus.forEach((item) => {
          selectedOption.push(item.sensorCode);
        });
        if (selectedOption.includes("")) {
          return;
        }
        vm.sensorOptions.forEach((item) => {
          if (!selectedOption.includes(item.value)) {
            notSelectedOption.push(item);
          }
        });
        vm.modifyStatus.push({
          sensorOptions: notSelectedOption,
          statusOptions: [
            {
              value: "1",
              text: "正常",
            },
            {
              value: "0",
              text: "異常",
            },
          ],
          sensorCode: notSelectedOption[0].value,
          sensorSetting: "1",
          choice: false,
        });
        vm._ctrlOptionRepeat(index);
      } else if (mode == "reduce") {
        if (vm.modifyStatus.length > 2) {
          //把移除的感測點選項加回其他感測點的選項中
          if (vm.choiceSensor != "no choice") {
            vm.modifyStatus.splice(vm.choiceSensor, 1);
            vm.choiceSensor = "no choice";
            vm._ctrlOptionRepeat(index);
          }
        }
      }
    },
    _removeRepeatOption(combineIndex) {
      //感測點選擇變動時，移除其他感測點的重複選項
      let vm = this;
      vm._ctrlOptionRepeat(combineIndex);
    },
    _ctrlOptionRepeat(combineIndex) {
      //控制已選擇選項不重複
      let vm = this;
      let selectedOption = [];
      vm.modifyStatus.forEach((item) => {
        selectedOption.push(item.sensorCode);
      });
      vm.modifyStatus.forEach((item, index) => {
        let repeatOption = [];
        selectedOption.forEach((option) => {
          if (option != item.sensorCode) {
            repeatOption.push(option);
          }
        });
        let newOption = [];
        vm.sensorOptions.forEach((option) => {
          if (!repeatOption.includes(option.value)) {
            newOption.push(option);
          }
        });
        vm.modifyStatus[index].sensorOptions = newOption;
      });
    },
    _choiceSensor(sensorIndex) {
      //選擇感應器->選擇後按下"-"按鈕刪除該感應器
      let vm = this;
      vm.modifyStatus.forEach((item, index) => {
        vm.modifyStatus[index].choice = false;
      });
      vm.modifyStatus[sensorIndex].choice = true;
      vm.choiceSensor = sensorIndex;
    },
    _queryAbnormal() {
      let vm = this;
      if (vm.modelSelected == null) {
        vm.setSetTimeOutAlertMsg("請選擇機台型號");
        vm.toggleSetTimeOutAlertModal();
      } else {
        let params = {};
        params["vuePage"] = vm.selectPage;
        params["methods"] = "POST";
        params["whichFunction"] = "query_light_error_list";
        params["model"] = vm.modelSelected;
        params["cus_id"] = "1";
        vm.axiosAction(params).then(() => {
          let result = vm.axiosResult;
          if (result["Response"] == "ok") {
            vm.abnormalCombine = result["QueryTableData"];
          } else {
            vm.setSetTimeOutAlertMsg("查無資料");
            vm.toggleSetTimeOutAlertModal();
          }
        });
      }
    },
    _modifyItem(index, mod_id) {
      let vm = this;
      vm.choiceId = mod_id;
      vm.modifyMod = true;
      vm.modifyItem = index;
      vm.fields = [
        {
          key: "status",
          label: "感測狀態",
          thClass: "abnormal-query-table-th",
        },
        {
          key: "adjustbtn",
          label: "",
          thClass: "abnormal-query-adjustbtn",
        },
        {
          key: "reason",
          label: "異常原因",
          thClass: "abnormal-query-table-th",
        },
        {
          key: "solution",
          label: "建議排除方式",
          thClass: "abnormal-query-table-th",
        },
      ];
      vm.modifyReasonText = vm.abnormalCombine[index].reason;
      vm.modifySolutionText = vm.abnormalCombine[index].solution;
      let sensorKeys = Object.keys(vm.abnormalCombine[index].light_list);
      sensorKeys.forEach((item) => {
        let statusObj = {
          sensorOptions: vm.sensorOptions,
          statusOptions: [
            {
              value: "1",
              text: "正常",
            },
            {
              value: "0",
              text: "異常",
            },
          ],
          sensorCode: item,
          sensorSetting: vm.abnormalCombine[index].light_list[item],
          choice: false,
        };
        vm.modifyStatus.push(statusObj);
      });
      vm._ctrlOptionRepeat();
      console.log(vm.modifyStatus);
    },
    _modifyItemClose() {
      let vm = this;
      vm.modifyMod = false;
      vm.modifyItem = "";
      vm.fields = [
        {
          key: "status",
          label: "感測狀態",
          thClass: "abnormal-query-status",
        },
        {
          key: "reason",
          label: "異常原因",
          thClass: "abnormal-query-reason",
        },
        {
          key: "solution",
          label: "建議排除方式",
          thClass: "abnormal-query-solution",
        },
        {
          key: "modify",
          label: "修改",
          thClass: "abnormal-query-modify",
        },
        {
          key: "delete",
          label: "刪除",
          thClass: "abnormal-query-delete",
        },
      ];
      vm.modifyReasonText = "";
      vm.modifySolutionText = "";
      vm.modifyStatus = [];
      vm.choiceSensor = "no choice";
      vm.choiceId = "no choice";
      vm.repeatPrompt = false;
      vm.warnClass = {
        solution: false,
        reason: false,
      };
    },
    _saveModify() {
      let vm = this;
      let errorText = [];
      if (
        vm.modifyReasonText.length == 0 &&
        vm.modifySolutionText.length == 0
      ) {
        errorText.push("異常原因");
        vm.warnClass.reason = true;
        errorText.push("建議排除方式");
        vm.warnClass.solution = true;
      } else if (
        vm.modifyReasonText.length == 0 ||
        vm.modifySolutionText.length == 0
      ) {
        if (vm.modifyReasonText.length == 0) {
          errorText.push("異常原因");
          vm.warnClass.reason = true;
           vm.warnClass.solution = false;
        } else if (vm.modifySolutionText.length == 0) {
          errorText.push("建議排除方式");
          vm.warnClass.reason = false;
          vm.warnClass.solution = true;
        }
      }
      if (errorText.length > 0) {
        console.log(vm.warnClass);
        let exportText = errorText.join("、");
        vm.setSetTimeOutAlertMsg("請輸入" + exportText);
        vm.toggleSetTimeOutAlertModal();
      } else {
        let params = {};
        let sendData = {
          status: vm.modifyStatus,
          reason: vm.modifyReasonText,
          solution: vm.modifySolutionText,
        };
        params["vuePage"] = vm.selectPage;
        params["methods"] = "POST";
        params["whichFunction"] = "UpdateLightErrorList";
        params["mod_id"] = vm.choiceId;
        params["combineData"] = sendData;
        params["model"] = vm.modelSelected;
        console.log(params);
        vm.axiosAction(params)
          .then(() => {
            let result = vm.axiosResult;
            if (result["Response"] == "ok") {
              vm.setSetTimeOutAlertMsg("修改成功");
              vm._modifyItemClose();
            } else if (result["Response"] == "update_new_combine_repeat") {
              vm.setSetTimeOutAlertMsg("組合重複，請重新修改");
              vm.repeatPrompt = true;
            } else {
              vm.setSetTimeOutAlertMsg(result["Response"]);
            }
            vm.toggleSetTimeOutAlertModal();
          })
          .finally(() => {
            //等待提示視窗結束
            setTimeout(() => {
              vm._queryAbnormal();
            }, 1000);
          });
      }
    },
    _deleteItem(del_id) {
      let vm = this;
      let params = {};
      params["vuePage"] = vm.selectPage;
      params["methods"] = "POST";
      params["whichFunction"] = "DeleteLightErrorList";
      params["del_id"] = del_id;
      console.log(params);
      vm.axiosAction(params)
        .then(() => {
          let result = vm.axiosResult;
          if (result["Response"] == "ok") {
            vm.setSetTimeOutAlertMsg("刪除成功");
          } else {
            vm.setSetTimeOutAlertMsg(result["Response"]);
          }
          vm.toggleSetTimeOutAlertModal();
        })
        .finally(() => {
          //等待提示視窗結束
          setTimeout(() => {
            vm._queryAbnormal();
          }, 1000);
        });
    },
  },
  destroyed() {},
};
</script>
<style scoped>
div,
p,
span {
  font-family: dfyuanstd-w7;
}
</style>

<style lang="scss" scope>
.adjust-value-btn {
  color: #ffffff !important;
  background-color: #536172 !important;
  border-radius: 100%;
  text-align: center;
  width: 20px;
  height: 20px;
  line-height: 16px;
  display: inline-block;
  cursor: pointer;
  margin: 5px;
  vertical-align: middle;
}
.abnormal-query-table td {
  white-space: pre-line;
  font-family: dfyuanstd-w7;
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
}
.abnormal-query-status {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
  width: 45%;
}
.abnormal-query-reason {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
  width: 14%;
}
.abnormal-query-solution {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
  width: 25%;
  white-space: pre-line;
}
.abnormal-query-modify {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
  width: 8%;
}
.abnormal-query-delete {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
  width: 8%;
}
.abnormal-query-adjustbtn {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
  width: 10%;
}
.abnormal-query-table tr {
  border-left: 1px solid #65646a;
  border-right: 1px solid #65646a;
}

.abnormal-query-table tr:nth-child(even) {
  background-color: #cccccc;
}
.abnormal-query-table-th {
  border-top: 1px solid #65646a;
  border-bottom: 1px solid #65646a;
  background-color: #536172;
  color: #ffffff;
  font-size: 1.125rem;
  margin: 0px;
}
.table > tbody > tr > td {
  vertical-align: middle;
}
.warning-border {
  border: 1px solid #c10000 !important;
}
</style>