const state = {
    userId:"",
    selectedCustomer:""
};
const getters = {
  get_selectedCustomer: state =>{
    return state.selectedCustomer;
  },
};
const actions = {
  set_selectedCustomer(context,data){
    context.commit("SET_SELECTEDCUSTOMER",data);
  },
};
const mutations = {
  SET_SELECTEDCUSTOMER(state, data) {
    state.selectedCustomer = data;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
