    <div id="Logging" ref="Logging">
        <b-row
            class="h-100 mh-100"
            style="overflow-x: auto"
            :class="{ publicMinWidth550: this_route != 'logging' }"
        >
            <template v-if="this_route != 'logging'">
                <featureListBar />
                <div class="featureContent customize_scrollbar h-100 mh-100">
                    <b-container fluid>
                        <router-view
                            class="edoc_router pt-2"
                            style="height: calc(100vh - 76px)"
                        >
                        </router-view>
                    </b-container>
                </div>
                <!-- <b-col
                    xl="11"
                    lg="12"
                    md="12"
                    sm="12"
                    class="featureContent customize_scrollbar h-100 mh-100"
                >
                    <b-row
                        class="info"
                        v-if="
                            featureMainData.body[
                                $router.currentRoute.path.split('/')[
                                    $router.currentRoute.path.split('/')
                                        .length - 1
                                ]
                            ].showtitlebanner
                        "
                    >
                        <b-container>
                            <h2 class="publicText pt-5 pb-5">
                                {{
                                    featureMainData.body[
                                        $router.currentRoute.path.split("/")[
                                            $router.currentRoute.path.split("/")
                                                .length - 1
                                        ]
                                    ].title
                                }}
                            </h2>
                            <h5 class="publicText  pb-5">
                                {{
                                    featureMainData.body[
                                        $router.currentRoute.path.split("/")[
                                            $router.currentRoute.path.split("/")
                                                .length - 1
                                        ]
                                    ].content
                                }}
                            </h5>
                        </b-container>
                    </b-row>
                    <router-view class="edoc_router"> </router-view>
                </b-col> -->
            </template>
            <!-- <template v-else>
                <b-container fluid class="h-100 mh-100 publicMinWidth550">
                    <b-row
                        align-h="center"
                        align-v="center"
                        class="h-100 mh-100"
                        style="overflow: auto"
                    >
                        <featureMain></featureMain>
                    </b-row>
                </b-container>
            </template> -->
        </b-row>
    </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import featureMain from "@/components/featureMain.vue";
import featureListBar from "@/components/featureListBar.vue";
export default {
    name: "Logging",
    data() {
        return {
            this_route: "logging",
            featureMainData: {
                title: "日誌模組",
                cols: {
                    xl: "4",
                    lg: "6",
                    md: "12",
                    sm: "12",
                },
                body: {
                    immediate: {
                        title: "即時日誌訊息",
                        showtitlebanner: true,
                        content:
                            "系統日誌資料是以明碼型態傳送，且定義事件的種類及嚴重等級等，並定時抓取資料庫資料並顯示。",
                        img: require("@/assets/logging/immediate.png"),
                    },
                    history: {
                        title: "歷史日誌紀錄",
                        showtitlebanner: true,
                        content:
                            "透過這個分析系統，可以協助網管人員依api分類系統區分並過濾數量龐大的log檔，也能避免攻擊者侵入導致事後追查無門的局面。",
                        img: require("@/assets/logging/history.png"),
                    },
                },
            },
            featureListBarData: {
                body: {
                    immediate: {
                        title: "即時日誌訊息",
                    },
                    history: {
                        title: "歷史日誌紀錄",
                    },
                },
            },
        };
    },
    created() {
        console.log(this.$route);
        if (/(\/logging)$/.test(this.$router.currentRoute.path)) {
            this.setClickRoute("immediate");
            // this.setFeatureMainData(this.featureMainData);
        } else {
            this.this_route = this.$router.currentRoute.path.split("/")[
                this.$router.currentRoute.path.split("/").length - 1
            ];
        }
        console.log(this.$router.currentRoute.path);

        this.setFeatureListBarData(this.featureListBarData);
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            clickRoute: "featuremain/get_clickRoute",
            clickChild: "featurelistbar/get_clickChild",
        }),
    },
    components: {
        featureMain,
        featureListBar,
    },
    watch: {
        clickRoute: {
            handler(value) {
                console.log(value);
                console.log(this.$router.currentRoute.path);
                if (value != "") {
                    this.$router.push({
                        name: value,
                        params: {
                            SYSTEM: this.$route.params.SYSTEM,
                        },
                    });
                    this.this_route = value;
                    this.setClickChild({
                        child_name: value,
                        data: {},
                    });
                    this.setClickRoute("");
                }
            },
        },
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
            },
        },
    },
    methods: {
        ...mapActions({
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            setFeatureMainData: "featuremain/set_featureMainData",
            setFeatureListBarData: "featurelistbar/set_featureListBarData",
            setClickRoute: "featuremain/set_clickRoute",
            setClickChild: "featurelistbar/set_clickChild",
        }),

        _onResize() {
            let vm = this;
            console.log(window.innerWidth);
            if (window.innerWidth < 1200) {
                // vm.resize_style["margin-top"] =
                //     vm.$refs.leftblock.clientHeight + "px";
                vm.searchBarFixed = true;
            } else {
                vm.searchBarFixed = false;
                // vm.resize_style["margin-top"] = "0px";
            }
        },

        _handleScroll() {
            let scrollTop =
                window.pageYOffset ||
                document.documentElement.scrollTop ||
                document.body.scrollTop;
            console.log(scrollTop);

            let offsetTop = document.querySelector("#leftblock").offsetTop;
            console.log(offsetTop);
            if (window.innerWidth < 1200) {
                if (scrollTop > offsetTop) {
                    if (!this.searchBarFixed) {
                        setTimeout(() => {
                            this.searchBarFixed = true;
                        }, 50);
                    } else {
                        this.searchBarFixed = true;
                    }
                } else {
                    this.searchBarFixed = false;
                }
                this.monitorScrollTop = scrollTop;
            }

            this.$nextTick(() => {
                if (this.monitorScrollTop > scrollTop) {
                    console.log("~~~~");
                    this.leftblockShow = true;
                } else {
                    console.log("@@@@");
                    this.leftblockShow = false;
                }
            });
            this.monitorScrollTop = scrollTop;
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
    beforeRouteUpdate(to, from, next) {
        console.log(to);
        console.log(from);
        this.this_route = to.name;
        if (to.name === "logging") {
            console.log(this.featureMainData);
            this.setClickRoute("immediate");
            // this.setFeatureMainData(this.featureMainData);
        } else {
            this.setClickChild({
                child_name: to.name,
                data: {},
            });
            this.$nextTick(() => {
                window.addEventListener("resize", this._onResize);
                window.addEventListener("scroll", this._handleScroll);
                console.log("####");
            });
        }
        next();
    },
    //調用router守衛，在離開時將socket disconnect
    beforeRouteLeave(to, from, next) {
        this._reset([]);
        next();
    },
    beforeDestroy() {
        let vm = this;
        window.removeEventListener("resize", vm._onResize);
        window.removeEventListener("scroll", vm._handleScroll);
    },
};
</script>
<style scoped>
.PaaS_LOG_table {
    max-height: calc(100vh - 400px);
}
.customizeTab_div {
    /* margin: 11px 0 4px; */
    border-width: 0 3px 3px 3px;
    border-color: #536172 !important;
    /* border-radius: 0 0 4px 4px; */
    /* height: 450px; */
    overflow-y: auto;
    /* text-align:center; */
    padding: 15px;
    /* border-top-color: #06122a !important; */
    max-height: calc(100vh - 300px);
    min-height: 450px;
}
.customizeTab_div pre {
    color: white;
}
</style>
