const state = {
    loginAccount: null,
    loginUsername: null,
    loginCustomerId:null,
    loginStatus: false,
    loginAccessList: null,
    isLoginMoment: false,
    isRoot: false,
    DEFAULT_pageList: {
        "name": ["mes", "operation", "edoc", "logging", "setting", "bigdata"],
        "trans": ["機台管理", "營運模組", "電子文件", "日誌模組", "設定", "AI診斷履歷"],
    },
    pageList: [],
    pageListTrans: [],
    pageAccess: {
        logging: { authority: false },
        edoc: { authority: false },
        operation: { authority: false },
        mes: { authority: false },
        setting: { authority: false },
        bigdata: { authority: false },
    },
    DEFAULT_pageAccess: {
        logging: { authority: false },
        edoc: { authority: false },
        operation: { authority: false },
        mes: { authority: false },
        setting: { authority: false },
        bigdata: { authority: false },
    },
};
const getters = {
    get_loginData: state => {
        var loginData = {};
        loginData.account = state.loginAccount;
        loginData.username = state.loginUsername;
        loginData.status = state.loginStatus;
        loginData.customerId = state.loginCustomerId;
        loginData.accesslist = state.loginAccessList;
        return loginData;
    },
    get_isLoginMoment: state => {
        return state.isLoginMoment;
    },
    get_isRoot: state => {
        return state.isRoot;
    },
    get_pageList: state => {
        return { "name": state.pageList, "trans": state.pageListTrans };
    },
    get_DEFAULT_pageList: state => {
        return state.DEFAULT_pageList
    },
    get_pageAccess: state => {
        console.log(state.pageAccess);
        return state.pageAccess;
    },
    get_DEFAULT_pageAccess: state => {
        return state.DEFAULT_pageAccess;
    },
    get_depDetail: state => {
        return state.depDetail;
    },
};
const actions = {
    change_loginData(context, obj) {
        context.commit("CHANGE_LOGINDATA", obj);
    },
    change_isLoginMoment(context, status) {
        context.commit("CHANGE_ISLOGINMOMENT", status);
    },
    change_isRoot(context, status) {
        context.commit("CHANGE_ISROOT", status);
    },
    set_pageList(context, obj) {
        context.commit("SET_PAGELIST", obj);
    },
    change_pageAccess(context, obj) {
        context.commit("CHANGE_PAGEACCESS", obj);
    }
};
const mutations = {
    CHANGE_LOGINDATA(state, obj) {
        state.loginAccount = obj.account;
        state.loginStatus = obj.status;
        state.loginUsername = obj.username;
        state.loginAccessList = obj.accesslist;
        state.loginCustomerId = obj.customerId;
    },
    CHANGE_ISLOGINMOMENT(state, status) {
        state.isLoginMoment = status;
    },
    CHANGE_ISROOT(state, status) {
        state.isRoot = status;
    },
    SET_PAGELIST(state, obj) {
        state.pageList = obj.name
        state.pageListTrans = obj.trans
    },
    CHANGE_PAGEACCESS(state, obj) {
        state.pageAccess = obj
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
