const state = {
    inspectionUseInformationProjectData: null,
    inspectionUseInformationFields: [
        {
            key: "time",
            label: "檢驗時間",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "code",
            label: "檢驗項目",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "standardValue",
            label: "標準值",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "inspectionValue",
            label: "檢驗值",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "result",
            label: "檢驗結果",
            sortable: false,
            thClass: "publicTH"
        },
    ],
    inspectionUseInformationUseProject: true,
    inspectionUseInformationItemData: [],
    inspectionUseInformationData: {},
}
const getters = {
    get_inspectionUseInformationProjectData: state => {
        return state.inspectionUseInformationProjectData
    },
    get_inspectionUseInformationFields: state => {
        return state.inspectionUseInformationFields
    },
    get_inspectionUseInformationUseProject: state => {
        return state.inspectionUseInformationUseProject
    },
    get_inspectionUseInformationItemData: state => {
        return state.inspectionUseInformationItemData
    },
    get_inspectionUseInformationData: state => {
        return state.inspectionUseInformationData
    },
}
const actions = {
    set_inspectionUseInformationProjectData(context, array) {
        context.commit('SET_INSPECTIONUSEINFORMATIONPROJECTDATA', array);
    },
    set_inspectionUseInformationFields(context, array) {
        context.commit('SET_INSPECTIONUSEINFORMATIONFIELDS', array);
    },
    set_inspectionUseInformationUseProject(context, boolean) {
        context.commit('SET_INSPECTIONUSEINFORMATIONUSEPROJECT', boolean);
    },
    set_inspectionUseInformationItemData(context, array) {
        context.commit('SET_INSPECTIONUSEINFORMATIONITEMDATA', array);
    },
    set_inspectionUseInformationData(context, obj) {
        context.commit('SET_INSPECTIONUSEINFORMATIONDATA', obj);
    },
    clear_inspectionUseInformation(context) {
        context.commit('CLEAR_INSPECTIONUSEINFORMATION');
    }
}
const mutations = {
    SET_INSPECTIONUSEINFORMATIONPROJECTDATA(state, array) {
        state.inspectionUseInformationProjectData = array;
    },
    SET_INSPECTIONUSEINFORMATIONFIELDS(state, array) {
        state.inspectionUseInformationFields = array;
    },
    SET_INSPECTIONUSEINFORMATIONUSEPROJECT(state, boolean) {
        state.inspectionUseInformationUseProject = boolean;
    },
    SET_INSPECTIONUSEINFORMATIONITEMDATA(state, array) {
        state.inspectionUseInformationItemData = array;
    },
    SET_INSPECTIONUSEINFORMATIONDATA(state, obj) {
        state.inspectionUseInformationData = obj;
    },
    CLEAR_INSPECTIONUSEINFORMATION(state) {
        state.inspectionUseInformationProjectData = null;
        state.inspectionUseInformationData = {};
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
