const getters = {
    get_sideBar: state => {
        return state.sideBar;
    },
    get_sideBarSelect: state => {
        return state.sideBarSelect;
    }
};
const actions = {
    set_sideBarSelect(context, array) {
        context.commit("SET_SIDEBARSELECT", array);
    }
};
const mutations = {
    SET_SIDEBARSELECT(state, array) {
        let page = array[0];
        console.log(array);
        if (page == 'elecboard') {
            let model = array[1];
            let group = array[2];
            let data = state.sideBar.elecboard.other.model;
            state.sideBarSelect = {
                page: page,
                model: data[model].name,
                group: {
                    name: data[model].group[group].name,
                    device: data[model].group[group].device,
                    user: data[model].group[group].user
                },
                change: Math.random()
            };
        }
        console.log(state.sideBarSelect);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
