const state = {
    featureListBarData: {
        title: "",
        cols: {},
        body: {},
    },
    clickChild: { child_name: "", data: null },
    collapseIDList: {},
    toggleCollapseID: null,
};
const getters = {
    get_featureListBarData: state => {
        return state.featureListBarData;
    },
    get_clickChild: state => {
        console.log(state.clickChild);
        return state.clickChild;
    },
    get_collapseIDList: state => {
        return state.collapseIDList;
    },
    get_toggleCollapseID: state => {
        return state.toggleCollapseID;
    }
};
const actions = {
    set_featureListBarData(context, obj) {
        context.commit("SET_FEATURELISTBARDATA", obj);
    },
    set_clickChild(context, data) {
        console.log(data);
        console.log(state.clickChild);
        context.commit("SET_CLICKCHILD", data);
    },
    set_collapseIDList(context, data) {
        context.commit("SET_COLLAPSEIDLIST", data);
    },
    toggle_collapseID(context, data) {
        context.commit("TOGGLE_COLLAPSEID", data);
    },
};
const mutations = {
    SET_FEATURELISTBARDATA(state, obj) {
        state.featureListBarData.title = obj.title;
        state.featureListBarData.cols = obj.cols;
        state.featureListBarData.body = obj.body;
    },
    SET_CLICKCHILD(state, data) {
        state.clickChild = data;
    },
    SET_COLLAPSEIDLIST(state, data) {
        state.collapseIDList = data;
    },
    TOGGLE_COLLAPSEID(state, data) {
        state.toggleCollapseID = data;
        if (Object.keys(state.collapseIDList).length > 0) {
            Object.keys(state.collapseIDList).map((element) => {
                if (element != data) {
                    state.collapseIDList[element] = false
                }
            })
        }
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
