const state = {
    activationDataFields: [
        {
            key: "date",
            label: "日期",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "activation",
            label: "稼動率",
            sortable: true,
            thClass: "publicTH"
        }
    ],
    activationDataItem: []
}
const getters = {
    get_activationDataFields: state => {
        return state.activationDataFields
    },
    get_activationDataItem: state => {
        return state.activationDataItem
    }
}
const actions = {
    set_activationDataFields(context, array) {
        context.commit('SET_ACTIVATIONDATAFIELDS', array);
    },
    set_activationDataItem(context, array) {
        context.commit('SET_ACTIVATIONDATAITEM', array);
    }
}
const mutations = {
    SET_ACTIVATIONDATAFIELDS(state, array) {
        state.activationDataItem = array;
    },
    SET_ACTIVATIONDATAITEM(state, array) {
        state.activationDataItem = array;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
