const state = {
    wireUseInformationFields: [
        {
            key: "index",
            label: "項次",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "code",
            label: "線材捲號",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "furnaceCode",
            label: "爐號",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "consumption",
            label: "耗用量(KG)",
            sortable: false,
            thClass: "publicTH"
        },
        {
            key: "attrition",
            label: "損耗率(%)",
            sortable: false,
            thClass: "publicTH"
        }
    ],
    wireUseInformationItem: [],
}
const getters = {
    get_wireUseInformationFields: state => {
        return state.wireUseInformationFields
    },
    get_wireUseInformationItem: state => {
        return state.wireUseInformationItem
    },
}
const actions = {
    set_wireUseInformationFields(context, array) {
        context.commit('SET_WIREUSEINFORMATIONFIELDS', array);
    },
    set_wireUseInformationItem(context, array) {
        context.commit('SET_WIREUSEINFORMATIONITEM', array);
    },
    clear_wireUseInformationItem(context) {
        context.commit('CLEAR_WIREUSEINFORMATIONITEM');
    },
}
const mutations = {
    SET_WIREUSEINFORMATIONFIELDS(state, array) {
        state.wireUseInformationItem = array;
    },
    SET_WIREUSEINFORMATIONITEM(state, array) {
        state.wireUseInformationItem = array;
    },
    CLEAR_WIREUSEINFORMATIONITEM(state) {
        state.wireUseInformationItem = [];
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
