<template>
    <div class="featureMain">
        <!-- <b-row align-h="center" align-v="center" class="h-100 mh-100"> -->
        <b-row align-h="center" class="main-text">
            <b-col cols="auto" class="mt-5">
                <h1>{{ featureMainData.title }}</h1>
            </b-col>
        </b-row>
        <b-row align-h="center" class="main-block">
            <b-col
                cols="auto"
                :xl="featureMainData.cols.xl"
                :lg="featureMainData.cols.lg"
                :md="featureMainData.cols.md"
                :sm="featureMainData.cols.sm"
                v-for="(item, index) in Object.entries(featureMainData.body)"
                :key="index"
                :class="{ evenbg: index % 2 != 0 }"
                class="mt-4"
            >
                {{ clickRoute }}
                <b-card class="mt-2" @click.prevent="setClickRoute(item[0])">
                    <template #header>
                        <template v-if="item[1].hasOwnProperty('title')">
                            <span>{{ item[1].title }}</span>
                        </template>
                    </template>
                    <div class="text" :style="_cardBodyTextStyle(item[1])">
                        <b-card-text>
                            {{ item[1].content }}
                        </b-card-text>
                    </div>
                    <div class="img" :style="_cardBodyImgStyle(item[1])">
                        <img :src="item[1].img" />
                    </div>
                </b-card>
            </b-col>
        </b-row>
        <!-- </b-row> -->
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "modal",
    data() {
        return {};
    },
    computed: {
        ...mapGetters({
            featureMainData: "featuremain/get_featureMainData",
            clickRoute: "featuremain/get_clickRoute",
        }),
    },
    methods: {
        ...mapActions({
            setClickRoute: "featuremain/set_clickRoute",
        }),

        _cardBodyTextStyle(element) {
            if (
                element.hasOwnProperty("style") &&
                element["style"].hasOwnProperty("card-body-text")
            ) {
                return element["style"]["card-body-text"];
            }
        },

        _cardBodyImgStyle(element) {},
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.featureMain {
    // display: flex;
    // align-items: center;
    // justify-content: center;
    .main-text {
        color: #88a2be;
        margin-bottom: 3rem !important;
    }
    .main-block {
        > div {
            &:hover {
                background-color: #eeeeee !important;
            }
        }
        .card {
            background-color: unset !important;
            border: unset !important;
            cursor: pointer;
        }
        .card-header {
            background-color: unset !important;
            border: unset !important;
            text-align: center;
            span {
                border: 2px solid rgba(112, 112, 112, 1);
                border-radius: 8px;
                padding: 5px;
                font-weight: bold;
                color: $grey-color;
                font-size: 25px;
            }
        }
        .card-body {
            .img {
                text-align: center;
                img {
                    width: 125px;
                    height: 125px;
                }
            }
            .text {
                min-height: 125px;
                padding: 0 1rem;
                text-align: center;
                p {
                    font-size: 20px;
                }
            }
        }
        .evenbg {
            background-color: #f6f9fc;
        }
    }
}
@media (min-width: 1200px) {
    .featureMain {
        .main-block {
            margin-bottom: 13rem !important;
            // .evenbg {
            //     background-color: #f6f9fc;
            // }
        }
    }
}
@media (max-width: 1200px) {
    .featureMain {
        .main-block {
            margin-bottom: 13rem !important;
            // .evenbg {
            //     background-color: #63809c;
            // }
        }
    }
}
</style>
