<template>
    <div
        id="device"
        ref="device"
        class="customize_scrollbar"
        style="position: relative"
    >
        <b-row :class="deviceblock">
            <b-col cols="12" class="text-center">
                <h3 v-if="is_add_update == 'add'">
                    {{ action_config[action] }}
                </h3>
                <h3 v-else>客戶機台查詢</h3>
            </b-col>
            <template v-if="action == 'cud'">
                <b-col cols="12" class="p-3">
                    <b-row>
                        <b-col cols="3">
                            <span>客戶編號:</span>
                        </b-col>
                        <b-col cols="3">
                            <span>機台型號:</span>
                        </b-col>
                        <b-col cols="3">
                            <span>機台編號:</span>
                        </b-col>
                        <b-col cols="3">
                            <span>購買日期:</span>
                        </b-col>
                    </b-row>
                    <b-form @submit="_onSubmit">
                        <b-row>
                            <b-col cols="3">
                                <b-form-select
                                    v-model="customer_selected"
                                    :options="customer_options"
                                    :disabled="is_add_update == 'update'"
                                ></b-form-select>
                            </b-col>
                            <b-col cols="3">
                                <b-form-select
                                    v-model="devicetype_selected"
                                    :options="devicetype_options"
                                    @change="_onChange($event)"
                                ></b-form-select>
                            </b-col>
                            <b-col cols="3">
                                <b-form-input v-model="deviceID"></b-form-input>
                            </b-col>
                            <b-col cols="3">
                                <b-form-datepicker
                                    id="example-datepicker"
                                    v-model="purchasedate"
                                ></b-form-datepicker>
                            </b-col>
                        </b-row>
                        <b-row>
                            <b-table
                                sticky-header="600px"
                                :items="cud_items"
                                :fields="cud_fields"
                                :fixed="false"
                                responsive
                                :head-variant="'light'"
                                class="customize_scrollbar text-center mt-3"
                                v-if="cud_items.length != 0"
                                :tbody-tr-attr="_rowClass"
                            >
                                <template v-slot:cell(type)="row">
                                    {{ type_config[row.item.type] }}
                                </template>
                                <template v-slot:cell(enable)="row">
                                    <b-form-checkbox
                                        v-model="row.item.enable"
                                        value="Y"
                                        unchecked-value="N"
                                    >
                                    </b-form-checkbox>
                                </template>
                                <template v-slot:cell(range)="row">
                                    <b-form-select
                                        v-model="row.item.range"
                                        :options="range_options"
                                        v-if="row.item.type == 'sensor'"
                                    ></b-form-select>
                                    <span v-else>-</span>
                                </template>
                                <template v-slot:cell(error_min)="row">
                                    <template v-if="row.item.range === null">
                                        <span>-</span>
                                    </template>
                                    <b-form-input
                                        v-else-if="
                                            ['min', 'between'].indexOf(
                                                row.item.range
                                            ) != -1
                                        "
                                        type="number"
                                        v-model="row.item.error_min"
                                        step="0.1"
                                        :required="row.item.range == 'between'"
                                    ></b-form-input>
                                    <span v-else>-</span>
                                </template>
                                <template v-slot:cell(warn_min)="row">
                                    <template v-if="row.item.range === null">
                                        <span>-</span>
                                    </template>
                                    <b-form-input
                                        type="number"
                                        v-model="row.item.warn_min"
                                        v-else-if="
                                            ['min', 'between'].indexOf(
                                                row.item.range
                                            ) != -1
                                        "
                                        step="0.1"
                                        :required="row.item.range == 'between'"
                                    ></b-form-input>
                                    <span v-else>-</span>
                                </template>
                                <template v-slot:cell(warn_max)="row">
                                    <template v-if="row.item.range === null">
                                        <span>-</span>
                                    </template>
                                    <b-form-input
                                        type="number"
                                        v-model="row.item.warn_max"
                                        v-else-if="
                                            ['max', 'between'].indexOf(
                                                row.item.range
                                            ) != -1
                                        "
                                        step="0.1"
                                        :required="row.item.range == 'between'"
                                    ></b-form-input>
                                    <span v-else>-</span>
                                </template>
                                <template v-slot:cell(error_max)="row">
                                    <template v-if="row.item.range === null">
                                        <span>-</span>
                                    </template>
                                    <b-form-input
                                        type="number"
                                        v-model="row.item.error_max"
                                        v-else-if="
                                            ['max', 'between'].indexOf(
                                                row.item.range
                                            ) != -1
                                        "
                                        step="0.1"
                                        :required="row.item.range == 'between'"
                                    ></b-form-input>
                                    <span v-else>-</span>
                                </template>
                            </b-table>
                        </b-row>
                        <b-row align-h="center">
                            <template v-if="is_add_update == 'add'">
                                <b-button
                                    type="submit"
                                    class="mt-3 publicButton"
                                    v-if="cud_items.length != 0"
                                    :disabled="
                                        customer_selected == '' ||
                                        devicetype_selected == '' ||
                                        devicetype_selected == '' ||
                                        deviceID == '' ||
                                        purchasedate == ''
                                    "
                                    >送出</b-button
                                >
                            </template>
                            <template v-else>
                                <b-button
                                    class="mt-3 publicButton"
                                    @click.prevent="
                                        _reset([
                                            'query_items',
                                            'customer_selected',
                                            'customer_options',
                                        ]);
                                        action = 'query';
                                    "
                                    >返回</b-button
                                >
                                <b-button
                                    type="submit"
                                    class="ml-2 mt-3 publicButton"
                                    >修改</b-button
                                >
                            </template>
                        </b-row>
                    </b-form>
                </b-col>
            </template>
            <template v-else>
                <b-col cols="12">
                    <b-row>
                        <b-col cols="12">
                            <span>客戶編號:</span>
                        </b-col>
                    </b-row>
                    <b-row align-h="start">
                        <b-col cols="4">
                            <b-form-select
                                v-model="customer_selected"
                                :options="customer_options"
                                required
                            ></b-form-select>
                        </b-col>
                        <b-col cols="4">
                            <b-button
                                class="publicButton"
                                :disabled="
                                    customer_options.length == 0 ||
                                    customer_selected == ''
                                "
                                @click.prevent="_queryCustomerDevice"
                                >送出</b-button
                            >
                        </b-col>
                    </b-row>
                    <br />
                    <b-row align-h="center">
                        <b-table
                            sticky-header="600px"
                            :items="query_items"
                            :fields="query_fields"
                            :fixed="false"
                            responsive
                            :head-variant="'light'"
                            class="customize_scrollbar text-center mt-3"
                            v-if="query_items.length != 0"
                            :tbody-tr-attr="_rowClass"
                        >
                            <template v-slot:cell(detail)="row">
                                <b-button
                                    size="sm"
                                    class="publicButton"
                                    @click="_checkDetail(row.item)"
                                >
                                    內容
                                </b-button>
                            </template>
                        </b-table>
                    </b-row>
                </b-col>
            </template>
        </b-row>
    </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import Temp from "../Temp.vue";
export default {
    components: { Temp },
    name: "device",
    data() {
        let vm = this;
        return {
            action: "cud",
            action_config: {
                客戶機台新增: "cud",
                客戶機台查詢: "query",
                cud: "客戶機台新增",
                query: "客戶機台查詢",
            },
            customer_selected: "",
            customer_options: [],
            devicetype_selected: "",
            devicetype_options: [],
            deviceID: "",
            purchasedate: "",
            cud_fields: [
                {
                    key: "type",
                    label: "類別",
                    thClass: "publicTH",
                },
                {
                    key: "name",
                    label: "感測點名稱",
                    thClass: "publicTH",
                },
                {
                    key: "enable",
                    label: "啟用",
                    thClass: "publicTH",
                },
                {
                    key: "range",
                    label: "條件",
                    thClass: "publicTH",
                },
                {
                    key: "error_min",
                    label: "異常下限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
                {
                    key: "warn_min",
                    label: "注意下限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
                {
                    key: "warn_max",
                    label: "注意上限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
                {
                    key: "error_max",
                    label: "異常上限",
                    thClass: "publicTH w100px",
                    tdClass: "w100px",
                },
            ],
            cud_items: [],
            query_fields: [
                {
                    key: "cus_id",
                    label: "客戶編號",
                    thClass: "publicTH",
                },
                {
                    key: "cus_name",
                    label: "客戶名稱",
                    thClass: "publicTH",
                },
                {
                    key: "device_name",
                    label: "機台編號",
                    thClass: "publicTH",
                },
                {
                    key: "model",
                    label: "機台型號",
                    thClass: "publicTH",
                },
                {
                    key: "detail",
                    label: "詳細資料",
                    thClass: "publicTH",
                },
            ],
            query_items: [],
            is_add_update: "add",
            range_options: [
                { text: "最大值", value: "max" },
                { text: "最小值", value: "min" },
                { text: "範圍", value: "between" },
                { text: "Null", value: null },
            ],
            type_config: {
                light: "燈號點",
                sensor: "感測點",
            },
            preload: {
                main_key: "",
                combine_key: "",
                combine_list: {
                    device_id: "",
                    device_name: "",
                    device_model: "",
                    device_category: "",
                    machine_image: "",
                    machine_camera: "",
                    machine_sensor_list: "",
                    machine_status: "",
                    operatelog_information: "",
                    stack_bar_information: "",
                    device_run_time: "",
                    device_activation: 0,
                    device_rpm: 0,
                    device_day_count: 0,
                    device_day_start_count: 0,
                    device_now_count: 0,
                    machine_abn_list: "",
                    machine_light_list: "",
                    error_level: "",
                    machine_light_value: "",
                    machine_abn_id: "",
                    machine_abn_description: "",
                    machine_abn_solution: "",
                    machine_main: "",
                    machine_emeter: "",
                    machine_vibbearing: "",
                    machine_vibMotor: "",
                    machine_servod: "",
                    wire_weight: 0,
                    machine_loadCell: "",
                    machine_smb: "",
                    power_expend: "",
                },
            },
            update_devID: "",
            update_ole_device_name: "",
        };
    },
    computed: {
        ...mapGetters({
            loginData: "getlogin/get_loginData",
            axiosResult: "commonaxios/get_axiosResult",
            // system: "apidetail/get_system",
            DEFAULT_commonModalConfig: "usemodal/get_DEFAULT_commonModalConfig",
            clickChild: "featurelistbar/get_clickChild",
        }),
        deviceblock: function () {
            if (this.action == "cud") {
                return {
                    "device-block": true,
                };
            } else {
                return {
                    "device-block": false,
                };
            }
        },
    },
    watch: {
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
                console.log(vm.action_config[value.split("_")[1]]);
                vm._reset([]);
                vm.action = vm.action_config[value.split("_")[1]];
                console.log(vm.action);
                this._queryCusID(); //抓取客戶編號
                this._queryMachineModel(); //抓取機台機型
            },
        },
    },
    created() {
        // this._checkApiDetailExist();
        // this.apipath_prefix += this.$route.params.SYSTEM;
        console.log(this.$router);
        console.log(this.$router.currentRoute.matched);
        console.log(this.endpoint);
        console.log(this.$route.params.SYSTEM);
        console.log(this.clickChild);
        console.log(
            this.action_config[this.clickChild.child_name.split("_")[1]]
        );
        if (
            this.action_config[this.clickChild.child_name.split("_")[1]] ==
            "query"
        ) {
            this.action = "query";
        }
        this._queryCusID(); //抓取客戶編號
        this._queryMachineModel(); //抓取機台機型
    },
    mounted() {
        // var VueScrollTo = require("vue-scrollto");
        // var options = {
        //     container: "#device",
        //     easing: "ease-in",
        //     offset: -60,
        //     force: true,
        //     cancelable: true,
        //     onStart: function (element) {
        //         // scrolling started
        //     },
        //     onDone: function (element) {
        //         // scrolling is done
        //     },
        //     onCancel: function () {
        //         // scrolling has been interrupted
        //     },
        //     x: false,
        //     y: true,
        // };
        // var cancelScroll = VueScrollTo.scrollTo("#device", options);
        // // or alternatively inside your components you can use
        // // cancelScroll = this.$scrollTo(element, duration, options);
        // // to cancel scrolling you can call the returned function
        // cancelScroll();
        // // this.$scrollTo("#device");
    },
    methods: {
        ...mapActions({
            axiosAction: "commonaxios/axiosAction",
            axiosGetAction: "commonaxios/axiosGetAction",
            axiosPostAction: "commonaxios/axiosPostAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleCommonModal: "usemodal/toggle_commonModal",
            setCommonModalConfig: "usemodal/set_commonModalConfig",
        }),

        _rowClass(item, type) {
            if (!item || type !== "row") return;
            if (item.enable == "N")
                return { style: "background-color:rgba(0, 0, 0, 0.144)" };
        },

        _onSubmit(event) {
            event.preventDefault();
            event.stopPropagation();
            let vm = this;
            console.log(vm.cud_items);
            console.log(typeof vm.customer_selected);
            let postdatas = {
                device_basic: {
                    cus_id: vm.customer_selected,
                    model: vm.devicetype_selected,
                    device_name: vm.deviceID,
                    purchase_date: vm.purchasedate,
                    delete_at: null,
                    creator: vm.loginData.account,
                    modifier: vm.loginData.account,
                },
                machine_sensor_list: [],
                machine_light_list: [],
                preload: vm.preload,
                check_devID: "no",
            };
            if (vm.is_add_update == "update") {
                postdatas.device_basic["id"] = vm.update_devID;
                delete postdatas.device_basic.creator;
                if (vm.update_ole_device_name != vm.deviceID) {
                    delete postdatas.device_basic.delete_at;
                    postdatas.check_devID = "yes";
                }
            }
            let isok = true;
            for (let i = 0; i < vm.cud_items.length; i++) {
                const element = vm.cud_items[i];
                let sensor_warn = null;
                let sensor_error = null;
                if (
                    ["min", "max"].indexOf(element.range) != -1 &&
                    element.type == "sensor"
                ) {
                    if (
                        (element["warn_" + element.range] === null ||
                            element["warn_" + element.range] == "") &&
                        (element["error_" + element.range] === null ||
                            element["error_" + element.range] == "") &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg("資料尚未輸入完整");
                        break;
                    }

                    if (
                        element["warn_" + element.range] != null &&
                        element["warn_" + element.range] != "" &&
                        element["error_" + element.range] != null &&
                        element["error_" + element.range] != "" &&
                        element.enable == "Y"
                    ) {
                        if (
                            element.range == "max" &&
                            element.error_max < element.warn_max
                        ) {
                            console.log(element["warn_" + element.range]);
                            console.log(element["error_" + element.range]);
                            console.log(element);

                            isok = false;
                            vm.setSetTimeOutAlertMsg(
                                "「異常上限」需大於等於「注意上限」"
                            );
                            break;
                        }

                        if (
                            element.range == "min" &&
                            element.warn_min < element.error_min
                        ) {
                            isok = false;
                            vm.setSetTimeOutAlertMsg(
                                "「注意下限」需大於等於「異常下限」"
                            );
                            break;
                        }
                    }

                    if (element["warn_" + element.range] != null) {
                        sensor_warn = String(element["warn_" + element.range]);
                    }
                    if (element["error_" + element.range] != null) {
                        sensor_error = String(
                            element["error_" + element.range]
                        );
                    }
                } else if (
                    element.range == "between" &&
                    element.type == "sensor"
                ) {
                    if (
                        element.error_max < element.warn_max &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg(
                            "「異常上限」需大於等於「注意上限」"
                        );
                        break;
                    }

                    if (
                        element.warn_min < element.error_min &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg(
                            "「注意下限」需大於等於「異常下限」"
                        );
                        break;
                    }

                    if (
                        element.warn_max <= element.warn_min &&
                        element.enable == "Y"
                    ) {
                        isok = false;
                        vm.setSetTimeOutAlertMsg(
                            "「注意上限」需大於「注意上限」"
                        );
                        break;
                    }

                    sensor_warn =
                        String(element.warn_min) +
                        "," +
                        String(element.warn_max);
                    sensor_error =
                        String(element.error_min) +
                        "," +
                        String(element.error_max);
                }

                if (element.type == "sensor") {
                    postdatas.machine_sensor_list.push({
                        device_id: element.device_id,
                        model_sensor_id: element.model_sensor_id,
                        sensor_id: element.sensor_id,
                        sensor_enable: element.enable,
                        sensor_range: element.range,
                        sensor_warn: sensor_warn,
                        sensor_error: sensor_error,
                        sensor_unit: element.sensor_unit,
                        creator: element.creator,
                        modifier: vm.loginData.account,
                        created_at: element.created_at,
                    });
                } else {
                    postdatas.machine_light_list.push({
                        device_id: element.device_id,
                        model_light_id: element.model_light_id,
                        light_id: element.light_id,
                        light_enable: element.enable,
                        creator: element.creator,
                        modifier: vm.loginData.account,
                        created_at: element.created_at,
                    });
                }
            }
            if (!isok) {
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            console.log(postdatas.machine_sensor_list);
            console.log(postdatas.machine_light_list);

            if (vm.is_add_update == "add") {
                let vm = this;
                vm.toggleAlertModal(true);
                console.log("go add");
                let params = {};
                params["url"] =
                    "/api/" +
                    vm.$route.params.SYSTEM +
                    "/1.0/Customized/Device/Macro";
                params["postdata"] = postdatas;
                console.log(params);

                vm.axiosPostAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result.status != 200) {
                            vm.setSetTimeOutAlertMsg(result.data);
                            return;
                        }
                        if (result.data["Response"] == "ok") {
                            vm.setSetTimeOutAlertMsg("新增成功");
                            vm.purchasedate = "";
                            vm._onChange(vm.devicetype_selected);
                        } else {
                            vm.setSetTimeOutAlertMsg(result.data["Response"]);
                        }
                    })
                    .catch((response) => {
                        console.log(response);
                        vm.setSetTimeOutAlertMsg(response);
                    })
                    .finally(() => {
                        console.log("done");
                        vm.deviceID = "";
                        vm.toggleAlertModal(false);
                        vm.toggleSetTimeOutAlertModal();
                    });
            } else {
                console.log("go update");
                let params = {};
                params["methods"] = "PATCH";
                params["whichFunction"] = "deviceUpdate";
                params["postdata"] = postdatas;
                console.log(params);
                let success = false;
                vm.axiosAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result["Response"] == "ok") {
                            vm.setSetTimeOutAlertMsg("修改成功");
                            success = true;
                        } else {
                            vm.setSetTimeOutAlertMsg(result["Response"]);
                        }
                    })
                    .catch((response) => {
                        console.log(response);
                        vm.setSetTimeOutAlertMsg(response);
                    })
                    .finally(() => {
                        vm.toggleSetTimeOutAlertModal();
                        if (success) {
                            vm._reset([
                                "customer_selected",
                                "customer_options",
                            ]);
                            vm.action = "query";
                            vm._queryCustomerDevice();
                        }
                    });
            }
        },

        _onChange(event) {
            console.log(event);
            let vm = this;
            if (event != "") {
                vm.toggleAlertModal(true);
                vm._reset([
                    "action",
                    "is_add_update",
                    "customer_selected",
                    "customer_options",
                    "devicetype_selected",
                    "devicetype_options",
                    "deviceID",
                    "purchasedate",
                    "query_items",
                    "update_devID",
                    "update_ole_device_name",
                ]);
                if (vm.is_add_update == "add") {
                    //抓取該機型的預設感測點
                    vm._queryModelSensorListSensorList(event);
                } else {
                    console.log("$$$$$$$$$$$$$$$$$$$$");
                    //抓取該機型的感測點
                    vm._queryMachineSensorListSensorList(event);
                }
            }
        },

        _queryCusID() {
            let vm = this;
            let params = {};
            vm._reset(["action", "query_items"]);
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/TableData";
            params["urlparams"] = {
                table: "customer",
            };
            console.log(params);
            let showmsg = true;
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    let options = [];
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length > 0) {
                            result.data["QueryTableData"].map((ele) => {
                                if (ele.delete_enable == "N") {
                                    options.push({
                                        text: ele.id + " " + ele.cus_name,
                                        value: ele.id,
                                    });
                                    showmsg = false;
                                }
                            });
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
