        _queryModelSensorListSensorList(model) {
            let vm = this;
            let params = {};
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/2.0/my/CommonUse/SqlSyntax/JoinMultiTable";
            params["urlparams"] = {
                getSqlSyntax: "no",
            };
            params["postdata"] = {
                condition_1: {
                    fields: {
                        model_sensor_list: "",
                        sensor_list: ["sensor_name"],
                    },
                    join: {
                        model_sensor_list: [
                            {
                                sensor_list: {
                                    sensor_id: "id",
                                },
                            },
                        ],
                    },
                    jointype: { model_sensor_list_sensor_list: "left" },
                    tables: ["model_sensor_list", "sensor_list"],
                    limit: ["0", "9999999"],
                    where: {
                        model_sensor_list: {
                            model: [model],
                        },
                    },
                    symbols: {
                        model_sensor_list: {
                            model: ["equal"],
                        },
                    },
                    orderby: "",
                    subquery: "",
                },
            };
            console.log(params);
            let showmsg = true;
            vm.axiosPostAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length == 0) {
                            vm.setSetTimeOutAlertMsg(
                                "查無此機型預測感測點資料"
                            );
                        } else {
                            result.data["QueryTableData"].map((ele) => {
                                if (ele["model_sensor_list$enable"] == "Y") {
                                    let warn = {
                                        min: null,
                                        max: null,
                                    };
                                    let error = {
                                        min: null,
                                        max: null,
                                    };
                                    if (
                                        ele["model_sensor_list$sensor_range"] ==
                                        "between"
                                    ) {
                                        if (
                                            ele[
                                                "model_sensor_list$sensor_warn"
                                            ] != null
                                        ) {
                                            warn.min = ele[
                                                "model_sensor_list$sensor_warn"
                                            ].split(",")[0];
                                            warn.max = ele[
                                                "model_sensor_list$sensor_warn"
                                            ].split(",")[1];
                                        }
                                        if (
                                            ele[
                                                "model_sensor_list$sensor_error"
                                            ] != null
                                        ) {
                                            error.min = ele[
                                                "model_sensor_list$sensor_error"
                                            ].split(",")[0];
                                            error.max = ele[
                                                "model_sensor_list$sensor_error"
                                            ].split(",")[1];
                                        }
                                    } else if (
                                        ele["model_sensor_list$sensor_range"] ==
                                            "max" ||
                                        ele["model_sensor_list$sensor_range"] ==
                                            "min"
                                    ) {
                                        if (
                                            ele[
                                                "model_sensor_list$sensor_warn"
                                            ] != null
                                        ) {
                                            warn[
                                                ele[
                                                    "model_sensor_list$sensor_range"
                                                ]
                                            ] =
                                                ele[
                                                    "model_sensor_list$sensor_warn"
                                                ];
                                        }
                                        if (
                                            ele[
                                                "model_sensor_list$sensor_error"
                                            ] != null
                                        ) {
                                            error[
                                                ele[
                                                    "model_sensor_list$sensor_range"
                                                ]
                                            ] =
                                                ele[
                                                    "model_sensor_list$sensor_error"
                                                ];
                                        }
                                    }
                                    vm.cud_items.push({
                                        model_sensor_id:
                                            ele["model_sensor_list$id"],
                                        sensor_id:
                                            ele["model_sensor_list$sensor_id"],
                                        type: "sensor",
                                        enable: ele["model_sensor_list$enable"],
                                        name: ele["sensor_list$sensor_name"],
                                        range:
                                            ele[
                                                "model_sensor_list$sensor_range"
                                            ],
                                        error_min: error.min,
                                        warn_min: warn.min,
                                        warn_max: warn.max,
                                        error_max: error.max,
                                        sensor_unit:
                                            ele[
                                                "model_sensor_list$sensor_unit"
                                            ],
                                    });
                                    showmsg = false;
                                }
                            });
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    console.log("done");
                    //抓取該機型的預設燈號點
                    vm._queryModelLightListLightList(model);
                    if (showmsg) vm.toggleSetTimeOutAlertModal();
