const state = {
    apiDetail: {},
    apiMap: [],
    sandboxApiMap: [],
    system:""
};
const getters = {
    get_apiDetail: state => {
        return state.apiDetail;
    },
    get_apiMap: state => {
        return state.apiMap;
    },
    get_sandboxApiMap: state => {
        return state.sandboxApiMap;
    },
    get_system: state => {
        return state.system;
    }
};
const actions = {
    set_apiDetail(context, obj) {
        context.commit("SET_APIDETAIL", obj);
    },
    set_apiMap(context, ary) {
        context.commit("SET_APIMAP", ary);
    },
    set_sandboxApiMap(context, ary) {
        context.commit("SET_SANDBOXAPIMAP", ary);
    },
    set_system(context, data) {
        context.commit("SET_SYSTEM", data);
    }
};
const mutations = {
    SET_APIDETAIL(state, obj) {
        state.apiDetail = obj;
    },
    SET_APIMAP(state, ary) {
        state.apiMap = ary;
    },
    SET_SANDBOXAPIMAP(state, ary) {
        state.sandboxApiMap = ary;
    },
    SET_SYSTEM(state, data) {
        state.system = data;
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
