<template>
    <div id="Immediate" class="customize_scrollbar">
        <b-row class="immediate-block">
            <b-col sm="12" class="text-right">
                <!-- <button
                        type="button"
                        class="mr-3 btn btn-primary"
                        @click="_socketConnect"
                    >
                        連接socket
                    </button> -->
                <span class="publicText">選擇欲查看之日誌總類：</span>
                <b-form-select
                    v-model="selected"
                    :options="options"
                    style="width: 200px; border-color: #06122a"
                    class="publicText"
                    size="md"
                >
                    <template v-slot:first>
                        <b-form-select-option :value="null" disabled
                            >-- 請選擇一個選項 --</b-form-select-option
                        >
                    </template>
                </b-form-select>
                <button
                    type="button"
                    class="publicButton btn ml-2"
                    @click.prevent="_monitor"
                    :disabled="!connected || waiting"
                >
                    送出
                </button>
            </b-col>
            <b-col sm="12">
                <div
                    id="loggingBlock"
                    class="mt-3 customize_scrollbar publicBorder publicBlueBG-level3"
                    v-if="log_list.length != 0"
                >
                    <template v-if="monitorNow === 'PaaS_LOG'">
                        <b-table
                            hover
                            borderless
                            sticky-header="550px"
                            responsive="xl"
                            :items="log_list"
                            :fields="paaslog_fields"
                            id="paaslogTable"
                            class="paaslogTable customize_scrollbar"
                        >
                            <template v-slot:cell(show_details)="row">
                                <b-button
                                    size="sm"
                                    @click="row.toggleDetails"
                                    class="mr-2"
                                    >{{
                                        row.detailsShowing ? "隱藏" : "顯示"
                                    }}訊息</b-button
                                >
                            </template>

                            <template v-slot:row-details="row">
                                <!-- <b-card>
                                    <b-row class="mb-2">
                                                    <b-col class="text-sm-left">
                                                        
                                                    </b-col>
                                                </b-row> -->
                                <jsonView
                                    v-if="
                                        typeof JSON.parse(row.item.message) !=
                                        'string'
                                    "
                                    :data="JSON.parse(row.item.message)"
                                    :theme="''"
                                    :deep="3"
                                    :iconStyle="'triangle'"
                                    :fontSize="15"
                                    :lineHeight="24"
                                    :closed="false"
                                    :iconColor="[]"
                                />
                                <p v-else>{{ row.item.message }}</p>
                                <!-- <b-button
                                                    size="sm"
                                                    @click="row.toggleDetails"
                                                    >Hide Message</b-button
                                                >
                                    </b-card> -->
                            </template>
                        </b-table>
                    </template>
                    <template v-else>
                        <pre
                            v-for="(item, key) in log_list"
                            :key="key"
                            class="customize_scrollbar"
                            >{{ item }}</pre
                        >
                    </template>
                </div>
                <h3 v-else class="publicText mt-2 ml-2">無資料</h3>
            </b-col>
        </b-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import jsonView from "@/components/json-view/index.vue";
export default {
    name: "Immediate",
    data() {
        return {
            paaslog_fields: [
                {
                    key: "upload_at",
                    label: "上傳時間",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "acstime",
                    label: "發生錯誤時間",
                    sortable: true,
                    thClass: "publicTH",
                },
                {
                    key: "levelname",
                    label: "錯誤等級",
                    sortable: true,
                    thClass: "publicTH",
                },
                {
                    key: "filename",
                    label: "發生錯誤檔案",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "lineno",
                    label: "發生錯誤檔案行號",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "process",
                    label: "進程編號",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "thread",
                    label: "線程編號",
                    sortable: false,
                    thClass: "publicTH",
                },
                {
                    key: "show_details",
                    label: "錯誤訊息",
                    sortable: false,
                    thClass: "publicTH",
                },
            ],
            log_list: [],
            set_latest_number: 0,
            options: [],
            selected: null,
            monitorNow: null,
            sid: null,
            connected: false,
            waiting: false,
        };
    },
    created() {
        this._getLogFile();
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            clickChild: "featurelistbar/get_clickChild",
        }),
    },
    components: {
        jsonView,
    },
    mounted() {
        let vm = this;
        console.log(vm.clickChild.child_name);
        if (vm.clickChild.child_name.split("_").length > 1) {
            //定位至選擇點
            vm.$nextTick(() => {
                vm.$el
                    .querySelector("#" + vm.clickChild.child_name)
                    .scrollIntoView({
                        behavior: "auto",
                        block: "start",
                    });
            });
        }
    },
    watch: {
        set_latest_number: {
            handler() {
                console.log("@@@@@@@");
                // this.QueryData();
                this.$socket.emit("keep");
                // this.$socket.emit("keep",this.selected);
            },
        },
    },
    updated() {
        // 聊天定位到底部
        //https://zhuanlan.zhihu.com/p/89906315
        //https://lianjy357.github.io/baymax/2019/04/24/Vue%E8%81%8A%E5%A4%A9%E6%A1%86%E9%BB%98%E8%AE%A4%E6%BB%9A%E5%8A%A8%E5%88%B0%E5%BA%95%E9%83%A8/
        if (this.log_list.length != 0) {
            if (this.monitorNow === "PaaS_LOG") {
                this.$nextTick(() => {
                    let ele = document.getElementById("paaslogTable");
                    ele.scrollTop = ele.scrollHeight;
                    console.log(ele.scrollTop);
                    console.log(ele.scrollHeight);
                });
            } else {
                let ele = document.getElementById("loggingBlock");
                ele.scrollTop = ele.scrollHeight;
            }
            console.log("======================");
        }
    },
    methods: {
        ...mapActions({
            axiosGetAction: "commonaxios/axiosGetAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
        }),

        // 连接socket
        _socketConnect() {
            console.log(this.set_latest_number);
            this.toggleAlertModal(true);
            if (!this.$socket.connected) {
                console.log(this.$socket);
                console.log(this.sockets);
                // 开始连接socket
                this.$socket.connect();
                // this.sockets.listener.subscribe("re_connect", (val) => {
                this.sockets.subscribe("re_connect", (val) => {
                    console.log(val);
                    this.sid = val.sid;
                    console.log("成功连接服务！！！");
                    this.connected = true;
                    // this.setSetTimeOutAlertMsg("socket連接成功!!");
                    // this.toggleSetTimeOutAlertModal();
                });
            } else {
                this.setSetTimeOutAlertMsg("socket已經連接!!");
                this.toggleSetTimeOutAlertModal();
                console.log("already connected");
            }
            this.toggleAlertModal(false);
            // this.socket_subscribe_watch_log();
        },

        _getLogFile() {
            let vm = this;
            var params = {};
            params["url"] =
                "api/PaaS/1.0/sysps/LogType/" + vm.$route.params.SYSTEM;
            console.log(params);
            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    let config = {
                        uWSGI_LOG: "uWSGI日誌",
                        PaaS_LOG: "API日誌",
                    };
                    result.data["FileType"].forEach((element) => {
                        let obj = {
                            value: element,
                            text: config[element],
                        };
                        vm.options.push(obj);
                    });
                    vm._socketConnect();
                }
            });
        },

        //註冊監聽watch_log事件
        _monitor() {
            let vm = this;
            console.log(vm.selected);
            console.log(vm.$socket.connected);
            if (!vm.$socket.connected) {
                vm.setSetTimeOutAlertMsg("請先連接socket!!");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            if (vm.selected === null) {
                vm.setSetTimeOutAlertMsg("必須選擇一個選項");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            vm.toggleAlertModal(true);
            vm.waiting = true;
            vm.log_list = [];
            vm.set_latest_number = 0;
            console.log("~~~socket_subscribe~~~~");
            vm.$socket.emit("monitor", {
                filename: vm.selected,
                system: vm.$route.params.SYSTEM,
            });
            console.log(vm.$route.params.SYSTEM);
            console.log(vm.selected);
            vm.monitorNow = vm.selected;
            console.log(vm.monitorNow);
            console.log("~~~socket_subscribe~~~~");
            vm.options.forEach((element) => {
                //將舊的訂閱全部取消，以免同時抓取多種訂閱
                // vm.sockets.listener.unsubscribe("watch_" + element);
                vm.sockets.unsubscribe("watch_" + element);
            });
            // vm.sockets.listener.subscribe(
            vm.sockets.subscribe("watch_" + vm.selected, (val) => {
                if (
                    vm.set_latest_number < val.already_print_num ||
                    (vm.set_latest_number == 0 && val.already_print_num == 0)
                ) {
                    console.log(val.data);
                    console.log(vm.set_latest_number);
                    console.log(val.already_print_num);
                    vm.log_list.push(val.data);
                    if (vm.set_latest_number == 0) {
                        console.log("$$$$$$$$$$$");
                        vm.toggleAlertModal(false);
                        vm.waiting = false;
                    }
                    vm.set_latest_number = val.already_print_num;
                }
            });
            console.log(vm.sockets.listener);
            console.log(vm.sockets);
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
    //調用router守衛，在離開時將socket disconnect
    beforeRouteLeave(to, from, next) {
        console.log(to);
        console.log(from);
        console.log(this.$socket.connected);
        if (this.$socket.connected) {
            console.log(this.$socket.disconnected);
            this.$socket.disconnect(); // 結束socket
            // this.$socket.emit("disconnect",this.selected);
            // this.sockets.listener.unsubscribe("re_connect");
            this.sockets.unsubscribe("re_connect");
            console.log(this.$socket.disconnected);
        }
        next();
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.immediate-block {
    // padding: 2rem !important;
}
#loggingBlock {
    padding: 1rem;
    max-height: 600px;
    overflow-y: auto;
}
.input-block {
    margin-bottom: 3rem !important;
    .title {
        font-size: 20px;
        font-weight: bold;
        color: #0566ca;
        // display: flex;
        // align-items: center;
        // justify-content: center;
    }
    .text {
        display: inline-block;
        width: 200px;
        margin-left: 10px;
    }
    .submit {
        &:disabled {
            cursor: not-allowed;
            color: white;
        }
    }
}
.table-block {
    margin-bottom: 5rem !important;
}
</style>

