<template>
    <div
        :id="chartid"
        :style="styleGroup"
        :chartData="chartData"
        :changePage="changePage"
        :chartName="chartName"
        :windowInnerWidth="windowInnerWidth"
    >
        {{ chartid }}
    </div>
    <!-- <div :id="chartid" :style="{width:chartWidth, height:chartHeight}">{{chartid}}</div> -->
</template>

<script>
import { mapGetters } from "vuex";
import "echarts-liquidfill/src/liquidFill.js";
export default {
    name: "alertModal",
    props: {
        chartId: String,
        styleGroup: Object,
        chartData: Object,
        changePage: Number,
        chartName: String,
        windowInnerWidth: Number,
    },
    data() {
        return {
            chartid: this.chartId,
            reset_check: {
                width: 0,
            },
            // chartName: this.chartName
        };
    },
    computed: {
        ...mapGetters({}),
    },
    created() {},
    mounted() {
        this._drawChart();
    },
    watch: {
        chartData: {
            handler() {
                this._drawChart();
            },
            //object & array watch change
            deep: true,
        },
        changePage: {
            handler() {
                var myEvent = new Event("resize");
                window.dispatchEvent(myEvent);
            },
        },
        windowInnerWidth: {
            handler() {
                let vm = this;
                if (vm.windowInnerWidth > 992) {
                    if (vm.reset_check.width != 992) {
                        vm.reset_check.width = 992;
                        this._drawChart();
                    }
                } else if (vm.windowInnerWidth > 768) {
                    if (vm.reset_check.width != 768) {
                        vm.reset_check.width = 768;
                        this._drawChart();
                    }
                } else {
                    if (vm.reset_check.width != 0) {
                        vm.reset_check.width = 0;
                        this._drawChart();
                    }
                }
            },
        },
    },
    methods: {
        _drawChart: function () {
            let vm = this;
            var type = vm.chartData.senType;
            if (type === "line") {
                vm._drawLineChart();
            } else if (type === "areaLine") {
                vm._drawAreaLineChart();
            } else if (type === "horizonalBar") {
                vm._drawHorizonalBarChart();
            } else if (type === "verticalBar") {
                vm._drawVerticalBarChart();
            } else if (type === "pie") {
                vm._drawPieChart();
            } else if (type === "hollowPie") {
                vm._drawHollowPieChart();
            } else if (type === "scatter") {
                vm._drawScatterChart();
            } else if (type === "gauge") {
                vm._drawGaugeChart();
            } else if (type === "groupVerticalBar") {
                vm._drawGroupVerticalBarChart();
            } else if (type === "piePercent") {
                vm._drawPiePercentChart();
            } else if (type === "VerticalBarLine") {
                vm._drawVerticalBarLineChart();
            } else if (type === "SmartMeter") {
                vm._drawSmartMeterChart();
            } else if (type === "Self") {
                vm._drawSelfChart();
            } else if (type === "API") {
                vm._drawAPIChart();
            } else if (type === "CPU") {
                vm._drawCPUChart();
            } else if (type === "test") {
                vm._drawTestChart();
            } else if (type === "Machine") {
                vm._drawMachineChart();
            } else if (type === "DISK") {
                vm._drawDISKChart();
            } else if (type === "RAM") {
                vm._drawRAMChart();
            } else if (type === "Time") {
                vm._drawTimeChart();
            }
        },

        _drawLineChart() {
            //折線圖
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    text: vm.chartName,
                    left: "center",
                    // x: "center",
                    // y: "top",
                    // textAlign: "left"
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985",
                        },
                    },
                },
                legend: {
                    data: ["邮件营销"],
                },
                xAxis: {
                    type: "category",
                    data: vm.chartData.senTime,
                },
                yAxis: {
                    type: "value",
                },
                series: {
                    type: "line",
                    data: vm.chartData.senData,
                },
            });
            // vm.reQuerySensor(myChart, index);
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawAreaLineChart() {
            //區塊折線圖
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    text: vm.chartName,
                    left: "center",
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985",
                        },
                    },
                },
                // legend: {
                // data: ["a"] //圖標會抓name
                // },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true,
                },
                xAxis: {
                    type: "category",
                    boundaryGap: false,
                    data: vm.chartData.senTime,
                },
                yAxis: {
                    type: "value",
                    boundaryGap: [0, 0.01],
                },
                series: [
                    {
                        // name: "a",
                        data: vm.chartData.senData,
                        type: "line",
                        areaStyle: {},
                    },
                ],
            });

            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawHorizonalBarChart() {
            //橫式長條圖
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    text: vm.chartName,
                    left: "center",
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985",
                        },
                    },
                },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true,
                },
                xAxis: {
                    type: "value",
                    boundaryGap: [0, 0.01],
                },
                yAxis: {
                    data: vm.chartData.senTime,
                },
                series: [
                    {
                        // name: "",
                        type: "bar",
                        data: vm.chartData.senData,
                    },
                    //不只一種類型的資料
                    // {
                    //   name: "test",
                    //   type: "bar",
                    //   data: [22, 18, 19, 23, 29]
                    // }
                ],
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawVerticalBarChart() {
            //直式長條圖
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            // myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    text: vm.chartName,
                    left: "center",
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985",
                        },
                    },
                },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true,
                },
                xAxis: {
                    data: vm.chartData.senTime,
                },
                yAxis: {
                    type: "value",
                    boundaryGap: [0, 0.01],
                },
                series: [
                    {
                        // name: "",
                        type: "bar",
                        data: vm.chartData.senData,
                    },
                    //不只一種類型的資料
                    // {
                    //   name: "test",
                    //   type: "bar",
                    //   data: [22, 18, 19, 23, 29]
                    // }
                ],
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawPieChart() {
            //圓餅圖
            let vm = this;
            let sensorValArray = [];
            // let i = 0;
            // vm.chartData.senData.forEach(data => {
            //   sensorValArray.push({
            //     name: vm.chartData.senTime[i],
            //     value: data
            //   });
            //   i++;
            // });
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    text: vm.chartName,
                    left: "right",
                },
                tooltip: {
                    trigger: "item",
                    formatter: "{b} : {c} ({d}%)",
                },
                series: [
                    {
                        data: vm.chartData.senData,
                        type: "pie",
                        radius: "80%",
                        center: ["50%", "60%"],
                        emphasis: {
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: "rgba(0, 0, 0, 0.5)",
                            },
                        },
                        silent: true,
                    },
                ],
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawHollowPieChart() {
            //同心圓餅圖
            let vm = this;
            let sensorValArray = [];
            let i = 0;
            vm.chartData.senData.forEach((data) => {
                sensorValArray.push({
                    name: vm.chartData.senTime[i],
                    value: data,
                });
                i++;
            });
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    text: vm.chartName,
                    left: "center",
                },
                series: [
                    {
                        data: sensorValArray,
                        type: "pie",
                        radius: ["50%", "70%"],
                        avoidLabelOverlap: false,
                        label: {
                            normal: {
                                show: false,
                                position: "center",
                            },
                            emphasis: {
                                show: true,
                                textStyle: {
                                    fontSize: "30",
                                    fontWeight: "bold",
                                },
                            },
                        },
                        labelLine: {
                            normal: {
                                show: false,
                            },
                        },
                    },
                ],
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawScatterChart() {
            const vm = this;
            let sensorValArray = [];
            for (let i = 0; i < vm.chartData.senTime.length; i++) {
                sensorValArray.push([
                    vm.chartData.senTime[i],
                    vm.chartData.senData[i],
                ]);
            }
            //散點圖
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    text: vm.chartName,
                    left: "center",
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985",
                        },
                    },
                },
                legend: {
                    // data: ["a"], //圖標會抓name
                },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true,
                },
                xAxis: {
                    type: "value",
                    splitLine: {
                        lineStyle: {
                            type: "dashed",
                        },
                    },
                    splitNumber: 20,
                },
                yAxis: {
                    type: "value",
                    splitLine: {
                        lineStyle: {
                            type: "dashed",
                        },
                    },
                },
                series: [
                    {
                        // name: "a",
                        data: sensorValArray,
                        type: "scatter",
                        emphasis: {
                            label: {
                                show: true,
                                position: "left",
                                color: "blue",
                                fontSize: 16,
                            },
                        },
                    },
                ],
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawGaugeChart() {
            //半圓圖
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                series: [
                    {
                        type: "gauge",
                        center: ["50%", "65%"],
                        startAngle: 180,
                        endAngle: 0,
                        min: vm.chartData.senMin,
                        max: vm.chartData.senMax,
                        splitNumber: 1,
                        itemStyle: vm.chartData.senItemStyle,
                        // {
                        //   color: "#00b152",
                        // },

                        progress: {
                            show: true,
                            width: 30,
                        },
                        pointer: {
                            icon:
                                "path://M2090.36389,615.30999 L2090.36389,615.30999 C2091.48372,615.30999 2092.40383,616.194028 2092.44859,617.312956 L2096.90698,728.755929 C2097.05155,732.369577 2094.2393,735.416212 2090.62566,735.56078 C2090.53845,735.564269 2090.45117,735.566014 2090.36389,735.566014 L2090.36389,735.566014 C2086.74736,735.566014 2083.81557,732.63423 2083.81557,729.017692 C2083.81557,728.930412 2083.81732,728.84314 2083.82081,728.755929 L2088.2792,617.312956 C2088.32396,616.194028 2089.24407,615.30999 2090.36389,615.30999 Z",
                            length: "45%",
                            width: 6,
                            offsetCenter: [0, "5%"],
                        },
                        axisLine: {
                            lineStyle: {
                                width: 30,
                            },
                        },
                        axisTick: {
                            lineStyle: {
                                width: 0,
                            },
                        },
                        splitLine: {
                            distance: -25,
                            length: 0,
                            lineStyle: {
                                width: 0,
                            },
                        },
                        axisLabel: {
                            distance: 0,
                            color: "#999",
                            fontSize: 15,
                        },
                        title: {
                            show: false,
                        },
                        detail: {
                            show: false,
                        },
                        data: [vm.chartData.senData],
                    },
                ],
                // series: chartDataList,
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
            // setInterval(function() {
            //   var test = (Math.random() * 100).toFixed(2) - 0;
            //   var option = {};
            //   myChart.setOption({
            //     series: [
            //       {
            //         axisLine: {
            //           lineStyle: {
            //             color: [
            //               [test / 100, "#ffd200"],
            //               [1, "#808080"]
            //             ]
            //           }
            //         },
            //         data: [{ value: test, name: "" }]
            //       }
            //     ]
            //   });
            //   // myChart.setOption(option);
            // }, 2000);
        },

        _drawGroupVerticalBarChart() {
            //直式長條圖
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                title: {
                    // text: vm.chartName,
                    // left: "center"
                },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985",
                        },
                    },
                },
                dataset: {
                    source: vm.chartData.senData,
                },
                grid: {
                    top: "10%",
                    left: "3%",
                    right: "4%",
                    bottom: "20%",
                    containLabel: true,
                },
                xAxis: {
                    type: "category",
                    // data: vm.chartData.senTime
                },
                yAxis: {
                    type: "value",
                    boundaryGap: [0, 0.1],
                },
                series: [
                    { type: "bar", itemStyle: { color: "#83899d" } },
                    { type: "bar", itemStyle: { color: "#ff8c1a" } },
                    { type: "bar", itemStyle: { color: "#a2a2a2" } },
                ],
                legend: { bottom: 20 },
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawPiePercentChart() {
            //半圓圖
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                // title: {
                //   // text: vm.chartName,
                //   // left: "center"
                // },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true,
                },
                series: [
                    {
                        type: "pie",
                        radius: ["80%", "60%"],
                        avoidLabelOverlap: true,
                        label: {
                            show: true,
                            position: "center",
                            fontSize: 20,
                            color: "#000000",
                        },
                        color: ["#70AD47", "transparent"],
                        data: [
                            {
                                value: vm.chartData.senData[0],
                                name: vm.chartData.senData[0] + "%",
                            },
                            { value: 100 - vm.chartData.senData[0], name: "" },
                        ],
                        silent: true,
                    },
                ],
            });

            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawVerticalBarLineChart() {
            //直式長條圖+折線圖
            let vm = this;

            let getKey = Object.keys(vm.chartData.senData.bar);
            let chartTypeTitle = [];
            let chartDataList = [];
            let lineData = [];
            getKey.forEach((dataTitle) => {
                chartTypeTitle.push(dataTitle);

                lineData.push(vm.chartData.senData.bar[dataTitle].data);

                if (chartDataList.length === 0) {
                    chartDataList.push({
                        name: dataTitle,
                        type: "bar",
                        data: vm.chartData.senData.bar[dataTitle].data,
                        itemStyle: {
                            color: "#83899d",
                        },
                    });
                } else if (chartDataList.length === 1) {
                    chartDataList.push({
                        name: dataTitle,
                        type: "bar",
                        data: vm.chartData.senData.bar[dataTitle].data,
                        itemStyle: {
                            color: "#ff8c1a",
                        },
                    });
                } else if (chartDataList.length === 2) {
                    chartDataList.push({
                        name: dataTitle,
                        type: "bar",
                        data: vm.chartData.senData.bar[dataTitle].data,
                        itemStyle: {
                            color: "#a2a2a2",
                        },
                    });
                } else {
                    chartDataList.push({
                        name: dataTitle,
                        type: "bar",
                        data: vm.chartData.senData.bar[dataTitle].data,
                    });
                }
            });
            let lineDataList = [];
            for (let i = 0; i < lineData.length; i++) {
                for (let j = 0; j < lineData[i].length; j++) {
                    if (i === 0) {
                        lineDataList.push(lineData[i][j]);
                    } else {
                        lineDataList[j] = lineDataList[j] + lineData[i][j];
                    }
                    if (i === lineData.length - 1) {
                        lineDataList[j] = (
                            lineDataList[j] / lineData.length
                        ).toFixed(2);
                    }
                }
            }
            chartTypeTitle.push(vm.chartData.senData.line.name);
            chartDataList.push({
                name: vm.chartData.senData.line.name,
                type: "line",
                data: lineDataList,
                smooth: true,
                itemStyle: {
                    color: vm.chartData.senData.line.color,
                },
            });
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                // title: {
                //   text: vm.chartName,
                //   left: "center"
                // },
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985",
                        },
                    },
                },
                legend: {
                    data: chartTypeTitle,
                    bottom: 0,
                },
                grid: {
                    top: "20%",
                    left: "3%",
                    right: "4%",
                    bottom: "12%",
                    containLabel: true,
                },
                xAxis: [
                    {
                        type: "category",
                        data: vm.chartData.senTime,
                        axisPointer: {
                            type: "shadow",
                        },
                    },
                ],
                yAxis: [
                    {
                        type: "value",
                        name: "單位 " + vm.chartData.senData.unit,
                        minInterval: 1,
                    },
                ],
                series: chartDataList,
            });
            window.addEventListener("resize", function () {
                myChart.resize(); //初始化的
            });
        },

        _drawSmartMeterChart() {
            let vm = this;
            let echarts = require("echarts");
            let myChart = echarts.init(document.getElementById(vm.chartid));
            myChart.clear(); //clear()可清空series的內容，dispose()可完全清空
            myChart.setOption({
                color: ["#80FFA5", "#00DDFF", "#37A2FF", "#FF0087", "#FFBF00"],
                title: {
                    text: "生產功率",
                    left: "10%",
                    top: "10%",
                    textAlign: "center",
                },
                title: [
                    {
                        text: "生產功率",
                        left: "10%",
                        top: "1%",
                        textAlign: "center",
                    },
                    {
                        text: "耗電量",
                        left: "10%",
                        top: "58%",
                        textAlign: "center",
                    },
                    {
                        text: "碳排放量",
                        left: "45%",
                        top: "58%",
                        textAlign: "center",
                    },
                    {
                        text: "用電量",
                        left: "80%",
                        top: "58%",
                        textAlign: "center",
                    },
                ],

                legend: {
                    data: ["Line 1"],
                },
                // toolbox: {
                //   feature: {
                //     saveAsImage: {},
                //   },
                // },
                grid: {
                    left: "10%",
                    right: "10%",
                    bottom: "58%",
                    containLabel: true,
                },
                xAxis: [
                    {
                        type: "category",
                        boundaryGap: false,
                        data: [
                            "",
                            "20Hz",
                            "40Hz",
                            "60Hz",
                            "80Hz",
                            "100Hz",
                            "120Hz",
                        ],
                    },
                ],
                yAxis: [
                    {
                        type: "value",
                    },
                ],
                series: [
                    {
                        name: "",
                        type: "line",
                        stack: "",
                        smooth: true,
                        lineStyle: {
                            width: 0,
                        },
                        showSymbol: false,
                        areaStyle: {
                            opacity: 0.8, //漸層
                            color: new echarts.graphic.LinearGradient(
                                0,
                                0,
                                0,
                                1,
                                [
                                    {
                                        offset: 0,
                                        color: "rgba(128, 255, 165)", //綠
                                    },
                                    {
                                        offset: 1,
                                        color: "rgba(1, 191, 236)", //藍
                                    },
                                ]
                            ),
                        },
                        emphasis: {
                            focus: "series",
                        },
                        data: [14, 23, 10, 26, 70, 34, 55],
                    },

                    {
