<template>
  <div class="inspectionUseInformation table-responsive col-12 mb-2">
    <li>檢驗數據</li>
    <template v-if="inspectionUseInformationUseProject">
      <ul class="nav nav-pills mb-1 text-center" id="pills-tab" role="tablist">
        <li
          v-for="(item, key) in projectData"
          :key="key"
          class="nav-item"
          role="presentation"
        >
          <a
            class="nav-link"
            :class="{ active: key == 0 }"
            :id="'pills-' + item.code + '-tab'"
            data-toggle="pill"
            :href="'#pills-' + item.code"
            role="tab"
            :aria-controls="'pills-' + item.code"
            aria-selected="true"
            @click="tab_index = item.code"
            >{{ item.chinese_name }}</a
          >
        </li>
      </ul>
      <div class="tab-content" id="myTabContent">
        <!-- <p v-if="selectInspectionProject" class="text-left">
        第{{ selectInspectionPage }}筆檢測時間:
        <span>{{
          inspectionData[selectInspectionProject][selectInspectionPage - 1][
            "timestamp"
          ]
        }}</span>
      </p> -->
        <div
          v-for="(item, key) in projectData"
          :key="key"
          :class="{ show: key == 0, active: key == 0 }"
          class="tab-pane fade"
          :id="'pills-' + item.code"
          role="tabpanel"
          :aria-labelledby="'pills-' + item.code + '-tab'"
        >
          <b-table
            :items="inspectionItems"
            :fields="inspectionUseInformationFields"
            sticky-header="186px"
            :no-border-collapse="noBorderCollapse"
            class="text-center inspectionUseInformation-table-detail mb-2 customize_scrollbar"
          >
            <template #cell(inspectionValue)="row">
              <template v-if="row.item['result'] == 'NG'">
                <b style="color: red">{{ row.item["inspectionValue"] }}</b>
              </template>
              <template v-else>
                {{ row.item["inspectionValue"] }}
              </template>
            </template>
            <template #cell(result)="row">
              <template v-if="row.item['result'] == 'NG'">
                <b style="color: red">{{ row.item["result"] }}</b>
              </template>
              <template v-else>
                {{ row.item["result"] }}
              </template>
            </template>
          </b-table>
          <b-pagination-nav
            :number-of-pages="projectLength"
            v-model="selectInspectionPage"
            base-url="#"
            first-text="第一筆"
            prev-text="上一筆"
            next-text="下一筆"
            last-text="最後一筆"
            use-router
            align="center"
          ></b-pagination-nav>
        </div>
      </div>
    </template>
    <template v-else>
      <div>
        <b-table
          :items="inspectionItems"
          :fields="inspectionUseInformationFields"
          sticky-header="186px"
          :no-border-collapse="noBorderCollapse"
          class="text-center inspectionUseInformation-table-detail mb-2 customize_scrollbar"
        >
          <template #cell(inspectionValue)="row">
            <template v-if="row.item['result'] == 'NG'">
              <b style="color: red">{{ row.item["inspectionValue"] }}</b>
            </template>
            <template v-else>
              {{ row.item["inspectionValue"] }}
            </template>
          </template>
          <template #cell(result)="row">
            <template v-if="row.item['result'] == 'NG'">
              <b style="color: red">{{ row.item["result"] }}</b>
            </template>
            <template v-else>
              {{ row.item["result"] }}
            </template>
          </template>
        </b-table>
        <b-pagination-nav
          :number-of-pages="projectLength"
          v-model="selectInspectionPage"
          base-url="#"
          first-text="第一筆"
          prev-text="上一筆"
          next-text="下一筆"
          last-text="最後一筆"
          use-router
          align="center"
        ></b-pagination-nav>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "inspectionUseInformation",
  data() {
    return {
      noBorderCollapse: true,
      projectData: [],
      selectInspectionProject: 0,
      selectInspectionPage: 1,
      inspectionData: null,
      inspectionItems: [],
      projectCode: [],
      projectLength: 1,
      tab_index: 1,
      // selectInspectionProject: null,
      // selectInspectionUseInformationPage: 1,
      // projectData: [],
      // inspectionItems: [],
      // projectLength: 1,
    };
  },
  created: function () {
    this._getSelectProject();
    this._setFirstInspection();
  },
  mounted: function () {},
  watch: {
    selectInspectionProject: {
      handler() {
        let vm = this;
        //改變製程的檢驗筆數
        if (vm.inspectionData != undefined) {
          if (vm.inspectionData[vm.selectInspectionProject] != undefined) {
            vm.projectLength =
              vm.inspectionData[vm.selectInspectionProject].length;
          } else {
            vm.inspectionItems = [];
          }
        }
      },
    },
    selectInspectionPage: {
      handler() {
        let vm = this;
        //抓選擇的檢測紀錄
        if (vm.inspectionData != undefined) {
          if (vm.inspectionData.length > 0) {
            //沒有製程選項的
            let selectInspection =
              vm.inspectionData[vm.selectInspectionPage - 1]["inspection"];
            let select_inspection_time =
              vm.inspectionData[vm.selectInspectionPage - 1]["timestamp"];
            let inspectionArray = [];
            for (let i = 0; i < selectInspection.length; i++) {
              let inspectionObj = {};
              inspectionObj["time"] = select_inspection_time;
              inspectionObj["code"] = selectInspection[i]["code"];
              inspectionObj["standardValue"] =
                selectInspection[i]["standardValue"];
              inspectionObj["inspectionValue"] =
                selectInspection[i]["inspectionValue"];
              inspectionObj["result"] = selectInspection[i]["result"];
              inspectionArray.push(inspectionObj);
            }
            console.log(inspectionArray);
            if (inspectionArray.length != 0) {
              vm.inspectionItems = inspectionArray;
            }
          } else {
            let selectInspection =
              vm.inspectionData[vm.selectInspectionProject][
                vm.selectInspectionPage - 1
              ]["inspection"];
            let select_inspection_time =
              vm.inspectionData[vm.selectInspectionProject][
                vm.selectInspectionPage - 1
              ]["timestamp"];
            let inspectionArray = [];
            for (let i = 0; i < selectInspection.length; i++) {
              let inspectionObj = {};
              inspectionObj["time"] = select_inspection_time;
              inspectionObj["code"] = selectInspection[i]["code"];
              inspectionObj["standardValue"] =
                selectInspection[i]["standardValue"];
              inspectionObj["inspectionValue"] =
                selectInspection[i]["inspectionValue"];
              inspectionObj["result"] = selectInspection[i]["result"];
              inspectionArray.push(inspectionObj);
            }
            console.log(inspectionArray);
            if (inspectionArray.length != 0) {
              vm.inspectionItems = inspectionArray;
            }
          }
        } else {
          vm.inspectionItems = [];
        }
      },
    },
    inspectionUseInformationProjectData: {
      handler() {
        let vm = this;
        vm.projectCode = Object.keys(vm.inspectionUseInformationProjectData[0]);
        vm.selectInspectionProject = vm.projectCode[0];
        this.tab_index = vm.selectInspectionProject;
      },
    },
    inspectionUseInformationData: {
      handler() {
        this._setFirstInspection();
      },
    },
    inspectionUseInformationItemData: {
      handler() {
        this._setFirstInspection();
      },
    },
    tab_index: {
      handler() {
        let vm = this;
        vm.inspectionUseInformationProjectData;
        if (vm.inspectionData != undefined) {
          if (vm.inspectionData[vm.selectInspectionProject] != undefined) {
            vm.projectLength =
              vm.inspectionData[vm.selectInspectionProject].length;
          }
        }
        vm.selectInspectionPage = 1;
        vm._getSelectProject();
      },
    },
  },
  computed: {
    ...mapGetters({
      inspectionUseInformationProjectData:
        "mesInspectionUseInformation/get_inspectionUseInformationProjectData",
      inspectionUseInformationFields:
        "mesInspectionUseInformation/get_inspectionUseInformationFields",
      inspectionUseInformationItemData:
        "mesInspectionUseInformation/get_inspectionUseInformationItemData",
      inspectionUseInformationUseProject:
        "mesInspectionUseInformation/get_inspectionUseInformationUseProject",
      inspectionUseInformationData:
        "mesInspectionUseInformation/get_inspectionUseInformationData",
    }),
  },
  methods: {
    _getSelectProject() {
      let vm = this;
      console.log(vm.inspectionUseInformationProjectData);

      let project_array = [];
      if (vm.inspectionUseInformationProjectData == null) {
        return;
      }
      vm.projectCode = Object.keys(vm.inspectionUseInformationProjectData[0]);
      for (let i = 0; i < vm.projectCode.length; i++) {
        project_array.push({
          code: vm.projectCode[i],
          chinese_name:
            vm.inspectionUseInformationProjectData[0][vm.projectCode[i]],
        });
      }
      vm.projectData = project_array;

      let inspectionArray = [];
      vm.selectInspectionProject = vm.tab_index;
      vm.inspectionData = vm.inspectionUseInformationProjectData[1];

      if (
        vm.inspectionUseInformationProjectData[1][vm.selectInspectionProject] !=
        undefined
      ) {
        let first_inspection =
          vm.inspectionUseInformationProjectData[1][
            vm.selectInspectionProject
          ][0]["inspection"];
        let first_inspection_time =
          vm.inspectionUseInformationProjectData[1][
            vm.selectInspectionProject
          ][0]["timestamp"];
        vm.projectLength =
          vm.inspectionUseInformationProjectData[1][
            vm.selectInspectionProject
          ].length;
        console.log(first_inspection);
        for (let i = 0; i < first_inspection.length; i++) {
          let inspectionObj = {};
          inspectionObj["time"] = first_inspection_time;
          inspectionObj["code"] = first_inspection[i]["code"];
          inspectionObj["standardValue"] = first_inspection[i]["standardValue"];
          inspectionObj["inspectionValue"] =
            first_inspection[i]["inspectionValue"];
          inspectionObj["result"] = first_inspection[i]["result"];
          inspectionArray.push(inspectionObj);
        }
      }
      if (inspectionArray.length != 0) {
        vm.inspectionItems = inspectionArray;
      }
    },

    _setFirstInspection() {
      let vm = this;
      let inspectionArray = [];
      vm.selectInspectionProject = vm.projectCode[0];
      if (vm.inspectionUseInformationUseProject) {
        if (vm.inspectionUseInformationProjectData != undefined) {
          vm.inspectionData = vm.inspectionUseInformationData;
          if (vm.inspectionData.length > 0) {
            let first_inspection =
              vm.inspectionUseInformationData[vm.selectInspectionProject][0][
                "inspection"
              ];
            let first_inspection_time =
              vm.inspectionUseInformationData[vm.selectInspectionProject][0][
                "timestamp"
              ];
            vm.projectLength =
              vm.inspectionUseInformationData[
                vm.selectInspectionProject
              ].length;
            for (let i = 0; i < first_inspection.length; i++) {
              let inspectionObj = {};
              inspectionObj["time"] = first_inspection_time;
              inspectionObj["code"] = first_inspection[i]["code"];
              inspectionObj["standardValue"] =
                first_inspection[i]["standardValue"];
              inspectionObj["inspectionValue"] =
                first_inspection[i]["inspectionValue"];
              inspectionObj["result"] = first_inspection[i]["result"];
              inspectionArray.push(inspectionObj);
            }
            if (inspectionArray.length != 0) {
              vm.inspectionItems = inspectionArray;
            }
          } else {
            vm.inspectionItems = [];
          }
        }
      } else {
        //沒有製程按鈕
        vm.inspectionItemData = vm.inspectionUseInformationItemData;
        if (vm.inspectionItemData.length >= vm.selectInspectionPage) {
          let first_inspection =
            vm.inspectionItemData[vm.selectInspectionPage - 1]["inspection"];
          let first_inspection_time =
            vm.inspectionItemData[vm.selectInspectionPage - 1]["timestamp"];
          vm.projectLength = vm.inspectionItemData.length;
          vm.inspectionData = vm.inspectionItemData;
          for (let i = 0; i < first_inspection.length; i++) {
            let inspectionObj = {};
            inspectionObj["time"] = first_inspection_time;
            inspectionObj["code"] = first_inspection[i]["code"];
            inspectionObj["standardValue"] =
              first_inspection[i]["standardValue"];
            inspectionObj["inspectionValue"] =
              first_inspection[i]["inspectionValue"];
            inspectionObj["result"] = first_inspection[i]["result"];
            inspectionArray.push(inspectionObj);
          }
          if (inspectionArray.length != 0) {
            vm.inspectionItems = inspectionArray;
          }
        } else {
          vm.inspectionItems = [];
        }
      }
    },
  },
};
</script>

<style lang="scss">
// .inspectionUseInformation-table-detail th,
// .inspectionUseInformation-table-detail td {
