.page-link {
  color: #fff;
  background-color: #83899d;
  border: 1px solid #83899d;
}
.page-item.active .page-link {
  background-color: #ff8c1a !important;
  border-color: #ff8c1a !important;
}
.page-item.disabled .page-link {
  color: #fff;
  background-color: #83899d;
  border: 1px solid #83899d;
}
.inspectionUseInformation .nav-pills .nav-link.active,
.inspectionUseInformation .nav-pills .show > .nav-link {
  background-color: #ff8c1a;
  color: #ffffff;
}
.inspectionUseInformation .nav-pills .nav-link,
.inspectionUseInformation .nav-pills .show > .nav-link {
  background-color: #83899d;
  color: #ffffff;
}
</style>
