<template>
  <div id="MES" ref="MES">
    <b-row
      class="h-100 mh-100"
      style="overflow-x: auto"
      :class="{ publicMinWidth550: this_route != 'mes' }"
    >
      <template v-if="this_route != 'mes'">
        <featureListBar />
        <div class="featureContent customize_scrollbar h-100 mh-100">
          <router-view
            class="mes_router pt-2"
            style="height: calc(100vh - 76px)"
          >
          </router-view>
        </div>
        <!-- <b-col
          xl="11"
          lg="12"
          md="12"
          sm="12"
          class="featureContent customize_scrollbar h-100 mh-100"
        >
          <b-row
            class="info"
            v-if="
              this_route != 'elecBoard' &&
              featureMainData.body[
                $router.currentRoute.path.split('/')[
                  $router.currentRoute.path.split('/').length - 1
                ]
              ].showtitlebanner
            "
          >
            <b-container>
              <h2 class="publicText pt-5 pb-5">
                {{
                  featureMainData.body[
                    $router.currentRoute.path.split("/")[
                      $router.currentRoute.path.split("/").length - 1
                    ]
                  ].title
                }}
              </h2>
              <h5 class="publicText pb-5">
                {{
                  featureMainData.body[
                    $router.currentRoute.path.split("/")[
                      $router.currentRoute.path.split("/").length - 1
                    ]
                  ].content
                }}
              </h5>
            </b-container>
          </b-row>
          <router-view class="mes_router"> </router-view>
        </b-col> -->
      </template>
      <!-- <template v-else>
        <b-container fluid class="h-100 mh-100 publicMinWidth550">
          <b-row
            align-h="center"
            align-v="center"
            class="h-100 mh-100"
            style="overflow: auto"
          >
            <featureMain></featureMain>
          </b-row>
        </b-container>
      </template> -->
    </b-row>
    <loadScreenModal />
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import featureMain from "@/components/featureMain.vue";
import featureListBar from "@/components/featureListBar.vue";
import loadScreenModal from "@/components/MES/loadScreenModal.vue";
export default {
  name: "MES",
  data() {
    let vm = this;
    return {
      this_route: "mes",
      collapse_apilist: false,
      collapse_info: false,
      featureMainData: {
        title: "機台管理",
        cols: {
          xl: 3,
          lg: 6,
          md: 6,
          sm: 12,
        },
        body: {
          // overView: {
          //   title: "平面總覽",
          //   showtitlebanner: false,
          //   content: "",
          //   img: require("@/assets/main/apidoc.png"),
          // },
          elecBoard: {
            title: "即時看板",
            showtitlebanner: false,
            content:
              "提供使用者查看所有註冊後機台的運行狀態，即時掌握各機台運轉資訊，使異狀檢測即刻預知以保障產能及現場人員安全",
            img: require("@/assets/main/apidoc.png"),
          },
          activation: {
            title: "稼動率",
            showtitlebanner: true,
            content:
              "用來衡量機器設備使用效率，扣除掉因保養、維修、假日等停機狀況，機器設備可以運轉的時間之比率。",
            img: require("@/assets/main/apiinfo.png"),
          },
          operateLog: {
            title: "運轉日誌",
            showtitlebanner: true,
            content:
              "可選擇指定日期查詢在當天，機器運轉的過程中所發生的機台狀況(如：關機、開機、待機、異常)",
            img: require("@/assets/main/apisandbox.png"),
          },
          alarmHistory: {
            title: "警報履歷",
            showtitlebanner: true,
            content:
              "顯示一個月內機台在運轉的過程中，所發生的任何問題。以可視性高的圖表方式記錄次數，與詳細的狀態起訖時間。",
            img: require("@/assets/main/apitableschema.png"),
          },
          // deviceSetting: {
          //   title: "機台設定",
          //   showtitlebanner: true,
          //   content: "設定機台的異常值與範圍值。",
          //   img: require("@/assets/main/apitableschema.png"),
          // },
          inspectionRecord: {
            title: "檢測履歷",
            showtitlebanner: true,
            content: "test",
            img: require("@/assets/main/apitableschema.png"),
          },
        },
      },
      featureListBarData: {
        body: {
          overView: {
            title: "平面總覽",
          },
          elecBoard: {
            title: "即時看板",
            // collapse: {
            //   childitem: {
            //     "v-for": ["成型機", "打頭機", "輾牙機"],
            //     click: {
            //       data: "",
            //     },
            //     content: {},
            //   },
            // },
          },
          activation: {
            title: "稼動率",
          },
          operateLog: {
            title: "運轉日誌",
          },
          alarmHistory: {
            title: "警報履歷",
          },
          // deviceSetting: {
          //   title: "機台設定",
          // },
          inspectionRecord: {
            title: "檢測履歷",
          },
        },
      },
      searchBarFixed: false,
      leftblockShow: true,
      monitorScrollTop: 0,
    };
  },
  created() {
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("mes_selectdCustomer", this.selectCustomer);
    });
    if (!sessionStorage.getItem("mes_selectdCustomer")) {
      this.setSelectedCustomer(0);
    } else {
      this.setSelectedCustomer(sessionStorage.getItem("mes_selectdCustomer"));
    }
    sessionStorage.removeItem("mes_selectdCustomer");

    if (/(\/mes)$/.test(this.$router.currentRoute.path)) {
      this.setClickRoute("elecBoard");
    } else {
      this.this_route = this.$router.currentRoute.path.split("/")[
        this.$router.currentRoute.path.split("/").length - 1
      ];
    }
    this._setListBar(1);
    this.setSelectedCustomer(1);
    console.log(this.$router.currentRoute.path);

    // if (this.$route.name == "mes") {
    //   if (
    //     this.pageAccess.logging.authority == false &&
    //     this.pageAccess.setting.authority == false &&
    //     this.pageAccess.operation.authority == false
    //   ) {
    //     this.$router.push("/" + this.$route.params.SYSTEM + "/mes/overView");
    //   } else {
    //     this.$router.push(
    //       "/" + this.$route.params.SYSTEM + "/mes/deviceManage"
    //     );
    //   }
    // }
  },
  computed: {
    ...mapGetters({
      axiosResult: "commonaxios/get_axiosResult",
      clickRoute: "featuremain/get_clickRoute",
      clickChild: "featurelistbar/get_clickChild",
      pageAccess: "getlogin/get_pageAccess",
      loginData: "getlogin/get_loginData",
      selectCustomer: "mesData/get_selectedCustomer",
    }),
  },
  components: {
    featureMain,
    featureListBar,
    loadScreenModal,
  },
  watch: {
    clickRoute: {
      handler(value) {
        console.log(value);
        console.log(this.$router.currentRoute.path);
        if (value != "") {
          this.$router.push({
            name: value,
            params: {
              SYSTEM: this.$route.params.SYSTEM,
            },
          });
          this.this_route = value;
          console.log(this.this_route);
          console.log(this.$router.currentRoute.path);
          console.log(/(\/mes)$/.test(this.$router.currentRoute.path));
          // if (value === "elecboard") {
          //   console.log(Object.entries(this.api_group));
          //   let thisclick = Object.entries(this.api_group);
          //   console.log(thisclick);
          //   this.setClickChild({
          //     child_name: value,
          //     data: this.api_version,
          //   });
          // } else {
          this.setClickChild({
            child_name: value,
            data: {},
          });
          // }
          this.setClickRoute("");
        }
      },
    },
    "clickChild.child_name": {
      handler(value) {
        let vm = this;
        let child_name = value.split("_");
        if (child_name.length >= 2) {
          if (child_name[0] == "elecBoard") {
            sessionStorage.setItem("mes_elecboard_process", child_name[1]);
          }
        }
        if (child_name.length >= 2) {
          if (child_name[0] == "deviceSetting") {
            sessionStorage.setItem("mes_deviceSetting_process", child_name[1]);
          }
        }
        console.log("@@@@@@@");
        console.log(vm.clickChild);
        console.log(value);
      },
    },
  },
  mounted() {},
  methods: {
    ...mapActions({
      setFeatureMainData: "featuremain/set_featureMainData",
      setFeatureListBarData: "featurelistbar/set_featureListBarData",
      setClickRoute: "featuremain/set_clickRoute",
      setClickChild: "featurelistbar/set_clickChild",
      setCollapseIDList: "featurelistbar/set_collapseIDList",
      toggleCollapseID: "featurelistbar/toggle_collapseID",
      setSelectedCustomer: "mesData/set_selectedCustomer",
    }),

    _onResize() {
      let vm = this;
      console.log(window.innerWidth);
      if (window.innerWidth < 1200) {
        // vm.resize_style["margin-top"] =
        //     vm.$refs.leftblock.clientHeight + "px";
        vm.searchBarFixed = true;
      } else {
        vm.searchBarFixed = false;
        // vm.resize_style["margin-top"] = "0px";
      }
    },

    _handleScroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      console.log(scrollTop);

      let offsetTop = document.querySelector("#featureListBar").offsetTop;
      console.log(offsetTop);
      if (window.innerWidth < 1200) {
        if (scrollTop > offsetTop) {
          if (!this.searchBarFixed) {
            setTimeout(() => {
              this.searchBarFixed = true;
            }, 50);
          } else {
            this.searchBarFixed = true;
          }
        } else {
          this.searchBarFixed = false;
        }
        this.monitorScrollTop = scrollTop;
      }

      this.$nextTick(() => {
        if (this.monitorScrollTop > scrollTop) {
          console.log("~~~~");
          this.leftblockShow = true;
        } else {
          console.log("@@@@");
          this.leftblockShow = false;
        }
      });
      this.monitorScrollTop = scrollTop;
    },
    _setListBar(customer) {
      let vm = this;
      if (customer == "0") {
        vm.featureMainData.body = {
          deviceManage: {
            title: "機台管理",
            showtitlebanner: false,
            content: "",
            img: require("@/assets/main/apidoc.png"),
          },
        };
        vm.featureListBarData.body = {
          deviceManage: {
            title: "機台管理",
          },
        };
      } else {
        vm.featureMainData.body = {
          elecBoard: {
            title: "即時看板",
            showtitlebanner: false,
            content:
              "提供使用者查看所有註冊後機台的運行狀態，即時掌握各機台運轉資訊，使異狀檢測即刻預知以保障產能及現場人員安全",
            img: require("@/assets/main/apidoc.png"),
          },
          activation: {
            title: "稼動率",
            showtitlebanner: true,
            content:
              "用來衡量機器設備使用效率，扣除掉因保養、維修、假日等停機狀況，機器設備可以運轉的時間之比率。",
            img: require("@/assets/main/apiinfo.png"),
          },
          operateLog: {
            title: "運轉日誌",
            showtitlebanner: true,
            content:
              "可選擇指定日期查詢在當天，機器運轉的過程中所發生的機台狀況(如：關機、開機、待機、異常)",
            img: require("@/assets/main/apisandbox.png"),
          },
          alarmHistory: {
            title: "警報履歷",
            showtitlebanner: true,
            content:
              "顯示一個月內機台在運轉的過程中，所發生的任何問題。以可視性高的圖表方式記錄次數，與詳細的狀態起訖時間。",
            img: require("@/assets/main/apitableschema.png"),
          },
          inspectionRecord: {
            title: "檢測履歷",
            showtitlebanner: true,
            content: "test",
            img: require("@/assets/main/apitableschema.png"),
          },
        };
        vm.featureListBarData.body = {
          elecBoard: {
            title: "即時看板",
          },
          activation: {
            title: "稼動率",
          },
          operateLog: {
            title: "運轉日誌",
          },
          alarmHistory: {
            title: "警報履歷",
          },
          inspectionRecord: {
            title: "檢測履歷",
          },
        };
      }
      this.setFeatureListBarData(this.featureListBarData);
    },
    //資料reset
    _reset(keep) {
      var def = this.$options.data();
      for (var i = 0; i < keep.length; i++) {
        def[keep[i]] = this[keep[i]];
      }
      Object.assign(this.$data, def);
      //https://codepen.io/karimcossutti/pen/ObXyKq
    },
  },
  beforeRouteUpdate(to, from, next) {
    console.warn(to);
    console.warn(from);
    console.warn(next);
    // this._reset(["api_group", "api_version", "api_group_display_type"]);
    this.this_route = to.name;
    if (to.name === "mes") {
      this.setClickRoute("elecBoard");
      //   this.setFeatureMainData(this.featureMainData);
    } else {
      // if (to.name != "elecboard") this.toggleCollapseID(to.name);
      this.setClickChild({
        child_name: to.name,
        data: {},
      });
      this.$nextTick(() => {
        window.addEventListener("resize", this._onResize);
        window.addEventListener("scroll", this._handleScroll);
        console.log("####");
      });
    }
    next();
  },
  //調用router守衛，在離開時reset
  beforeRouteLeave(to, from, next) {
    this._reset([]);
    next();
  },
  beforeDestroy() {
    let vm = this;
    // vm.setSelectedCustomer(0);
    window.removeEventListener("resize", vm._onResize);
    window.removeEventListener("scroll", vm._handleScroll);
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.card,
.card-header {
  border: 0;
}
.apipath {
  color: #007bff;
  cursor: pointer;
}
.apimethod {
  font-weight: bold;
  min-width: 60px;
  /* color: green; */
}
.bcardtext {
  display: flex;
  flex-direction: row;
  -webkit-box-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  align-items: center;
  height: 36px;
  min-height: 36px;
  margin: 5px;
  padding: 5px;
}
</style>

<style lang="scss">
@font-face {
  font-family: "dfyuanstd-w3";
  src: url("../assets/MES/css/dfyuanstd-w3.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}
