      bucketUseInformationItem:
        "mesBucketUseInformation/get_bucketUseInformationItem",
      bucketUseInformationTotalWeight:
        "mesBucketUseInformation/get_bucketUseInformationTotalWeight",
    }),
  },
};
</script>

