const state = {
  loginAccount: null,
  loginUsername: null,
  loginStatus: false,
  loginAccessList: null,
  selectPage: "",
  selectPageName: "",
  pageName: null,
  PageAccess: {
    elecboard: false,
    machstatus: false,
    yieldstatistics: false,
    activation: false,
    operatelog: false,
    alarmhistory: false,
    workorderhistory: false,
    qualityinsp: false,
    systemset: false,
    machmalfunction: false,
    deviceseting: false,
    inspectionrecord:false,
    testpage: false
  }
};
const getters = {
  get_loginData: state => {
    var loginData = {};
    loginData.account = state.loginAccount;
    loginData.username = state.loginUsername;
    loginData.status = state.loginStatus;
    loginData.accesslist = state.loginAccessList;
    return loginData;
  },
  get_selectPage: state => {
    var selectPage = state.selectPage;
    return selectPage;
  },
  get_selectPageName: state => {
    var selectPageName = state.selectPageName;
    return selectPageName;
  },
  get_pageName: state => {
    var pageName = state.pageName;
    return pageName;
  },
  get_pageAccess: state => {
    var pageAccess = {};
    pageAccess.elecboard = state.PageAccess.elecboard;
    pageAccess.machstatus = state.PageAccess.machstatus;
    pageAccess.yieldstatistics = state.PageAccess.yieldstatistics;
    pageAccess.activation = state.PageAccess.activation;
    pageAccess.operatelog = state.PageAccess.operatelog;
    pageAccess.alarmhistory = state.PageAccess.alarmhistory;
    pageAccess.workorderhistory = state.PageAccess.workorderhistory;
    pageAccess.qualityinsp = state.PageAccess.qualityinsp;
    pageAccess.systemset = state.PageAccess.systemset;
    pageAccess.machmalfunction = state.PageAccess.machmalfunction;
    pageAccess.testpage = state.PageAccess.testpage;
    return pageAccess;
  }
};
const actions = {
  change_loginData(context, obj) {
    context.commit("CHANGE_LOGINDATA", obj);
  },
  change_selectPage(context, obj) {
    context.commit("CHANGE_SELECTPAGE", obj);
  },
  change_selectPageName(context, obj) {
    context.commit("CHANGE_SELECTPAGENAME", obj);
  },
  set_pageName(context) {
    context.commit("SET_PAGENAME");
  },
  change_pageAccess(context, obj) {
    context.commit("CHANGE_PAGEACCESS", obj);
  },
};
const mutations = {
  CHANGE_LOGINDATA(state, obj) {
    state.loginAccount = obj.account;
    state.loginStatus = obj.status;
    state.loginUsername = obj.username;
    state.loginAccessList = obj.accesslist;
  },
  CHANGE_SELECTPAGE(state, value) {
    state.selectPage = value;
  },
  CHANGE_SELECTPAGENAME(state, value) {
    state.selectPageName = value;
  },
  SET_PAGENAME(state) {
    state.pageName = {
      elecboard: "電子看板",
      machstatus: "機台狀態",
      yieldstatistics: "產量統計",
      activation: "稼動率",
      operatelog: "運轉日誌",
      alarmhistory: "警報履歷",
      workorderhistory: "工單履歷",
      qualityinsp: "品檢統計",
      systemset: "系統設定",
      machmalfunction: "機台故障",
      testpage: "即時工單狀態",
    };
  },
  CHANGE_PAGEACCESS(state, obj) {
    state.PageAccess.elecboard = obj.elecboard;
    state.PageAccess.machstatus = obj.machstatus;
    state.PageAccess.yieldstatistics = obj.yieldstatistics;
    state.PageAccess.activation = obj.activation;
    state.PageAccess.operatelog = obj.operatelog;
    state.PageAccess.alarmhistory = obj.alarmhistory;
    state.PageAccess.workorderhistory = obj.workorderhistory;
    state.PageAccess.qualityinsp = obj.qualityinsp;
    state.PageAccess.systemset = obj.systemset;
    state.PageAccess.machmalfunction = obj.machmalfunction;
    state.PageAccess.testpage = obj.testpage;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
