const state = {
    featureMainData: {
        title: "",
        cols: {},
        body: {},
    },
    clickRoute: ""
};
const getters = {
    get_featureMainData: state => {
        return state.featureMainData;
    },
    get_clickRoute: state => {
        return state.clickRoute;
    }
};
const actions = {
    set_featureMainData(context, obj) {
        context.commit("SET_FEATUREMAINDATA", obj);
    },
    set_clickRoute(context, data) {
        context.commit("SET_CLICKROUTE", data);
    },
};
const mutations = {
    SET_FEATUREMAINDATA(state, obj) {
        state.featureMainData.title = obj.title;
        state.featureMainData.cols = obj.cols;
        state.featureMainData.body = obj.body;
    },
    SET_CLICKROUTE(state, data) {
        state.clickRoute = data;
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
