<template>
    <div id="Sensor" ref="Sensor">
        <b-row
            class="h-100 mh-100"
            style="overflow-x: auto"
            :class="{ publicMinWidth550: this_route != 'sensor' }"
        >
            <template v-if="this_route != 'sensor'">
                <featureListBar />
                <!-- <b-col
                    xl="1"
                    lg="12"
                    md="12"
                    sm="12"
                    class="leftblock customize_scrollbar publicBlueBG-level2"
                    id="leftblock"
                    ref="leftblock"
                    :class="searchBarFixed == true ? 'isFixed' : ''"
                >
                    <b-icon
                        icon="list"
                        scale="1.5"
                        style="color: blue"
                        class="mt-2 feature-list-icon"
                        @click.prevent="toggleSideBarStatus"
                    ></b-icon>
                    <b-row class="edoc-feature-list">
                        <b-col xl="12" lg="3" md="3" class="mt-2">
                            <p
                                v-b-toggle.collapse-register
                                @click="
                                    _reset([
                                        'collapse_register',
                                        'this_route',
                                        'this_click',
                                    ])
                                "
                                class="font-weight-bold"
                                :class="{
                                    titlechoose:
                                        this_click['child_name'].split(
                                            '_'
                                        )[0] == 'register',
                                }"
                            >
                                機台註冊
                                <b-icon
                                    icon="caret-up-fill"
                                    v-if="collapse_register"
                                ></b-icon>
                                <b-icon icon="caret-down-fill" v-else></b-icon>
                            </p>
                            <b-collapse
                                id="collapse-register"
                                class="mt-2"
                                v-model="collapse_register"
                            >
                                <router-link
                                    :to="{
                                        name: 'register',
                                        params: {
                                            SYSTEM: $route.params.SYSTEM,
                                        },
                                    }"
                                >
                                    <b-button-group vertical>
                                        <div
                                            v-for="(item, index) in [
                                                '主機台',
                                                '智慧電表',
                                                '振動主馬達',
                                                '振動連座軸承',
                                                '伺服驅動器',
                                            ]"
                                            :key="index"
                                            @click="_goto(index)"
                                            class="insidebtn"
                                            :class="{
                                                inside_click:
                                                    this_click['child_name'] ==
                                                    'register_' + index,
                                            }"
                                        >
                                            {{ item }}
                                        </div>
                                    </b-button-group>
                                </router-link>
                            </b-collapse>
                        </b-col>
                    </b-row>
                </b-col> -->
                <b-col
                    xl="11"
                    lg="12"
                    md="12"
                    sm="12"
                    class="featureContent customize_scrollbar h-100 mh-100"
                >
                    <b-row
                        class="info"
                        v-if="
                            this_route != 'sensor' &&
                            featureMainData.body[
                                $router.currentRoute.path.split('/')[
                                    $router.currentRoute.path.split('/')
                                        .length - 1
                                ]
                            ].showtitlebanner
                        "
                    >
                        <b-container>
                            <h1 class="publicText pt-5 pb-5">
                                {{
                                    featureMainData.body[
                                        $router.currentRoute.path.split("/")[
                                            $router.currentRoute.path.split("/")
                                                .length - 1
                                        ]
                                    ].hasOwnProperty("title")
                                        ? featureMainData.body[
                                              $router.currentRoute.path.split(
                                                  "/"
                                              )[
                                                  $router.currentRoute.path.split(
                                                      "/"
                                                  ).length - 1
                                              ]
                                          ].title
                                        : featureMainData.title
                                }}
                            </h1>
                            <h2 class="publicText pt-5 pb-5">
                                {{
                                    featureMainData.body[
                                        $router.currentRoute.path.split("/")[
                                            $router.currentRoute.path.split("/")
                                                .length - 1
                                        ]
                                    ].content
                                }}
                            </h2>
                        </b-container>
                    </b-row>
                    <router-view class="edoc_router"> </router-view>
                </b-col>
            </template>
            <template v-else>
                <b-container fluid class="h-100 mh-100 publicMinWidth550">
                    <b-row
                        align-h="center"
                        align-v="center"
                        class="h-100 mh-100"
                        style="overflow: auto"
                    >
                        <featureMain></featureMain>
                    </b-row>
                </b-container>
            </template>
        </b-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import featureMain from "@/components/featureMain.vue";
import featureListBar from "@/components/featureListBar.vue";
export default {
    name: "Sensor",
    data() {
        return {
            this_route: "register",
            featureMainData: {
                title: "機台註冊",
                cols: {
                    xl: "auto",
                    lg: "auto",
                    md: "auto",
                    sm: "auto",
                },
                body: {
                    register: {
                        showtitlebanner: true,
                        content:
                            "持續追蹤使用記錄及機台運轉資訊，並對產線進行大量資料收集與分析。藉由分析的資訊到實體機台控制，針對相關數據資料接收處理後，提供應用數位模擬模型 進行機具設計與精進。",
                        img: require("@/assets/main/sensor.png"),
                        style: {
                            "card-body-text": {
                                "max-width": "400px",
                            },
                        },
                    },
                },
            },
            featureListBarData: {
                body: {
                    register: {
                        title: "機台註冊",
                        collapse: {
                            "childitem": {
                                "v-for": [
                                    "主機台",
                                    "智慧電表",
                                    "振動",
                                    "伺服驅動器",
                                    "荷重元"
                                ],
                                click: {
                                    data: {},
                                },
                                content: {},
                            },
                        },
                    },
                },
            },
            searchBarFixed: false,
            collapse_register: true,
        };
    },
    created() {
        console.log(this.$router);
        if (/(\/sensor)$/.test(this.$router.currentRoute.path)) {
            this.setFeatureMainData(this.featureMainData);
        } else {
            this.this_route = this.$router.currentRoute.path.split("/")[
                this.$router.currentRoute.path.split("/").length - 1
            ];
        }
        this.toggleCollapseID("register");
        console.log(this.$router.currentRoute.path);
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            sideBarStatus: "featuresidebar/get_sideBarStatus",
            clickRoute: "featuremain/get_clickRoute",
        }),
    },
    components: {
        featureMain,
        featureListBar,
    },
    watch: {
        clickRoute: {
            handler(value) {
                console.log(value);
                console.log(this.$router.currentRoute.path);
                if (value != "") {
                    this.$router.push({
                        name: value,
                        params: {
                            SYSTEM: this.$route.params.SYSTEM,
                        },
                    });
                    this.this_route = value;
                    console.log(this.this_route);
                    console.log(this.$router.currentRoute.path);
                    this.setClickChild({
                        child_name: value,
                        data: {},
                    });
                    this.setClickRoute("");
                }
            },
        },
    },
    mounted() {
        this.setFeatureListBarData(this.featureListBarData);
    },
    methods: {
        ...mapActions({
            setFeatureMainData: "featuremain/set_featureMainData",
            setClickRoute: "featuremain/set_clickRoute",
            toggleSideBarStatus: "featuresidebar/toggle_sideBarStatus",
            setFeatureListBarData: "featurelistbar/set_featureListBarData",
            setClickChild: "featurelistbar/set_clickChild",
            toggleCollapseID: "featurelistbar/toggle_collapseID",
        }),

        _onResize() {
            let vm = this;
            console.log(window.innerWidth);
            if (window.innerWidth < 1200) {
                // vm.resize_style["margin-top"] =
                //     vm.$refs.leftblock.clientHeight + "px";
                vm.searchBarFixed = true;
            } else {
                vm.searchBarFixed = false;
                // vm.resize_style["margin-top"] = "0px";
            }
        },

        _handleScroll() {
            let scrollTop =
                window.pageYOffset ||
                document.documentElement.scrollTop ||
                document.body.scrollTop;
            console.log(scrollTop);

            let offsetTop = document.querySelector("#leftblock").offsetTop;
            console.log(offsetTop);
            if (window.innerWidth < 1200) {
                if (scrollTop > offsetTop) {
                    if (!this.searchBarFixed) {
                        setTimeout(() => {
                            this.searchBarFixed = true;
                        }, 50);
                    } else {
                        this.searchBarFixed = true;
                    }
                } else {
                    this.searchBarFixed = false;
                }
                this.monitorScrollTop = scrollTop;
            }

            this.$nextTick(() => {
                if (this.monitorScrollTop > scrollTop) {
                    console.log("~~~~");
                    this.leftblockShow = true;
                } else {
                    console.log("@@@@");
                    this.leftblockShow = false;
                }
            });
            this.monitorScrollTop = scrollTop;
        },

        _goto(index) {
            console.log(index);
            this.this_click["child_name"] = "register_" + index;
            console.log(this.this_click);
        },

        //資料reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
    beforeRouteUpdate(to, from, next) {
        console.warn(to);
        console.warn(from);
        console.warn(next);
        this._reset([]);
        if (to.name === "sensor") {
            this.setFeatureMainData(this.featureMainData);
        } else {
            this.this_route = to.name;
            this.$nextTick(() => {
                window.addEventListener("resize", this._onResize);
                window.addEventListener("scroll", this._handleScroll);
                console.log("####");
            });
        }
        next();
    },
    //調用router守衛，在離開時將this_click reset
    beforeRouteLeave(to, from, next) {
        this._reset([]);
        next();
    },
    beforeDestroy() {
        let vm = this;
        window.removeEventListener("resize", vm._onResize);
        window.removeEventListener("scroll", vm._handleScroll);
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.publicMinWidth550 {
    min-width: 550px;
}
.edoc_router {
    // max-height: calc(100vh - 150px);
    // overflow-y: auto;
}
.card,
.card-header {
    border: 0;
}
.apipath {
    color: #007bff;
    cursor: pointer;
}
.apimethod {
    font-weight: bold;
    min-width: 60px;
    /* color: green; */
}
.bcardtext {
    display: flex;
    flex-direction: row;
    -webkit-box-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    align-items: center;
    height: 36px;
    min-height: 36px;
    margin: 5px;
    padding: 5px;
}
</style>

