<template>
  <div class="machineHealth">
    <b-container class="bv-example-row px-0">
      <b-row class="mx-0 text-center">
        <b-col
          v-for="(item, key) in machineHealthData"
          :key="key"
          cols="12"
          sm="12"
          md="6"
          lg="6"
          xl="4"
          class="px-3 m-0 mb-2"
        >
          <b-table
            :items="item.table.items"
            :fields="item.table.fields"
            :no-border-collapse="noCollapse"
            responsive
            class="machineHealth-table-detail"
          >
            <template #thead-top="">
              <b-tr>
                <b-th
                  colspan="5"
                  style="
                    background-color: #a3bedb;
                    color: #ffffff;
                    border-color: #a3bedb;
                    font-family: 'dfyuanstd-w7';
                    font-size: 1.125rem;
                  "
                >
                  <span>{{ item.name }}</span>
                </b-th>
              </b-tr>
