const state = {
    customerOptions:{}
};
const getters = {
  get_customerOptions: state =>{
    return state.customerOptions;
  },
};
const actions = {
  set_customerOptions(context,data){
    context.commit("SET_CUSTOMEROPTIONS",data);
  },
};
const mutations = {
  SET_CUSTOMEROPTIONS(state, data) {
    state.customerOptions = data;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
