<template>
    <div id="apisandbox">
        <b-row class="h-100 mh-100 p-2" style="margin: 0">
            <div
                class="h-100 mh-100 customize_scrollbar"
                style="overflow-y: auto; width: 40%"
            >
                <b-row class="h-100 mh-100">
                    <b-col xl="12" align-self="start">
                        <b-tabs
                            v-if="Object.keys(api_group).length != 0"
                            class="customizeTab"
                            v-model="tabIndex"
                        >
                            <b-tab
                                v-for="(item, index) in Object.keys(api_group)"
                                :key="index"
                                :title="item"
                            >
                                <b-row class="mt-1">
                                    <b-col
                                        v-for="(
                                            item2, index2, keys2
                                        ) in _sortKeys(api_group[item])"
                                        :key="keys2"
                                    >
                                        <b-button
                                            variant="light"
                                            @click.prevent="
                                                this_api_method = index2
                                            "
                                            :class="{
                                                publicButton:
                                                    this_api_method == index2,
                                            }"
                                            block
                                            >{{ index2 }}</b-button
                                        >
                                    </b-col>
                                </b-row>
                                <div
                                    class="customizeTab_div customize_scrollbar"
                                >
                                    <div
                                        v-for="(item3, index3) in api_group[
                                            item
                                        ][this_api_method]"
                                        :key="index3"
                                        class="bcardtext"
                                    >
                                        <span class="apimethod"
                                            >{{ item3[2] }}&nbsp;</span
                                        >
                                        <a
                                            class="apipath"
                                            @click="_getApiDoc(item3[1])"
                                            >{{ item3[4] }}</a
                                        >
                                    </div>
                                </div>
                            </b-tab>
                        </b-tabs>
                    </b-col>
                    <b-col xl="12" align-self="end"
                        ><strong
                            id="sidebar_footer"
                            class="mr-auto"
                            @click="
                                sidebar_show = false;
                                main_show = false;
                            "
                            >關於此工具</strong
                        ></b-col
                    >
                </b-row>
            </div>

            <div
                class="h-100 mh-100 customize_scrollbar featureContentInside"
                style="overflow-y: auto; width: 60%"
            >
                <div id="sandboxinfopage" v-if="!main_show">
                    <h3 class="text-center">可提供的測試資料表有以下</h3>
                    <h4>機台device</h4>
                    <b-table
                        :fields="fields"
                        :items="device_items"
                        :fixed="false"
                        responsive
                        :head-variant="'light'"
                    ></b-table>
                    <h4>場域place</h4>
                    <b-table
                        :fields="fields"
                        :items="place_items"
                        :fixed="false"
                        responsive
                        :head-variant="'light'"
                    ></b-table>
                </div>
                <div id="sandboxmain" v-else>
                    <!-- <div
                        class="page-header text-center"
                        style="border: 0; margin: 0"
                    >
                        <span class="apimethod">{{ main_apimethod }}</span
                        >&nbsp;&nbsp;
                        <span class="apipath">{{ main_apipath }}</span>
                    </div> -->
                    <div class="text-right">
                        <button
                            class="btn btn-primary ml-5"
                            type="button"
                            size="sm"
                            @click="toggleCommonModal(true)"
                        >
                            回覆訊息
                        </button>
                    </div>
                    <br />
                    <b-row>
                        <b-col>
                            <b-row>
                                <b-col sm="2">
                                    <label>URL</label>
                                </b-col>
                                <b-col sm="10">
                                    <b-form-input
                                        size="sm"
                                        readonly
                                        :value="'/api/' + main_apipath"
                                    ></b-form-input>
                                </b-col>
                            </b-row>
                            <b-row>
                                <b-col sm="2">
                                    <label>Request URL</label>
                                </b-col>
                                <b-col sm="10">
                                    <b-form-input
                                        size="sm"
                                        readonly
                                        :value="request_url"
                                    ></b-form-input>
                                </b-col>
                            </b-row>
                            <b-form @submit="_onSubmit">
                                <template
                                    v-if="apiDetail.doc.API_path_parameters"
                                >
                                    <b-card no-body class="mb-2 bkcolorgreen">
                                        <b-card-header
                                            header-tag="header"
                                            class="p-1 bkcolorgreen"
                                            role="tab"
                                        >
                                            <b-button
                                                variant="light"
                                                block
                                                style="font-weight: bold"
                                                class="bkcolorgreen"
                                                @click="
                                                    collapses_show.path_parameters = !collapses_show.path_parameters
                                                "
                                                >URL参数</b-button
                                            >
                                        </b-card-header>
                                        <b-collapse
                                            id="path_parameters"
                                            class="mt-2 mb-3 bcollapse"
                                            :visible="
                                                collapses_show.path_parameters
                                            "
                                        >
                                            <b-card-body>
                                                <b-row
                                                    v-for="(v, index) in $v
                                                        .valid_data
                                                        .path_parameters.$each
                                                        .$iter"
                                                    :key="index"
                                                    :class="{
                                                        'form-group--error':
                                                            v.value.$error,
                                                    }"
                                                >
                                                    <b-col sm="2">
                                                        <label
                                                            :for="v.$model.key"
                                                            >{{
                                                                v.$model.key
                                                            }}</label
                                                        >
                                                    </b-col>
                                                    <b-col sm="10">
                                                        <b-form-input
                                                            size="sm"
                                                            :id="
                                                                'path_parameters-' +
                                                                v.$model.key
                                                            "
                                                            ref="inputtext"
                                                            v-model.trim="
                                                                v.$model.value
                                                            "
                                                        ></b-form-input>
                                                        <template
                                                            v-if="
                                                                _checkRequired(
                                                                    v.value
                                                                        .required,
                                                                    v.$model
                                                                )
                                                            "
                                                        >
                                                            <div class="error">
                                                                Is required
                                                            </div>
                                                        </template>
                                                    </b-col>
                                                </b-row>
                                            </b-card-body>
                                        </b-collapse>
                                    </b-card>
                                </template>

                                <template v-if="apiDetail.doc.API_parameters">
                                    <b-card no-body class="mb-2 bkcolorgreen">
                                        <b-card-header
                                            header-tag="header"
                                            class="p-1 bkcolorgreen"
                                            role="tab"
                                        >
                                            <b-button
                                                variant="light"
                                                block
                                                style="font-weight: bold"
                                                class="bkcolorgreen"
                                                @click="
                                                    collapses_show.parameters = !collapses_show.parameters
                                                "
                                                >参数</b-button
                                            >
                                        </b-card-header>
                                        <b-collapse
                                            id="parameters"
                                            class="mt-2 mb-3 bcollapse"
                                            :visible="collapses_show.parameters"
                                        >
                                            <b-card-body>
                                                <b-row
                                                    v-for="(v, index) in $v
                                                        .valid_data.parameters
                                                        .$each.$iter"
                                                    :key="index"
                                                    :class="{
                                                        'form-group--error':
                                                            v.value.$error,
                                                    }"
                                                >
                                                    <b-col sm="2">
                                                        <label
                                                            :for="v.$model.key"
                                                            >{{
                                                                v.$model.key
                                                            }}</label
                                                        >
                                                    </b-col>
                                                    <b-col sm="10">
                                                        <b-form-input
                                                            size="sm"
                                                            :id="
                                                                'parameters-' +
                                                                v.$model.key
                                                            "
                                                            v-model.trim="
                                                                v.$model.value
                                                            "
                                                        ></b-form-input>
                                                        <template
                                                            v-if="
                                                                _checkRequired(
                                                                    v.value
                                                                        .required,
                                                                    v.$model
                                                                )
                                                            "
                                                        >
                                                            <div class="error">
                                                                Is required
                                                            </div>
                                                        </template>
                                                    </b-col>
                                                </b-row>
                                            </b-card-body>
                                        </b-collapse>
                                    </b-card>
                                </template>

                                <template v-if="apiDetail.doc.API_postData">
                                    <b-card no-body class="mb-2 bkcolorgreen">
                                        <b-card-header
                                            header-tag="header"
                                            class="p-1 bkcolorgreen"
                                            role="tab"
                                        >
                                            <b-button
                                                variant="light"
                                                block
                                                style="font-weight: bold"
                                                class="bkcolorgreen"
                                                @click="
                                                    collapses_show.postData = !collapses_show.postData
                                                "
                                                >POST DATA</b-button
                                            >
                                        </b-card-header>
                                        <b-collapse
                                            id="postData"
                                            class="mt-2 mb-3 bcollapse"
                                            :visible="collapses_show.postData"
                                        >
                                            <b-card-body>
                                                <template
                                                    v-if="
                                                        apiDetail.doc
                                                            .API_postData
                                                            .parameters
                                                    "
                                                >
                                                    <template
                                                        v-if="
                                                            apiDetail.doc
                                                                .API_postData
                                                                .bodyschema ==
                                                            '{}'
                                                        "
                                                    >
                                                        <b-row
                                                            v-for="(
                                                                v, index
                                                            ) in $v.valid_data
                                                                .postData.$each
                                                                .$iter"
                                                            :key="index"
                                                            :class="{
                                                                'form-group--error':
                                                                    v.value
                                                                        .$error,
                                                            }"
                                                        >
                                                            <b-col sm="2">
                                                                <label
                                                                    :for="
                                                                        v.$model
                                                                            .key
                                                                    "
                                                                    >{{
                                                                        v.$model
                                                                            .key
                                                                    }}</label
                                                                >
                                                            </b-col>
                                                            <b-col sm="10">
                                                                <b-form-input
                                                                    size="sm"
                                                                    :id="
                                                                        'postData-' +
                                                                        v.$model
                                                                            .key
                                                                    "
                                                                    v-model.trim="
                                                                        v.$model
                                                                            .value
                                                                    "
                                                                ></b-form-input>
                                                                <template
                                                                    v-if="
                                                                        _checkRequired(
                                                                            v
                                                                                .value
                                                                                .required,
                                                                            v.$model
                                                                        )
                                                                    "
                                                                >
                                                                    <div
                                                                        class="error"
                                                                    >
                                                                        Is
                                                                        required
                                                                    </div>
                                                                </template>
                                                                <template
                                                                    v-else
                                                                >
                                                                    <div
                                                                        class="jsonerror"
                                                                        v-if="
                                                                            _checkJsonValid(
                                                                                v
                                                                                    .value
                                                                                    .jsonvalidator,
                                                                                v.$model
                                                                            )
                                                                        "
                                                                    >
                                                                        不是正確的JSON格式，字串須加雙引號，陣列要以[
                                                                        ]包起來，物件要以{
                                                                        }包起來
                                                                    </div>
                                                                </template>
                                                            </b-col>
                                                        </b-row>
                                                    </template>
                                                    <template v-else>
                                                        <b-row
                                                            :class="{
                                                                'form-group--error':
                                                                    $v
                                                                        .valid_data
                                                                        .postData
                                                                        .$each
                                                                        .$iter[0]
                                                                        .value
                                                                        .$error,
                                                            }"
                                                        >
                                                            <b-col sm="12">
                                                                <p
                                                                    style="
                                                                        color: gray;
                                                                    "
                                                                >
                                                                    請在下面的textarea輸入postData
                                                                </p>
                                                                <b-form-textarea
                                                                    rows="15"
                                                                    max-rows="15"
                                                                    v-model.trim="
                                                                        $v
                                                                            .valid_data
                                                                            .postData
                                                                            .$each
                                                                            .$iter[0]
                                                                            .$model
                                                                            .value
                                                                    "
                                                                ></b-form-textarea>
                                                                <template
                                                                    v-if="
                                                                        _checkRequired(
                                                                            $v
                                                                                .valid_data
                                                                                .postData
                                                                                .$each
                                                                                .$iter[0]
                                                                                .value
                                                                                .required,
                                                                            $v
                                                                                .valid_data
                                                                                .postData
                                                                                .$each
                                                                                .$iter[0]
                                                                                .$model
                                                                        )
                                                                    "
                                                                >
                                                                    <div
                                                                        class="error"
                                                                    >
                                                                        Is
                                                                        required
                                                                    </div>
                                                                </template>
                                                                <template
                                                                    v-else
                                                                >
                                                                    <div
                                                                        class="jsonerror"
                                                                        v-if="
                                                                            _checkJsonValid(
                                                                                $v
                                                                                    .valid_data
                                                                                    .postData
                                                                                    .$each
                                                                                    .$iter[0]
                                                                                    .value
                                                                                    .jsonvalidator,
                                                                                $v
                                                                                    .valid_data
                                                                                    .postData
                                                                                    .$each
                                                                                    .$iter[0]
                                                                                    .$model
                                                                            )
                                                                        "
                                                                    >
                                                                        不是正確的JSON格式，字串須加雙引號，陣列要以[
                                                                        ]包起來，物件要以{
                                                                        }包起來
                                                                    </div>
                                                                </template>
                                                            </b-col>
                                                        </b-row>
                                                    </template>
                                                </template>
                                                <template v-else>
                                                    <b-row
                                                        v-for="(v, index) in $v
                                                            .valid_data
                                                            .add_postData.$each
                                                            .$iter"
                                                        :key="index"
                                                        style="
                                                            position: relative;
                                                        "
                                                    >
                                                        <div
                                                            class="delthis"
                                                            @click.prevent="
                                                                _delAddField(
                                                                    index
                                                                )
                                                            "
                                                        ></div>
                                                        <b-col
                                                            sm="2"
                                                            :class="{
                                                                'form-group--error':
                                                                    v.key
                                                                        .$error,
                                                            }"
                                                        >
                                                            <b-form-input
                                                                size="sm"
                                                                v-model.trim="
                                                                    v.$model.key
                                                                "
                                                            ></b-form-input>
                                                            <template
                                                                v-if="
                                                                    _checkAddPostDataKeyRequired(
                                                                        v.key
                                                                            .required,
                                                                        v.$model
                                                                    )
                                                                "
                                                            >
                                                                <div
                                                                    class="error"
                                                                >
                                                                    Is required
                                                                </div>
                                                            </template>
                                                            <template v-else>
                                                                <div
                                                                    class="jsonerror"
                                                                    v-if="
                                                                        _checkAddPostDataKeyJsonValid(
                                                                            v
                                                                                .key
                                                                                .jsonvalidator,
                                                                            v.$model
                                                                        )
                                                                    "
                                                                >
                                                                    不是正確的JSON格式，字串須加雙引號，陣列要以[
                                                                    ]包起來，物件要以{
                                                                    }包起來
                                                                </div>
                                                            </template>
                                                        </b-col>
                                                        <b-col
                                                            sm="10"
                                                            :class="{
                                                                'form-group--error':
                                                                    v.value
                                                                        .$error,
                                                            }"
                                                        >
                                                            <b-form-input
                                                                size="sm"
                                                                v-model.trim="
                                                                    v.$model
                                                                        .value
                                                                "
                                                            ></b-form-input>
                                                            <template
                                                                v-if="
                                                                    _checkAddPostDataValueRequired(
                                                                        v.value
                                                                            .required,
                                                                        v.$model
                                                                    )
                                                                "
                                                            >
                                                                <div
                                                                    class="error"
                                                                >
                                                                    Is required
                                                                </div>
                                                            </template>
                                                            <template v-else>
                                                                <div
                                                                    class="jsonerror"
                                                                    v-if="
                                                                        _checkAddPostDataValueJsonValid(
                                                                            v
                                                                                .value
                                                                                .jsonvalidator,
                                                                            v.$model
                                                                        )
                                                                    "
                                                                >
                                                                    不是正確的JSON格式，字串須加雙引號，陣列要以[
                                                                    ]包起來，物件要以{
                                                                    }包起來
                                                                </div>
                                                            </template>
                                                        </b-col>
                                                    </b-row>
                                                    <div class="text-center">
                                                        <button
                                                            type="button"
                                                            id="addfieldbtn"
                                                            class="btn btn-success"
                                                            @click="_addField()"
                                                        >
                                                            +
                                                        </button>
                                                    </div>
                                                </template>
                                            </b-card-body>
                                        </b-collapse>
                                    </b-card>
                                </template>

                                <div class="text-center">
                                    <button
                                        type="submit"
                                        class="btn btn-info"
                                        id="sandboxbtn"
                                        @click="$v.valid_data.$touch"
                                    >
                                        送出
                                    </button>
                                    <button
                                        class="btn btn-danger ml-5"
                                        type="reset"
                                        @click="_onReset"
                                    >
                                        清空
                                    </button>
                                </div>
                            </b-form>
                        </b-col>
                    </b-row>
                </div>
            </div>
        </b-row>

        <!-- 回覆訊息modal -->
        <modal>
            <template v-slot:modalheader>
                <h5>回覆訊息</h5>
            </template>
            <template v-slot:default>
                <template v-if="Object.keys(api_response_data).length === 0">
                    <h6>尚無資料</h6>
                </template>
                <template v-else>
                    <jsonView
                        :data="api_response_data"
                        :theme="''"
                        :deep="2"
                        :iconStyle="'triangle'"
                        :fontSize="15"
                        :lineHeight="24"
                        :closed="false"
                        :iconColor="[]"
                    />
                </template>
            </template>
            <template v-slot:modalfooter>
                <div class="w-100">
                    <b-button
                        variant="success"
                        size="sm"
                        class="float-right"
                        style="margin-right: 10px"
                        @click="toggleCommonModal(false)"
                        >close</b-button
                    >
                </div>
            </template>
        </modal>
    </div>
</template>

<script>
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import modal from "@/components/modal.vue";
import { validationMixin } from "vuelidate"; // 表單驗證
import { required, minLength, between } from "vuelidate/lib/validators";
import jsonView from "@/components/json-view/index.vue";

//設置json validator
const jsonvalidator = (jsonData) => {
    let status = true;
    try {
        JSON.parse(jsonData);
    } catch (e) {
        //console.log('Error data', e);
        status = false;
    }
    return status;
};
export default {
    name: "apisandbox",
    data() {
        let vm = this;
        return {
            tabIndex: 0,
            system: "APIDOC",
            main_show: false,
            sidebar_show: false,
            this_api_method: "GET",
            api_group: {},
            fields: [
                {
                    key: "Field",
                    thClass: "publicTH",
                    tdClass: "w67px",
                },
                {
                    key: "Type",
                    thClass: "publicTH",
                    tdClass: "w67px",
                },
                {
                    key: "Null",
                    thClass: "publicTH",
                    tdClass: "w67px",
                },
                {
                    key: "Key",
                    thClass: "publicTH",
                    tdClass: "w67px",
                },
                {
                    key: "Default",
                    thClass: "publicTH",
                    tdClass: "w67px",
                },
                {
                    key: "comment",
                    thClass: "publicTH",
                    tdClass: "w67px",
                },
            ],
            //說明頁的表格內容
            device_items: [
                {
                    Field: "devID",
                    Type: "varchar(15)",
                    Null: "NO",
                    Key: "PRI",
                    Default: "NULL",
                    comment: "設備編號",
                },
                {
                    Field: "devName",
                    Type: "varchar(128)",
                    Null: "YES",
                    Key: "",
                    Default: "NULL",
                    comment: "設備名稱",
                },
                {
                    Field: "category",
                    Type: "varchar(15)",
                    Null: "YES",
                    Key: "",
                    Default: "NULL",
                    comment: "類別",
                },
                {
                    Field: "devInfo",
                    Type: "varchar(256)",
                    Null: "YES",
                    Key: "",
                    Default: "NULL",
                    comment: "描述",
                },
                {
                    Field: "placeID",
                    Type: "varchar(15)",
                    Null: "YES",
                    Key: "",
                    Default: "NULL",
                    comment: "場域編號",
                },
                {
                    Field: "creatorID",
                    Type: "varchar(24)",
                    Null: "YES",
                    Key: "",
                    Default: "NULL",
                    comment: "創建者ID",
                },
                {
                    Field: "created_at",
                    Type: "timestamp",
                    Null: "NO",
                    Key: "",
                    Default: "current_timestamp()",
                    comment: "創建時間",
                },
                {
                    Field: "updated_at",
                    Type: "timestamp",
                    Null: "NO",
                    Key: "",
                    Default: "current_timestamp()",
                    comment: "最後修改時間",
                },
            ],
            place_items: [
                {
                    Field: "placeID",
                    Type: "varchar(15)",
                    Null: "NO",
                    Key: "PRI",
                    Default: "NULL",
                    comment: "場域編號",
                },
                {
                    Field: "placeName",
                    Type: "varchar(128)",
                    Null: "YES",
                    Key: "",
                    Default: "NULL",
                    comment: "場域名稱",
                },
                {
                    Field: "creatorID",
                    Type: "varchar(24)",
                    Null: "YES",
                    Key: "",
                    Default: "NULL",
                    comment: "創建者ID",
                },
                {
                    Field: "created_at",
                    Type: "timestamp",
                    Null: "NO",
                    Key: "",
                    Default: "current_timestamp()",
                    comment: "創建時間",
                },
                {
                    Field: "updated_at",
                    Type: "timestamp",
                    Null: "NO",
                    Key: "",
                    Default: "current_timestamp()",
                    comment: "最後修改時間",
                },
            ],
            //sandbox上方的apimethod&path
            main_apimethod: "",
            main_apipath: "",
            request_url: "",

            //所有api參數狀態
            api_parameter_status: {
                path_parameters: false,
                parameters: false,
                postData: false,
            },
            //各參數的collapses_show
            collapses_show: {
                path_parameters: false,
                parameters: false,
                postData: false,
            },
            valid_data: {
                path_parameters: [],
                parameters: [],
                postData: [],
                add_postData: [],
            },
            //api_response
            api_response_data: {},
            //set axios
            axiox_methods: {
                GET: this.axiosGetAction,
                POST: this.axiosPostAction,
                PATCH: this.axiosPatchAction,
                DELETE: this.axiosDeleteAction,
            },
            api_group_config: {
                my: ["管理性資料庫", "MariaDB"],
                ms: ["管理性資料庫", "MSSQL"],
                myps: ["跨資料庫", "Mix"],
                ps: ["串流資料庫", "Postgres"],
                other: ["其他", "Other"],
            },
        };
    },
    watch: {
        tabIndex: {
            handler(value) {
                console.log(value);
                console.log(Object.keys(this.api_group)[value]);
                this.this_api_method = Object.keys(
                    this._sortKeys(
                        this.api_group[Object.keys(this.api_group)[value]]
                    )
                )[0];
            },
        },
    },
    // 表單驗證引入
    mixins: [validationMixin],
    //驗證欄位參數
    validations: {
        valid_data: {
            path_parameters: {
                $each: {
                    value: {
                        required,
                    },
                },
            },
            parameters: {
                $each: {
                    value: {
                        required,
                    },
                },
            },
            postData: {
                $each: {
                    value: {
                        required,
                        jsonvalidator,
                    },
                },
            },
            add_postData: {
                $each: {
                    value: {
                        required,
                        jsonvalidator,
                    },
                    key: {
                        required,
                        jsonvalidator,
                    },
                },
            },
        },
    },
    created() {
        this._getSandboxApiMap();

        let commonModalConfig = JSON.parse(
            JSON.stringify(this.DEFAULT_commonModalConfig)
        );
        commonModalConfig.size = "xl";
        console.log(commonModalConfig);
        this.setCommonModalConfig(commonModalConfig);
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
            apiDetail: "apidetail/get_apiDetail",
            sandboxApiMap: "apidetail/get_sandboxApiMap",
            DEFAULT_commonModalConfig: "usemodal/get_DEFAULT_commonModalConfig",
        }),
    },
    components: {
        modal,
        jsonView,
    },
    methods: {
        ...mapActions({
            axiosGetAction: "commonaxios/axiosGetAction",
            axiosPostAction: "commonaxios/axiosPostAction",
            axiosPatchAction: "commonaxios/axiosPatchAction",
            axiosDeleteAction: "commonaxios/axiosDeleteAction",
            setApiDetail: "apidetail/set_apiDetail",
            setSandboxApiMap: "apidetail/set_sandboxApiMap",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            setCommonModalConfig: "usemodal/set_commonModalConfig",
            toggleCommonModal: "usemodal/toggle_commonModal",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
        }),

        _getSandboxApiMap() {
            let vm = this;
            var params = {};
            params["url"] = "/sandbox";
            vm.axiosGetAction(params).then(() => {
                var result = vm.axiosResult;
                console.log(result);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] === "ok") {
                    vm.setSandboxApiMap(result.data["api_map"]);
                    vm._renderingApiList();
                } else {
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
        },

        //整理分組api
        _renderingApiList() {
            let vm = this;
            console.log(vm.sandboxApiMap);
            let obj = {};
            vm.sandboxApiMap.forEach((element) => {
                if (typeof obj[element[3].split("/")[1]] === "undefined") {
                    obj[element[3].split("/")[1]] = {};
                }

                if (
                    typeof obj[element[3].split("/")[1]][element[2]] ===
                    "undefined"
                ) {
                    obj[element[3].split("/")[1]][element[2]] = [element];
                } else {
                    obj[element[3].split("/")[1]][element[2]].push(element);
                }
            });
            console.log(obj);
            vm.api_group = JSON.parse(JSON.stringify(obj));
            console.log(vm.api_group);
        },

        //抓取個別api文件
        _getApiDoc(endpoint) {
            var vm = this;
            vm.toggleAlertModal(true);

            var params = {};
            params["url"] = "/edoc/" + endpoint;
            console.log(params);

            vm.axiosGetAction(params).then(() => {
                vm.toggleAlertModal(false);
                var result = vm.axiosResult;
                console.log(result);
                if (result.status != 200) {
                    vm.setSetTimeOutAlertMsg(result.data);
                    vm.toggleSetTimeOutAlertModal();
                    return;
                }
                if (result.data["Response"] == "ok") {
                    vm.setApiDetail(result.data["api"]);
                    console.log(vm.apiDetail);
                    vm.main_apimethod = vm.apiDetail["methods"];
                    vm.main_apipath = vm.apiDetail["url"];
                    vm.sidebar_show = false;
                    vm.main_show = true;
                    vm._onReset();
                    vm._checkApiData();
                } else {
                    vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    vm.toggleSetTimeOutAlertModal();
                }
            });
