        _sortKeys(data) {
            const ordered = {};
            Object.keys(data)
                .sort((a, b) => {
                    if (a == "GET") return -1;
                    if (b == "GET") return 1;
                    return a - b;
                })
                .forEach(function (key) {
                    ordered[key] = data[key];
                });
            console.log(ordered);
            return ordered;
        },

        //檢查此api有哪些參數要顯示
        _checkApiData() {
            let vm = this;
            if (vm.apiDetail.doc.API_path_parameters) {
                vm.api_parameter_status.path_parameters = true;
                Object.keys(vm.apiDetail.doc.API_path_parameters).forEach(
                    (element) => {
                        vm._renderingValidData("path_parameters", element);
                    }
                );
            }
            if (vm.apiDetail.doc.API_parameters) {
                vm.api_parameter_status.parameters = true;
                Object.keys(vm.apiDetail.doc.API_parameters).forEach(
                    (element) => {
                        vm._renderingValidData("parameters", element);
                    }
                );
            }
            if (vm.apiDetail.doc.API_postData) {
                vm.api_parameter_status.postData = true;
                if (vm.apiDetail.doc.API_postData.parameters) {
                    console.log(vm.apiDetail.doc.API_postData.bodyschema);
                    if (vm.apiDetail.doc.API_postData.bodyschema == "{}") {
                        Object.keys(
                            vm.apiDetail.doc.API_postData.parameters
                        ).forEach((element) => {
                            vm._renderingValidData("postData", element);
                        });
                    } else {
                        vm._renderingValidData("postData", "textareaval");
                    }
                } else {
                    // vm._renderingValidData("add_postData", "");
                }
            }
        },

        //初始化參數欄位驗證data
        _renderingValidData(key, data) {
            let vm = this;
            let tempobj = {
                key: data,
                value: "",
                invalid: false,
                jsonvalid: false,
            };
            if (key === "add_postData") {
                tempobj["key_invalid"] = false;
                tempobj["key_jsonvalid"] = false;
            }
            vm.valid_data[key].push(tempobj);
        },

        //檢查資料欄位->是否有值＆JSON格式合法
        _checkRequired(required, model) {
            model.invalid = required;
            return !required;
        },
        _checkJsonValid(jsonvalid, model) {
            model.jsonvalid = jsonvalid;
            return !jsonvalid;
        },

        _checkAddPostDataKeyRequired(required, model) {
            model.key_invalid = required;
            return !required;
        },

        _checkAddPostDataValueRequired(required, model) {
            model.invalid = required;
            return !required;
        },

        _checkAddPostDataKeyJsonValid(jsonvalid, model) {
            model.key_jsonvalid = jsonvalid;
            return !jsonvalid;
        },

        _checkAddPostDataValueJsonValid(jsonvalid, model) {
            model.jsonvalid = jsonvalid;
            return !jsonvalid;
        },
        //////////////↑////////////////

        //動態新增欄位add_postData
        _addField() {
            let vm = this;
            vm._renderingValidData("add_postData", "");
            console.log(vm.valid_data.add_postData[0].key_invalid);
        },

        //動態刪除欄位add_postData
        _delAddField(thiskey) {
            console.log(thiskey);
            let vm = this;
            vm.valid_data["add_postData"].splice(thiskey, 1);
        },

        //送出
        _onSubmit(evt) {
            evt.preventDefault();
            let vm = this;
            let status = true;
            vm.toggleAlertModal(true);
            Object.entries(vm.api_parameter_status).forEach((item) => {
                if (item[1]) {
                    vm.collapses_show[item[0]] = true;
                    vm.valid_data[item[0]].forEach((element) => {
                        if (!element.invalid) {
                            status = false;
                        } else {
                            if (
                                element.key === "textareaval" &&
                                !element.jsonvalid
                            ) {
                                status = false;
                            }
                        }
                    });
                    vm.valid_data["add_postData"].forEach((element) => {
                        if (
                            !(
                                element.invalid &&
                                element.jsonvalid &&
                                element.key_invalid &&
                                element.key_jsonvalid
                            )
                        ) {
                            vm.collapses_show["postData"] = true;
                            status = false;
                        }
                    });
                }
            });
            if (status) {
                console.log("call api");
                vm._callApi();
            } else {
                vm.toggleAlertModal(false);
            }
        },

        _callApi() {
            let vm = this;
            console.log(vm.valid_data);
            //組apipath
            let tempurl = vm.main_apipath;
            vm.valid_data.path_parameters.forEach((element) => {
                let tempregkey = "<" + element.key + ">";
                var re = new RegExp(tempregkey, "g");
                tempurl = tempurl.replace(new RegExp(re, "g"), element.value);
            });
            vm.request_url = "/api/" + tempurl;
            //組url後面的參數
            let tempparams = [];
            vm.valid_data.parameters.forEach((element) => {
                tempparams.push(element.key + "=" + element.value);
            });
            if (tempparams.length != 0) {
                vm.request_url = vm.request_url + "?" + tempparams.join("&");
            }
            console.log(vm.request_url);
            //組postData
            let temparray = ["postData", "add_postData"];
            let temppostdata = {};
            temparray.forEach((item) => {
                vm.valid_data[item].forEach((element) => {
                    if (element.key === "textareaval") {
                        temppostdata = {
                            ...temppostdata,
                            ...JSON.parse(element.value),
                        };
                    } else {
                        temppostdata[element.key] = JSON.parse(element.value);
                    }
                });
            });
            console.log(temppostdata);

            var params = {};
            params["url"] = vm.request_url;
            params["postdata"] = temppostdata;

            console.log(params);
            vm.axiox_methods[vm.main_apimethod](params).then(() => {
                vm._apiResponse();
            });
        },

        //reset
        _onReset(evt) {
            let vm = this;
            vm.api_response_data = {};
            vm.request_url = "";
            console.log(evt);
            //evt存在時表示是由form表單觸發的reset
            if (evt) {
                evt.preventDefault();
                Object.entries(vm.api_parameter_status).forEach((item) => {
                    if (item[1]) {
                        vm.collapses_show[item[0]] = true;
                        vm.valid_data[item[0]].forEach((element) => {
                            element.value = null;
                        });
                    }
                });
                vm.valid_data.add_postData = [];
            } else {
                //reset valid_data
                vm.valid_data = {
                    path_parameters: [],
                    parameters: [],
                    postData: [],
                    add_postData: [],
                };
                //reset collapses_show
                vm.collapses_show = {
                    path_parameters: false,
                    parameters: false,
                    postData: false,
                };
            }
            vm.$nextTick(() => {
                console.log(vm.$v.$reset());
                vm.$v.$reset();
            });
        },

        _apiResponse() {
            let vm = this;
            vm.toggleAlertModal(false);
            var result = vm.axiosResult;
            console.log(result);
            vm.api_response_data = result.data;
            vm.toggleCommonModal(true);
        },
    },
};
</script>

