const state = {
    operateLogDataFields: [
        {
            key: "status",
            label: "狀態",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "alarmCode",
            label: "警報代碼",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "alarmDetail",
            label: "警報代碼說明",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "startTime",
            label: "開始時間",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "endTime",
            label: "結束時間",
            sortable: true,
            thClass: "publicTH"
        },
        {
            key: "durationTime",
            label: "持續時間",
            sortable: true,
            thClass: "publicTH"
        }
    ],
    operateLogDataItem: [],
}
const getters = {
    get_operateLogDataFields: state => {
        return state.operateLogDataFields
    },
    get_operateLogDataItem: state => {
        return state.operateLogDataItem
    },
}
const actions = {
    set_operateLogDataFields(context, array) {
        context.commit('SET_OPERATELOGDATAFIELDS', array);
    },
    set_operateLogDataItem(context, array) {
        context.commit('SET_OPERATELOGDATAITEM', array);
    },
    clear_operateLogDataItem(context, array) {
        context.commit('CLEAR_OPERATELOGDATAITEM', array);
    },
}
const mutations = {
    SET_OPERATELOGDATAFIELDS(state, array) {
        state.operateLogDataItem = array;
    },
    SET_OPERATELOGDATAITEM(state, array) {
        state.operateLogDataItem = array;
    },
    CLEAR_OPERATELOGDATAITEM(state, array) {
        state.operateLogDataItem = [];
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
